import React from 'react';
import styled from 'styled-components';
import { SidePanel } from './SidePanel';

export interface ILayoutProps {
  /**
   * 左侧面版 jsx
   */
  leftPanel?: JSX.Element;
  /**
   * 右侧面版 jsx
   */
  rightPanel?: JSX.Element;
  /**
   * 右侧面版 jsx
   */
  topPanel?: JSX.Element;
  /**
   *  底部面版 jsx
   */
  bottomPanel?: JSX.Element;
  /**
   * 左侧面板最大的允许宽度
   */
  maxLeftPanelWidth?: number;
  /**
   * 左侧面板最小的允许宽度
   */
  minLeftPanelWidth?: number;
  /**
   * 右侧面板最大的允许宽度
   */
  maxRightPanelWidth?: number;
  /**
   * 右侧面板最小的允许宽度
   */
  minRightPanelWidth?: number;
  /**
   * 左侧面板宽度
   */
  leftPanelWidth?: number;
  /**
   * 右侧面板宽度
   */
  rightPanelWidth?: number;
  /**
   * 是否允许右侧面板调整宽度
   */
  allowRightPanelResize?: boolean;
  /**
   * 是否允许左侧面板调整宽度
   */
  allowLeftPanelResize?: boolean;
  /**
   * 侧面板宽度回调
   */
  onSidePanelWidthChange?: (width: number, side: string) => void;
  /**
   * 是否允许右侧面板关闭
   */
  allowRightPanelClose?: boolean;
  /**
   * 是否允许左侧面板关闭
   */
  allowLeftPanelClose?: boolean;
  /**
   * 右侧面板关闭按钮回调
   */
  onRightPanelClose?: () => void;
  /**
   * 左侧面板关闭按钮回调
   */
  onLeftPanelClose?: () => void;
  /**
   * 样式
   */
  style?: any;
}

const Container = styled.div`
  display:flex;
  flex-direction:column;
  height:100vh;
`;

const Main = styled.div`
  flex:1;
  display:flex;
  flex-direction:row;
  height:0;
`;

const Center = styled.div`
  flex:1;
  width:0;
`;

export const Layout: React.FC<ILayoutProps> = ({
  leftPanel,
  allowLeftPanelResize,
  allowRightPanelResize,
  maxLeftPanelWidth = Infinity,
  minLeftPanelWidth = 0,
  leftPanelWidth,
  rightPanelWidth,
  maxRightPanelWidth = Infinity,
  minRightPanelWidth = 0,
  rightPanel,
  topPanel,
  bottomPanel,
  children,
  allowLeftPanelClose,
  allowRightPanelClose,
  onLeftPanelClose,
  onRightPanelClose,
  onSidePanelWidthChange,
  style,
}) => (
  <Container style={style}>
    {topPanel ? <div>{topPanel}</div> : null}
    <Main>
      {leftPanel ? (
        <SidePanel
          closable={allowLeftPanelClose}
          onClose={onLeftPanelClose}
          width={leftPanelWidth}
          isLeft
          onPanelWidthChange={onSidePanelWidthChange}
          maxWidth={maxLeftPanelWidth}
          minWidth={minLeftPanelWidth}
          allowResize={allowLeftPanelResize}
        >
          {leftPanel}
        </SidePanel>
      ) : null}
      <Center>
        {children}
      </Center>
      {rightPanel ? (
        <SidePanel
          closable={allowRightPanelClose}
          onClose={onRightPanelClose}
          width={rightPanelWidth}
          maxWidth={maxRightPanelWidth}
          minWidth={minRightPanelWidth}
          onPanelWidthChange={onSidePanelWidthChange}
          allowResize={allowRightPanelResize}
        >
          {rightPanel}
        </SidePanel>
      ) : null}
    </Main>
    {bottomPanel ? <div>{bottomPanel}</div> : null}
  </Container>
);
