import React, { ReactNode } from 'react';

export const isSelectOption = (child: ReactNode) => {
  return React.isValidElement(child)
    && child.type
    && (child.type as React.FC).displayName === 'SelectOption';
};

export const isSelectOptionGroup = (child: ReactNode) => {
  return React.isValidElement(child)
    && child.type
    && (child.type as React.FC).displayName === 'SelectOptionGroup';
};

export const isMultiSelectOption = (child: ReactNode) => {
  return React.isValidElement(child)
    && child.type
    && (child.type as React.FC).displayName === 'MultiSelectOption';
};
