import React, { useContext } from 'react';
import { IMenuItemGroupProps } from './interface';
import {
  MenuItemGroupWrap,
  MenuItemGroupTitle,
  MenuItemGroupList,
} from './styled';
import { TextIndentContext } from './contexts';

export const MenuItemGroup: React.FC<IMenuItemGroupProps> = React.memo(
  props => {
    const { title, children, role } = props;
    // 锁进
    const textIndent = useContext(TextIndentContext);

    return title ? (
      <MenuItemGroupWrap data-role={role}>
        <MenuItemGroupTitle>{title}</MenuItemGroupTitle>
        <MenuItemGroupList>
          <TextIndentContext.Provider value={textIndent + 1}>
            {children}
          </TextIndentContext.Provider>
        </MenuItemGroupList>
      </MenuItemGroupWrap>
    ) : (
      (children as React.ReactElement)
    );
  },
);
