/* eslint-disable max-lines-per-function */
/* eslint-disable no-nested-ternary */
/* eslint-disable no-param-reassign */
/* eslint-disable react/no-array-index-key */
import React, { useCallback, useMemo, useRef } from 'react'
import { DragDropContext, Droppable, Draggable, DropResult } from 'react-beautiful-dnd'
import tinycolor from 'tinycolor2'
import { uuid, uniqBy, isEqual, throttle } from '../../utils'
import { Modal } from '../../components/modal'
import { Button } from '../../components/button'
import { useTranslate } from '@ali/we-util'
import useControlledValue from '../../hooks/useControlledValue'
import { PlusNormal, DeleteNormal, BgfontcolorNormal } from '../../components/icons'
import type { IOptionsModalOption, IOptionsModalProps, IOptionsModalTheme } from './interface'
import {
  OptionsWrap,
  OptionItem,
  FooterWrap,
  TitleWrap,
  RightButtons,
  ButtonInner,
  ColorsHeaderWrap,
  TipTextWrap,
} from './styled'
import { DragableIcon } from './DragableIcon'
import { Input } from '../input'
import { message } from '../message'
import { ColorPicker } from '../color-picker'
import { composeRef } from '../../utils/ref'
import { themes as defaultThemes } from './themes'
import { Theme } from './Theme'
import useMemorizeCallback from '../../hooks/useMemorizeCallback'

const throttleMessage = throttle((msg: string) => message.error(msg), 3000)

const isTheme = (themeColors: string[], colors: string[]) => {
  const themeColorsFixed: string[] = []
  for (let i = 0, len = colors.length; i < len; i++) {
    const colorIndex = i % themeColors.length
    themeColorsFixed.push(themeColors[colorIndex])
  }
  return isEqual(
    themeColorsFixed.map((c) => c.toLowerCase()),
    colors.map((c) => c.toLowerCase()),
  )
}

// eslint-disable-next-line max-lines-per-function
export const OptionsModal: React.FC<IOptionsModalProps> = React.memo((props) => {
  const {
    onChangeOptions,
    removable,
    onOk,
    onClickRemove,
    zIndex,
    testid,
    themes,
    tipText,
    ...restProps
  } = props

  const optionsWrapRef = useRef<HTMLDivElement>(null)
  const [options, setOptions] = useControlledValue(props.options, props.defaultOptions)

  // 国际化文案
  const t = useTranslate(props.locale)
  const title = t('we_ui_options_modal_title')
  const okText = t('we_ui_ok_text')
  const cancelText = t('we_ui_cancel_text')
  const removeText = t('we_ui_options_modal_clear')
  const warningText = t('we_ui_options_modal_warning_message')
  const colorListLabel = t('we_ui_options_modal_color_list_label')
  const addText = t('we_ui_options_modal_add_item')

  // 给每个option增加ID
  const optionsWithId = useMemo(() => {
    return options!.map((option) => {
      return { ...option, id: option.id || uuid() }
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [options!.length])

  // ID不能变化，不然输入框会失去焦点，但是值需要同步
  options!.forEach((option, index) => {
    optionsWithId[index].id = option.id || optionsWithId[index].id
    optionsWithId[index].value = option.value
    optionsWithId[index].color = option.color
  })

  // 主题增加无颜色
  const allThemes = useMemo<IOptionsModalTheme[]>(() => {
    return [
      {
        iconColors: [],
        colors: [],
      },
      ...(themes || []),
    ]
  }, [themes])

  // 计算当前主题的 index
  const currentThemeIndex = useMemo(() => {
    if (options!.every((o) => !o.color)) {
      return 0
    }
    const colors = options!.map((o) => o.color || '')
    let themeIndex = -1
    themes!.some((theme, index) => {
      if (isTheme(theme.colors, colors)) {
        themeIndex = index + 1
        return true
      }
      return false
    })
    return themeIndex
  }, [options, themes])

  const notNullThemeIndex = useRef(currentThemeIndex)

  if (currentThemeIndex !== -1) {
    notNullThemeIndex.current = currentThemeIndex
  }

  /**
   * 输入框输入
   *
   * @param {number} index
   * @param {React.ChangeEvent<HTMLInputElement>} e
   */
  const changeValue = useMemorizeCallback((index: number) => {
    return (e: React.ChangeEvent<HTMLInputElement>) => {
      const { value } = e.target
      setOptions((oldOptions) => {
        const newOptions = [...oldOptions!]
        newOptions[index] = { ...newOptions[index], value }
        if (onChangeOptions) {
          onChangeOptions(newOptions)
        }
        return newOptions
      })
    }
  })

  /**
   * 添加单项
   */
  const addOption = useCallback(() => {
    const currentTheme = allThemes[notNullThemeIndex.current]
    setOptions((oldOptions) => {
      const newOption: IOptionsModalOption =
        currentThemeIndex === 0 || !currentTheme
          ? { value: '', id: uuid() }
          : {
              color: currentTheme.colors[oldOptions!.length % currentTheme.colors.length],
              value: '',
              id: uuid(),
            }
      const newOptions = [...oldOptions!, newOption]
      if (onChangeOptions) {
        onChangeOptions(newOptions)
      }
      return newOptions
    })
    // 效果为自动滚动到底部
    setTimeout(() => {
      if (optionsWrapRef.current) {
        optionsWrapRef.current.scrollTop = optionsWrapRef.current.scrollHeight
      }
    }, 0)
  }, [currentThemeIndex, allThemes, onChangeOptions, setOptions])

  /**
   * 点击确定按钮处理函数
   *
   * @return {void}
   */
  const handleOk = useCallback(() => {
    const uniqOptions: IOptionsModalOption[] = uniqBy(options!, 'value')
      .filter((o) => o.value)
      .map((o) => {
        const newOption = { ...o }
        if (typeof newOption.color === 'undefined') {
          delete newOption.color
        } else if (typeof newOption.color !== 'undefined') {
          newOption.color = tinycolor(newOption.color).toString()
        }
        return newOption
      })
    if (uniqOptions.length < 2) {
      throttleMessage(warningText)
    } else {
      if (onOk) {
        onOk(uniqOptions)
      }
      if (onChangeOptions) {
        onChangeOptions(uniqOptions)
      }
      setOptions(uniqOptions)
    }
  }, [options, setOptions, onOk, warningText, onChangeOptions])

  /**
   * 删除选项
   *
   * @param {number} index
   */
  const deleteOption = useMemorizeCallback((index: number) => {
    return () => {
      setOptions((oldOptions) => {
        const newOptions = [...oldOptions!]
        newOptions.splice(index, 1)
        if (onChangeOptions) {
          onChangeOptions(newOptions)
        }
        return newOptions
      })
    }
  })

  /**
   * 拖拽结束更换顺序
   *
   * @param {DropResult} e
   */
  const handleDragEnd = useCallback(
    (e: DropResult) => {
      const { destination, source } = e
      if (destination && destination.index !== source.index) {
        const { index: destinationIndex } = destination
        const { index: sourceIndex } = source
        setOptions((oldOptions) => {
          const newOptions = [...oldOptions!]
          const sourceOption = newOptions![sourceIndex]
          newOptions.splice(sourceIndex, 1)
          newOptions.splice(destinationIndex, 0, sourceOption)
          if (onChangeOptions) {
            onChangeOptions(newOptions)
          }
          return newOptions
        })
      }
    },
    [onChangeOptions, setOptions],
  )

  /**
   * 修改颜色
   *
   * @param {number} index
   * @param {string} color
   */
  const changeColor = useMemorizeCallback((index: number) => {
    return (color: string) => {
      setOptions((oldOptions) => {
        const newOptions = [...oldOptions!]
        newOptions[index] = { ...newOptions[index], color }
        if (onChangeOptions) {
          onChangeOptions(newOptions)
        }
        return newOptions
      })
    }
  })

  /**
   * 修改主题
   *
   * @param {number} index
   * @param {IOptionsModalTheme} theme
   */
  const changeTheme = useMemorizeCallback((index: number, theme: IOptionsModalTheme) => {
    return () => {
      const { colors: themeColors } = theme
      setOptions((oldOptions) => {
        const newOptions = oldOptions!.map((o) => ({ ...o }))
        newOptions.forEach((option, optionIndex) => {
          if (index === 0) {
            delete option.color
          } else {
            option.color = themeColors[optionIndex % themeColors.length]
          }
        })
        if (onChangeOptions) {
          onChangeOptions(newOptions)
        }
        return newOptions
      })
    }
  })

  return (
    <Modal
      zIndex={zIndex}
      testid={testid}
      title={<TitleWrap>{title}</TitleWrap>}
      width={360}
      footer={
        <FooterWrap>
          <Button
            shape="round"
            onClick={onClickRemove}
            disabled={!removable}
            testid={`${testid}_remove`}
          >
            {removeText}
          </Button>
          <RightButtons>
            <Button
              shape="round"
              onClick={props.onCancel}
              style={{ marginRight: 8 }}
              testid={`${testid}_cancel`}
            >
              {cancelText}
            </Button>
            <Button shape="round" type="primary" onClick={handleOk} testid={`${testid}_ok`}>
              {okText}
            </Button>
          </RightButtons>
        </FooterWrap>
      }
      closable={false}
      {...restProps}
    >
      <ColorsHeaderWrap>
        <label>{colorListLabel}：</label>
        <ul>
          {allThemes.map((theme, index) => {
            return (
              <li
                key={`${index}`}
                className={index === currentThemeIndex ? 'active' : undefined}
                onClick={changeTheme(index, theme)}
              >
                <Theme theme={theme} />
                <i className="rect" />
              </li>
            )
          })}
        </ul>
      </ColorsHeaderWrap>
      {!!tipText && <TipTextWrap>{tipText}</TipTextWrap>}
      <DragDropContext onDragEnd={handleDragEnd}>
        <Droppable droppableId="table-dropdown-list">
          {(dropProvided) => (
            <OptionsWrap
              ref={composeRef(optionsWrapRef, dropProvided.innerRef)}
              {...dropProvided.droppableProps}
            >
              {optionsWithId.map((option, index) => {
                const isWhite = !option.color || tinycolor.equals('#ffffff', option.color)
                const pathColors = isWhite
                  ? ['rgba(17,31,44,0.72)', '#ffffff', 'rgba(17,31,44,0.72)']
                  : [option.color!, option.color!, 'rgba(17,31,44,0.72)']

                const isLight = option.color ? tinycolor(option.color).isLight() : true
                const placeholderColor = isLight
                  ? 'rgba(17, 31, 44, 0.56)'
                  : 'rgba(255, 255, 255, 0.56)'
                const color = isLight ? 'rgba(17, 31, 44, 1)' : 'rgba(255, 255, 255, 1)'

                return (
                  <Draggable key={option.id} draggableId={option.id} index={index}>
                    {(dragProvider) => (
                      <OptionItem
                        ref={dragProvider.innerRef}
                        {...dragProvider.draggableProps}
                        {...dragProvider.dragHandleProps}
                        tabIndex="-1"
                      >
                        <DragableIcon />
                        <Input
                          className="option-input"
                          value={option.value}
                          onChange={changeValue(index, onChangeOptions)}
                          testid={testid}
                          placeholderColor={placeholderColor}
                          style={{
                            backgroundColor: option.color ? option.color : 'transparent',
                          }}
                          inputStyle={{ color }}
                        />
                        <ColorPicker
                          color={option.color}
                          onColorPicker={changeColor(index, onChangeOptions)}
                          zIndex={typeof zIndex === 'number' ? zIndex + 1 : undefined}
                          colorBlockText="A"
                          automaticColor="rgba(1,2,3,0)"
                          clearColor="rgba(1,2,3,0)"
                        >
                          <Button
                            bordered={false}
                            style={{ marginLeft: 4 }}
                            icon={
                              <BgfontcolorNormal
                                testid={`${testid}_color`}
                                pathColors={pathColors}
                              />
                            }
                          />
                        </ColorPicker>
                        <Button
                          bordered={false}
                          disabled={optionsWithId.length <= 2}
                          icon={<DeleteNormal className="close-icon" testid={`${testid}_delete`} />}
                          onClick={deleteOption(index, onChangeOptions)}
                        />
                      </OptionItem>
                    )}
                  </Draggable>
                )
              })}
              {dropProvided.placeholder}
            </OptionsWrap>
          )}
        </Droppable>
      </DragDropContext>
      <Button
        type="text"
        size="small"
        onClick={addOption}
        testid={testid}
        style={{ lineHeight: 1, height: 'auto' }}
      >
        <ButtonInner>
          <PlusNormal className="add-icon" />
          <span className="text">{addText}</span>
        </ButtonInner>
      </Button>
    </Modal>
  )
})

OptionsModal.defaultProps = {
  defaultOptions: [
    { value: '', color: defaultThemes[0].colors[0] },
    { value: '', color: defaultThemes[0].colors[1] },
    { value: '', color: defaultThemes[0].colors[2] },
  ],
  themes: defaultThemes,
  removable: true,
  zIndex: 500,
}
