import { useContext } from 'react';
import { FileMenuContext } from './context';

/**
 * 获取 Controller
 *
 * @param {String} name Component's name
 * @return {Controller | never}
 */
export const useController = (name: string) => {
  const ctrl = useContext(FileMenuContext);
  if (!ctrl) {
    throw new Error(`${name} 组件必须为 FileMenu 组件的子组件`);
  }
  return ctrl;
};
