/* eslint-disable no-param-reassign */
import React, { useCallback } from 'react';
import { SwitchButton, SwitchInner } from './styled';
import useControlledValue from '../../hooks/useControlledValue';
import { KEY_CODES } from '../../utils/consts';
import { Tooltip } from '../tooltip';

export interface ISwitchProps {
  /**
   * 指定当前是否选中
   */
  checked?: boolean;
  /**
   * 初始是否选中
   */
  defaultChecked?: boolean;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 尺寸
   */
  size?: 'default' | 'small';
  /**
   * 变化时回调函数
   */
  onChange?: (e: boolean) => void;
  /**
   * Switch容器类名
   */
  className?: string;
  /**
   * ID
   */
  id?: string;
  /**
   * Switch容器样式
   */
  style?: React.CSSProperties;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 测试使用
   */
  testid?: string;
  /**
   * 是否自动 focus
   */
  autoFocus?: boolean;
}

const Switch: React.FC<ISwitchProps> = React.memo(((props:ISwitchProps) => {
  const {
    className,
    style,
    onChange,
    size,
    disabled,
    id,
    title,
    testid,
    autoFocus,
  } = props;

  const [checked, setChecked] = useControlledValue(props.checked, props.defaultChecked);

  /**
   * 变化处理函数
   *
   * @param {boolean|undefined}
   * @return void
   */
  const handleToggleChecked = useCallback((newValue?: boolean) => {
    if (typeof newValue !== 'boolean') {
      newValue = !checked;
    }
    if (newValue !== checked && !disabled) {
      if (onChange) {
        onChange(newValue);
      }
      setChecked(newValue);
    }
  }, [checked, disabled, setChecked, onChange]);

  /**
   * 键盘左右键处理
   *
   * @param {React.KeyboardEvent<HTMLButtonElement>} e
   * @return {void}
   */
  const handleKeyDown = useCallback((e: React.KeyboardEvent<HTMLButtonElement>) => {
    if (e.which === KEY_CODES.LEFT) {
      handleToggleChecked(false);
    } else if (e.which === KEY_CODES.RIGHT) {
      handleToggleChecked(true);
    }
  }, [handleToggleChecked]);

  const switchNode = (
    <SwitchButton
      autoFocus={autoFocus}
      checked={checked}
      className={className}
      style={style}
      onClick={handleToggleChecked}
      onKeyDown={handleKeyDown}
      size={size}
      disabled={disabled}
      id={id}
      data-testid={testid ? `Switch-${testid}` : undefined}
    >
      <SwitchInner size={size} checked={checked} />
    </SwitchButton>
  );

  return title ? (
    <Tooltip title={title}>
      {switchNode}
    </Tooltip>
  ) : switchNode;
}));

Switch.displayName = 'Switch';

export { Switch };
