import * as React from 'react';
import * as ReactDOM from 'react-dom';

export interface ITriggerRect {
  left: number;
  top: number;
  width: number;
  height: number;
}

interface IPortalProps {
  /**
   * 挂载的内容
   */
  overlay: React.ReactElement;
  /**
   * 是否显示
   */
  visible?: boolean;
  /**
   * css类名
   */
  className?: string;
  /**
   * 最小宽度与触发点保持相同
   */
  portalMatchTrigger: boolean;
  /**
   * 触发点的矩形位置
   */
  triggerRect?: ITriggerRect;
  /**
   * 偏移位置
   */
  offset: number[];
  /**
   * 位置
   */
  position?: {
    top: number;
    left: number;
  };
  /**
   * 挂载容器
   */
  getContainer?: () => HTMLElement;
}

export class Portal extends React.Component<IPortalProps> {
  triggerRef = React.createRef<HTMLDivElement>();

  portalRef = React.createRef<HTMLDivElement>();

  portal: HTMLDivElement | null;

  oContainer: HTMLElement = document.body;

  hasAppended = false;

  constructor(props: IPortalProps) {
    super(props);
    const { className } = props;
    // Ref: https://reactjs.org/docs/portals.html#event-bubbling-through-portals
    this.portal = document.createElement('div');

    if (className) {
      this.portal.classList.add(className);
    }
  }

  // todo add throttle
  updatePortalPosition = () => {
    const { visible, portalMatchTrigger, triggerRect } = this.props;
    if (!visible) {
      return;
    }

    if ((!this.triggerRef.current && !triggerRect) || !this.portalRef.current) {
      return;
    }

    const trigger = this.triggerRef.current as HTMLDivElement;
    const portal = this.portalRef.current as HTMLDivElement;

    let curTriggerRect = triggerRect;
    if (!triggerRect && trigger) {
      const tRect = trigger.getBoundingClientRect();
      const { left: tLeft, top: tTop, width: tWidth, height: tHeight } = tRect;
      curTriggerRect = { left: tLeft, top: tTop, width: tWidth, height: tHeight };
    }
    if (!curTriggerRect) {
      return;
    }

    const { offset, position } = this.props;
    const { height: portalHeight, width: portalWidth } = portal.getBoundingClientRect();

    if (portalMatchTrigger) {
      portal.style.minWidth = `${curTriggerRect.width}px`;
    }
    /* 当有外部传入固定position时，优先采用position */
    if (position) {
      portal.style.top = `${position.top - portalHeight}px`;
      portal.style.left = `${position.left}px`;
    } else {
      // 计算底部剩余空间，若剩余空间能够容纳 portal，则向下展示 portal
      // 如果下方剩余空间无法容纳 portal，还需要判断上方空间能否容纳
      // 若上方也无法容纳(triggerRect.top < height)，则仍然放到下方
      // 左右则优先放右侧
      const [offsetX, offsetY] = offset;
      const top = curTriggerRect.top + curTriggerRect.height + window.scrollY;
      const left = curTriggerRect.left + window.scrollX;
      const bottomSpace = this.oContainer.clientHeight - top;
      const rightSpace = this.oContainer.clientWidth - left;
      portal.style.top =
        bottomSpace < portalHeight && curTriggerRect.top > portalHeight
          ? `${curTriggerRect.top + window.scrollY + offsetY - portalHeight - 4}px` // 放到上方
          : `${top + offsetY}px`;
      portal.style.left =
        rightSpace < portalWidth && (curTriggerRect.left + curTriggerRect.width) > portalWidth
          ? `${left + curTriggerRect.width + offsetX - portalWidth - 4}px`
          : `${left + offsetX}px`;
    }
    portal.style.zIndex = '999';
  };

  // eslint-disable-next-line @typescript-eslint/member-ordering
  static defaultProps = {
    offset: [0, 0],
    portalMatchTrigger: true,
    className: '',
    visible: false,
  };

  componentDidMount() {
    const { getContainer } = this.props;
    // container 可能是 portal 的父组件，因此在 mount 后再获取 container
    if (getContainer) this.oContainer = getContainer() || document.body;
    this.appendIfVisible();
    this.updatePortalPosition();
  }

  componentDidUpdate() {
    this.appendIfVisible();
    this.updatePortalPosition();
  }

  componentWillUnmount() {
    if (this.hasAppended && this.portal) {
      this.oContainer.removeChild(this.portal);
    }
    if (this.portal) {
      this.portal = null;
    }
    // this.updatePortalPosition.cancel();
  }

  appendIfVisible() {
    if (this.hasAppended) return;

    const { oContainer, portal } = this;
    const { visible } = this.props;
    if (visible && portal) {
      oContainer.appendChild(portal);
      this.hasAppended = true;
    }
  }

  render() {
    const { children, overlay, visible } = this.props;
    return (
      <React.Fragment>
        {children && React.cloneElement(children as React.ReactElement, {
          ref: this.triggerRef,
        })}
        {visible && this.portal
          ? ReactDOM.createPortal(
            React.cloneElement(overlay, { ref: this.portalRef }),
            this.portal,
          )
          : null}
      </React.Fragment>
    );
  }
}
