/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable react/static-property-placement */
import React from 'react';
import { classnames } from '../../utils';
import {
  setTransform,
  isTransform3dSupported,
  getLeft,
  getActiveIndex,
  noop,
} from './utils';
import { InkTabBar } from './styled';
import { ISaveRef, IGetRef } from './interface';
import { ITabBarProps } from './TabBar';

// eslint-disable-next-line @typescript-eslint/no-explicit-any
function componentDidUpdate(component: any, init?: boolean) {
  const { styles, panes, activeKey, type } = component.props;
  const rootNode = component.props.getRef('root');
  const wrapNode = component.props.getRef('nav') || rootNode;
  const inkBarNode = component.props.getRef('inkBar');
  const activeTab = component.props.getRef('activeTab');
  const inkBarNodeStyle = inkBarNode.style;
  if (init) {
    // prevent mount animation
    inkBarNodeStyle.display = 'none';
  }
  if (activeTab) {
    const tabNode = activeTab;
    const transformSupported = isTransform3dSupported(inkBarNodeStyle);

    // Reset current style
    setTransform(inkBarNodeStyle, '');
    inkBarNodeStyle.width = '';
    inkBarNodeStyle.height = '';
    inkBarNodeStyle.left = '';
    inkBarNodeStyle.top = '';
    inkBarNodeStyle.bottom = '';
    inkBarNodeStyle.right = '';

    let left = getLeft(tabNode, wrapNode);
    let width = tabNode.offsetWidth;

    // If tabNode'width equal to wrapNode'width when tabBarPosition is top or bottom
    // It means no css working, then ink bar should not have width until css is loaded
    // Fix https://github.com/ant-design/ant-design/issues/7564
    if (width === rootNode.offsetWidth) {
      width = 0;
    } else if (styles?.inkBar && styles?.inkBar.width !== undefined) {
      // 传入 styles ，则使用传入的宽度，并重算 left
      width = parseInt(styles?.inkBar.width, 10);
      if (width) {
        left += (tabNode.offsetWidth - width) / 2;
      }
    }

    if (type === 'd-design') {
      left += 14;
      width -= 28;
    }

    // 可关闭时左右 padding 不等，为使线条在文字内容中间对齐，需左移 (24 - 8) / 2 = 8px
    if (type === 'editable-line') {
      left -= 8;
    }
    // 可关闭时，偏移到tab title 的中间，去除关闭按钮的影响, 需左移动 （2 + 16) / 2 = 9px
    if (type === 'editable-biz') {
      left -= 9;
    }
    // use 3d gpu to optimize render
    if (transformSupported) {
      setTransform(inkBarNodeStyle, `translate3d(${left}px,0,0)`);
    } else {
      inkBarNodeStyle.left = `${left}px`;
    }
    inkBarNodeStyle.width = `${width}px`;
  }

  const activeIndex = getActiveIndex(panes, activeKey);
  inkBarNodeStyle.display = (activeIndex !== -1) ? 'block' : 'none';
}

export interface IInkTabBarNodeProps extends ITabBarProps {
  saveRef: ISaveRef;
  getRef: IGetRef;
}

export default class InkTabBarNodeTop extends React.Component<IInkTabBarNodeProps> {
  static defaultProps = {
    styles: {},
    saveRef: noop,
  };

  timeoutID = -1;

  componentDidMount() {
    // ref https://github.com/ant-design/ant-design/issues/8678
    // ref https://github.com/react-component/tabs/issues/135
    // InkTabBarNode need parent/root ref for calculating position
    // since parent componentDidMount triggered after child componentDidMount
    // we're doing a quick fix here to use setTimeout to calculate position
    // after parent/root component mounted
    this.timeoutID = window.setTimeout(() => {
      componentDidUpdate(this, true);
    }, 0);
  }

  componentDidUpdate() {
    componentDidUpdate(this);
  }

  componentWillUnmount() {
    clearTimeout(this.timeoutID);
  }

  render() {
    const { saveRef, animated, type } = this.props;
    return (
      <InkTabBar
        className={classnames(animated && 'animated', type)}
        ref={saveRef('inkBar')}
      />
    );
  }
}
