import React, { useCallback, useContext } from 'react';
import {
  CheckboxWrap,
  CheckboxContent,
  CheckboxInner,
  CheckboxText,
  CheckboxSelect,
} from './styled';
import useControlledValue from '../../hooks/useControlledValue';
import { KEY_CODES } from '../../utils/consts';
import { classnames } from '../../utils';
import { CheckboxContext } from './CheckboxContext';
export interface ICheckboxProps {
  /**
   * 是否选中
   */
  checked?: boolean;
  /**
   * 默认是否选中
   */
  defaultChecked?: boolean;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 使用Group时每个Checkbox必须指定value
   */
  value?: string;
  /**
   * 改变处理函数，Group包住时失效
   */
  onChange?: React.ChangeEventHandler<HTMLInputElement>;
  /**
   * label，优先级高于children传入
   */
  label?: React.ReactNode;
  /**
   * 设置 indeterminate 状态，只负责样式控制
   */
  indeterminate?: boolean;
}

const Checkbox: React.FC<ICheckboxProps> = React.memo(
  React.forwardRef<HTMLInputElement, ICheckboxProps>((props, ref) => {
    const {
      children,
      onChange,
      value,
      label,
      defaultChecked = false,
      indeterminate = false,
    } = props;

    // eslint-disable-next-line prefer-const
    let [checked, setChecked] = useControlledValue(
      props.checked,
      defaultChecked,
    );

    const provider = useContext(CheckboxContext);

    // 如果有Group，直接根据 Group的选中数组判断
    if (provider && provider.checkedValues) {
      checked = provider.checkedValues.indexOf(value as string) !== -1;
    }

    // disabeld状态，如果有Group包住，且Group的disabed为true，则disabled为true
    const disabled = provider && provider.disabled ? true : props.disabled;

    // Group 修改参数函数
    const onToggleValue = provider && provider.onToggleValue;

    /**
     * 修改选中状态
     *
     * @param {React.ChangeEvent<HTMLInputElement>} e
     * @return {void}
     */
    const handleChange = useCallback(
      (e: React.ChangeEvent<HTMLInputElement>) => {
        if (onToggleValue && value) {
          onToggleValue({
            checked: e.target.checked,
            value,
          });
        } else {
          if (onChange) {
            onChange(e);
          }
          setChecked(e.target.checked);
        }
      },
      [onChange, onToggleValue, value, setChecked],
    );

    /**
     * Enter键切换选中状态
     *
     * @param {React.KeyboardEvent<HTMLInputElement>} e
     * @return {void}
     */
    const handleKeyDown = useCallback(
      (e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.which === KEY_CODES.ENTER) {
          handleChange({
            target: { checked: !checked },
          } as React.ChangeEvent<HTMLInputElement>);
        }
      },
      [checked, handleChange],
    );

    const labelNode = label || children;

    return (
      <CheckboxWrap
        checked={checked}
        disabled={disabled}
        indeterminate={indeterminate}
        data-testid={
          provider && provider.testid
            ? `CheckboxGroup-${provider.testid}`
            : undefined
        }
      >
        <CheckboxContent disabled={disabled} checked={checked}>
          <input
            ref={ref}
            type="checkbox"
            onChange={handleChange}
            onKeyDown={handleKeyDown}
            disabled={disabled}
            checked={checked}
          />
          <CheckboxInner className={classnames({ disabled })}>
            <CheckboxSelect
              className={classnames({ disabled, checked, indeterminate })}
            />
          </CheckboxInner>
        </CheckboxContent>
        {labelNode && <CheckboxText>{labelNode}</CheckboxText>}
      </CheckboxWrap>
    );
  }),
);

Checkbox.displayName = 'Checkbox';

export { Checkbox };
