import React, { useCallback, useEffect } from 'react';
import { usePrevious } from '../../hooks';
import { classnames, uuid } from '../../utils';
import { useController } from './hooks';
import { ArrowRightNormal } from '../icons';
import { MenuItemWrap, MenuItemTitle, SuffixWrapper } from './styled';
import { IFileMenuItemProps } from './interface';

export const FileMenuItem: React.FC<IFileMenuItemProps> = React.memo((props) => {
  const {
    children,
    menuKey,
    disabled,
    style,
    testid,
    title,
    suffix,
    onClick,
    autoClose,
  } = props;
  const withChildren = !!children;
  const prevChildren = usePrevious(children);
  const prevAutoClose = usePrevious(autoClose);

  const ctrl = useController('FileMenuItem');

  if (typeof prevChildren === 'undefined' && !disabled) {
    ctrl.setInitialContent(menuKey, children, !!autoClose);
  }

  if (
    (prevChildren !== children || prevAutoClose !== autoClose) &&
    ctrl.currentMenuKey === menuKey
  ) {
    ctrl.updateContent(menuKey, children, !!autoClose);
  }

  const handleClick = useCallback((e: React.MouseEvent<HTMLElement>) => {
    let clickReturned;
    if (!disabled) {
      if (onClick) {
        clickReturned = onClick(menuKey, e);
      }
      if (ctrl.onClick) {
        ctrl.onClick(menuKey, e);
      }
      if (ctrl.currentMenuKey !== menuKey && withChildren) {
        ctrl.updateContent(menuKey, children, !!autoClose);
      }
    }
    if (clickReturned !== false && autoClose && !withChildren) {
      ctrl.setVisible(false);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    ctrl.currentMenuKey,
    ctrl.setVisible,
    ctrl.updateContent,
    ctrl.onClick,
    menuKey,
    withChildren,
    onClick,
    autoClose,
    children,
    disabled,
  ]);

  useEffect(() => {
    return () => {
      ctrl.onMenuItemUnmount(menuKey);
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [menuKey]);

  return (
    <MenuItemWrap
      className={classnames({ disabled, active: ctrl.currentMenuKey === menuKey })}
      onClick={handleClick}
      style={style}
      data-testid={testid ? `file-menu-item-${testid}` : undefined}
    >
      <MenuItemTitle>{title}</MenuItemTitle>
      <ArrowRightNormal
        className={classnames('icon arrow-icon', { disabled })}
        style={{ display: withChildren ? 'inline-block' : 'none' }}
      />
      <SuffixWrapper
        style={{ display: !withChildren ? 'inline-block' : 'none' }}
      >
        {suffix}
      </SuffixWrapper>
    </MenuItemWrap>
  );
});

FileMenuItem.defaultProps = {
  autoClose: true,
  disabled: false,
  menuKey: `file-menu-item-${uuid()}`,
};
