/* eslint-disable react/destructuring-assignment */
import React, {
  useState,
  useCallback,
  useMemo,
  useRef,
  useImperativeHandle,
} from 'react';
import useControlledValue from '../../hooks/useControlledValue';
import { useTranslate } from '@ali/we-util';
import { CloseNormal } from '@ali/we-icon';
import { classnames, uuid } from '../../utils';
import { InputNode, InputPrefix, InputSuffix, InputWrap } from './styled';
import { KEY_CODES } from '../../utils/consts';
import { FormItemInterface } from '../form';
import { Meta } from 'rc-field-form/es/interface';

export type InputSize = 'small' | 'large';

export interface IInputProps
  extends Omit<
      Partial<React.InputHTMLAttributes<HTMLInputElement>>,
      'prefix' | 'size'
    >,
    FormItemInterface<string> {
  /**
   * 设置国际语言
   */
  locale?: string;
  /**
   * 输入框值
   */
  value?: string;
  /**
   * 输入框默认值
   */
  defaultValue?: string;
  /**
   * 提示
   */
  placeholder?: string;
  /**
   * 带有前缀图标的 input
   */
  prefix?: React.ReactNode;
  /**
   * 带有后缀图标的 input
   */
  suffix?: React.ReactNode;
  /**
   * 可以点击清除图标删除内容
   */
  allowClear?: boolean;
  /**
   * 输入框内容变化时的回调
   */
  onChange?: React.ChangeEventHandler<HTMLInputElement>;
  /**
   * 输入框聚焦时的回调
   */
  onFocus?: React.FocusEventHandler<HTMLInputElement>;
  /**
   * 输入框失焦时的回调
   */
  onBlur?: React.FocusEventHandler<HTMLInputElement>;
  /**
   * 类名
   */
  className?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 是否有边框
   */
  bordered?: boolean;
  /**
   * Input的尺寸，默认为small 28px
   */
  size?: InputSize;
  /**
   * 样式
   */
  placeholderColor?: string;
  /**
   * 回车
   */
  onEnter?: React.KeyboardEventHandler<HTMLInputElement>;
  /**
   * 测试使用
   */
  testid?: string;
  /**
   * Input 样式
   */
  inputStyle?: React.CSSProperties;
  /**
   * 接受 Form.Item 传入的 errors 等参数
   */
  meta?: Meta;
  /**
   * 是否自动获取焦点
   */
  autoFocus?: boolean;
  /**
   * 允许输入的字符数量限制
   */
  maxLength?: number;
}

export interface IInputHandle {
  focus: () => void;
  select: () => void;
  blur: () => void;
}

function blockEvent(e: React.MouseEvent) {
  e.stopPropagation();
  e.preventDefault();
}

// eslint-disable-next-line max-lines-per-function
const Input = React.forwardRef<IInputHandle, IInputProps>((props, ref) => {
  const {
    suffix,
    prefix,
    onChange,
    type = 'text',
    locale,
    onFocus,
    onBlur,
    allowClear,
    id,
    className,
    disabled,
    size,
    bordered = true,
    maxLength,
    style,
    onKeyDown,
    onEnter,
    testid,
    meta,
    autoFocus,
    placeholderColor,
    defaultValue,
    inputStyle,
    ...restProps
  } = props as IInputProps & React.InputHTMLAttributes<HTMLInputElement>;

  const inputRef = useRef<HTMLInputElement>(null);

  const [isFocused, setIsFocused] = useState(false);
  const [value, setValue] = useControlledValue(props.value, defaultValue);

  const uid = useMemo(() => id || uuid(), [id]);

  useImperativeHandle(ref, () => ({
    focus: () => {
      inputRef.current!.focus();
    },
    select: () => {
      inputRef.current!.select();
    },
    blur: () => {
      inputRef.current!.blur();
    },
  }));

  const t = useTranslate(locale);

  const placeholder: string =
    typeof props.placeholder === 'string'
      ? props.placeholder
      : t('we_ui_input_placeholder');

  const handleChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      if (onChange) {
        onChange(e);
      }
      setValue(e.target.value);
    },
    [onChange, setValue],
  );

  const handleFocus = useCallback(
    (e: React.FocusEvent<HTMLInputElement>) => {
      if (onFocus) {
        onFocus(e);
      }
      setIsFocused(true);
    },
    [onFocus],
  );

  const handleBlur = useCallback(
    (e: React.FocusEvent<HTMLInputElement>) => {
      if (onBlur) {
        onBlur(e);
      }
      setIsFocused(false);
    },
    [onBlur],
  );

  const handleReset = useCallback(() => {
    handleChange({
      target: { value: '' },
    } as React.FocusEvent<HTMLInputElement>);
  }, [handleChange]);

  const handleKeyDown = useCallback(
    (e: React.KeyboardEvent<HTMLInputElement>) => {
      if (onKeyDown) {
        onKeyDown(e);
      }
      if (e.which === KEY_CODES.ENTER && onEnter) {
        onEnter(e);
      }
    },
    [onKeyDown, onEnter],
  );

  return (
    <InputWrap
      htmlFor={uid}
      className={classnames(className, {
        focused: isFocused,
        disabled,
        error: meta && meta.errors.length > 0,
      })}
      style={style}
      size={size}
      bordered={bordered}
      data-testid={testid ? `Input_${testid}_wrap` : undefined}
    >
      {!!prefix && (
        <InputPrefix
          data-testid={testid ? `Input_${testid}_prefix` : undefined}
        >
          {prefix}
        </InputPrefix>
      )}
      <InputNode
        {...restProps}
        ref={inputRef}
        maxLength={maxLength}
        autoFocus={autoFocus}
        value={value || ''}
        onChange={handleChange}
        type={type}
        placeholder={placeholder}
        id={uid}
        key={uid}
        onFocus={handleFocus}
        onBlur={handleBlur}
        disabled={!!disabled}
        onKeyDown={handleKeyDown}
        data-testid={testid ? `Input_${testid}` : undefined}
        placeholderColor={placeholderColor}
        style={inputStyle}
      />
      {!!allowClear && !!value && (
        <CloseNormal
          data-testid={testid ? `Input_${testid}_clear` : undefined}
          onClick={handleReset}
          onMouseDown={blockEvent}
          className="clear-icon"
        />
      )}
      {!!suffix && (
        <InputSuffix
          allowClear={!!allowClear}
          data-testid={testid ? `Input_${testid}_suffix` : undefined}
        >
          {suffix}
        </InputSuffix>
      )}
    </InputWrap>
  );
});

Input.defaultProps = {
  allowClear: false,
  size: 'small',
  bordered: true,
  autoFocus: false,
};

const InputMemo = React.memo(Input) as typeof Input;

export { InputMemo as Input };
