/* eslint-disable no-nested-ternary */
import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
  PropsWithChildren,
} from 'react';
import { SelectOptionWrap } from './styled';
import { IMultiSelectOptionProps, TOptionTitle, IRawValue } from './interface';
import { classnames } from '../../utils';
import { Tooltip } from '../../components/tooltip';
import { createMultiSelectContext } from './SelectContext';
import { IPlacement } from '../trigger';
import { SelectedNormalNormal } from '@ali/we-icon';

const noopWrapperStyle = {};

export const getTitleContent = (
  title: TOptionTitle | undefined,
): string | undefined => {
  if (typeof title === 'string' || !title) {
    return title;
  }
  return title.title;
};

export const getTitlePlacement = (
  title: TOptionTitle | undefined,
): IPlacement => {
  if (typeof title === 'string' || !title) {
    return 'right';
  }
  return title.placement;
};

// eslint-disable-next-line max-lines-per-function
const MultiSelectOptionFC = <VT extends IRawValue = IRawValue>(
  props: PropsWithChildren<IMultiSelectOptionProps<VT>>,
) => {
  const {
    children,
    style,
    disabled,
    disabledTip,
    value,
    onSelect,
    testid,
    className,
    title,
    onClick,
  } = props;

  const SelectContext = createMultiSelectContext();

  const context = useContext(SelectContext);
  const [selected, updateSelected] = useState<boolean>(false);

  if (!context) {
    throw new Error('[we-design] Select.Option 必须为 Select 的子组件');
  }

  useEffect(() => {
    if (value) {
      context.onUpdateOption(value, children);
    }
    return () => {
      if (value) {
        context.onRemoveOption(value);
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value]);

  useEffect(() => {
    if (value) {
      context.onCacheOptionDisabled(value, !!disabled);
    }
    changeSelected();
    return () => {
      if (value) {
        context.onUncacheOptionDisabled(value);
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value, disabled]);

  // 是否选中
  const changeSelected = useCallback(() => {
    if (typeof value === 'undefined') {
      return;
    }

    updateSelected(context.value.indexOf(value) !== -1);
  }, [context.value, value]);

  useEffect(() => {
    changeSelected();
  }, [changeSelected]);

  // 容器样式
  const optionWrapperStyle = context.optionWrapperStyle || noopWrapperStyle;

  // 输入过程中是否隐藏
  const hidden =
    context.isSearching && context.showOptionValues.indexOf(value!) === -1;

  // 键盘事件中高亮
  const hovered =
    context && context.keyboardAt === value && value !== undefined;

  const { showSelectOnRight } = context;

  /**
   * 点击选中
   *
   * @return {void}
   */
  const handleClick = useCallback((e: React.MouseEvent): void => {
    if (value !== undefined) {
      context.onClickOption({
        value: value!,
        disabled: !!disabled,
      });
    } else if (onSelect) {
      onSelect();
    }
    changeSelected();
    onClick && onClick(e);
  }, [value, onSelect, changeSelected, onClick, context, disabled]);

  // 样式
  const styleCombined = useMemo(
    () => ({
      ...optionWrapperStyle,
      ...style,
    }),
    [optionWrapperStyle, style],
  );

  return hidden ? null : (
    <Tooltip
      title={disabled && disabledTip ? disabledTip : getTitleContent(title)}
      placement={disabled && disabledTip ? 'top' : getTitlePlacement(title)}
    >
      <SelectOptionWrap
        style={styleCombined}
        className={classnames(className, {
          selected,
          disabled,
          hovered,
        })}
        onClick={handleClick}
        data-testid={testid && `option-${testid}`}
        showSelectOnRight={showSelectOnRight}
      >
        {!!context.selectable && !!selected && (
          <SelectedNormalNormal className="check-icon" />
        )}
        {children}
      </SelectOptionWrap>
    </Tooltip>
  );
};

(MultiSelectOptionFC as React.FC).defaultProps = {
  style: {},
};
// (MultiSelectOptionFC as React.FC).displayName = 'MultiSelectOption';

const MultiSelectOption = React.memo(
  MultiSelectOptionFC,
) as typeof MultiSelectOptionFC;

(MultiSelectOption as React.FC).displayName = 'MultiSelectOption';

export { MultiSelectOption };
