/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable react/prefer-stateless-function */
import React from 'react';
import InkTabBarNodeTop from './InkTabBarNodeTop';
import TabBarTabsNode from './TabBarTabsNode';
import TabBarRootNode from './TabBarRootNode';
import TabBarNode from './TabBarNode';
import SaveRef from './SaveRef';
import { ITabsProps, TabType } from './Tabs';
import InkTabBarNodeLeft from './InkTabBarNodeLeft';
import { NavBarDivider } from './styled';
import { classnames } from '../../utils';
import { TabBarExtraPosition, TabBarExtraMap, TabBarExtraContent } from './interface';

export interface ITabBarProps extends ITabsProps {
  panes: React.ReactNode;
  onKeyDown: (e: React.KeyboardEvent) => void;
  styles?: any;
}

export interface ITabListItem {
  key: string;
  tab?: React.ReactNode;
  id?: string;
}

const getInkTabBarNodeStyles = (type?: TabType) => {
  if (type === 'd-design') {
    return null;
  }
  return type === 'editable-biz'
    ? {
      inkBar: {
        width: '28px',
      },
    }
    : {
      inkBar: {
        height: '32px',
        width: '24px',
      },
    };
};

// tab额外元素包裹组件
const ExtraContent = ({ position = 'right', extra }: { position?: TabBarExtraPosition; extra?: TabBarExtraContent }) => {
  if (!extra) return null;

  let content: React.ReactNode = null;

  const assertExtra = extra as TabBarExtraMap;

  if (position === 'right') {
    content = assertExtra.right || (!assertExtra.left && assertExtra) || null;
  } else if (position === 'left') {
    content = assertExtra.left || null;
  }

  return content && <div className="extra-content" style={{ flex: 'none' }} >{content}</div>;
};

const TabBar: React.FC<ITabBarProps> = (props) => {
  const { panes, type, tabBarExtraContent } = props;
  const singleTab = React.Children.count(panes) === 1;
  const inkTabBarNodeStyles = React.useMemo(() => {
    return getInkTabBarNodeStyles(type);
  }, [type]);
  const hasInk = React.useMemo(() => {
    return (
      type === 'line' ||
      type === 'editable-line' ||
      type === 'biz' ||
      type === 'editable-biz' ||
      type === 'd-design'
    );
  }, [type]);

  return (
    <SaveRef>
      {(saveRef, getRef) => (
        <TabBarRootNode {...props} saveRef={saveRef} getRef={getRef}>
          <ExtraContent position="left" extra={tabBarExtraContent} />
          <TabBarNode
            {...props}
            getRef={getRef}
            saveRef={saveRef}
            testid={props.testid}
          >
            <TabBarTabsNode
              saveRef={saveRef}
              {...props}
              className={singleTab ? 'single-tab' : undefined}
              singleTab={singleTab}
            />
            {hasInk &&
              !singleTab &&
              (props?.tabPosition === 'left' ? (
                <InkTabBarNodeLeft
                  {...props}
                  saveRef={saveRef}
                  getRef={getRef}
                  styles={inkTabBarNodeStyles}
                />
              ) : (
                <InkTabBarNodeTop
                  {...props}
                  saveRef={saveRef}
                  getRef={getRef}
                  styles={inkTabBarNodeStyles}
                />
              ))}
          </TabBarNode>
          <ExtraContent extra={tabBarExtraContent} />
          {props?.tabPosition !== 'left' && (
            <NavBarDivider className={classnames('nav-bar-divider', type)} />
          )}
        </TabBarRootNode>
      )}
    </SaveRef>
  );
};

export default TabBar;
