/* eslint-disable no-plusplus */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
import React from 'react';
import ReactDOM from 'react-dom';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import {
  ContainerRoot,
  NotificationTitle,
  NotificationBody,
  NotificationFooter,
  NotificationWrap,
} from './styled';


export interface INotificationConfig {
  key?: string;
  duration?: number;
  title: React.ReactNode;
  body: React.ReactNode;
  footer?: React.ReactNode;
}

export interface INotificationProps {
  notifications: Array<Required<INotificationConfig>>;
}

export const Notification: React.FC<INotificationProps> = (props) => {
  const {
    notifications,
  } = props;

  return (
    <ContainerRoot>
      <CSSTransitionGroup
        transitionName="notification"
        transitionEnterTimeout={240}
        transitionLeaveTimeout={240}
      >
        {
          notifications.map((noti) => (
            <NotificationWrap key={noti.key}>
              <NotificationTitle>{noti.title || null}</NotificationTitle>
              <NotificationBody>{noti.body || null}</NotificationBody>
              {
                noti.footer !== null && (
                  <NotificationFooter>{noti.footer}</NotificationFooter>
                )
              }
            </NotificationWrap>
          ))
        }
      </CSSTransitionGroup>
    </ContainerRoot>
  );
};

export const notification = (() => {
  let uuid = 0;
  let notifications: INotificationConfig[] = [];
  let handleUpdate: any = null!;

  function init() {
    if (handleUpdate) {
      return;
    }
    const oDiv = document.createElement('div');
    oDiv.style.position = 'absolute';
    oDiv.style.width = '100%';
    oDiv.style.top = '0px';
    oDiv.style.left = '0px';

    document.body.appendChild(oDiv);

    handleUpdate = (notices: Array<Required<INotificationConfig>>) => {
      ReactDOM.render(
        <Notification notifications={notices} />,
        oDiv,
      );
    };
  }


  return {
    open(config: INotificationConfig) {
      init();
      config.key = config.key || `noid-${(++uuid)}`;
      if (uuid > 10000000) { uuid = 0; }
      config.duration = typeof config.duration !== 'undefined' ? config.duration || 0 : 4.5;

      if (config.duration !== 0) {
        setTimeout(() => this.close(config.key), config.duration * 1000);
      }

      notifications = [...notifications, config];
      handleUpdate(notifications as Array<Required<INotificationConfig>>);
    },

    close(key?: string) {
      init();
      if (key) {
        const index = notifications.findIndex((n) => n.key === key);
        if (index !== -1) {
          notifications = [...notifications];
          notifications.splice(index, 1);
        }
      } else {
        notifications = [];
      }
      handleUpdate(notifications as Array<Required<INotificationConfig>>);
    },
  };
})();
