/* eslint-disable @typescript-eslint/explicit-function-return-type */
import styled, { createGlobalStyle } from 'styled-components';
import { withTheme } from '../theme';

export const GlobalStyle = createGlobalStyle`
  .ctxmenu-hidden {
    overflow: hidden;
  }
`;

export const ContextRoot = withTheme(styled.div`
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
  z-index: ${({ theme }) => (theme.ui.zIndexModal + 2)};
`);

export const ContextContainer = withTheme(styled.div`
  position: fixed;
  left: -100%;
  top: -100%;
  border: 1px solid ${({ theme }) => theme.ui.dropdownBorderColor};
  box-shadow: ${({ theme }) => theme.ui.boxShadow};
  background-color: ${({ theme }) => theme.ui.colorGray1};
  border-radius: ${({ theme }) => theme.ui.dropdownBorderRadius}px;
  max-height: 480px;
  overflow-y: auto;
  z-index: ${({ theme }) => (theme.ui.zIndexModal + 3)};
`);
