import React from 'react'
import tinycolor from 'tinycolor2'
import { ColorPalette } from './ColorPalette'
import { ButtonSize } from '../../components/button'
import {
  SplitButtonWrapper,
  ColorPaletteWrapper,
  FullFillColorArea,
  FillColorPickerButton,
} from './styled'

export interface IFillColorPickerProps {
  /**
   * SplitButton的默认激活状态
   */
  defaultActive?: boolean
  /**
   * 按钮激活状态，受控模式
   */
  active?: boolean
  /**
   * 激活状态 变化时的回调
   */
  onActiveChange?: (active: boolean) => void
  /**
   * 默认的初始颜色值
   */
  defaultColor?: string
  /**
   * 当前选中的颜色值
   */
  color?: string
  /**
   * 默认颜色块的颜色值
   */
  automaticColor: string
  /**
   * 要显示清楚线的颜色块的颜色值
   */
  clearColor?: string
  /**
   * 是否显示 colorPicker 组件, 即：选择“更多”颜色的组件
   */
  picker?: boolean
  /**
   * 颜色选中后的回调函数
   */
  onColorPicker: (color: string) => void
  /**
   * SplitButton的大小
   */
  size?: ButtonSize
  /**
   * 颜色选择按钮的组件
   */
  style?: React.CSSProperties
  /**
   * 本地语言类型，如zh-CN
   */
  locale?: string
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode
  /**
   * 是否显示自动
   */
  showAutomatic?: boolean
  /**
   * 弹层类名
   */
  overlayClassName?: string
}

interface IFillColorPickerState {
  currentColor: string // 内部维护的颜色状态值
  currentActive: boolean // 是否显示下拉框
}

const DEFAULT_COLOR = '#ffffff'

export class FillColorPicker extends React.PureComponent<
  IFillColorPickerProps,
  IFillColorPickerState
> {
  constructor(props: IFillColorPickerProps) {
    super(props)
    this.state = {
      currentColor: props.color || props.defaultColor || DEFAULT_COLOR,
      currentActive: props.defaultActive || false,
    }
  }

  handleColorPick = (color: string): void => {
    this.setState({ currentColor: color })
    const { onColorPicker, active, onActiveChange } = this.props
    if (active !== undefined && onActiveChange) {
      onActiveChange(false)
    } else {
      this.setState({ currentActive: false })
    }
    if (onColorPicker) {
      onColorPicker(color)
    }
  }

  handleCustomColorPick = (color: string): void => {
    const { onColorPicker } = this.props
    this.setState({
      currentColor: color,
    })
    if (onColorPicker) {
      onColorPicker(color)
    }
  }

  handlePaletteClick = (e: any) => {
    // 阻止冒泡
    e.stopPropagation()
    // 阻止默认行为
    e.preventDefault()
  }

  handleClickLeft = (): void => {
    const { onColorPicker } = this.props
    const { currentColor } = this.state
    onColorPicker(currentColor)
  }

  handleActive = (active: boolean) => {
    const { onActiveChange } = this.props
    if (onActiveChange) {
      onActiveChange(active)
    }
    this.setState({ currentActive: active })
  }

  getColorPalette = (): React.ReactElement => {
    const {
      color,
      automaticColor,
      clearColor,
      picker,
      locale,
      showAutomatic,
      overlayClassName,
    } = this.props
    const { currentColor } = this.state
    return (
      <ColorPaletteWrapper 
        onClick={this.handlePaletteClick}
        className={overlayClassName}
      >
        <ColorPalette
          color={color || currentColor}
          onPick={this.handleColorPick}
          onCustomColorPick={this.handleCustomColorPick}
          automaticColor={automaticColor}
          clearColor={clearColor}
          picker={picker}
          locale={locale}
          showAutomatic={showAutomatic}
        />
      </ColorPaletteWrapper>
    )
  }

  render() {
    const { currentColor, currentActive } = this.state
    const { color, size, style, active: propActive, title } = this.props
    const active = propActive !== undefined ? propActive : currentActive
    const activeColor = color || currentColor
    const isWhite = tinycolor.equals(activeColor, '#ffffff')
    console.log(isWhite)

    return (
      <FillColorPickerButton
        overlay={this.getColorPalette()}
        onClickLeft={this.handleClickLeft}
        active={active}
        onActiveChange={this.handleActive}
        style={style}
        size={size}
        testid="FillColorPicker"
        title={title}
      >
        <SplitButtonWrapper>
          <FullFillColorArea isWhite={isWhite} color={activeColor} />
        </SplitButtonWrapper>
      </FillColorPickerButton>
    )
  }
}
