import React, { ReactNode, useCallback, useMemo, useState } from 'react'
import { Dropdown } from '../dropdown'
import { TriggerWrapper, LeftWrap, RightWrap } from './styled'
import { IFileMenuProps, IFileMenuItemProps } from './interface'
import { CatalogueBetaNormal } from '../icons'
import { FileMenuDivider } from './Divider'
import { FileMenuItem } from './FileMenuItem'
import { useTranslate } from '@ali/we-util'
import { useControlledValue } from '../../hooks'
import { classnames } from '../../utils'
import { FileMenuContext } from './context'

interface ICurrentItem {
  menuKey: string
  content: ReactNode
  autoClose: boolean
}

interface MenuSubComponents {
  Item: React.FC<IFileMenuItemProps>
  Divider: React.FC
}

// eslint-disable-next-line max-lines-per-function
export const FileMenu = (React.memo<IFileMenuProps>((props) => {
  const {
    style,
    overlayStyle,
    zIndex,
    contentStyle,
    children,
    text,
    locale,
    trigger,
    onClick,
    onVisibleChange,
  } = props

  const t = useTranslate(locale)
  const [visible, setVisible] = useControlledValue(props.visible, props.defaultVisible)
  const [currentItem, setCurrentItem] = useState<ICurrentItem | null>(null)

  const fileText = text || t('we_ui_file_menu_text')

  const handleVisibleChange = useCallback(
    (e: boolean) => {
      if (onVisibleChange) {
        onVisibleChange(e)
      }
      setVisible(e)
    },
    [onVisibleChange, setVisible],
  )

  const setInitialContent = useCallback(
    (menuKey: string, content: React.ReactNode, autoClose: boolean) => {
      setCurrentItem((item) => {
        if (item) {
          return item
        }
        return { menuKey, content, autoClose }
      })
    },
    [],
  )

  /**
   * 更新Item内容
   */
  const updateContent = useCallback(
    (menuKey: string, content: React.ReactNode, autoClose: boolean) => {
      setCurrentItem({ menuKey, content, autoClose })
    },
    [],
  )

  const currentMenuKey = (currentItem && currentItem.menuKey) || ''

  const onMenuItemUnmount = useCallback(
    (menuKey: string) => {
      if (menuKey === currentMenuKey) {
        setCurrentItem(null)
      }
    },
    [currentMenuKey],
  )

  const ctx = useMemo(() => {
    return {
      updateContent,
      currentMenuKey,
      setInitialContent,
      onMenuItemUnmount,
      setVisible: handleVisibleChange,
      onClick,
    }
  }, [
    updateContent,
    currentMenuKey,
    setInitialContent,
    onMenuItemUnmount,
    handleVisibleChange,
    onClick,
  ])

  const overlayStyleCombined = useMemo(() => {
    return {
      display: 'flex',
      alignItems: 'stretch',
      ...(overlayStyle || {}),
    }
  }, [overlayStyle])

  const handleClickRight = useCallback(() => {
    if (currentItem && currentItem.autoClose) {
      handleVisibleChange(false)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [!!(currentItem && currentItem.autoClose), handleVisibleChange])

  return (
    <Dropdown
      overlay={
        <FileMenuContext.Provider value={ctx}>
          <LeftWrap>{children}</LeftWrap>
          {currentItem && (
            <RightWrap style={contentStyle} onClick={handleClickRight}>
              {currentItem.content}
            </RightWrap>
          )}
        </FileMenuContext.Provider>
      }
      zIndex={zIndex}
      overlayStyle={overlayStyleCombined}
      trigger={trigger}
      visible={visible}
      onVisibleChange={handleVisibleChange}
    >
      <TriggerWrapper style={style} className={classnames({ active: visible })}>
        <CatalogueBetaNormal className="trigger-icon" />
        <span>{fileText}</span>
      </TriggerWrapper>
    </Dropdown>
  )
}) as unknown) as React.FC<IFileMenuProps> & MenuSubComponents

FileMenu.defaultProps = {
  trigger: 'click',
  defaultVisible: false,
}

FileMenu.Divider = FileMenuDivider
FileMenu.Item = FileMenuItem
