/* eslint-disable max-lines-per-function */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
import React, { useRef } from 'react';
import warning from 'warning';
import { CloseBigNormal } from '@ali/we-icon';
import { classnames } from '../../utils';
import { RoundcloseFillBetaNormal, ClosedNormal } from '../icons';
import { ISaveRef } from './interface';
import { TabBarTab, TabBarTabItemWrap, TabBarWrapper } from './styled';
import { ITabBarProps } from './TabBar';

interface ITabBarTabsNodeProps extends ITabBarProps {
  saveRef: ISaveRef;
  panes: React.ReactNode;
  id?: string;
  singleTab: boolean;
}

const TabBarTabsNode: React.FC<ITabBarTabsNodeProps> = (props) => {
  const {
    saveRef,
    panes,
    size,
    activeKey,
    type,
    className,
    tabPosition,
    testid,
    tabBarItemClassName = '',
    onCloseAll,
    onClose,
    singleTab,
  } = props;
  const rst: React.ReactElement[] = [];
  // 下面两个ref是为了缓存函数引用，根据key进行缓存
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const onTabClicks = useRef<any>({});
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const onTabCloses = useRef<any>({});

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  React.Children.forEach(panes as React.ReactElement[], (child: any) => {
    if (!child) {
      return;
    }
    const key = child.key as string;
    const isActived = activeKey === key;

    if (
      !onTabClicks.current[key] ||
      onTabClicks.current[key] !== props.onTabClick
    ) {
      onTabClicks.current[key] = (e: React.MouseEvent<HTMLDivElement>) => {
        if (props.onTabClick) {
          props.onTabClick(key, e);
        }
      };
    }

    if (
      !onTabCloses.current[key] ||
      onTabCloses.current[key] !== props.onClose
    ) {
      onTabCloses.current[key] = (e: React.MouseEvent<HTMLElement>) => {
        e.stopPropagation();
        if (props.onClose) {
          props.onClose(key);
        }
      };
    }

    warning(
      'tab' in child.props,
      'There must be `tab` property on children of Tabs.',
    );

    const id = props.id ? `${key}-${props.id}` : key;

    const canCloseAll = typeof onCloseAll === 'function';
    const canShowSingleClose = !canCloseAll || !singleTab;

    const closable =
      typeof child.props.closable !== 'undefined'
        ? !!child.props.closable && canShowSingleClose
        : canShowSingleClose;

    const node = (
      <TabBarTab
        role="tab"
        aria-disabled={child.props.disabled ? 'true' : 'false'}
        aria-selected={isActived ? 'true' : 'false'}
        aria-controls={`tabpane-${id}`}
        tabIndex={isActived ? 0 : -1}
        key={key}
        id={`tab-${id}`}
        className={classnames(
          type,
          size,
          tabPosition,
          className,
          `${tabBarItemClassName}-wrapper`,
          {
            disabled: !!child.props.disabled,
            actived: isActived,
            single: singleTab,
          },
        )}
        ref={isActived ? saveRef('activeTab') : saveRef(`tab_${key}`)}
        onClick={
          !isActived && !child.props.disabled
            ? onTabClicks.current[key]
            : undefined
        }
        data-testid={`${testid}_${id}`}
      >
        <TabBarTabItemWrap
          className={classnames(
            type,
            size,
            tabPosition,
            className,
            `${tabBarItemClassName}-item`,
            {
              actived: isActived,
              disabled: !!child.props.disabled,
              single: singleTab,
            },
          )}
        >
          {child.props.tab}
          {type === 'editable-card' && closable && (
            <ClosedNormal
              onClick={onTabCloses.current[key]}
              className="card-tab-close-button"
              style={{ fontSize: 14 }}
              testid={`${testid}_${id}_close`}
            />
          )}
          {(type === 'editable-line')
          && closable
           && (
           <CloseBigNormal
             onClick={onTabCloses.current[key]}
             className="line-tab-close-button"
             style={{ fontSize: 14 }}
             testid={`${testid}_${id}_close`}
           />
           )}
           {(type === 'd-design')
            && onClose
              && (
              <CloseBigNormal
                onClick={onTabCloses.current[key]}
                className="line-tab-close-button"
                style={{ fontSize: 20 }}
                testid={`${testid}_${id}_close`}
              />
           )}
          {type === 'editable-biz' && closable && (
            <RoundcloseFillBetaNormal
              onClick={onTabCloses.current[key]}
              className={classnames('biz-tab-close-button')}
              testid={`${testid}_${id}_close`}
            />
          )}
        </TabBarTabItemWrap>
      </TabBarTab>
    );

    rst.push(node);
  });

  return (
    <TabBarWrapper
      className={classnames(
        tabPosition && `${tabPosition}`,
        tabBarItemClassName,
      )}
      ref={saveRef('navTabsContainer')}
    >
      {rst}
    </TabBarWrapper>
  );
};

export default TabBarTabsNode;
