import React from 'react';
import styled from 'styled-components';
import { Dropdown } from '../dropdown';
import { Dot, dropdownOverlayStyle } from './styled';

export interface IProfileInfo {
   /**
   * 如果需要颜色
   */
   color?: string;
   /**
   * 用户名字
   */
   name?: string;
   /**
   * 状态
   */
   status?: string;
}
  
export interface IAvatarPopover extends IProfileInfo {
  overlay?: React.ReactElement | null;
};

const PopoverWrapper = styled.div``;

const AvatarOuterWrapper = styled.div`
  display: flex;
  align-items: center;
`;

const ProfileInfoWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 8px 12px;
`;

const ProfileInfoInnerLeft = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-right: 8px;
`;

const ProfileInfoInnerCenter = styled.div`
  display: flex;
  min-width: 105px;
  align-items: center;
  justify-content: space-between;
`;

const ProfileInfoInnerRight = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
`;

const NickText = styled.span`
  flex: 1;
  width: 50%;
  max-width: 96px;
  font-weight: 400;
  font-size: 13px;
  color: #171a1d;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin-right: 8px;
`;

const StatusText = styled.span`
  font-weight: 400;
  font-size: 12px;
  margin-right: 4px;
  color:rgba(23, 26, 29, 0.4);
`;

const SplitLine = styled.div`
  height: 1px;
  margin: 0 4px;
  background: rgba(126, 134, 142, 0.16);
`;

const ProfileInfo: React.FC<IProfileInfo> = (props) => {
  const { name, color, status, children } = props;
  return (
    <ProfileInfoWrapper>
      <ProfileInfoInnerLeft>
        {
          children
        }
      </ProfileInfoInnerLeft>
      <ProfileInfoInnerCenter>
        <NickText>{name}</NickText>
        {
          status && <StatusText>{status}</StatusText>
        }
      </ProfileInfoInnerCenter>
      <ProfileInfoInnerRight>
        {
          color && <Dot color={color} />
        }
      </ProfileInfoInnerRight>
    </ProfileInfoWrapper>
  )
}

const AvatarPopover: React.FC<IAvatarPopover> = (props) => {
  const { overlay, color, status, name, children, } = props;

  const avatar = children && React.Children.toArray(children).map((child: any) => {
    return React.cloneElement(child, {
      shape: 'rect',
      popoverOverlay: false,
      active: false,
      size: 'normal',
      onClick: undefined,
      color: undefined,
    });
  })[0];
  
  return (
    <Dropdown
      zIndex={1001}
      overlayStyle={dropdownOverlayStyle}
      overlay={
        <PopoverWrapper>
          <ProfileInfo
            color={color}
            status={status}
            name={name}
          >
            {
              avatar
            }
          </ProfileInfo>
          {
            overlay && <SplitLine />
          }
          {
            overlay
          }
        </PopoverWrapper>
      }
      trigger="hover"
      placement="bottomLeft"
      delayShow={200}
    >
      <AvatarOuterWrapper>{ children }</AvatarOuterWrapper>
    </Dropdown>
  )
};

export default AvatarPopover;
