import React from 'react';
import { Spin } from './spin';
import { SpinCardWrapper } from './styled';
import { ISpinCardProps } from './interface';

export const SpinCard = (props: ISpinCardProps) => {
  const { size = 'small', theme, testid, ...rest } = props;
  return (
    <SpinCardWrapper {...rest} data-testid={testid}>
      <Spin size={size} theme={theme} />
    </SpinCardWrapper>
  );
};

SpinCard.defaultProps = {
  size: 'small',
  theme: 'gray',
  transparent: false,
};
