import React, { useState, useCallback, useRef } from 'react';
import styled from 'styled-components';

export const baseAvatarStyle = `
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
`;

const TextAvatar = styled.div`
  ${baseAvatarStyle}
  color: #fff;
  background-color: rgb(0, 137, 255);
`;

const Avatar = styled.span`
  ${baseAvatarStyle}

  img {
    width: 100%;
    height: 100%;
    vertical-align: unset;
  }
`;

function getLastNickName(nick?: string, num = 2) {
  if (!nick) return '';
  const broken = Array.from(nick);
  return broken.slice(broken.length - num).join('');
}

export default function SingleAvatar(props: {
  url?: string;
  fallbackUrl?: string;
  nick?: string;
  style?: React.CSSProperties;
  fallbackTo?: 'text' | 'null';
  className?: string;
}) {
  const imgRef = useRef<HTMLImageElement | null>(null);
  const [failed, setFailed] = useState(false);
  const {
    url,
    fallbackUrl,
    className,
    nick,
    style,
    fallbackTo = 'text',
  } = props;
  const nickName = getLastNickName(nick, 1);

  const handleError = useCallback(
    () => {
      if (fallbackUrl && imgRef.current && imgRef.current.src !== fallbackUrl) {
        // 1. fallback to fallback src
        imgRef.current.src = fallbackUrl;
      } else {
        // 2. fallback to TextAvatar
        setFailed(true);
      }
    },
    [fallbackUrl],
  );

  if (!url || (failed && fallbackTo === 'text')) {
    return (
      <Avatar style={style} className={className}>
        <TextAvatar>{nickName}</TextAvatar>
      </Avatar>
    );
  }

  return failed ? null : (
    <Avatar style={style} className={className}>
      <img ref={imgRef} src={url} onError={handleError} />
    </Avatar>
  );
}
