/* eslint-disable @typescript-eslint/explicit-function-return-type */
import styled, { keyframes } from 'styled-components';
import { withTheme } from '../theme';

const NotificationFadeIn = keyframes`
  0% {
    left: 365px;
    opacity: 0;
  }
  100% {
    left: 0;
    opacity: 1;
  }
`;

const NotificationFadeOut = keyframes`
  0% {
    left: 0;
    opacity: 1;
  }
  100% {
    left: 365px;
    opacity: 0;
  }
`;

export const ContainerRoot = withTheme(styled.div`
  position: fixed;
  right: 0px;
  top: 24px;
  bottom: auto;
  box-sizing: border-box;
  width: 384px;
  margin: 0;
  padding: 0;
  margin-right: 24px;
  max-width: calc(100vw - 32px);
  z-index: ${({ theme }) => theme.ui.zIndexMessage};
  font-size: ${({ theme }) => theme.ui.fontSizeLarge};

  * {
    box-sizing: border-box;
  }
`);

export const NotificationWrap = withTheme(styled.div`
  position: relative;
  margin-bottom: ${({ theme }) => theme.ui.margin};
  box-shadow: ${({ theme }) => theme.ui.boxShadow};
  border: 1px solid ${({ theme }) => theme.ui.borderColor};
  border-radius: ${({ theme }) => theme.ui.marginMedium};
  padding: ${({ theme }) => theme.ui.notificationPadding};
  background-color: ${({ theme }) => theme.ui.colorGray1};

  &.notification-enter {
    animation: ${NotificationFadeIn} .24s cubic-bezier(.645,.045,.355,1);
    animation-play-state: paused;
  }

  &.notification-enter-active {
    animation-name: ${NotificationFadeIn};
    animation-play-state: running;
  }

  &.notification-leave {
    animation: ${NotificationFadeOut} .24s cubic-bezier(.645,.045,.355,1);
    animation-play-state: paused;
  }

  &.notification-leave-active {
    animation-name: ${NotificationFadeOut};
    animation-play-state: running;
  }
`);

export const NotificationTitle = withTheme(styled.div`
  line-height: 1.5;
  font-weight: 500;
  margin-bottom: ${({ theme }) => theme.ui.marginSmall};
`);
export const NotificationBody = withTheme(styled.div`
  line-height: 1.5;
  color: ${({ theme }) => theme.ui.colorText4};
`);
export const NotificationFooter = withTheme(styled.div`
  margin-top: ${({ theme }) => theme.ui.marginSmall};
`);
