/* eslint-disable react/no-array-index-key */
import React, { useMemo } from 'react'
import dayjs, { Dayjs } from 'dayjs'
import localeData from 'dayjs/plugin/localeData'
import useControlledValue from '../../hooks/useControlledValue'
import { useTranslate, useDayjsLocale } from '@ali/we-util'
import { classnames } from '../../utils'
import useComponentDidUpdate from '../../hooks/useComponentDidUpdate'
import { Control } from './Control'
import {
  DatePanelWrap,
  DatePanelBody,
  ControlWrap,
  PanelControlWrap,
  CanlendarWeekday,
  CanlendarTable,
  CanlendarDate,
} from './styled'
import { DatePickerPanelType } from './DatePicker'
dayjs.extend(localeData)

export interface IDatePanelProps {
  /**
   * 国际化设置
   */
  locale?: string
  /**
   * 日期值
   */
  value?: Dayjs | null
  /**
   * 日期默认值
   */
  defaultValue?: Dayjs | null
  /**
   * 是否带左右的控制条
   */
  controls?: string[]
  /**
   * 修改dayjs值
   */
  onChange?: (e: Dayjs) => void
  /**
   * 点击控制条
   */
  onClickControl?: (e: React.MouseEvent<HTMLElement>) => void
  /**
   * 样式
   */
  style?: React.CSSProperties
  /**
   * 临时查看的时间
   */
  temporaryValue?: Dayjs
  /**
   * 默认临时查看的时间
   */
  defaultTemporaryValue?: Dayjs
  /**
   * 修改临时查看的时间
   */
  onChangeTemporaryValue?: (e: Dayjs) => void
  /**
   * 不可选择的日期
   */
  disabledDate?: (currentDate: Dayjs, view: DatePickerPanelType) => boolean
  /**
   * 测试使用
   */
  testid?: string
}

// eslint-disable-next-line max-lines-per-function
const DatePanel: React.FC<IDatePanelProps> = (props) => {
  const {
    onChange,
    controls,
    locale,
    onClickControl,
    onChangeTemporaryValue,
    style,
    disabledDate,
    testid,
  } = props

  // 国际化
  const t = useTranslate(locale)
  const dayjsLocale = useDayjsLocale(locale) as any

  // 拿到语言包短月份数组
  const weekdays: string[] = useMemo(() => {
    return dayjs().locale(dayjsLocale).localeData().weekdaysMin()
  }, [dayjsLocale])

  const [value, setValue] = useControlledValue(props.value, props.defaultValue)

  const [temporaryValue, setTemporaryValue] = useControlledValue(
    props.temporaryValue,
    value || dayjs()
  )

  // value 变化时，应该 reset temporary value
  useComponentDidUpdate(() => {
    setTemporaryValue(props.value || dayjs())
  }, [props.value])

  // 计算要显示的天，遵循 temporaryValue 的值
  // 月份不同，可能所占的行数不一致，为了不抖动，统一取42天
  const daysArray = useMemo(() => {
    const results: Dayjs[] = []
    const firstDate = temporaryValue!.date(1)
    const lastDate = temporaryValue!.add(1, 'month').date(0)
    // 获取 1 号是星期几，前面要补全整个星期
    const firstDateDay = firstDate.day()
    for (let i = 0; i < firstDateDay; i += 1) {
      results.unshift(firstDate.subtract(i + 1, 'day'))
    }
    // 本月
    const thisMonthLastDate = lastDate.date()
    for (let i = 0; i < thisMonthLastDate; i += 1) {
      results.push(firstDate.add(i, 'day'))
    }
    // 最后补全 42 个数字
    let i = 1
    while (results.length < 42) {
      results.push(lastDate.add(i, 'day'))
      i += 1
    }
    // 7 个为一组
    const newResults: Dayjs[][] = []
    let group: Dayjs[] = []
    results.forEach((item) => {
      group.push(item)
      if (group.length === 7) {
        newResults.push(group)
        group = []
      }
    })
    return newResults
  }, [temporaryValue])

  /**
   * 修改 dayjs 值
   *
   * @param {Dayjs} e
   * @return {void}
   */
  const handleChangeDate = (e: Dayjs): void => {
    if (onChange) {
      onChange(e)
    }
    setTemporaryValue(e)
    setValue(e)
  }

  // 控制条显示文案国际化
  const controlText = useMemo(() => {
    return temporaryValue!.format(t('we_ui_date_year_month_format'))
  }, [temporaryValue, t])

  /**
   * 控制条点击左右箭头增减时间处理函数
   * 1）+1 月
   * 2）-1 月
   * 3）+1 年
   * 4）-1 年
   *
   * @return {void}
   */
  const temporaryValueChangeHandlers = useMemo(() => {
    const commonHandler = (
      fnName: 'add' | 'subtract',
      dt: number,
      unit: any
    ): void => {
      const newTemporaryValue = temporaryValue![fnName](dt, unit)
      if (onChangeTemporaryValue) {
        onChangeTemporaryValue(newTemporaryValue)
      }
      setTemporaryValue(newTemporaryValue)
    }

    return {
      addMonth: (): void => commonHandler('add', 1, 'month'),
      subtractMonth: (): void => commonHandler('subtract', 1, 'month'),
      addYear: (): void => commonHandler('add', 1, 'year'),
      subtractYear: (): void => commonHandler('subtract', 1, 'year'),
    }
  }, [temporaryValue, onChangeTemporaryValue, setTemporaryValue])

  return (
    <DatePanelWrap style={style}>
      <ControlWrap>
        <Control
          hasNext={controls?.indexOf('next') !== -1}
          hasPrev={controls?.indexOf('prev') !== -1}
          hasSuperNext={controls?.indexOf('superNext') !== -1}
          hasSuperPrev={controls?.indexOf('superPrev') !== -1}
          onClickNext={temporaryValueChangeHandlers.addMonth}
          onClickPrev={temporaryValueChangeHandlers.subtractMonth}
          onClickSuperNext={temporaryValueChangeHandlers.addYear}
          onClickSuperPrev={temporaryValueChangeHandlers.subtractYear}
          testid={testid ? `DatePanel-${testid}` : undefined}
        >
          <PanelControlWrap
            className={classnames({ clickable: !!onClickControl })}
            onClick={onClickControl}
          >
            {controlText}
          </PanelControlWrap>
        </Control>
      </ControlWrap>
      <DatePanelBody>
        <CanlendarTable>
          <thead>
            <tr>
              {weekdays.map((weekday) => {
                return (
                  <th key={weekday}>
                    <CanlendarWeekday>{weekday}</CanlendarWeekday>
                  </th>
                )
              })}
            </tr>
          </thead>
          <tbody>
            {daysArray.map((days, index) => {
              return (
                <tr key={index}>
                  {days.map((item) => {
                    const isDisabled =
                      typeof disabledDate === 'function'
                        ? disabledDate(item, 'date')
                        : false

                    return (
                      <td key={item.format('YYYY-MM-DD')}>
                        <CanlendarDate
                          onClick={(): void => {
                            !isDisabled && handleChangeDate(item)
                          }}
                          className={classnames({
                            notCurrentMonth: !item.isSame(
                              temporaryValue!,
                              'month'
                            ),
                            isToday: item.isSame(dayjs(), 'date'),
                            selected: value && item.isSame(value, 'date'),
                            isDisabled,
                          })}
                        >
                          {item.format('DD')}
                        </CanlendarDate>
                      </td>
                    )
                  })}
                </tr>
              )
            })}
          </tbody>
        </CanlendarTable>
      </DatePanelBody>
    </DatePanelWrap>
  )
}

DatePanel.defaultProps = {
  controls: ['superPrev', 'superNext', 'next', 'prev'],
}

const DatePanelMemo = React.memo(DatePanel)

export { DatePanelMemo as DatePanel }
