import React from 'react';
import { Button, ButtonSize } from '../button';
import { TablePanel } from './TablePanel';
import { Dropdown } from '../../components/dropdown';

export interface ITableSetProps {
  /**
   * 选择器的总行数
   */
  row?: number;
  /**
   * 选择器的总列数
   */
  col?: number;
  /**
   * 选择 row行 col列 后的回调函数
   */
  onChange?: (row: number, col: number) => void;
  /**
   * 按钮的icon
   */
  btnIcon?: React.ReactNode ;
  /**
   * Button 的样式
   */
  style?: React.CSSProperties;
  /**
   * Button 是否显示边框
   */
  bordered?: boolean;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 类名
   */
  className?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 按钮的大小
   */
  size?: ButtonSize
}

export class TableSet extends React.PureComponent<ITableSetProps, any> {
  state = {
    active: false,
  };

  getTablePanel = () => {
    const { row, col } = this.props;
    return (
      <TablePanel
        row={row}
        col={col}
        onPick={this.handlePick}
      />
    );
  };

  handlePick = (row: number, col: number) => {
    const { onChange } = this.props;
    if (onChange) {
      onChange(row, col);
    }
    this.setState({ active: false });
  };

  handleChangeActive = (active: boolean) => {
    this.setState({ active });
  };

  render() {
    const { btnIcon, style, bordered = false, zIndex, className, disabled, title, size } = this.props;
    const { active } = this.state;

    return (
      <Dropdown
        visible={active}
        overlay={this.getTablePanel()}
        onVisibleChange={this.handleChangeActive}
        zIndex={zIndex}
        title={title}
      >
        <Button
          active={active}
          style={style}
          icon={btnIcon}
          bordered={bordered}
          className={className}
          disabled={disabled}
          size={size}
          data-testid="button-TableSet"
        />
      </Dropdown>
    );
  }
}
