import React from 'react';
import ReactDOM from 'react-dom';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { CSSProp } from 'styled-components';
import {
  NoticeErrorNormal,
  NoticeSuccessNormal,
  NoticeTipNormal,
  NoticeWarningNormal,
} from '@ali/we-icon';
import { classnames } from '../../utils';
import {
  ContainerRoot,
  MessageWrap,
  MessageContent,
  Container,
  MessageBox,
  MessageFlex,
} from './styled';

type MessageType = 'success' | 'info' | 'warning' | 'error' | 'loading';

export interface IMessageConfig {
  key?: string;
  duration?: number;
  onClose?: () => void;
  content: string | React.ReactElement;
  type: MessageType;
  animated?: boolean;
  node?: React.ReactElement;
}

export interface IMessageProps {
  messages: Array<Required<IMessageConfig>>;
  style?: CSSProp;
}

export const Message: React.FC<IMessageProps> = (props) => {
  const { messages, style } = props;

  const IconNodes: Record<MessageType, React.ReactNode> = {
    success: <NoticeSuccessNormal className="icon success-icon" />,
    error: <NoticeErrorNormal className="icon error-icon" />,
    info: <NoticeTipNormal className="icon info-icon" />,
    warning: <NoticeWarningNormal className="icon warning-icon" />,
    loading: (
      <img
        width="20px"
        height="20px"
        src="https://img.alicdn.com/imgextra/i3/O1CN01uT6E7d1CkXrhFshZm_!!6000000000119-1-tps-375-375.gif"
      />
    ),
  };

  return (
    <ContainerRoot style={style}>
      <Container>
        <CSSTransitionGroup
          transitionName="message"
          transitionEnterTimeout={300}
          transitionLeaveTimeout={300}
        >
          {messages.map((msg) => {
            const { key, type, content, animated = true, node } = msg;
            return node ? (
              <MessageBox key={key}>
                <div className={classnames({ animated })}>{node}</div>
              </MessageBox>
            ) : (
              <MessageBox key={key}>
                <MessageWrap className={classnames({ animated })}>
                  {
                    <MessageFlex>
                      {IconNodes[type]}
                      <MessageContent>{content}</MessageContent>
                    </MessageFlex>
                  }
                </MessageWrap>
              </MessageBox>
            );
          })}
        </CSSTransitionGroup>
      </Container>
    </ContainerRoot>
  );
};

export const message = (() => {
  let uuid = 0;
  let messages: IMessageConfig[] = [];
  let handleUpdate: any = null!;

  function init() {
    if (handleUpdate) {
      return;
    }
    const oDiv = document.createElement('div');
    oDiv.style.position = 'absolute';
    oDiv.style.width = '100%';
    oDiv.style.top = '0px';
    oDiv.style.left = '0px';

    document.body.appendChild(oDiv);

    handleUpdate = (msgs: Array<Required<IMessageConfig>>) => {
      ReactDOM.render(<Message messages={msgs} />, oDiv);
    };
  }

  const getConfig = (
    config: string | IMessageConfig,
    duration?: number,
  ): IMessageConfig => {
    const newConfig = (typeof config === 'string'
      ? {
          content: config,
          duration: typeof duration !== 'undefined' ? duration : 3,
        }
      : config) as IMessageConfig;

    if (typeof duration !== 'undefined') {
      // TODO: 坑，config 中也可以指定 duration，此处认为 单独声明的 duration 优先级更高
      newConfig.duration = duration;
    }

    newConfig.key = newConfig.key || `msgid-${++uuid}`;
    if (uuid > 10000000) {
      uuid = 0;
    }

    return newConfig;
  };

  return {
    open(config: IMessageConfig) {
      init();
      messages = [...messages, config];
      handleUpdate(messages as Array<Required<IMessageConfig>>);
      if ((config.duration as number) > 0) {
        setTimeout(() => {
          const { key, onClose } = config;
          this.close(key);
          onClose && onClose();
        }, (config.duration as number) * 1000);
      }
    },

    custom(
      node: React.ReactElement,
      duration?: number,
      config?: Omit<IMessageConfig, 'node'>,
    ) {
      const newConfig = getConfig(config || '', duration);
      newConfig.node = node;
      this.open(newConfig);
    },

    close(key?: string) {
      if (key) {
        const index = messages.findIndex((m) => m.key === key);
        if (index !== -1) {
          messages = [...messages];
          messages.splice(index, 1);
        }
      } else {
        messages = [];
      }
      if (handleUpdate) {
        handleUpdate(messages as Array<Required<IMessageConfig>>);
      }
    },

    success(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'success';
      this.open(config);
    },

    error(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'error';
      this.open(config);
    },

    warning(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'warning';
      this.open(config);
    },

    loading(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'loading';
      this.open(config);
    },

    info(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'info';
      this.open(config);
    },
  };
})();
