import React from 'react';
import { IMenuItemGroupProps } from './interface';
import {
  InlineMenuItemContainer,
  MenuItemGroupTitle,
} from './styled';

export const InlineMenuItemGroup: React.FC<IMenuItemGroupProps> = React.memo(
  props => {
    const { title, children } = props;

    return (
      <React.Fragment>
        {title ? <MenuItemGroupTitle>{title}</MenuItemGroupTitle> : null}
        <InlineMenuItemContainer>{children}</InlineMenuItemContainer>
      </React.Fragment>
    );
  },
);
