import React, {
  useCallback,
  useContext,
  useEffect,
  useRef,
  useMemo,
  useState,
} from 'react';
import { classnames } from '../../utils';
import { ISubMenuProps } from './interface';
import { PathContext, MenuKeyContext, SubMenuContext } from './contexts';
import { Dropdown } from '../dropdown';
import { ArrowRightNormal } from '../icons';
import {
  SubMenuWrap,
  SubMenuInnerWrap,
  TriggerWrap,
  TriggerIconWrap,
  TriggerTitleWrap,
  SubMenuTransitionWrap,
} from './styled';
import { EMenuMode, MENU_VISIBLE_TOGGLE_DELAY } from './consts';
import {
  useController,
  useMenuKeyContext,
  usePath,
  useHotkeyController,
  useHotKeyScrollRef,
  useMousePosition,
} from './hooks';
import { Tooltip } from '../tooltip';

import { DropdownAnimation } from '@ali/we-util';

// eslint-disable-next-line max-lines-per-function
export const SubMenu: React.FC<ISubMenuProps> = React.memo((props) => {
  const {
    children,
    title,
    showArrow,
    disabled,
    disabledTip,
    tooltipZIndex,
    overlayClassName,
    overlayStyle,
    testid,
    icon,
    maxHeight,
    forceUnfoldByClick = false,
    role,
    style,
  } = props;

  const controller = useController('SubMenu');

  const {
    mode,
    zIndex,
    menuId,
    destroyOverlayWhenInvisible,
    shortcutKey,
    menuRootRef,
    openedMenuKeys,
    getContainer: getContainerCtr,
    minWidth,
  } = controller;

  const {
    hotkeyPosition,
    setHotkeyPosition,
    isHotkeyType,
    setHotkeyType,
    getActionNode,
    registerValidNode,
    unRegisterValidNode,
  } = useHotkeyController('SubMenu');

  const [shortcutKeyLock, setShortcutKeyLock] = useState<boolean>(false);

  const shortcutKeyLockRef = useRef<boolean>(false);

  const shortcutKeyLockTimer = useRef<number>(0);

  // 处理menuKey
  const [menuKeyCtxValue, menuKey] = useMenuKeyContext(props.menuKey);

  // key路径
  const path = usePath(menuKey);

  // 层级
  const level = path.split('::').length - 1;

  // 是否可见
  const visible = controller.isSubMenuOpened(menuKey);

  // 是否为第一级横向菜单
  const isHorizontal = mode === EMenuMode.horizontal && level === 0;

  const { setMousePosition, mousePosition } = useMousePosition();

  // 第一级横向菜单在其他菜单展开 / 非第一级横向菜单的 action 都是 hover
  let dropdownTrigger: 'hover' | 'click' | 'contextMenu' =
    (!isHorizontal || controller.isAnySubMenuOpened(path)) && !isHotkeyType
      ? 'hover'
      : 'click';
  // 在 safari 浏览器和 chrome 会出现 menuItem onClick 无法触发的bug
  if (openedMenuKeys?.length === 1) {
    dropdownTrigger = visible ? 'click' : 'hover';
  }

  const elRef = useHotKeyScrollRef(menuKey, isHotkeyType, hotkeyPosition);

  const subMenuContext = useContext(SubMenuContext);

  /**
   * 可见性变化处理
   * 只有显示的时候处理，不可见应该是通过click来实现
   *
   * @param {boolean} e
   * @return {void}
   */
  const handleVisibleChange = useCallback(
    (e: boolean): void => {
      if (e && !visible) {
        if (controller.showMenuTimer) {
          window.clearTimeout(controller.showMenuTimer);
        }
        controller.showMenuTimer = window.setTimeout(() => {
          !shortcutKeyLockRef.current && controller.showSubMenu(path);
        }, MENU_VISIBLE_TOGGLE_DELAY);
      } else if (!e && isHorizontal) {
        controller.closeAllMenu();
      }
    },
    [controller, path, visible, isHorizontal],
  );

  const finalVisible = disabled || shortcutKeyLock ? false : visible;

  const getContainer = () => {
    return menuRootRef?.current || document.body;
  };

  /**
   * 允许快捷键时绑定鼠标进入事件，记录当前位置
   */
  const handleMouseEnter = useCallback(
    (event: React.MouseEvent) => {
      if (shortcutKeyLockTimer.current)
        window.clearTimeout(shortcutKeyLockTimer.current);
      shortcutKeyLockRef.current = false;
      if (!shortcutKey) return;
      if (mousePosition === event.clientY) {
        setShortcutKeyLock(true);
        shortcutKeyLockRef.current = true;
        shortcutKeyLockTimer.current = window.setTimeout(() => {
          setShortcutKeyLock(false);
          shortcutKeyLockRef.current = false;
        }, 2000);
        return;
      }
      setShortcutKeyLock(false);
      shortcutKeyLockRef.current = false;
      setMousePosition(event.clientY);
      setHotkeyType(false);
      setHotkeyPosition(getActionNode(menuKey));
      if (
        (!isHorizontal || controller.isAnySubMenuOpened(path)) &&
        dropdownTrigger === 'click'
      ) {
        /**
         * 这个场景是在垂直模式下，首次鼠标hover到二级菜单的时候，不展开二级菜单
         * 这个问题的原因是这个时候dropdownTrigger为click，触发了mouseenter，然后isHotkeyType被置为false
         * dropdownTrigger会变成hover，但是不会触发handleVisibleChange，导致不会展开二级菜单
         * 这个解法的改动影响最小
         */
        controller.showMenuTimer = window.setTimeout(() => {
          controller.showSubMenu(path);
        }, MENU_VISIBLE_TOGGLE_DELAY);
      }
    },
    [
      visible,
      shortcutKey,
      mousePosition,
      controller,
      path,
      dropdownTrigger,
      isHorizontal,
    ],
  );

  /**
   * 允许垂直模式下通过点击事件展开submenu
   */
  const handleClick = useCallback(() => {
    if (!isHorizontal && !visible) {
      controller.showSubMenu(path);
    }
  }, [controller, isHorizontal, path, visible]);

  /**
   * 快捷键：注册有效节点
   */
  useEffect(() => {
    if (!disabled && shortcutKey) {
      registerValidNode({
        menuKey,
        path,
        children: [],
        parentPath: path.split('::').slice(0, -1).join('::'),
      });
      return () => {
        unRegisterValidNode({ menuKey, path });
      };
    }
  }, [shortcutKey]);

  const styleCombined = useMemo(
    () => ({
      padding: '4px 0',
      ...overlayStyle,
    }),
    [overlayStyle],
  );

  return (
    <Dropdown
      placement={isHorizontal ? 'bottomLeft' : 'rightTop'}
      trigger={dropdownTrigger}
      visible={finalVisible}
      getContainer={getContainerCtr || getContainer}
      onVisibleChange={handleVisibleChange}
      overlayStyle={{
        padding: 0,
        border: 'none',
        boxShadow: 'none',
        backgroundColor: 'transparent',
      }}
      overlayClassName={classnames(
        overlayClassName,
        `we-ui-submenu-${menuId}-overlay`,
      )}
      overlay={
        destroyOverlayWhenInvisible && !finalVisible ? null : (
          <SubMenuTransitionWrap>
            <SubMenuContext.Provider
              value={{
                showSelect:
                  props.showSelect !== undefined
                    ? props.showSelect
                    : subMenuContext.showSelect !== undefined
                    ? subMenuContext.showSelect
                    : controller.showSelect,
              }}
            >
              <PathContext.Provider value={path}>
                <MenuKeyContext.Provider value={menuKeyCtxValue}>
                  <DropdownAnimation
                    className="we-design-menu-scroll-box"
                    visible={visible}
                    maxHeight={maxHeight}
                    overlayStyle={styleCombined}
                  >
                    {children}
                  </DropdownAnimation>
                </MenuKeyContext.Provider>
              </PathContext.Provider>
            </SubMenuContext.Provider>
          </SubMenuTransitionWrap>
        )
      }
      offsetY={isHorizontal ? 0 : -4}
      offsetX={2}
      zIndex={typeof zIndex === 'number' ? zIndex + level : undefined}
      role={role}
    >
      <SubMenuWrap
        ref={elRef}
        path={path}
        menuId={controller.menuId}
        className={classnames(`level-${level}-wrap`, {
          vertical: !isHorizontal,
          horizontal: isHorizontal,
          opened: finalVisible,
        })}
        data-testid={testid ? `submenu-${testid}` : undefined}
        data-role={role}
        onMouseEnter={handleMouseEnter}
        onClick={forceUnfoldByClick ? handleClick : undefined}
      >
        <Tooltip
          disabled={!(disabled && disabledTip)}
          title={disabledTip}
          placement={'top'}
          zIndex={tooltipZIndex}
        >
          <SubMenuInnerWrap
            isHotkeyType={isHotkeyType}
            className={classnames(`level-${level}-inner-wrap`, {
              vertical: !isHorizontal,
              horizontal: isHorizontal,
              opened: finalVisible,
              disabled,
              hotkeyHover:
                hotkeyPosition &&
                menuKey === hotkeyPosition.menuKey &&
                isHotkeyType &&
                shortcutKey,
            })}
            style={{ minWidth, ...style }}
          >
            <TriggerWrap
              showSelect={
                (subMenuContext.showSelect !== undefined
                  ? subMenuContext.showSelect
                  : controller.showSelect) && level > 0
              }
              showSelectOnRight={controller.showSelectOnRight}
              className={classnames(`level-${level}-trigger`, {
                hasArrow: showArrow && !isHorizontal,
              })}
            >
              {icon && <TriggerIconWrap>{icon}</TriggerIconWrap>}
              <TriggerTitleWrap
                className={classnames({ vertical: !isHorizontal })}
              >
                {title}
              </TriggerTitleWrap>
            </TriggerWrap>
            <ArrowRightNormal
              className={classnames('icon arrow-icon', { disabled })}
              style={{
                display: showArrow && !isHorizontal ? 'inline-block' : 'none',
              }}
            />
          </SubMenuInnerWrap>
        </Tooltip>
      </SubMenuWrap>
    </Dropdown>
  );
});

SubMenu.defaultProps = {
  showArrow: true,
};
