import React, { useMemo } from 'react'
import dayjs, { Dayjs } from 'dayjs'
import localeData from 'dayjs/plugin/localeData'
import useControlledValue from '../../hooks/useControlledValue'
import { useTranslate, useDayjsLocale } from '@ali/we-util'
import { classnames } from '../../utils'
import { Control } from './Control'
import {
  MonthPanelWrap,
  MonthPanelBody,
  MonthPanelItem,
  ControlWrap,
  PanelControlWrap,
} from './styled'
import { DatePickerPanelType } from './DatePicker'
dayjs.extend(localeData)

export interface IMonthPanelProps {
  /**
   * 国际化设置
   */
  locale?: string
  /**
   * 日期值
   */
  value?: Dayjs
  /**
   * 日期默认值
   */
  defaultValue?: Dayjs
  /**
   * 是否带左右的控制条
   */
  control?: boolean
  /**
   * 预览的年份
   */
  year?: number
  /**
   * 修改dayjs值
   */
  onChange?: (e: Dayjs) => void
  /**
   * 修改显示年份
   */
  onChangeYear?: (e: number) => void
  /**
   * 点击控制条
   */
  onClickControl?: (e: React.MouseEvent<HTMLElement>) => void
  /**
   * 样式
   */
  style?: React.CSSProperties
  /**
   * 不可选择的日期
   */
  disabledDate?: (currentDate: Dayjs, view: DatePickerPanelType) => boolean
  /**
   * 测试使用
   */
  testid?: string
}

const MonthPanel: React.FC<IMonthPanelProps> = (props) => {
  const {
    onChange,
    control,
    onChangeYear,
    locale,
    onClickControl,
    style,
    disabledDate,
    testid,
  } = props

  // 国际化
  const t = useTranslate(locale)
  const dayjsLocale = useDayjsLocale(locale)

  // 可传dayjs值进来，受控
  const [value, setValue] = useControlledValue(props.value, props.defaultValue)

  // 可传当前显示年份，受控
  const [year, setYear] = useControlledValue(props.year, value!.year())

  // 拿到语言包短月份数组
  const months: string[] = useMemo(() => {
    return dayjs()
      .locale(dayjsLocale as any)
      .localeData()
      .monthsShort()
  }, [dayjsLocale])

  const monthDates = useMemo(() => {
    return months.map((_, i) => dayjs().year(year!).month(i))
  }, [year, months])

  /**
   * 修改dayjs值
   *
   * @param {number} e
   * @return {void}
   */
  const handleChange = (e: number): void => {
    const newValue = value!.month(e).year(year!)
    if (onChange) {
      onChange(newValue)
    }
    setValue(newValue)
  }

  /**
   * 按下左右按钮修改年份
   *
   * @param {number} e year
   * @return {void}
   */
  const handleChangeYear = (e: number): void => {
    if (onChangeYear) {
      onChangeYear(e)
    }
    setYear(e)
  }

  // 控制条显示文案国际化
  const yearString = useMemo(
    () => dayjs().year(year!).format(t('we_ui_date_year_format')),
    [year, t]
  )

  return (
    <MonthPanelWrap style={style}>
      {control !== false && (
        <ControlWrap>
          <Control
            hasNext={false}
            hasPrev={false}
            onClickSuperNext={(): void => handleChangeYear(year! + 1)}
            onClickSuperPrev={(): void => handleChangeYear(year! - 1)}
            testid={testid ? `MonthPanel-${testid}` : undefined}
          >
            <PanelControlWrap
              className={classnames({ clickable: !!onClickControl })}
              onClick={onClickControl}
            >
              {yearString}
            </PanelControlWrap>
          </Control>
        </ControlWrap>
      )}
      <MonthPanelBody>
        {months.map((month, index) => {
          const isDisabled =
            typeof disabledDate === 'function'
              ? disabledDate(monthDates[index], 'month')
              : false
          return (
            <MonthPanelItem key={month}>
              <span
                className={classnames({
                  actived: year === value!.year() && index === value!.month(),
                  isDisabled,
                })}
                onClick={(): void => {
                  !isDisabled && handleChange(index)
                }}
              >
                {month}
              </span>
            </MonthPanelItem>
          )
        })}
      </MonthPanelBody>
    </MonthPanelWrap>
  )
}

MonthPanel.defaultProps = {
  control: true,
  defaultValue: dayjs(),
}

const MonthPanelMemo = React.memo(MonthPanel)

export { MonthPanelMemo as MonthPanel }
