import React from 'react';
// 插入类型
export enum EInsertType {
  // 选人
  Person = 0,
  // 选群
  Group = 1,
  // 选文件
  Document = 2,
  // 选文件夹
  Folder = 3,
}
export enum FileTypeKey {
  OFFICE_WORD,
  OFFICE_EXCEL,
  OFFICE_POWERPOINT,
  DOC,
  SHEET,
  WHITEBOARD,
  PRESENTATION,
  MIND,
  FOLDER,
}
export const fileTypeKey2LargeIcon = (type?: number) => {
  switch (type) {
    case FileTypeKey.OFFICE_WORD:
      return 'https://img.alicdn.com/imgextra/i1/O1CN01NkJXrJ29ZblZQpgZo_!!6000000008082-55-tps-32-32.svg';
    case FileTypeKey.OFFICE_EXCEL:
      return 'https://img.alicdn.com/imgextra/i1/O1CN01RuCzQg1yjijsoL4LV_!!6000000006615-55-tps-32-32.svg';
    case FileTypeKey.OFFICE_POWERPOINT:
      return 'https://img.alicdn.com/imgextra/i1/O1CN01t0jjns1fiMry5bCiD_!!6000000004040-55-tps-32-32.svg';
    case FileTypeKey.DOC:
      return 'https://img.alicdn.com/imgextra/i1/O1CN01pG3xqM208HakjsQAZ_!!6000000006804-55-tps-32-32.svg';
    case FileTypeKey.SHEET:
      return 'https://img.alicdn.com/imgextra/i2/O1CN01DCPlBp1XxT00OCMdR_!!6000000002990-55-tps-32-32.svg';
    case FileTypeKey.WHITEBOARD:
      return 'https://img.alicdn.com/imgextra/i4/O1CN01V8EWMG1MRRWwdUoQ5_!!6000000001431-55-tps-32-32.svg';
    case FileTypeKey.PRESENTATION:
      return 'https://img.alicdn.com/imgextra/i4/O1CN01gClITa1khWDBEQTls_!!6000000004715-55-tps-32-32.svg';
    case FileTypeKey.MIND:
      return 'https://img.alicdn.com/imgextra/i1/O1CN01sH2OwL28G5GW0kYV0_!!6000000007904-55-tps-32-32.svg';
    case FileTypeKey.FOLDER:
      return 'https://img.alicdn.com/imgextra/i3/O1CN01hulOoL1psZkRw7RqX_!!6000000005416-55-tps-66-66.svg';
    // [FileTypeKey.SHEET]: 'https://img.alicdn.com/imgextra/i3/O1CN01SmA22l1stRLMYanKl_!!6000000005824-2-tps-128-128.png',
  }
};
// 基础列表项
export interface IInsertItemBase {
  /**
   * 业务层可以扩展字段
   */
  [key: string]: any;
  // 人 / 群 / 文件
  type: EInsertType;
  // uid / cid / dentryKey
  key: string;
  // 人名 / 群名 / 文件名
  title: string;
  // 所在组织 / 所在企业 / 所在文档位置
  description: string;
  // 作为附加描述, 会和 description 一行，在右侧显示
  descriptionSupplement?: string | React.ReactNode;
  // 选人时只有一个头像，取第0个元素；选群时可能会有多个头像
  iconUrls?: string[];
}
// 选人列表项
export interface IInsertItemPerson extends IInsertItemBase {
  // 插入人的用户昵称
  nickName: string;
}
// 选群列表项
export type IInsertItemGroup = IInsertItemBase;
// 选文件列表项
export interface IInsertItemDoc extends IInsertItemBase {
  docType: number;
  payload?: Record<string, string | number>;
}
export type IInsertItem = IInsertItemPerson | IInsertItemGroup | IInsertItemDoc;
// 列表分组，如：文档成员、最近
export interface IListGroup {
  title: string;
  list: IInsertItem[];
}
// 右上角的高级选项
export interface IAdvancedOption {
  icon: React.ReactNode;
  name: string;
  onClick: () => void;
}
export enum EQuickInsertStatus {
  loading = 0,
  ready = 1,
  loadMore = 2,
  skeleton = 3,
}
export interface IQuickInsertProps {
  data: IListGroup[];
  advanceOption?: IAdvancedOption;
  // 是否使用快捷键,默认false
  useHotKey?: boolean;
  locale?: string;
  status: EQuickInsertStatus;
  onItemClick?: (itemData: IInsertItem) => void;
  loadMore: () => void;
  // 异常提示
  errorMsg?: string;
  ErrorIcon?: React.Component;
  height?: number;
  width?: number;
  clearActive?: boolean;
  emptyUrl?: string;
  emptyMsg?: string;
}
