import React, { useCallback, useEffect } from 'react';
import {
  useController,
  useHotkeyController,
  useMousePosition,
  useHotKeyScrollRef,
} from './hooks';
import { IInlineMenuItemProps } from './interface';
import { InlineMenuItemIconWrapper } from './styled';
import { classnames } from '../../utils';
import { Tooltip } from '../tooltip';

export const InlineMenuItem: React.FC<IInlineMenuItemProps> = React.memo(
  (props) => {
    const {
      icon,
      testid,
      selected,
      disabled,
      tooltipZIndex,
      menuKey,
      autoClose,
      onClick,
      tooltip,
      role = '',
    } = props;
    const controller = useController('MenuItem');

    const hotkeyController = useHotkeyController('MenuItem');

    const {
      hotkeyPosition,
      registerValidNode,
      unRegisterValidNode,
      isHotkeyType,
    } = hotkeyController;

    const elRef = useHotKeyScrollRef(menuKey, isHotkeyType, hotkeyPosition);

    const { setMousePosition, mousePosition } = useMousePosition();

    // 是否选中，props.selected优先级最高
    const isSelected =
      typeof selected === 'boolean'
        ? selected
        : controller.isMenuItemSelected(menuKey);

    /**
     * 快捷键：注册有效节点
     */
    useEffect(() => {
      if (!disabled && controller.shortcutKey && elRef.current) {
        registerValidNode({
          menuKey,
          path: menuKey,
          onClick: handleClick,
          parentPath: '',
          type: 'inline',
        });
        return () => {
          unRegisterValidNode({
            menuKey,
            path: menuKey,
          });
        };
      }
    }, [disabled, controller.shortcutKey]);

    const handleClick = useCallback(
      (event: React.MouseEvent<HTMLElement>) => {
        // 自动关闭处理
        if (autoClose) {
          controller.closeAllMenu();
        }
        if (disabled) {
          return;
        }
        // 点击处理
        if (onClick) {
          onClick(menuKey, event);
        }
        // 选中处理
        controller.selectMenuItem(menuKey);
        if (controller.clickMenuItem) {
          controller.clickMenuItem(menuKey, event);
        }
      },
      [disabled, autoClose, controller, onClick, menuKey],
    );

    /**
     * 如果有快捷键时新增的鼠标进入事件
     */
    const hotkeyMouseEnterHander = useCallback(
      (event) => {
        if (!disabled && mousePosition !== event.clientY) {
          hotkeyController.setHotkeyType(false);
          setMousePosition(event.clientY);
          hotkeyController.setHotkeyPosition(
            hotkeyController.getActionNode(menuKey),
          );
        }
      },
      [disabled, menuKey, mousePosition],
    );

    const handleMouseEnter = useCallback(
      (event) => {
        if (controller.shortcutKey) {
          hotkeyMouseEnterHander(event);
        }
      },
      [controller, hotkeyMouseEnterHander],
    );

    return (
      <Tooltip title={tooltip} placement={'top'} zIndex={tooltipZIndex}>
        <InlineMenuItemIconWrapper
          onClick={handleClick}
          onMouseEnter={handleMouseEnter}
          data-path={menuKey}
          data-menuid={controller.menuId}
          data-testid={testid ? `menu-item-${testid}` : undefined}
          data-role={role}
          ref={elRef}
          className={classnames({
            selected: isSelected,
            disabled,
            hotkeyHover:
              hotkeyPosition &&
              menuKey === hotkeyPosition.menuKey &&
              !disabled &&
              controller.shortcutKey,
          })}
        >
          {icon}
        </InlineMenuItemIconWrapper>
      </Tooltip>
    );
  },
);
