import React, { useCallback } from 'react';
import { Avatar } from '..';
import { Tooltip } from '../tooltip';
import { LoadingNormal } from '../icons';
import { TranslateComp } from '@ali/we-util';
import {
  EInsertType,
  IInsertItemPerson,
  IInsertItem,
  fileTypeKey2LargeIcon,
  IInsertItemDoc,
} from './type';
import {
  ItemWrapper,
  ItemContent,
  ItemTitle,
  ItemDescription,
  EllipsisText,
  LoadingWrapper,
  LoadingText,
  MiddleEllipsisWrapper,
  AvatarWrapper,
  DescriptionLeft,
  DescriptionRight,
  Description,
  Skeleton,
} from './styled';
// 描述信息最后显示的字符数
const DESCRIPTION_LAST_CHAR_COUNT = 10;
interface IProps {
  // 当前列表项是否被激活
  active: boolean;
  // 列表项点击事件
  onItemClick?: (itemData: IInsertItem) => void;
  data: IInsertItem;
}
interface IMiddleEllipsisTextProps {
  width: number;
  text: string;
  lastCharNum: number;
}
export const MiddleEllipsisText = React.memo((props: IMiddleEllipsisTextProps) => {
  // 文本容器的引用
  const refContainer = React.useRef<HTMLDivElement>(null);
  // prop属性
  const { text, width, lastCharNum } = props;
  // state
  const [isTextOverflow, setTextOverflow] = React.useState<boolean>(false);
  const [divideIndex, setDivideIndex] = React.useState<number>(0);
  React.useEffect(() => {
    if (refContainer.current && text) {
      const rect = refContainer.current.getBoundingClientRect();
      const containerWidth = rect.width;
      const textWidth = refContainer.current.scrollWidth;
      if (containerWidth > 0 && textWidth > containerWidth) {
        setTextOverflow(true);
        // 一行展示不下时，中间省略，截取最后10个字符最后显示
        const lastLen = Math.min(text.length, lastCharNum);
        const diIndex = text.length - lastLen;
        setDivideIndex(diIndex);
      }
    }
  }, [text, width]);
  const text1 = text?.slice(0, divideIndex) || '';
  const text2 = text?.slice(divideIndex) || '';
  return (
    <Tooltip title={text} disabled={!isTextOverflow}>
      <MiddleEllipsisWrapper width={width} ref={refContainer}>
        {isTextOverflow ? (
          <>
            <EllipsisText>{text1}</EllipsisText>
            <span>{text2}</span>
          </>
        ) : (
          <span>{text}</span>
        )}
      </MiddleEllipsisWrapper>
    </Tooltip>
  );
});
export const QuickInsertItem = React.memo((props: IProps) => {
  // Description的引用
  const refDescription = React.useRef<HTMLDivElement>(null);
  // const descriptionWidth = React.useRef<number>(0);
  const [descriptionWidth, setDescriptionWidth] = React.useState<number>(0);
  React.useEffect(() => {
    if (refDescription.current) {
      const rect = refDescription.current.getBoundingClientRect();
      console.log(rect.width);
      setDescriptionWidth(rect.width);
    }
  }, []);
  const { data, active, onItemClick } = props;
  const { type, key, iconUrls, title, description, descriptionSupplement } = data;
  const nickName = type === EInsertType.Person ? (data as IInsertItemPerson).nickName : '';
  // 存在头像url并且第一个url不为空串
  const isNameAvatar = iconUrls && iconUrls?.length > 0 && iconUrls[0];
  const handleClick = useCallback((e: React.MouseEvent) => {
    e?.stopPropagation();
    if (typeof onItemClick === 'function') {
      onItemClick(data);
    }
  }, [data, onItemClick]);
  // 渲染文档icon
  const renderDocIcon = () => {
    // todo
    const { docType } = data as IInsertItemDoc;
    return (
      <AvatarWrapper>
        <img style={{ width: 32, height: 32 }} src={fileTypeKey2LargeIcon(docType)} />
      </AvatarWrapper>
    );
  };

  React.useEffect(() => {
    if (descriptionSupplement && description) {
      let span = document.createElement('span');
      let textWidth = span.offsetWidth;
      span.style.visibility = 'hidden';
      span.style.fontSize = '12px';
      span.style.display = 'inline-block';
      document.body.appendChild(span);
      if (typeof span.textContent !== 'undefined') {
        span.textContent = description;
      } else {
        span.innerText = description;
      }
      textWidth = span.clientWidth;
      setDescriptionWidth(textWidth);
      span?.parentNode?.removeChild(span); //删除节点
    }
  }, [title]);

  const renderSupplementDesc = () => {
    return (
      <Description>
        <Tooltip title={description} disabled={descriptionWidth <= 180}>
          <DescriptionLeft>{description} </DescriptionLeft>
        </Tooltip>
        <DescriptionRight>{descriptionSupplement}</DescriptionRight>
      </Description>
    );
  };
  return (
    <ItemWrapper key={key} active={active} onClick={handleClick}>
      {type === EInsertType.Document ? (
        renderDocIcon()
      ) : (
        <AvatarWrapper>
          <Avatar
            className="avatar"
            size="normal"
            url={isNameAvatar ? iconUrls : undefined}
            name={nickName}
          />
        </AvatarWrapper>
      )}
      <ItemContent>
        <ItemTitle>{title}</ItemTitle>
        {descriptionSupplement ? (
          renderSupplementDesc()
        ) : (
          <ItemDescription ref={refDescription}>
            <MiddleEllipsisText
              width={descriptionWidth}
              text={description}
              lastCharNum={DESCRIPTION_LAST_CHAR_COUNT}
            />
          </ItemDescription>
        )}
      </ItemContent>
    </ItemWrapper>
  );
});
export const LoadingItem = ({ locale }: { locale?: string }) => {
  return (
    <LoadingWrapper>
      <LoadingNormal spin />
      <LoadingText>
        <TranslateComp locale={locale} textKey={'we_ui_quick_insert_loading'} />
      </LoadingText>
    </LoadingWrapper>
  );
};

export const SkeletonLoading = (width: number) => {
  return (
    <ItemWrapper active={false}>
      <AvatarWrapper>
        <Skeleton width={'32px'} height={'32px'} borderRadius={'4px'} />
      </AvatarWrapper>
      <ItemContent>
        <Skeleton width={`${width}px`} height={'10px'} />
      </ItemContent>
    </ItemWrapper>
  );
};
