import dayjs from 'dayjs';

const DATE_RE = /^(\d{4})(\/|\s|-|\.)?(\d{1,2})?(\/|\s|-|\.)?(\d{1,2})?$/;
const TIME_RE = /^(\d{1,2}):(\d{1,2})?:(\d{1,2})?$/;

function checkDay(
  yearString: string,
  monthString: string,
  dayString: string,
) {
  const monthNumber = Number(monthString);
  const daysInMonth = dayjs().set('year', Number(yearString)).set('month', Number(monthNumber - 1)).daysInMonth();
  return !!Number(dayString) && Number(dayString) <= daysInMonth;
}

function checkMonth(monthString: string) {
  const monthNumber = Number(monthString);
  return !!monthString && monthNumber < 13;
}

export function checkDate(dateString: string) {
  const reg = dateString.match(DATE_RE);
  if (reg) {
    const [, years, deliYM, months, deliMD, day] = reg;
    return !!(years && deliYM && months && deliMD && day)
      && checkDay(years, months, day)
      && checkMonth(months);
  }
  return false;
}

export function checkTime(timeString: string) {
  const reg = timeString.match(TIME_RE);
  if (reg) {
    const [, hour, minute, second] = reg;
    return !!(hour && minute && second)
      && Number(hour) < 24
      && Number(minute) < 60
      && Number(second) < 60;
  }
  return false;
}
