import React, { useEffect, useState, useCallback, useRef } from 'react';
import { InputWrapper, Input, IconBox } from './styled';
import {
  SearchBetaNormal,
  RoundcloseFillBetaNormal,
} from '../../../components/icons';

const defaultIconStyle = { color: 'rgba(17, 31, 44, 0.40)' };

export interface InputProps {
  /**
   * 是否显示前面的icon
   */
  prefix?: boolean;

  /**
   * 显示value
   */

  text: string | number;

  /**
   * 提示
   */
  placeholder?: string;

  /**
   * text change回调
   */
  onChange: (value: string) => void;
}

export const InputComponent: React.FC<InputProps> = (props) => {
  const ref = useRef<HTMLInputElement>();
  const { text = '', onChange, prefix = true } = props;

  const [value, setValue] = useState<string>('');

  /**
   * value是否为空
   */
  const isEmptyValue = (): boolean => { return String(value) === ''; };

  /**
   * input change处理
   */
  const handleChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setValue(e.target.value);
    onChange(e.target.value);
  }, [onChange]);

  const handleClear = useCallback((e: React.MouseEvent) => {
    setValue('');
    onChange('');
    e.preventDefault();
  }, [onChange]);

  useEffect(() => {
    ref.current?.focus();
  }, []);
  useEffect(() => { setValue(String(text)); }, [text]);

  return (
    <InputWrapper>
      { prefix && (
      <IconBox className="left">
        <SearchBetaNormal style={defaultIconStyle} />
      </IconBox>)
      }
      <IconBox className="right">
        {!isEmptyValue() && (
        <RoundcloseFillBetaNormal
          style={defaultIconStyle}
          onMouseDown={handleClear}
        />)
        }
      </IconBox>
      <Input value={value} onChange={handleChange} ref={ref} placeholder={props.placeholder} />
    </InputWrapper>
  );
};
