import React from 'react';

export interface IToggleValueEvent {
  checked: boolean;
  value: string;
}

export interface ICheckboxContext {
  checkedValues: string[];
  onToggleValue: (e: IToggleValueEvent) => void;
  disabled: boolean;
  testid?: string;
}

export const CheckboxContext = React.createContext<ICheckboxContext | null>(null);
