import React, { useMemo } from 'react';
import { debounce } from 'lodash';

import ColorMatrix from './ColorMatrix';
import CustomColorIcon from './CustomColorIcon';
import {
  SelectorWrapper,
  SelectorTitle,
  SelectorHeader,
  SelectorIconWrapper,
} from './styled';
import { IColorSelector } from './types';
import { isMobile } from '../../utils/dom';

const ColorSelector: React.FC<IColorSelector> = (props) => {
  const {
    title,
    setCustomPanelVisible,
    setCustomColor,
    setCustomClickHandler,
    shouldShowPreview,
    enableCustomColor,
    triggerKey,
    setTriggerKey,
    customPanelVisible,
    ...rest
  } = props;

  const { currentColors, selectedKey, onClick } = rest;

  const toggleCustomPanel = () => {
    if (isMobile) {
      return;
    }
    // 多个 trigger 控制同一个 panel, key 相同时取反 visible, 否则更新 panel
    if (triggerKey === selectedKey) {
      return setCustomPanelVisible((visible) => !visible);
    }

    setCustomColor(currentColors[selectedKey] || '');
    if (onClick) {
      setCustomClickHandler(() => debounce(onClick, 30));
    }
    setCustomPanelVisible(true);
    setTriggerKey(selectedKey);
  };

  const isTriggerActive = useMemo(() => {
    return customPanelVisible && triggerKey === selectedKey;
  }, [customPanelVisible, triggerKey, selectedKey]);

  return (
    <SelectorWrapper>
      <SelectorHeader>
        <SelectorTitle>{title}</SelectorTitle>
        {enableCustomColor && (
          <SelectorIconWrapper
            onClick={toggleCustomPanel}
            active={isTriggerActive}
            data-testid={'custom-color-trigger'}
          >
            <CustomColorIcon />
          </SelectorIconWrapper>
        )}
      </SelectorHeader>
      <div>
        <ColorMatrix {...rest} />
      </div>
    </SelectorWrapper>
  );
};

export default ColorSelector;
