export const DatePickerTrigger = withTheme(styled.div`
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  width: 100%;
  min-width: 100px;
  height: ${({ theme }) => theme.ui.elementHeight};
  line-height: ${({ theme }) => `${parseInt(theme.ui.elementHeight, 10) - 2}px`};
  border: 1px solid ${({ theme }) => theme.ui.commonLineLightColor};
  border-radius: ${({ theme }) => theme.ui.radius};
  transition: all .24s ease;
  padding: 0 ${({ theme }) => theme.ui.padding};
  font-size: 0 ${({ theme }) => theme.ui.fontSize};
  user-select: none;

  &:hover {
    border-color: ${({ theme }) => theme.ui.primaryColor};
    cursor: pointer;
  }
  .calendar-icon {
    margin-left: ${({ theme }) => theme.ui.marginSmall};
    color: ${({ theme }) => theme.ui.colorText2};
  }

  &.placeholder {
    color: ${({ theme }) => theme.ui.colorText2};
  }

  &.disabled {
    color: ${({ theme }) => theme.ui.disabledColor};
    background-color: ${({ theme }) => theme.ui.disabledBackgroundColor};
    border-color: ${({ theme }) => theme.ui.disabledBorderColor};
    cursor: not-allowed;
  }

  &.allowClear {
    .clear-icon {
      position: absolute;
      color: ${({ theme }) => theme.ui.colorText2};
      right: ${({ theme }) => theme.ui.padding};
      display: none;
    }
    &:hover {
      .calendar-icon {
        opacity: 0;
      }
      .clear-icon {
        display: inline-block;
      }
    }
  }

  * {
    box-sizing: border-box;
  }
`);
