import React, { memo } from 'react';
import { useTranslate } from '@ali/we-util';
import {
  Title,
  Content,
  Footer,
  Header,
  Body,
  CloseButton,
  FooterWrap,
  CancelButton,
  ConfirmButton,
} from './styled';
import { LoadingNormal } from '../../components/icons';
import { IModalProps } from './interface';
import { TemplateCloseNormal } from '../../components/icons-toolbar';

type IModalContentProps = Omit<
  IModalProps,
  'visible' | 'maskStyle' | 'maskClosable' | 'keyboard'
> & {
  onMouseDown?: (event: React.MouseEvent) => void;
};

const stopPropagation = (e: React.MouseEvent) => e.stopPropagation();

export const ModalContent = memo<IModalContentProps>((props) => {
  const {
    title,
    children,
    width,
    footer,
    bodyStyle,
    style,
    closable,
    testid,
    confirmLoading,
    okButtonProps = {},
    cancelButtonProps = {},
    contentRef,
    onOk,
    onCancel,
    onMouseDown,
  } = props;

  // 国际化
  const t = useTranslate(props.locale);
  const {
    okText = t('we_ui_ok_text'),
    cancelText = t('we_ui_cancel_text'),
  } = props;

  return (
    <Content
      ref={contentRef}
      width={width}
      style={style}
      onMouseDown={onMouseDown}
      data-testid={testid ? `Modal_content_${testid}` : undefined}
    >
      {!!title && (
        <Header>
          <Title>{title}</Title>
        </Header>
      )}
      <Body style={bodyStyle}>{children}</Body>
      {footer !== null && (
        <Footer onMouseDown={stopPropagation}>
          {footer || (
            <FooterWrap>
              {
                cancelText !== '' &&
                <CancelButton
                  shape="round"
                  onClick={onCancel}
                  testid={testid ? `${testid}_close` : undefined}
                  {...cancelButtonProps}
                >
                  {cancelText}
                </CancelButton>
              }
              <ConfirmButton
                shape="round"
                type="primary"
                icon={confirmLoading ? <LoadingNormal spin /> : undefined}
                onClick={onOk}
                testid={testid ? `${testid}_confirm` : undefined}
                {...okButtonProps}
              >
                {okText}
              </ConfirmButton>
            </FooterWrap>
          )}
        </Footer>
      )}
      {!!closable && (
        <CloseButton onClick={onCancel}>
          <TemplateCloseNormal
            className="close-icon"
            testid={testid ? `${testid}_CloseButton` : undefined}
          />
        </CloseButton>
      )}
    </Content>
  );
});
