import React, {
  useState,
  useRef,
  useMemo,
  useLayoutEffect,
  useCallback,
  useEffect,
  cloneElement,
} from 'react';
import { createPortal } from 'react-dom';
import { KEY_CODES } from '../../utils';
import { ModalContent } from './ModalContent';
import { IModalProps } from './interface';
import { Mask, Root, Wrap } from './styled';

export const BaseModal: React.FC<IModalProps> = (props) => {
  const {
    visible,
    onOk,
    onCancel,
    confirmLoading,
    maskStyle,
    zIndex,
    testid,
    keyboard,
    maskClosable = true,
    onContentMouseDown,
    customizeChildren,
  } = props;

  const handleContentMouseDown = useCallback(
    (e: React.MouseEvent) => {
      if (onContentMouseDown) {
        onContentMouseDown(e);
      } else {
        e.stopPropagation();
      }
    },
    [onContentMouseDown],
  );

  // 是否在加载中
  const [confirming, setConfirming] = useState(false);
  const finalConfirming: boolean =
    confirmLoading === undefined ? confirming : confirmLoading;

  // 记录是否已经显示过
  const mounted = useRef(false);
  mounted.current = mounted.current || !!visible;

  // 组件是否已被卸载
  const isUnmounted = useRef(false);

  // 创建container节点
  const oContainer = useMemo(() => {
    if (mounted.current) {
      const oDiv = document.createElement('div');
      oDiv.style.position = 'absolute';
      oDiv.style.left = '0px';
      oDiv.style.top = '0px';
      oDiv.style.width = '100%';
      return oDiv;
    }
    return null;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [mounted.current]);

  // 插入节点到body
  useLayoutEffect(() => {
    if (oContainer) {
      document.body.appendChild(oContainer);
    }
    return (): void => {
      if (oContainer) {
        document.body.removeChild(oContainer);
      }
      isUnmounted.current = true;
    };
  }, [oContainer]);

  /**
   * 点击遮罩
   *
   * @return {void}
   */
  const handleClose = useCallback(() => {
    if (onCancel) {
      onCancel();
    }
  }, [onCancel]);

  /**
   * 确认点击
   */
  const handleConfirm = useCallback(() => {
    if (onOk) {
      const returnedValue: any = onOk();
      if (returnedValue && returnedValue instanceof Promise) {
        setConfirming(true);
        returnedValue
          .then(() => {
            if (!isUnmounted.current) setConfirming(false);
          })
          .catch((e) => {
            // eslint-disable-next-line no-console
            console.error(e);
          });
      }
    }
  }, [onOk, isUnmounted]);

  const maskStyleCombined = useMemo(() => {
    const v = { ...(maskStyle || {}) };
    if (typeof zIndex === 'number') {
      v.zIndex = zIndex;
    }
    return v;
  }, [maskStyle, zIndex]);

  const onClickMask = useCallback(() => {
    if (maskClosable) {
      handleClose();
    }
  }, [maskClosable, handleClose]);

  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.which === KEY_CODES.ESC) {
        handleClose();
      }
    };

    if (visible && keyboard) {
      document.addEventListener('keydown', handleKeyDown);
    }
    return () => {
      if (visible && keyboard) {
        document.removeEventListener('keydown', handleKeyDown);
      }
    };
  }, [handleClose, keyboard, visible]);

  const wrapStyle: React.CSSProperties = useMemo(
    () => ({
      ...(typeof zIndex === 'number' ? { zIndex } : {}),
    }),
    [zIndex],
  );

  return oContainer
    ? createPortal(
        <Root data-testid={testid ? `Modal_${testid}` : undefined}>
          <Mask
            visible={visible}
            data-testid={testid ? `Modal_mask_${testid}` : undefined}
            onMouseDown={onClickMask}
            style={maskStyleCombined}
          />
          <Wrap visible={visible} style={wrapStyle}>
            {customizeChildren ? (
              cloneElement(customizeChildren, {
                style: {
                  ...customizeChildren.props.style,
                  pointerEvents: 'auto',
                },
              })
            ) : (
              <ModalContent
                {...props}
                onCancel={handleClose}
                onOk={handleConfirm}
                confirmLoading={finalConfirming}
                onMouseDown={handleContentMouseDown}
              />
            )}
          </Wrap>
        </Root>,
        oContainer,
      )
    : null;
};
