import React, { useState, useMemo } from 'react';
import { SliderProps } from './slider.interface';
import { SliderContainer } from './styled';
import { InputNumber } from '../input-number';
import { uuid } from '../../utils';

export const Slider: React.FC<SliderProps> = (props) => {
  const id = useMemo(() => uuid(), []);
  const {
    disable = false,
    attachToMarksOnly = false,
    marks = [],
    max = 100,
    min = 0,
    step = 1,
    value: valueFromProps,
    onChange,
    style,
    className,
    title,
    numberInputVisible = true,
    headerVisible = true,
    defaultValue = (max - min) * 0.3 + min,
    percentage = false,
    formatter = (value: React.ReactText) => percentage ? value + '%' : value + '',
  } = props;

  const [sliderValue, setValue] = useState(defaultValue);
  const [isMove, setIsMove] = useState(false);
  const [inputError, setInputError] = useState(false)
  const [inputValue, setInputValue]: [number | string, Function] = useState(defaultValue);
  const value = valueFromProps || sliderValue;

  const mouseMove = (e: MouseEvent) => {
    const {clientX: x} = e;
    const sliderLeft = document.getElementById(`slider-${id}`)?.getBoundingClientRect().left;
    const sliderRight = document.getElementById(`slider-${id}`)?.getBoundingClientRect().right;
    let result = 0;
    if (sliderLeft && sliderRight) {
      if (x <= sliderLeft) {
        result = min;
      } else if (x >= sliderRight) {
        result = max;
      } else {
        let value = 0;
        let dist = Number.MAX_SAFE_INTEGER;
        if (!attachToMarksOnly) {
          const spanWidth = (sliderRight - sliderLeft) / ((max - min) / step)
          const order = Math.round((x - sliderLeft) / spanWidth);
          dist = Math.abs(spanWidth * order + sliderLeft - x);
          value = order * step;
        }
        marks.forEach((mark) => {
          const newDist = Math.abs(mark / (max - min) * (sliderRight - sliderLeft) + sliderLeft - x);
          if (newDist < dist) {
            dist = newDist;
            value = mark;
          }
        });
        result = value;
      }
    }
    if (!Number.isInteger(result)) {
      result = Number(result.toFixed(6));
    }
    onChange && onChange(result);
    setValue(result);
    setInputValue(result);
    setInputError(false);
  };

  const handleInputNumberBlurOrEnter = (inputTmpValue = inputValue) => {
    const value = Number(inputTmpValue);
    if (
      Number.isNaN(value)
      || value < min
      || value > max
      || (marks.indexOf(value) === -1
      && !Number.isInteger(Number(((value - min) / step).toFixed(6))))
    ) {
      setInputValue(inputTmpValue);
      setInputError(true);
    } else {
      setInputError(false);
      setValue(value);
      onChange && onChange(value);
    }
  };


  return (
    <SliderContainer
      isMove={isMove}
      style={style}
      disable={disable}
      className={className}
    >
      {headerVisible && <div className="slider-head">
        <div className="title">{title}</div>
        <div className="number-input-box" >
          {numberInputVisible && <InputNumber
            defaultValue={defaultValue}
            step={step}
            min={min}
            max={max}
            value={inputValue}
            disabled={disable}
            className={`number-input ${inputError ? 'error' : ''}`}
            formatter={formatter}
            onBlur={() => handleInputNumberBlurOrEnter()}
            onPressEnter={() => handleInputNumberBlurOrEnter()}
            onStep={(value) => handleInputNumberBlurOrEnter(value)}
            onChange={value => setInputValue(value)}
          />}
        </div>
      </div>}
      <div className="slider-wrap">
        <div className="slider" id={`slider-${id}`}>
          <div className="left" style={{ width: `${value / (max - min) * 100}%` }}/>
          <div className="slider-background" />
        </div>
        {marks.map((item, index) => (
          <div
            className="mark-box"
            key={index}
            style={{ left: `${item / (max - min) * 100}%` }}
            onClick={() => !disable && setValue(item)}
          >
            <div className="mark" />
            <div className="mark-text">{item}</div>
          </div>
        ))}
        <div
          onMouseDown={() => {
            if (!disable) {
              setIsMove(true);
              document.addEventListener("mouseup", () => {
                document.removeEventListener("mousemove", mouseMove);
                setIsMove(false);
              });
              document.addEventListener("mousemove", mouseMove);
            }
          }}
          className="dot"
          style={{ left: `${value / (max - min) * 100}%` }}
        >
          <div className={`tooltip${isMove ? ' move' : ''}`}>{value}</div>
        </div>
      </div>
    </SliderContainer>
  );
};

