/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable no-plusplus */
import React, { CSSProperties } from 'react';

export const noop: any = (): void => {};

export function isTransform3dSupported(style: CSSStyleDeclaration): boolean {
  return !!(
    ('transform' in style || 'webkitTransform' in style || 'MozTransform' in style) && window.atob
  );
}

export function setTransform(style: CSSStyleDeclaration, v: string): void {
  style.transform = v;
  style.webkitTransform = v;
}

export function setTransition(style: CSSStyleDeclaration, v: string): void {
  style.transition = v;
  style.webkitTransition = v;
}

export function toArray(children: React.ReactNode[]): React.ReactNode[] {
  // allow [c,[a,b]]
  const c: React.ReactNode[] = [];
  React.Children.forEach(children, (child) => {
    if (child) {
      c.push(child);
    }
  });
  return c;
}

export function getActiveIndex(children: any[], activeKey: string): number {
  const c: any[] = toArray(children);
  for (let i = 0; i < c.length; i++) {
    if (c[i].key === activeKey) {
      return i;
    }
  }
  return -1;
}

export function getActiveKey(children: any[], index: number): string {
  const c: any[] = toArray(children);
  return c[index].key;
}

export function getTransformPropValue(v: string): CSSProperties {
  return {
    transform: v,
    WebkitTransform: v,
  };
}

export function getTransformByIndex(index: number): string {
  return `translateX(${-index * 100}%) translateZ(0)`;
}

export function getMarginStyle(index: number): CSSProperties {
  return {
    marginLeft: `${-index * 100}%`,
  };
}

export function getStyle(el: HTMLElement, property: string): number {
  return +window
    .getComputedStyle(el)
    .getPropertyValue(property)
    .replace('px', '');
}

export function setPxStyle(el: HTMLElement, value: number): void {
  setTransform(el.style, `translate3d(${value}px, 0px, 0px)`);
}

export function getDataAttr(props: any): any {
  return Object.keys(props).reduce((prev: any, key) => {
    if (key.substr(0, 5) === 'aria-' || key.substr(0, 5) === 'data-' || key === 'role') {
      prev[key] = props[key];
    }
    return prev;
  }, {});
}

function toNum(style: CSSStyleDeclaration, property: string): number {
  return +style.getPropertyValue(property).replace('px', '');
}

function getTypeValue(
  start: string,
  current: string,
  end: string,
  tabNode: HTMLElement,
  wrapperNode: HTMLElement,
): number {
  let total = getStyle(wrapperNode, `padding-${start}`);
  if (!tabNode || !tabNode.parentNode) {
    return total;
  }

  const { childNodes } = tabNode.parentNode;
  Array.prototype.some.call(childNodes, (node) => {
    const style = window.getComputedStyle(node);

    if (node !== tabNode) {
      total += toNum(style, `margin-${start}`);
      total += node[current];
      total += toNum(style, `margin-${end}`);

      if (style.boxSizing === 'content-box') {
        total += toNum(style, `border-${start}-width`) + toNum(style, `border-${end}-width`);
      }
      return false;
    }

    // We need count current node margin
    // ref: https://github.com/react-component/tabs/pull/139#issuecomment-431005262
    total += toNum(style, `margin-${start}`);

    return true;
  });

  return total;
}

export function getLeft(tabNode: HTMLElement, wrapperNode: HTMLElement): number {
  return getTypeValue('left', 'offsetWidth', 'right', tabNode, wrapperNode);
}

export function getTop(tabNode: HTMLElement, wrapperNode: HTMLElement): number {
  return getTypeValue('top', 'offsetHeight', 'bottom', tabNode, wrapperNode);
}

// 计算可见 tab 时，去除 tab title 两边的 padding，使下拉菜单内展示的 tab 更符合直觉
export function getVisibleRangePadding(type: string, tabPosition: 'left' | 'top') {
  if (tabPosition === 'top') {
    if (type === 'editable-biz') {
      return {
        leftOrTopPadding: 4,
        rightOrBottomPadding: 20,
      }
    }
    if (type === 'biz') {
      return {
        leftOrTopPadding: 4,
        rightOrBottomPadding: 4,
      }
    }
    return {
      leftOrTopPadding: 16,
      rightOrBottomPadding: 16,
    }
  } else {
    if (type === 'biz' || type === 'editable-biz') {
      return {
        leftOrTopPadding: 7,
        rightOrBottomPadding: 7,
      }
    }
    return {
      leftOrTopPadding: 6,
      rightOrBottomPadding: 6,
    }
  }
}

// 测试是否是有效展示的react元素，所以不包含null/undefined/{}/''/boolean
export function isValidShowReactNode(value: any):boolean {
  if(value !== 0 && !value) return false;

  return (
    React.isValidElement(value) ||
    typeof value === 'string' ||
    typeof value === 'number'
  )
}