/* eslint-disable @typescript-eslint/no-explicit-any */
import { useRef, useEffect } from 'react';

export default (fn: any, conditions: any[]): void => {
  const isMounted = useRef(false);
  useEffect(() => {
    if (isMounted.current) {
      fn();
    } else {
      isMounted.current = true;
    }
  }, conditions);
};
