import React from 'react';
import { LineBox } from './styled';

interface ILineProps {
  /**
   * 线条颜色
   */
  color?: string;
  /**
   * 线条总宽
   */
  width?: number;
}

/**
 * 每个svg图形只承载了最小可重复单元，通过重复迭代生成线
 * 根据总宽度和项目宽度，生成一个最小重复迭代的key值列表
 * @param {number} totalWidth 线条的总宽度
 * @param {number} unitWidth svg item的宽度
 */
function getCountkeys(totalWidth: number, unitWidth: number): string[] {
  const count = Math.ceil(totalWidth / unitWidth);
  const keys: string[] = [];
  for (let i = 0; i < count; i += 1) {
    keys.push(`svg-${i}`);
  }
  return keys;
}

// props 属性是否相同
function isPropsEqual(oldP: ILineProps, P: ILineProps): boolean {
  return oldP.color === P.color && oldP.width === P.width;
}

// 缓存虚拟dom提升性能
const linesCache: { [key: string]: { props: ILineProps; vnode: React.ReactNode } } = {};

export const Thin = (props: ILineProps): React.ReactNode => {
  const cache = linesCache.thin;
  if (cache && isPropsEqual(cache.props, props)) return cache.vnode;

  const { color = 'black', width = 120 } = props;
  const vnode = (
    <LineBox style={{ width }}>
      {getCountkeys(width, 8).map(key => (
        <svg width="8px" height="2px" viewBox="0 0 8 2" key={key}>
          <rect fill={color} x="0" y="0" width="8" height="2" />
        </svg>
      ))}
    </LineBox>
  );
  linesCache.thin = { props: { ...props }, vnode };
  return vnode;
};

