import React from 'react';
import styled from 'styled-components';
import { CustomEventEmitter } from './utils';
import { DOUBLE_CLICK_INTERVAL, CLICK_INTERVAL } from './constants';

const ActionableBoxWrapper = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
`

function getDistance(touch1: Touch, touch2: Touch) {
  const { clientX: x1, clientY: y1 } = touch1;
  const { clientX: x2, clientY: y2 } = touch2;
  const deltaX = x1 - x2;
  const deltaY = y1 - y2;
  return Math.sqrt(Math.pow(deltaX, 2) + Math.pow(deltaY, 2));
}

const ActionableBox: React.FC<{ eventEmitter: CustomEventEmitter }> = (props) => {
  const { eventEmitter } = props;
  const touch1Ref = React.useRef<Touch | null>(null);
  const touch2Ref = React.useRef<Touch | null>(null);
  // 上一次判定为单击的操作
  const lastClickTimeStamp = React.useRef<number>(0);
  const touchStartTimeStamp = React.useRef<number>(0);
  const clickTimer = React.useRef<ReturnType<typeof window.setTimeout> | null>(null);
  const isMoved = React.useRef(false);

  React.useEffect(() => {
    return () => {
      clickTimer.current && clearTimeout(clickTimer.current);
    }
  }, []);

  const storeTouch = React.useCallback((touch1: Touch | null, touch2?: Touch) => {
    touch1Ref.current = touch1;
    touch2Ref.current = touch2 || null;
  }, []);

  const handleTouchStart = React.useCallback((e: TouchEvent) => {
    e.preventDefault();
    e.stopPropagation();
    clickTimer.current && clearTimeout(clickTimer.current);
    isMoved.current = false;
    const { touches } = e;
    const { length } = touches;
    if (length <= 2) {
      storeTouch(touches[0], touches[1]);
      eventEmitter.emit('touchStart');
    }
    touchStartTimeStamp.current = Date.now();
  }, []);

  const handleMoveAction = React.useCallback((touch: Touch) => {
    if (!touch1Ref.current) return;

    const dx = touch.clientX - touch1Ref.current?.clientX;
    const dy = touch.clientY - touch1Ref.current?.clientY;
    touch1Ref.current = touch;
    eventEmitter.emit('move', {
      x: dx,
      y: dy,
    });
  }, []);

  const handleScaleAction = React.useCallback((touch1: Touch, touch2: Touch) => {
    if (!touch2Ref.current || !touch1Ref.current) {
      return;
    }
    const prevDistance = getDistance(touch1Ref.current, touch2Ref.current);
    const distance = getDistance(touch1, touch2);
    const ratio = distance / prevDistance;
    eventEmitter.emit('scale', {
      ratio,
      center: {
        x: (touch1Ref.current.clientX + touch2Ref.current.clientX) / 2,
        y: (touch1Ref.current.clientY + touch2Ref.current.clientY) / 2,
      },
    });
  }, []);

  const handleTouchMove = React.useCallback((e: TouchEvent) => {
    e.preventDefault();
    e.stopPropagation();
    isMoved.current = true;
    const { touches } = e;
    const { length } = touches;
    if (length === 1) {
      handleMoveAction(touches[0]);
      if (touch1Ref.current && touch2Ref.current) {
        eventEmitter.emit('scaleEnd', {
          center: {
            x: (touch1Ref.current.clientX + touch2Ref.current.clientX) / 2,
            y: (touch1Ref.current.clientY + touch2Ref.current.clientY) / 2,
          },
        });
      }
      storeTouch(touches[0]);
    }
    if (length === 2) {
      const [touch1, touch2] = touches[0].identifier === touch1Ref.current?.identifier ? [touches[0], touches[1]] : [touches[1], touches[0]];
      handleScaleAction(touch1, touch2);
      storeTouch(touch1, touch2);
    }
  }, [handleScaleAction, handleMoveAction]);

  const handleTouchEndAction = React.useCallback(() => {
    const timeConsuming = Date.now() - touchStartTimeStamp.current;
    eventEmitter.emit('touchEnd', {
      timeConsuming,
    });
  }, []);

  const handleScaleEndAction = React.useCallback((touch1: Touch, touch2: Touch) => {
    if (!touch2Ref.current || !touch1Ref.current) {
      handleTouchEndAction();
      return;
    }
    const prevDistance = getDistance(touch1Ref.current, touch2Ref.current);
    const distance = getDistance(touch1, touch2);
    const dDistance = distance - prevDistance;
    eventEmitter.emit('scale', {
      dDistance,
      touch1Pos: {
        x: touch1.clientX,
        y: touch1.clientY,
      },
      touch2Pos: {
        x: touch2.clientX,
        y: touch2.clientY,
      },
    });
  }, []);

  const handleTouchEnd = React.useCallback((e: TouchEvent) => {
    e.preventDefault();
    e.stopPropagation();
    const { touches } = e;
    const { length } = touches;
    const isClick = Date.now() - touchStartTimeStamp.current < CLICK_INTERVAL && !isMoved.current;
    if (isClick && length < 2) {
      if (lastClickTimeStamp.current && Date.now() - lastClickTimeStamp.current < DOUBLE_CLICK_INTERVAL) {
        clickTimer.current && clearTimeout(clickTimer.current);
        if (touch1Ref.current) {
          eventEmitter.emit('doubleClick', {
            x: touch1Ref.current?.clientX,
            y: touch1Ref.current?.clientY,
          });
          return;
        }
      }
      lastClickTimeStamp.current = Date.now();
      clickTimer.current = setTimeout(() => {
        eventEmitter.emit('click');
      }, DOUBLE_CLICK_INTERVAL);
      return;
    }
    if (touch1Ref.current && touch2Ref.current) {
      eventEmitter.emit('scaleEnd', {
        center: {
          x: (touch1Ref.current.clientX + touch2Ref.current.clientX) / 2,
          y: (touch1Ref.current.clientY + touch2Ref.current.clientY) / 2,
        },
      });
      storeTouch(null);
      return;
    }
    storeTouch(null);
    if (length === 1) {
      handleTouchEndAction();
      return;
    }
    if (length === 2) {
      handleScaleEndAction(touches[0], touches[1]);
      return;
    }
    handleTouchEndAction();
  }, []);

  const onRef = React.useCallback((ref: HTMLDivElement) => {
    if (ref) {
      ref.addEventListener('touchstart', handleTouchStart, {
        passive: false,
      });
      ref.addEventListener('touchmove', handleTouchMove, {
        passive: false,
      });
      ref.addEventListener('touchend', handleTouchEnd);
    }
  }, [handleTouchStart, handleTouchMove, handleTouchEnd])

  const handleClick = React.useCallback(() => {
    eventEmitter.emit('click');
  }, [eventEmitter]);

  return (
    <ActionableBoxWrapper
      ref={onRef}
      onClick={handleClick}
    />
  );
}

export default ActionableBox;
