import React from 'react';
import ReactDOM from 'react-dom';
import Curtain, { ICurtainProps } from './Curtain';

export interface IMobileBottomCurtainProps extends ICurtainProps {
  container?: HTMLElement;
}

class Ghost {
  private ghost: HTMLDivElement | null = null;

  init () {
    if (!this.ghost) {
      this.ghost = document.createElement('div');
      document.body.appendChild(this.ghost);
    }
  }

  getGhost () {
    return this.ghost;
  }

  removeGhost () {
    this.ghost && document.body.removeChild(this.ghost);
    this.ghost = null;
  }
}

const MobileBottomCurtain: React.FC<IMobileBottomCurtainProps> = (props) => {
  const { container, children, ...extraProps } = props;
  const ghostRef = React.useRef(new Ghost());

  if (!container) {
    ghostRef.current.init();
  }

  React.useEffect(() => {
    return () => {
      ghostRef.current.removeGhost();
    }
  }, []);

  return ReactDOM.createPortal((
    <Curtain
      {...extraProps}
    >
      {children}
    </Curtain>
  ), container || ghostRef.current.getGhost()!);
};

export { 
  MobileBottomCurtain,
};
