import React, { useCallback, useMemo } from 'react';
import useControlledValue from '../../hooks/useControlledValue';
import { Dropdown } from '../../components/dropdown';
import { ArrowDownLineNormal } from '../../components/icons';
import {
  SplitButtonWrap,
  SplitButtonLeft,
  SplitButtonRight,
  SplitButtonInner,
} from './styled';
import { ButtonSize } from './Button';
import { classnames } from '../../utils';
import { DropdownAnimation } from '@ali/we-util';

export interface ISplitButtonProps {
  /**
   * 设置按钮大小
   */
  size?: ButtonSize;
  /**
   * 默认是否展开
   */
  defaultActive?: boolean;
  /**
   * 是否展开
   */
  active?: boolean;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 是否有边框
   */
  bordered?: boolean;
  /**
   * 类名
   */
  className?: string;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 下拉内容
   */
  overlay: React.ReactElement;
  /**
   * 下拉内容样式
   */
  overlayStyle?: React.CSSProperties;
  /**
   * 点击左边
   */
  onClickLeft?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 激活态变化
   */
  onActiveChange?: (active: boolean) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string;
  /**
   * Tooltip
   */
  title?: string | React.ReactNode;
  /**
   * 背景色高亮
   */
  highlight?: boolean;
  /**
   * 激活态变化
   */
  onVisibleChange?: (active: boolean) => void;
  getContainer?: () => HTMLElement;
  /**
   * 角色
   */
  role?: string;
  roleRight?: string;
}

const SplitButton: React.FC<ISplitButtonProps> = React.memo((props) => {
  const {
    disabled,
    overlay,
    overlayStyle,
    className,
    onActiveChange,
    onVisibleChange,
    children,
    style,
    size,
    bordered,
    onClickLeft,
    zIndex,
    testid,
    title,
    highlight,
    getContainer,
    role = '',
    roleRight = '',
  } = props;

  const [active, setActive] = useControlledValue(
    props.active,
    props.defaultActive,
  );

  /**
   * 下拉面板是否可见修改函数
   *
   * @param {boolean} visible
   * @return {void}
   */
  const handleVisibleChange = useCallback(
    (visible: boolean) => {
      onVisibleChange && onVisibleChange(visible);
      if (!disabled) {
        if (onActiveChange) {
          onActiveChange(visible);
        }
        setActive(visible);
      }
    },
    [onActiveChange, setActive, disabled],
  );

  /**
   * 点击左部区域
   *
   * @param {React.MouseEvent<HTMLElement>}
   * @return {void}
   */
  const handleClickLeft = useCallback(
    (e: React.MouseEvent<HTMLElement>) => {
      if (!disabled) {
        if (!active) {
          e.stopPropagation();
        }
        if (onClickLeft) {
          onClickLeft(e);
        }
      }
    },
    [disabled, active, onClickLeft],
  );

  const overlayMemo = useMemo(
    () =>
      React.cloneElement(overlay, {
        'data-testid': testid && `overlay-${testid}`,
      }),
    [overlay, testid],
  );

  const visible = disabled ? false : active;

  return (
    <Dropdown
      title={title}
      visible={visible}
      overlay={
        <DropdownAnimation
          visible={!!visible}
          data-testid={`splite-overlay-${testid}`}
        >
          {overlayMemo}
        </DropdownAnimation>
      }
      overlayStyle={overlayStyle}
      onVisibleChange={handleVisibleChange}
      zIndex={zIndex}
      getContainer={getContainer}
    >
      <SplitButtonWrap
        active={active}
        disabled={disabled}
        className={classnames(className, { active: active || highlight })}
        size={size}
        style={style}
        bordered={bordered}
      >
        <SplitButtonInner>
          <SplitButtonLeft
            disabled={disabled}
            onClick={handleClickLeft}
            data-testid={testid && `splitbutton-left-${testid}`}
            className='splitbutton-left'
            data-role={role}
          >
            {children}
          </SplitButtonLeft>
          <SplitButtonRight
            active={active}
            disabled={disabled}
            className='splitbutton-right'
            data-role={roleRight}
          >
            <ArrowDownLineNormal
              className={classnames('arrow-icon', {
                opened: active || highlight,
              })}
              data-testid={testid && `splitbutton-arrow-${testid}`}
            />
          </SplitButtonRight>
        </SplitButtonInner>
      </SplitButtonWrap>
    </Dropdown>
  );
});

SplitButton.defaultProps = {
  defaultActive: false,
  size: 'normal',
  bordered: false,
};

export { SplitButton };
