import React, { useCallback, useMemo, useState, useEffect } from 'react';
import { MenuDivider } from './MenuDivider';
import { MenuItem } from './MenuItem';
import { SubMenu } from './SubMenu';
import { MenuItemGroup } from './MenuItemGroup';
import {
  IMenuItemProps,
  ISubMenuProps,
  IMenuItemGroupProps,
  IMenuProps,
  IController,
  ISimpleTreeNode,
  IHotkeyPositionContext,
} from './interface';
import {
  ControllerContext,
  MenuKeyContext,
  PathContext,
  HotkeyPositionContext,
} from './contexts';
import { MenuRoot } from './styled';
import classnames from '../../utils/classnames';
import {
  useMenuKeyContext,
  useMousePosition,
  useRegisterValidNode,
} from './hooks';
import { useControlledValue } from '../../hooks';
import { EMenuMode } from './consts';
import { uuid, throttle } from '../../utils';
import { elementContain } from '../../utils/dom';

interface MenuSubComponents {
  SubMenu: React.FC<ISubMenuProps>;
  Item: React.FC<IMenuItemProps>;
  ItemGroup: React.FC<IMenuItemGroupProps>;
  Divider: React.FC;
}

/**
 * 获取同层级的所有的 menuKey
 *
 * @param {Record<string, string>} menuKeys
 * @param {string} path
 * @return {string[]}
 */
const getSameLevelMenuKeys = (menuKeys: any, path: string) => {
  const paths = path.split('::');
  paths.pop();
  const levelMenuKeys = Object.keys(
    paths.reduce<any>((keys, key) => keys[key], menuKeys),
  );
  return levelMenuKeys;
};

const noopArr: string[] = [];
const noop: (s: string[]) => void = () => {};
// eslint-disable-next-line max-lines-per-function
export const Menu = (React.memo<IMenuProps>((props) => {
  const {
    mode,
    children,
    className,
    style,
    selectedKeys,
    defaultSelectedKeys,
    zIndex,
    onClick,
    openKeys,
    defaultOpenKeys = noopArr,
    onSubmenuOpenedKeysChange = noop,
    autoClose,
    destroyOverlayWhenInvisible,
    shortcutKey,
    shouldSelectFirstItem,
    insertSubmenuToMenuRoot,
    showSelect,
    getContainer,
    capture = false,
    showSelectOnRight,
    onFocusItemChange,
    testid,
    minWidth,
    role = '',
    isPreventKeyEvent,
    subMenuAutoSelectWithEnter,
  } = props;

  const menuId = useMemo(() => testid || uuid(), [testid]);
  const menuRootRef = React.useRef<HTMLDivElement>();
  // 展开的submenu的key
  const [openedMenuKeys = [], setOpenedMenuKeys] = useControlledValue(
    openKeys,
    defaultOpenKeys,
  );
  // 处理menuKeys
  const [menuKeyCtxValue, , menuKeys] = useMenuKeyContext();
  // 选中的 key，受控
  const [selectedMenuKeys, setSelectedMenuKeys] = useControlledValue(
    selectedKeys,
    defaultSelectedKeys,
  );

  const { setMousePosition } = useMousePosition();
  const [isHotkeyType, setHotkeyType] = useState<boolean>(shortcutKey || false);

  const autoClickRef = React.useRef<boolean>(false);

  const {
    registerValidNode,
    unRegisterValidNode,
    hotkeyPosition,
    setHotkeyPosition,
    validNodes,
    hotKeyPath,
    selectFirstItem,
  } = useRegisterValidNode(menuId, !!shouldSelectFirstItem, openedMenuKeys);

  // useEffect(() => {
  //   // eslint-disable-next-line no-console
  //   console.log(
  //     '当前位置',
  //     hotkeyPosition,
  //     JSON.stringify(validNodes),
  //     openedMenuKeys
  //   );
  // }, [hotkeyPosition, validNodes, openedMenuKeys]);

  useEffect(() => {
    onFocusItemChange && onFocusItemChange(hotkeyPosition?.menuKey);
  }, [hotkeyPosition]);

  /**
   * 绑定快捷键
   */
  useEffect(() => {
    if (shortcutKey) {
      document.addEventListener('keydown', addHotKeys);
      return () => {
        document.removeEventListener('keydown', addHotKeys);
        hotKeyPath.current = undefined;
        setHotkeyPosition && setHotkeyPosition(null);
      };
    }
    return;
  }, [shortcutKey, mode]);

  /**
   * 添加快捷方式
   */
  const addHotKeys = useCallback(
    throttle((event: KeyboardEvent): void => {
      if (isPreventKeyEvent && isPreventKeyEvent(event)) {
        event.preventDefault();
        return;
      }
      const key = event.key.toLowerCase();
      setHotkeyType(true);
      const isArrowUp = key === 'arrowup' || event.keyCode === 38;
      const isArrowRigt = key === 'arrowright' || event.keyCode === 39;
      const isArrowDown = key === 'arrowdown' || event.keyCode === 40;
      const isArrowLeft = key === 'arrowleft' || event.keyCode === 37;
      const isEnter = key === 'enter' || event.keyCode === 13;
      if (validNodes.current.length === 0) return;
      if (!hotKeyPath.current) {
        (isArrowDown || isArrowRigt) && selectFirstItem();
        return;
      }
      if (isArrowUp) {
        event.preventDefault();
        if (
          hotKeyPath.current.path.split('::').length === 2 &&
          mode === EMenuMode.horizontal &&
          hotKeyPath.current.index === 0
        ) {
          arrowLeft();
        } else {
          arrowDownOrUp('up');
        }
      } else if (isArrowRigt) {
        event.preventDefault();
        if (
          hotKeyPath.current.path.split('::').length === 1 &&
          mode === EMenuMode.horizontal
        ) {
          arrowDownOrUp('down');
        } else {
          arrowRight();
        }
      } else if (isArrowDown) {
        event.preventDefault();
        if (
          hotKeyPath.current.path.split('::').length === 1 &&
          mode === EMenuMode.horizontal
        ) {
          arrowRight();
        } else {
          arrowDownOrUp('down');
        }
      } else if (isArrowLeft) {
        event.preventDefault();
        if (
          hotKeyPath.current.path.split('::').length === 2 &&
          mode === EMenuMode.horizontal
        ) {
          return;
        } else if (
          hotKeyPath.current.path.split('::').length === 1 &&
          mode === EMenuMode.horizontal
        ) {
          arrowDownOrUp('up');
        } else {
          arrowLeft();
        }
      } else if (isEnter) {
        event.preventDefault();
        if (
          subMenuAutoSelectWithEnter &&
          hotKeyPath.current &&
          hotKeyPath.current.children
        ) {
          // 打开二级菜单
          showSubMenu(hotKeyPath.current.path);
          autoClickRef.current = true;
          console.log(111);
        }
        if (hotKeyPath.current && hotKeyPath.current.onClick) {
          hotKeyPath.current.onClick();
          controller.closeAllMenu();
        }
        return;
      }
      if (setHotkeyPosition && hotKeyPath.current)
        setHotkeyPosition({ ...hotKeyPath.current, eventKey: key });
    }, 150),
    [openedMenuKeys, mode],
  );

  useEffect(() => {
    if (subMenuAutoSelectWithEnter && autoClickRef.current) {
      hotKeyPath.current?.onClick?.();
      autoClickRef.current = false;
    }
  }, [hotkeyPosition]);

  /**
   * 快捷键：从有效的结构树中获取节点
   */
  const getActionNode = useCallback(
    (key: string, nodes?: ISimpleTreeNode[]) => {
      let node = null;
      (nodes || validNodes.current).every((o) => {
        if (o.menuKey === key) {
          node = o;
          return false;
        }
        node = getActionNode(key, o.children || []);
        if (node) return false;
        return true;
      });
      return node;
    },
    [validNodes],
  );
  /**
   * 键盘向右
   */
  const arrowRight = () => {
    if (!hotKeyPath.current) return;
    if (hotKeyPath.current.children) {
      // 打开二级菜单
      showSubMenu(hotKeyPath.current.path);
      if (hotKeyPath.current && hotKeyPath.current.children.length > 0) {
        hotKeyPath.current = hotKeyPath.current.children[0];
      }
    } else if (hotKeyPath.current.type === 'inline') {
      const nodes = validNodes.current;
      const index = nodes.findIndex(
        (node) => hotKeyPath.current?.menuKey === node.menuKey,
      );
      // 存在inlineMenu的情况
      if (index < 0 || index + 1 >= nodes.length) return;
      if (nodes[index + 1].type === 'inline') {
        hotKeyPath.current = nodes[index + 1];
      }
    }
  };

  /**
   * 键盘向坐左
   */
  const arrowLeft = () => {
    if (!hotKeyPath.current) {
      return;
    }
    if (hotKeyPath.current.type === 'inline') {
      const nodes = validNodes.current;
      const index = nodes.findIndex(
        (node) => hotKeyPath.current?.menuKey === node.menuKey,
      );
      // 存在inlineMenu的情况
      if (index < 0 || index - 1 < 0) return;
      if (nodes[index - 1].type === 'inline') {
        hotKeyPath.current = nodes[index - 1];
      }
    } else {
      let current: ISimpleTreeNode;
      const findParentNode = (
        _validNodes: ISimpleTreeNode[],
      ): ISimpleTreeNode => {
        _validNodes.every((o: ISimpleTreeNode) => {
          if (!o.children) return true;
          if (hotKeyPath.current?.parentPath === o.path) {
            current = o;
            return false;
          }
          current = findParentNode(o.children);
          return true;
        });
        return current;
      };
      const node = findParentNode(validNodes.current);
      if (node) {
        controller.showSubMenu(node.path.split('::').slice(0, -1).join('::'));
        hotKeyPath.current = node;
      }
    }
  };
  /**
   * 键盘向上下
   */
  const arrowDownOrUp = (key: 'down' | 'up') => {
    if (!hotKeyPath.current) return;
    if (hotKeyPath.current?.type === 'inline') {
      const nodes = validNodes.current;
      const index = hotKeyPath.current?.index || 0;
      const findInlineNextNode = (
        _nodes: ISimpleTreeNode[],
        i: number,
      ): ISimpleTreeNode[] => {
        if (key === 'down' && i > _nodes.length - 1) return [];
        if (key === 'up' && i < 0) return [];
        const nextIndex = key === 'down' ? i + 1 : i - 1;
        const inlines = _nodes.filter((node) => node.index === nextIndex);
        if (inlines.length === 0) return findInlineNextNode(_nodes, nextIndex);
        return inlines;
      };
      const inlines = findInlineNextNode(nodes, index);
      if (inlines.length === 0) return;
      hotKeyPath.current =
        inlines.find(
          (inline) => inline.offset === hotKeyPath.current?.offset,
        ) || inlines[inlines.length - 1];
      return;
    }
    if (hotKeyPath.current.children && hotKeyPath.current.children.length > 0) {
      controller.showSubMenu(
        hotKeyPath.current.path.split('::').slice(0, -1).join('::'),
      );
    }
    let current: ISimpleTreeNode;
    const findNextNode = (
      _validNodes: ISimpleTreeNode[],
    ): ISimpleTreeNode | undefined => {
      if (_validNodes.length === 0) return undefined;
      _validNodes.every((o: ISimpleTreeNode, i: number) => {
        const resNode =
          key === 'down' ? _validNodes[i + 1] : _validNodes[i - 1];
        if (hotKeyPath.current?.menuKey === o.menuKey && resNode) {
          current = resNode;
          return false;
        } else {
          findNextNode(o.children || []);
        }
        return true;
      });
      return current;
    };
    const node = findNextNode(validNodes.current);
    if (node) {
      if (node.type === 'inline') {
        hotKeyPath.current = validNodes.current.find(
          (item) => item.index === node.index && item.offset === 0,
        );
      } else {
        hotKeyPath.current = node;
      }
    }
  };

  /**
   * 显示二级菜单
   *
   * @param {string} path 要显示的菜单项的key路径
   * @return {void}
   */
  const showSubMenu = useCallback(
    (path: string): void => {
      onSubmenuOpenedKeysChange(path.split('::'));
      setOpenedMenuKeys(path.split('::'));
    },
    [onSubmenuOpenedKeysChange, setOpenedMenuKeys],
  );

  /**
   * 二级菜单是否展开
   *
   * @param {string} menuKey
   * @return {boolean} isVisible
   */
  const isSubMenuOpened = useCallback(
    (menuKey: string): boolean => {
      return openedMenuKeys.includes(menuKey);
    },
    [openedMenuKeys],
  );

  /**
   * 同级的二级菜单是否展开
   *
   * @param {string} path 要显示的菜单项的key路径
   * @return {boolean}
   */
  const isAnySubMenuOpened = useCallback(
    (path: string): boolean => {
      const keys = getSameLevelMenuKeys(menuKeys, path);
      return keys.some((key) => openedMenuKeys.includes(key));
    },
    [openedMenuKeys, menuKeys],
  );

  /**
   * 菜单项是否被选中
   *
   * @param {string} menuKey
   * @return {boolean}
   */
  const isMenuItemSelected = useCallback(
    (menuKey: string) => {
      return selectedMenuKeys!.includes(menuKey);
    },
    [selectedMenuKeys],
  );

  /**
   * 关闭所有的展开菜单项
   *
   * @return {void}
   */
  const closeAllMenu = useCallback(() => {
    setHotkeyPosition(null);
    hotKeyPath.current = null;
    onSubmenuOpenedKeysChange([]);
    setOpenedMenuKeys([]);
  }, [
    hotKeyPath,
    onSubmenuOpenedKeysChange,
    setHotkeyPosition,
    setOpenedMenuKeys,
  ]);

  /**
   * 选中 MenuItem
   *
   * @param {string} menuKey
   * @return {void}
   */
  const selectMenuItem = useCallback(
    (menuKey: string) => {
      if (!selectedMenuKeys!.includes(menuKey)) {
        setSelectedMenuKeys([...selectedMenuKeys!, menuKey]);
      }
    },
    [selectedMenuKeys, setSelectedMenuKeys],
  );

  const controller = useMemo<IController>(
    () => ({
      mode: mode!,
      zIndex,
      autoClose: !!autoClose,
      menuId,
      isSubMenuOpened,
      isAnySubMenuOpened,
      showSubMenu,
      isMenuItemSelected,
      closeAllMenu,
      selectMenuItem,
      clickMenuItem: onClick,
      destroyOverlayWhenInvisible,
      showMenuTimer: 0,
      shortcutKey,
      menuRootRef: insertSubmenuToMenuRoot ? menuRootRef : undefined,
      getContainer,
      showSelect,
      openedMenuKeys,
      showSelectOnRight,
      minWidth,
    }),
    [
      mode,
      menuId,
      zIndex,
      autoClose,
      showSubMenu,
      isSubMenuOpened,
      isAnySubMenuOpened,
      isMenuItemSelected,
      closeAllMenu,
      selectMenuItem,
      onClick,
      destroyOverlayWhenInvisible,
      shortcutKey,
      menuRootRef,
      insertSubmenuToMenuRoot,
      showSelect,
      openedMenuKeys,
      getContainer,
      showSelectOnRight,
      minWidth,
    ],
  );

  const hotkeyPositionValue: IHotkeyPositionContext = {
    hotkeyPosition,
    isHotkeyType,
    getActionNode,
    registerValidNode,
    unRegisterValidNode,
    setHotkeyPosition: (node: ISimpleTreeNode | null) => {
      hotKeyPath.current = node;
      setHotkeyPosition(node);
    },
    setHotkeyType: (val) => {
      if (val === false) setHotkeyPosition(null);
      setHotkeyType(val);
    },
  };

  useEffect(() => {
    const handleMouseDown = (e: MouseEvent) => {
      // 后续寻求更好的办法
      const overlayElements = document.querySelectorAll(
        `.we-ui-submenu-${menuId}-overlay`,
      );

      // 是否点击的是overlay
      const inOverlay = Array.from(overlayElements).some((overlayElement) => {
        return elementContain(
          overlayElement as HTMLElement,
          e.target as HTMLElement,
        );
      });

      if (!inOverlay) {
        closeAllMenu();
      }
    };
    const { length } = openedMenuKeys;

    if (length) {
      document.addEventListener('mousedown', handleMouseDown, capture);
    }
    return () => {
      if (length) {
        document.removeEventListener('mousedown', handleMouseDown, capture);
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [menuId, openedMenuKeys.length, closeAllMenu]);

  const menuRootMouseMove = useCallback((e: React.MouseEvent) => {
    setMousePosition(e.clientY);
  }, []);

  return (
    <MenuRoot
      onMouseMove={shortcutKey ? menuRootMouseMove : null}
      ref={menuRootRef}
      className={classnames(`we-ui-submenu-${menuId}-overlay`, className, mode)}
      style={{ ...style, minWidth }}
      data-testid={menuId + '_root'}
      data-role={role}
    >
      <PathContext.Provider value="">
        <MenuKeyContext.Provider value={menuKeyCtxValue}>
          <ControllerContext.Provider value={controller}>
            <HotkeyPositionContext.Provider value={hotkeyPositionValue}>
              {children}
            </HotkeyPositionContext.Provider>
          </ControllerContext.Provider>
        </MenuKeyContext.Provider>
      </PathContext.Provider>
    </MenuRoot>
  );
}) as unknown) as React.FC<IMenuProps> & MenuSubComponents;

Menu.defaultProps = {
  mode: EMenuMode.vertical,
  defaultSelectedKeys: [],
  autoClose: true,
  destroyOverlayWhenInvisible: false,
  shortcutKey: false,
  shouldSelectFirstItem: false,
  showSelect: false,
};

Menu.Item = MenuItem;
Menu.ItemGroup = MenuItemGroup;
Menu.Divider = MenuDivider;
Menu.SubMenu = SubMenu;
