/* eslint-disable max-lines-per-function */
import React, { useCallback } from 'react';

import {
  FontColorIcon,
  NoColorIcon,
  ColorSelectedIcon,
  FontBgColorIcon,
} from './icons';
import { Container, MatrixWrapper, MoreIconWrapper } from './styled';
import { NO_COLOR } from '../constants';
import { EColorType, IColorMatrix } from '../types';
import MoreColorIcon from './icons/MoreColorIcon';
import ColorBox from './ColorBox';

const DEFAULT_COLUMN = 10;

const ColorMatrix: React.FC<IColorMatrix> = (props) => {
  const {
    colorType,
    selectedKey,
    colors,
    currentColors,
    onClick,
    defaultColor = colors[0],
    showMoreIcon,
    column = DEFAULT_COLUMN,
    isFirstRowDoubleGap,
    onMouseEnter,
    onMouseLeave,
  } = props;
  const currentColor = currentColors[selectedKey];

  const handleClick = useCallback(
    (e: React.MouseEvent) => {
      if (typeof onClick !== 'function') {
        return;
      }
      const colorBox = e.currentTarget;
      const color = colorBox.getAttribute('color')!;
      if (color === currentColor) {
        // 点击已选中的颜色， 恢复默认颜色
        return onClick(defaultColor);
      }
      onClick(color);
    },
    [currentColor, onClick],
  );

  const renderColorBoxContent = (color: string) => {
    // 内容为 A 的色块
    if (colorType === EColorType.font) {
      return <FontColorIcon color={color} />;
    }

    if (colorType === EColorType.fontBackground) {
      const isSelected = String(color) === currentColor;
      return isSelected ? (
        <ColorSelectedIcon color={color} />
      ) : (
        <FontBgColorIcon color={color} />
      );
    }

    // 内容为清除线的色块
    if (color === NO_COLOR) {
      return <NoColorIcon />;
    }

    // 内容为 ✅ 的色块
    if (color === currentColor) {
      // 背景色选中时需要处理反色
      return colorType === EColorType.background ? (
        <ColorSelectedIcon color={color} />
      ) : (
        <ColorSelectedIcon />
      );
    }
    return null;
  };

  const handleMouseEnter = (color: string) => {
    if (typeof onMouseEnter === 'function') {
      onMouseEnter(colorType, color);
    }
  };

  return (
    <Container>
      <MatrixWrapper
        column={column}
        isFirstRowDoubleGap={isFirstRowDoubleGap}
        onMouseLeave={onMouseLeave}
      >
        {colors.map((color, index) => (
          <ColorBox
            colorType={colorType}
            key={`${color}_${index}`}
            color={color || defaultColor}
            isSelected={String(color) === currentColor}
            onClick={handleClick}
            onMouseEnter={() => handleMouseEnter(color)}
          >
            {renderColorBoxContent(color)}
          </ColorBox>
        ))}
      </MatrixWrapper>
      {showMoreIcon && (
        <MoreIconWrapper>
          <MoreColorIcon />
        </MoreIconWrapper>
      )}
    </Container>
  );
};

export default ColorMatrix;
