import React from 'react';
import BaseImage from './BaseImage';
import { ImageWrapper, ImageActionWrapper, SAFE_MARGIN } from './styled';
import {
  SCALING_MAX_SCALE,
  SCALING_MIN_SCALE,
  STOP_MAX_SCALE,
  STOP_MIN_SCALE,
  QUICK_SLIDE_TIME,
  QUICK_SLIDE_DISTANCE,
} from './constants';
import { CustomEventEmitter } from './utils';

export interface IActionableImage {
  eventEmitter: CustomEventEmitter;
  index: number;
  currentIndex: number;
  src: string;
  outerHeight: number;
  outerWidth: number;
  total: number;
  onCurrentIndexChange: (index: number) => void;
  imgStyle?: React.CSSProperties;
}

export interface IPosition {
  x: number;
  y: number;
}

const START_MOVE_THRESHOLD = 1;

const ActionableImage: React.FC<IActionableImage> = (props) => {
  const {
    index,
    currentIndex,
    src,
    outerHeight,
    outerWidth,
    total,
    onCurrentIndexChange,
    eventEmitter,
    imgStyle,
  } = props;
  const dIndex = index - currentIndex;
  const [imageHeight, setImageHeight] = React.useState(outerHeight);
  const imageSize = React.useRef<{ w: number; h: number } | null>(null);
  // 单次touch 累计的位移
  const cumulativeTouchX = React.useRef<number>(0);
  const cumulativeTouchY = React.useRef<number>(0);
  // 图片容器的偏移
  const [wrapperTransformX, setWrapperTransformX] = React.useState<number>(
    () => {
      if (dIndex < 0) {
        return -outerWidth - SAFE_MARGIN;
      }
      if (dIndex > 0) {
        return outerHeight + SAFE_MARGIN;
      }
      return 0;
    },
  );
  // 当前图片的位移
  const [scale, setScale] = React.useState(1);
  const [transformX, setTransformX] = React.useState(0);
  const [transformY, setTransformY] = React.useState(0);
  const wrapperRef = React.useRef<HTMLDivElement>(null);
  const actionWrapperRef = React.useRef<HTMLDivElement>(null);
  const isTopEdgeVisible = React.useRef<boolean>(true);
  const isBottomEdgeVisible = React.useRef<boolean>(true);
  const isLeftEdgeVisible = React.useRef<boolean>(true);
  const isRightEdgeVisible = React.useRef<boolean>(true);
  // 应该响应移动的哪个方向
  const shouldMoveX = React.useRef<boolean | undefined>(undefined);
  const timerRef = React.useRef<ReturnType<typeof window.setTimeout> | null>(
    null,
  );
  const handleLoad = React.useCallback<
    React.ReactEventHandler<HTMLImageElement>
  >(
    (e) => {
      const { clientWidth, clientHeight } = e.target as HTMLImageElement;
      imageSize.current = {
        w: clientWidth,
        h: clientHeight,
      };
      setImageHeight(clientHeight);
      if (clientHeight < outerHeight) {
        const newTransformY = (outerHeight - clientHeight) / 2;
        setTransformY(newTransformY);
      }
    },
    [outerHeight],
  );

  const reset = React.useCallback(() => {
    if (!imageSize.current) return;
    setScale(1);
    setTransformX(0);
    let newY = 0;
    if (imageSize.current.h < outerHeight) {
      newY = (outerHeight - imageSize.current.h) / 2;
    }
    setTransformY(newY);
  }, [dIndex, outerHeight]);

  React.useEffect(() => {
    if (Math.abs(dIndex) < 2) {
      reset();
    }
  }, [dIndex]);

  const getEdgeVisible = React.useCallback(() => {
    if (!imageSize.current)
      return {
        left: true,
        right: true,
        top: true,
        bottom: true,
      };
    const { w, h } = imageSize.current;
    const origin = {
      x: (1 - scale) * imageSize.current.w * 0.5,
      y: (1 - scale) * imageSize.current.h * 0.5,
    };
    const l = transformX + origin.x;
    const isLeftEdgeVisible = l >= 0 && l <= outerWidth;
    const r = w * scale + (transformX + origin.x);
    const isRightEdgeVisible = r >= 0 && r <= outerWidth;
    const t = transformY + origin.y;
    const isTopEdgeVisible = t >= 0 && t <= outerHeight;
    const b = h * scale + (transformY + origin.y);
    const isBottomEdgeVisible = b >= 0 && b <= outerHeight;
    return {
      left: isLeftEdgeVisible,
      right: isRightEdgeVisible,
      top: isTopEdgeVisible,
      bottom: isBottomEdgeVisible,
    };
  }, [outerWidth, outerHeight, scale, transformX, transformY]);

  // 当前图片
  React.useEffect(() => {
    const handleTouchStart = () => {
      shouldMoveX.current = undefined;
      cumulativeTouchX.current = 0;
      cumulativeTouchY.current = 0;
      // 记录起始状态
      const { left, right, top, bottom } = getEdgeVisible();
      isLeftEdgeVisible.current = left;
      isRightEdgeVisible.current = right;
      isTopEdgeVisible.current = top;
      isBottomEdgeVisible.current = bottom;
    };
    eventEmitter.on('touchStart', handleTouchStart);
    return () => {
      eventEmitter.off('touchStart', handleTouchStart);
    };
  }, [getEdgeVisible]);

  const getFitWrapperTransformX = React.useCallback(
    (x: number) => {
      const maxTransformOffset = outerWidth + SAFE_MARGIN;
      return Math.min(
        Math.max(-maxTransformOffset, wrapperTransformX + x),
        maxTransformOffset,
      );
    },
    [outerWidth, wrapperTransformX],
  );

  const getFitImageTransformY = React.useCallback(
    (y: number, scale: number) => {
      if (!imageSize.current) return 0;
      const vHeight = imageSize.current.h * scale;
      const originY = (1 - scale) * imageSize.current.h * 0.5;
      if (vHeight < outerHeight) {
        return (outerHeight - vHeight) / 2 - originY;
      }
      const minTransformOffset = outerHeight - vHeight - originY;
      return Math.max(Math.min(-originY, transformY + y), minTransformOffset);
    },
    [outerHeight, scale, transformY],
  );

  const getFitImageTransformX = React.useCallback(
    (x: number, scale: number) => {
      if (!imageSize.current) return 0;
      const vWidth = imageSize.current.w * scale;
      const originX = (1 - scale) * imageSize.current.w * 0.5;
      const minTransformOffset = outerWidth - vWidth - originX;
      return Math.max(Math.min(-originX, transformX + x), minTransformOffset);
    },
    [outerWidth, scale, transformX],
  );

  React.useEffect(() => {
    const handleMove = (pos: { x: number; y: number }) => {
      if (
        !wrapperRef.current ||
        !actionWrapperRef.current ||
        actionWrapperRef.current.style.transition
      )
        return;
      const { x: singleX, y: singleY } = pos;
      let x = singleX;
      let y = singleY;
      if (shouldMoveX.current === undefined) {
        cumulativeTouchX.current += x;
        cumulativeTouchY.current += y;
        if (
          Math.abs(cumulativeTouchX.current) > START_MOVE_THRESHOLD ||
          Math.abs(cumulativeTouchY.current) > START_MOVE_THRESHOLD
        ) {
          shouldMoveX.current =
            Math.abs(cumulativeTouchX.current) >
            Math.abs(cumulativeTouchY.current);
          x = cumulativeTouchX.current;
          y = cumulativeTouchY.current;
        } else {
          return;
        }
      }
      // 图片未加载完成时
      if (!imageSize.current) {
        // 只处理x轴的移动
        if (x) {
          // 特殊处理首尾的图片
          if (
            (x < 0 && currentIndex === total - 1) ||
            (x > 0 && currentIndex === 0)
          ) {
            return;
          }
          const newWrapperTransformX = getFitWrapperTransformX(x);
          setWrapperTransformX(newWrapperTransformX);
          if (newWrapperTransformX < 0) {
            eventEmitter.emit('moveNext', newWrapperTransformX);
          } else if (newWrapperTransformX > 0) {
            eventEmitter.emit('movePrev', newWrapperTransformX);
          } else {
            eventEmitter.emit('movePrev', 0);
            eventEmitter.emit('moveNext', 0);
          }
        }
        return;
        // TODO: y 轴快速滑动后关闭图片预览组件
      }
      if (scale === 1) {
        if (shouldMoveX.current && x) {
          const newWrapperTransformX = getFitWrapperTransformX(x);
          if (newWrapperTransformX === wrapperTransformX) {
            return;
          }
          setWrapperTransformX(newWrapperTransformX);
          if (newWrapperTransformX < 0) {
            eventEmitter.emit('moveNext', newWrapperTransformX);
          } else if (newWrapperTransformX > 0) {
            eventEmitter.emit('movePrev', newWrapperTransformX);
          } else {
            eventEmitter.emit('movePrev', 0);
            eventEmitter.emit('moveNext', 0);
          }
          return;
        }
        if (!shouldMoveX.current && y) {
          const newTransformY = getFitImageTransformY(y, scale);
          if (newTransformY === transformY) {
            return;
          }
          setTransformY(newTransformY);
        }
        // 缩放后滑动
      } else {
        // 滑动时优先改变 wrapper 的位移
        if (wrapperTransformX) {
          const newTransformWrapperX = getFitWrapperTransformX(x);
          setWrapperTransformX(newTransformWrapperX);
          if (newTransformWrapperX < 0) {
            eventEmitter.emit('moveNext', newTransformWrapperX);
          } else if (newTransformWrapperX > 0) {
            eventEmitter.emit('movePrev', newTransformWrapperX);
          } else {
            eventEmitter.emit('movePrev', 0);
            eventEmitter.emit('moveNext', 0);
          }
        } else {
          // 移动图片
          const newTransformX = getFitImageTransformX(x, scale);
          const newTransformY = getFitImageTransformY(y, scale);
          // 移动图片
          if (newTransformX !== transformX || newTransformY !== transformY) {
            setTransformX(newTransformX);
            setTransformY(newTransformY);
            setScale(scale);
            // 图片无滑动空间时，可以继续左滑右滑
          } else {
            const newTransformWrapperX = getFitWrapperTransformX(x);
            if (
              (x > 0 && currentIndex !== 0) ||
              (x < 0 && currentIndex !== total - 1)
            ) {
              if (newTransformWrapperX === wrapperTransformX) {
                return;
              }
              setWrapperTransformX(newTransformWrapperX);
              if (newTransformWrapperX < 0) {
                eventEmitter.emit('moveNext', newTransformWrapperX);
              } else if (newTransformWrapperX > 0) {
                eventEmitter.emit('movePrev', newTransformWrapperX);
              } else {
                eventEmitter.emit('movePrev', 0);
                eventEmitter.emit('moveNext', 0);
              }
            }
          }
        }
      }
    };
    if (dIndex === 0) {
      eventEmitter.on('move', handleMove);
    }
    return () => {
      eventEmitter.off('move', handleMove);
    };
  }, [
    dIndex,
    outerHeight,
    scale,
    transformY,
    transformX,
    getFitImageTransformY,
    getFitImageTransformX,
    getFitWrapperTransformX,
    wrapperTransformX,
  ]);

  React.useEffect(() => {
    const handleScale = (params: {
      ratio: number;
      center: IPosition /* touchPos1: IPosition; touchPos2: IPosition; */;
    }) => {
      if (!imageSize.current) return;
      const { ratio, center } = params;
      let realRatio = ratio;
      let newScale = scale * ratio;
      if (newScale > SCALING_MAX_SCALE) {
        realRatio = SCALING_MAX_SCALE / scale;
        newScale = SCALING_MAX_SCALE;
      } else if (newScale < SCALING_MIN_SCALE) {
        realRatio = SCALING_MIN_SCALE / scale;
        newScale = SCALING_MIN_SCALE;
      }
      const origin = {
        x: (realRatio - 1) * imageSize.current?.w * 0.5,
        y: (realRatio - 1) * imageSize.current?.h * 0.5,
      };

      const newTransformX =
        transformX - ((realRatio - 1) * (center.x - transformX) - origin.x);
      const newTransformY =
        transformY - ((realRatio - 1) * (center.y - transformY) - origin.y);
      setTransformX(newTransformX);
      setTransformY(newTransformY);
      setScale(newScale);
    };
    if (dIndex === 0) {
      eventEmitter.on('scale', handleScale);
    }
    return () => {
      eventEmitter.off('scale', handleScale);
    };
  }, [dIndex, eventEmitter, scale, transformX, transformY]);

  const updateScaleTransform = React.useCallback(
    (dx: number, dy: number, scale: number) => {
      const newTransformX = getFitImageTransformX(dx, scale);
      const newTransformY = getFitImageTransformY(dy, scale);
      setTransformX(newTransformX);
      setTransformY(newTransformY);
      setScale(scale);
    },
    [getFitImageTransformX, getFitImageTransformY],
  );

  const handleScaleEnd = React.useCallback(
    (params: { center: IPosition }) => {
      if (!imageSize.current) return;
      const { center } = params;
      let realRatio;
      let newScale;
      if (scale > STOP_MAX_SCALE) {
        realRatio = STOP_MAX_SCALE / scale;
        newScale = STOP_MAX_SCALE;
      } else if (scale < STOP_MIN_SCALE) {
        realRatio = STOP_MIN_SCALE / scale;
        newScale = STOP_MIN_SCALE;
      } else {
        return;
      }
      const origin = {
        x: (realRatio - 1) * imageSize.current?.w * 0.5,
        y: (realRatio - 1) * imageSize.current?.h * 0.5,
      };
      const dx = -((realRatio - 1) * (center.x - transformX) - origin.x);
      const dy = -((realRatio - 1) * (center.y - transformY) - origin.y);
      updateScaleTransform(dx, dy, newScale);
    },
    [scale, transformX, transformY, updateScaleTransform],
  );

  // 双击
  React.useEffect(() => {
    const handleDoubleClick = (pos: IPosition) => {
      if (!imageSize.current) return;
      let newScale;
      let realRatio;
      if (scale !== STOP_MIN_SCALE) {
        realRatio = STOP_MIN_SCALE / scale;
        newScale = STOP_MIN_SCALE;
      } else {
        realRatio = STOP_MAX_SCALE / scale;
        newScale = STOP_MAX_SCALE;
      }
      const origin = {
        x: (realRatio - 1) * imageSize.current.w * 0.5,
        y: (realRatio - 1) * imageSize.current.h * 0.5,
      };
      const dx = -((realRatio - 1) * (pos.x - transformX) - origin.x);
      const dy = -((realRatio - 1) * (pos.y - transformY) - origin.y);
      updateScaleTransform(dx, dy, newScale);
    };
    if (dIndex === 0) {
      eventEmitter.on('doubleClick', handleDoubleClick);
    }
    return () => {
      eventEmitter.off('doubleClick', handleDoubleClick);
    };
  }, [
    dIndex,
    eventEmitter,
    scale,
    transformX,
    transformY,
    updateScaleTransform,
  ]);

  React.useEffect(() => {
    if (dIndex === 0) {
      eventEmitter.on('scaleEnd', handleScaleEnd);
    }
    return () => {
      eventEmitter.off('scaleEnd', handleScaleEnd);
    };
  }, [dIndex, handleScaleEnd]);

  const restoreWrapperTransform = React.useCallback(() => {
    if (!wrapperRef.current) {
      return;
    }
    wrapperRef.current.style.transition = 'transform 0.3s ease';
    setWrapperTransformX(0);
    timerRef.current && clearTimeout(timerRef.current);
    timerRef.current = setTimeout(() => {
      if (wrapperRef.current) {
        wrapperRef.current.style.transition = '';
      }
    }, 300);
    eventEmitter.emit('movePrev', 0, true);
    eventEmitter.emit('moveNext', 0, true);
  }, []);

  React.useEffect(() => {
    const handleTouchEnd = (params: { timeConsuming: number }) => {
      shouldMoveX.current = undefined;
      if (!wrapperRef.current) return;
      const { timeConsuming } = params;
      if (wrapperTransformX) {
        // 快速滑动或者慢速滑动超过一半触发切换
        if (
          (Math.abs(wrapperTransformX) > QUICK_SLIDE_DISTANCE &&
            timeConsuming <= QUICK_SLIDE_TIME) ||
          Math.abs(wrapperTransformX) >= outerWidth / 2
        ) {
          if (wrapperTransformX < 0) {
            if (currentIndex !== total - 1) {
              if (isRightEdgeVisible.current) {
                wrapperRef.current.style.transition = 'transform 0.3s ease';
                setWrapperTransformX(-outerWidth - SAFE_MARGIN);
                eventEmitter.emit('moveNextEnd');
                timerRef.current = setTimeout(() => {
                  onCurrentIndexChange(currentIndex + 1);
                  if (wrapperRef.current) {
                    wrapperRef.current.style.transition = '';
                    // setWrapperTransformX(-outerWidth - SAFE_MARGIN);
                  }
                }, 300);
                return;
              } else {
                // touch start 时右边缘不可见，不切换到下一张
                restoreWrapperTransform();
              }
            } else {
              // 最后一张不可切下一张
              restoreWrapperTransform();
            }
          }
          if (wrapperTransformX > 0) {
            if (currentIndex !== 0) {
              if (isLeftEdgeVisible.current) {
                wrapperRef.current.style.transition = 'transform 0.3s ease';
                setWrapperTransformX(outerWidth + SAFE_MARGIN);
                eventEmitter.emit('movePrevEnd');
                timerRef.current && clearTimeout(timerRef.current);
                timerRef.current = setTimeout(() => {
                  onCurrentIndexChange(currentIndex - 1);
                  if (wrapperRef.current) {
                    wrapperRef.current.style.transition = '';
                  }
                }, 300);
                return;
              } else {
                // touch start 时左边缘不可见，不切换到上一张
                restoreWrapperTransform();
              }
            } else {
              // 第一张，不切换到上一张
              restoreWrapperTransform();
            }
          }
        } else {
          // 滑动未超过一半，恢复
          restoreWrapperTransform();
        }
      }
    };
    if (dIndex === 0) {
      eventEmitter.on('touchEnd', handleTouchEnd);
    }
    return () => {
      eventEmitter.off('touchEnd', handleTouchEnd);
    };
  }, [
    dIndex,
    outerWidth,
    currentIndex,
    onCurrentIndexChange,
    total,
    handleScaleEnd,
    wrapperTransformX,
  ]);

  // current 前一张图片
  React.useEffect(() => {
    const handlePrevMove = (x: number, hasTransition?: boolean) => {
      if (!wrapperRef.current) {
        return;
      }
      setWrapperTransformX(-outerWidth + x - SAFE_MARGIN);
      if (hasTransition) {
        wrapperRef.current.style.transition = 'transform 0.3s ease';
      }
    };
    if (dIndex === -1) {
      eventEmitter.on('movePrev', handlePrevMove);
    }
    return () => {
      eventEmitter.off('movePrev', handlePrevMove);
    };
  }, [dIndex, outerWidth, restoreWrapperTransform]);

  React.useEffect(() => {
    const handlePrevMoveEnd = () => {
      if (!wrapperRef.current) {
        return;
      }

      wrapperRef.current.style.transition = `transform 0.3s ease`;
      setWrapperTransformX(0);
    };
    if (dIndex === -1) {
      eventEmitter.on('movePrevEnd', handlePrevMoveEnd);
    }
    return () => {
      eventEmitter.off('movePrevEnd', handlePrevMoveEnd);
    };
  }, [dIndex]);

  // current 后一张图片
  React.useEffect(() => {
    const handleNextMove = (x: number) => {
      if (!wrapperRef.current) {
        return;
      }
      setWrapperTransformX(outerWidth + x + SAFE_MARGIN);
    };
    if (dIndex === 1) {
      eventEmitter.on('moveNext', handleNextMove);
    }
    return () => {
      eventEmitter.off('moveNext', handleNextMove);
    };
  }, [dIndex, outerWidth]);

  React.useEffect(() => {
    const handleNextMoveEnd = () => {
      if (!wrapperRef.current) {
        return;
      }
      wrapperRef.current.style.transition = `transform 0.3s ease`;
      setWrapperTransformX(0);
    };
    if (dIndex === 1) {
      eventEmitter.on('moveNextEnd', handleNextMoveEnd);
    }
    return () => {
      eventEmitter.off('moveNextEnd', handleNextMoveEnd);
    };
  }, [dIndex]);

  const handleTransitionEnd = React.useCallback(() => {
    if (!wrapperRef.current) return;
    wrapperRef.current.style.transition = '';
  }, [dIndex, onCurrentIndexChange]);

  return (
    <ImageWrapper
      ref={wrapperRef}
      onTransitionEnd={handleTransitionEnd}
      style={{
        transform: `translate(${wrapperTransformX}px, 0)`,
      }}
    >
      <ImageActionWrapper
        ref={actionWrapperRef}
        style={{
          height: imageHeight,
          transform: `translate(${transformX}px, ${transformY}px) scale(${scale})`,
        }}
      >
        <BaseImage src={src} onLoad={handleLoad} style={imgStyle} />
      </ImageActionWrapper>
    </ImageWrapper>
  );
};

export default ActionableImage;
