import React from 'react';
import classnames from 'classnames';
import {
  AvatarWrap,
  AvatarInner,
  AvatarCorner,
  NameWrap,
} from './styled';
import  AvatarImage from './AvatarImage';
import { isEmojiCharacter } from './util';
import { IAvatarSize, IAvatarShape } from './types';

export interface IAvatarContent {
  /**
   * 头像url，数组表示群聊
   */
   url?: string | string[];
   /**
    * fallbackUrl
    */
   fallbackUrl?: string;
   /**
    * 如果需要颜色, 不传没有边框
    */
   color?: string;
   /**
    * 用户名字
    */
   name?: string;
   /**
    * 样式
    */
   style?: React.CSSProperties;
   /**
    * 当前的状态
    */
   status?: string;
   /**
    * 激活态
    */
   active?: boolean;
   /**
    * 激活态动画
    */
   enableActiveAnimation?: boolean;
   /**
   * 点击头像
   */
  onClick?: React.MouseEventHandler<HTMLElement>;
  /**
   * 类名
   */
  className?: string;
  /**
   * 自定义角落图标
   */
  corner?: React.ReactNode;
  /**
   * 头像的尺寸
   */
  size?: IAvatarSize;
  /**
   * 名字大小
   */
  nameWrapStyle?: React.CSSProperties;
  /**
   * 头像的形状
   */
  shape?: IAvatarShape;
}

const GROUP_DEFAULT_AVATAR = 'data:image/png;base64,';

const SINGLE_DEFAULT_AVATAR = 'data:image/png;base64,';

const AvatarContent: React.FC<IAvatarContent> = (props) => {
  const {
    url,
    fallbackUrl,
    style,
    className,
    name,
    color,
    active,
    enableActiveAnimation,
    corner,
    onClick,
    size,
    nameWrapStyle,
    shape = 'circle',
  } = props as IAvatarContent;
  /**
   * 头像情况：
   * 1) 名字、蓝底作为头像：url不存在，且名字存在
   * 2）多人头像：url为数组，如果url为空数组，则为默认群聊头像
   * 2）单人头像：url为字符串，如果url不存在，则为默认单人头像
   */
   let urls: string[] = [];
   // 过滤 emoji
   const avatarFilterName = Array.from(name || '').reverse().find(element => !isEmojiCharacter(element));
 
   const isNameAvatar = !url && !!avatarFilterName; // 是否为名字头像
 
   if (!isNameAvatar) {
     // 统一转为字符串数组
     urls = (Array.isArray(url) ? url : [url]).filter(u => u).slice(-4) as string[];
     // 如果数组为空，根据是否群聊设置默认头像
     if (urls.length === 0) {
       urls.push(Array.isArray(url) ? GROUP_DEFAULT_AVATAR : SINGLE_DEFAULT_AVATAR);
     }
   }
  return (
    <AvatarWrap
      className={classnames(className, shape, {
        'is-active': active,
        'enable-active-animation': enableActiveAnimation,
      })}
      onClick={onClick}
      size={size}
      clickable={!!onClick}
    >
      <AvatarInner
        color={color}
        className={`${shape} avatar-border-wrapper`}
        style={style}
      >
        {
          urls.map((src, index) => {
            return (
              <AvatarImage
                key={src}
                className={`avatar_${urls.length}_${index + 1} ${shape}`}
                url={src}
                nick={name}
                fallbackUrl={fallbackUrl}
              />
            );
          })
        }
        {
          isNameAvatar && (
            <NameWrap>
              <span style={nameWrapStyle}>{avatarFilterName}</span>
            </NameWrap>
          )
        }
      </AvatarInner>
      {
        (corner) && (
          <AvatarCorner>
            {
              corner
            }
          </AvatarCorner>
        )
      }
    </AvatarWrap>
  );
}

export default AvatarContent;
