/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable react/static-property-placement */
import React from 'react';
import { omit, classnames, KEY_CODES } from '../../utils';
import { getDataAttr, noop } from './utils';
import { TabsWrap } from './styled';
import TabBar from './TabBar';
import TabContent from './TabContent';
import { Size, ExcessMode, TabBarExtraContent } from './interface';

function getDefaultActiveKey(props: any): string {
  let activeKey: string | undefined;
  React.Children.forEach(props.children, (child) => {
    if (child && !activeKey && !child.props.disabled) {
      activeKey = child.key;
    }
  });
  return activeKey as string;
}

function activeKeyIsValid(props: any, key: string): boolean {
  const keys = React.Children.map(
    props.children,
    (child) => child && child.key,
  );
  return keys.indexOf(key) >= 0;
}

export type TabType =
  | 'biz'
  | 'line'
  | 'card'
  | 'capsule'
  | 'editable-line'
  | 'editable-card'
  | 'custom'
  | 'editable-biz'
  | 'd-design';

export interface ITabsProps {
  /**
   * 设置根节点的类名，默认：undefined
   */
  className?: string;
  /**
   * 设置根节点样式，默认：undefined
   */
  style?: React.CSSProperties;
  /**
   * Tab的样式，默认：line
   */
  type?: TabType;
  /**
   * 'large' | 'default' | 'small'，默认：default
   */
  size?: Size;
  /**
   * 设置tabBar位置
   */
  tabPosition?: 'left' | 'top';
  /**
   * 激活的 tabKey，默认：undefined
   */
  activeKey?: string;
  /**
   * 默认激活的 tabKey，默认：undefined，如果有activeKey，则忽略
   */
  defaultActiveKey?: string;
  /**
   * 变化Tab的时候是否有动画效果，默认：true
   */
  animated?: boolean;
  /**
   * 支持键盘左右键进行切换tab，默认：true
   */
  keyboard?: boolean;
  /**
   * 是否强制销毁不显示的tab的节点，默认：false
   */
  destroyInactiveTabPane?: boolean;
  /**
   * activeKey发生变化时的回调函数
   */
  onChange?: (activeKey: string) => void;
  /**
   * TabBar 点击时的回调函数
   */
  onTabClick?: (nextKey: string, e?: React.MouseEvent<HTMLDivElement>) => void;
  /**
   * 当 type = 'editable-card' 的时候有效，关闭某个tab触发的回调函数
   */
  onClose?: (targetKey: string) => void;
  /**
   * 关闭所有的tab
   */
  onCloseAll?: () => void;
  /**
   * tab数量较多时，左滑动箭头的点击回调函数
   */
  onPrevClick?: (e: React.MouseEvent<HTMLDivElement>) => void;
  /**
   * tab数量较多时，右滑动箭头的点击回调函数
   */
  onNextClick?: (e: React.MouseEvent<HTMLDivElement>) => void;
  /**
   * 设置要不要显示TabBar
   */
  hasTabBar?: boolean;
  /**
   * 设置tabBar节点样式
   */
  tabBarStyle?: React.CSSProperties;
  /**
   * 设置tabContent节点样式
   */
  tabContentStyle?: React.CSSProperties;
  /**
   * 设置navContainer节点样式
   */
  navContainerStyle?: React.CSSProperties;
  /**
   * 多余选项卡的展示方式，默认：'arrow'
   */
  excessMode?: ExcessMode;
  /**
   * tab栏上的额外元素
   */
  tabBarExtraContent?: TabBarExtraContent;
  /**
   * 测试使用
   */
  testid?: string;
  tabBarItemClassName?: string;
}

interface ITabsState {
  activeKey: string;
}

export class Tabs extends React.Component<ITabsProps, ITabsState> {
  static defaultProps = {
    onChange: noop,
    keyboard: true,
    children: null,
    style: {},
    size: 'default',
    type: 'line',
    animated: true,
    destroyInactiveTabPane: false,
    hasTabBar: true,
    excessMode: 'arrow',
  };

  constructor(props: ITabsProps) {
    super(props);

    let activeKey;
    if (props.activeKey) {
      activeKey = props.activeKey;
    } else if (props.defaultActiveKey) {
      activeKey = props.defaultActiveKey;
    } else {
      activeKey = getDefaultActiveKey(props);
    }

    this.state = {
      activeKey: activeKey as string,
    };
  }

  static getDerivedStateFromProps(props: ITabsProps, state: ITabsState) {
    const newState = {} as ITabsState;
    if (props.activeKey) {
      newState.activeKey = props.activeKey as string;
    } else if (!activeKeyIsValid(props, state.activeKey)) {
      newState.activeKey = getDefaultActiveKey(props);
    }
    if (Object.keys(newState).length > 0) {
      return newState;
    }
    return null;
  }

  onTabClick = (activeKey: string, e?: React.MouseEvent<HTMLDivElement>) => {
    const { onTabClick } = this.props;
    if (onTabClick) {
      onTabClick(activeKey, e);
    }
    this.setActiveKey(activeKey);
  };

  onNavKeyDown = (e: React.KeyboardEvent) => {
    const { keyboard } = this.props;
    if (!keyboard) {
      return;
    }
    const eventKeyCode = e.keyCode;
    if (eventKeyCode === KEY_CODES.RIGHT) {
      e.preventDefault();
      const nextKey = this.getNextActiveKey(true);
      this.onTabClick(nextKey);
    } else if (eventKeyCode === KEY_CODES.LEFT) {
      e.preventDefault();
      const previousKey = this.getNextActiveKey(false);
      this.onTabClick(previousKey);
    }
  };

  onScroll = ({ target, currentTarget }: any) => {
    if (target === currentTarget && target.scrollLeft > 0) {
      target.scrollLeft = 0;
    }
  };

  setActiveKey = (nextActiveKey: string) => {
    const { activeKey, onChange } = this.props;
    if (activeKey !== nextActiveKey) {
      if (!activeKey) {
        this.setState({
          activeKey: nextActiveKey,
        });
      }
      if (onChange) {
        onChange(nextActiveKey);
      }
    }
  };

  /**
   * 传入 true 获得 activeKey 的下一个未被禁用的 key
   * 传入 false 获得上一个
   */
  getNextActiveKey = (next: boolean) => {
    const { activeKey } = this.state;
    const children: any[] = [];
    React.Children.forEach(this.props.children, (c: any) => {
      if (c && !c.props.disabled) {
        if (next) {
          children.push(c);
        } else {
          children.unshift(c);
        }
      }
    });
    const { length } = children;
    let ret = length && children[0].key;
    children.forEach((child, i) => {
      if (child.key === activeKey) {
        if (i === length - 1) {
          ret = children[0].key;
        } else {
          ret = children[i + 1].key;
        }
      }
    });
    return ret;
  };

  render() {
    const { props, state } = this;
    const { className, children, animated, hasTabBar, ...restProps } = props;

    return (
      <TabsWrap
        className={classnames(className, {
          animated,
        })}
        style={props.style}
        {...getDataAttr(restProps)}
        onScroll={this.onScroll}
      >
        {hasTabBar && (
          <TabBar
            {...omit(props, ['className'])}
            panes={children}
            activeKey={state.activeKey}
            onTabClick={this.onTabClick}
            onKeyDown={this.onNavKeyDown}
            testid={props.testid}
          />
        )}
        <TabContent {...omit(props, ['className'])} activeKey={state.activeKey}>
          {children}
        </TabContent>
      </TabsWrap>
    );
  }
}
