/* eslint-disable react/static-property-placement */

import React from 'react';
import { getDataAttr, noop } from './utils';
import { TabBarRoot } from './styled';
import { classnames } from '../../utils';
import { ISaveRef, IGetRef } from './interface';
import { ITabsProps } from './Tabs';

export interface ITabBarRootNodeProps extends ITabsProps {
  saveRef: ISaveRef;
  getRef: IGetRef;
  onKeyDown: (e: React.KeyboardEvent) => void;
}

const TabBarRootNode: React.FC<ITabBarRootNodeProps> = (props) => {
  const {
    onKeyDown,
    tabBarStyle: style,
    children,
    saveRef,
    type,
    testid,
    ...restProps
  } = props;

  return (
    <TabBarRoot
      className={classnames(type)}
      role="tablist"
      tabIndex={-1}
      ref={saveRef('root')}
      onKeyDown={onKeyDown}
      style={style}
      data-testid={testid ? `TabBarRootNode_${testid}` : undefined}
      {...getDataAttr(restProps)}
    >
      {children}
    </TabBarRoot>
  );
};

TabBarRootNode.defaultProps = {
  onKeyDown: noop,
};

export default TabBarRootNode;
