import React, { useRef, PropsWithChildren } from 'react';
import { classnames } from '../../utils';
import { TabPaneWrap } from './styled';
import { getDataAttr } from './utils';

export interface ITabPaneProps {
  /**
   * TabPane的ID
   */
  id?: string;
  /**
   * TabPane的唯一key
   */
  key: string;
  /**
   * tab名称
   */
  tab?: React.ReactNode;
  /**
   * TabPane的根节点类名
   */
  className?: string;
  /**
   * 当前TabPane不是激活态的时候是否强制渲染内容，默认值：false
   */
  forceRender?: boolean;
  /**
   * 能不能被选中，默认：false
   */
  disabled?: boolean;
  /**
   * 能不能关闭，当 type = 'editable-card' 的时候有效
   */
  closable?: boolean;
}

interface ITabPaneExtenedProps {
  actived?: boolean;
  destroyInactiveTabPane?: boolean;
  animated?: boolean;
}

export const TabPane: React.FC<ITabPaneProps> = (props) => {
  const {
    className,
    actived,
    forceRender,
    key,
    id,
    children,
    destroyInactiveTabPane,
    animated,
    ...restProps
  } = props as PropsWithChildren<ITabPaneProps> & ITabPaneExtenedProps;
  // 此缓存是为了让最近显示过的tab项不销毁
  const isActived = useRef<boolean>(!!actived);
  isActived.current = isActived.current || !!actived;

  const isRender = destroyInactiveTabPane ? actived : isActived.current;
  const shouldRender = isRender || forceRender;

  const tabKeyExists = key && String(key).length > 0;
  const uuid = tabKeyExists && (id ? `${key}-${id}` : `${key}`);

  return (
    <TabPaneWrap
      role="tabpanel"
      aria-hidden={actived ? 'false' : 'true'}
      tabIndex={actived ? 0 : -1}
      className={classnames(className, {
        actived,
        animated,
      })}
      id={uuid && `tabpane-${uuid}`}
      aria-labelledby={uuid && `tab-${uuid}`}
      {...getDataAttr(restProps)}
    >
      {shouldRender ? children : null}
    </TabPaneWrap>
  );
};

TabPane.defaultProps = {
  disabled: false,
};
