import React, { useMemo } from 'react';
import { getContrastingColor } from './utils';

const ColorSelectedIcon: React.FC<{
  color?: string;
}> = ({ color }) => {
  const strokeColor = useMemo(() => getContrastingColor(color), [color]);
  return (
    <svg
      width="14"
      height="10"
      viewBox="0 0 16 12"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M13.965 2.15533L5.63499 10.485L1.46999 6.32034"
        stroke={strokeColor}
        strokeOpacity="0.6"
        strokeWidth="2.38"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
};

export default ColorSelectedIcon;
