import React from 'react';
import classnames from 'classnames';
import { getStartTouch } from './utils';
import {
  Sticker,
  CurtainBox,
  Background,
  HeaderBox,
  TitleBox,
  TouchBarWrapper,
  TouchBar,
  FooterBox,
} from './styled';

export interface ICurtainProps {
  children: React.ReactNode;
  title?: React.ReactNode;
  footer?: React.ReactNode;
  isVisible: boolean;
  onClose: () => void;
  destroyAfterClose?: boolean;
  style?: React.CSSProperties;
  className?: string;
  height: number | string;
}

/**
 * 移动端底部窗帘组件，交互方式为从底部滑出
 */

const Curtain: React.FC<ICurtainProps> = (props) => {
  const {
    children,
    title,
    footer,
    destroyAfterClose,
    style,
    className,
    height,
    onClose,
    isVisible,
  } = props;

  const startTouchY = React.useRef<number>(0);
  const startTouch = React.useRef<number | undefined>(undefined);
  const translateY = React.useRef<number>(0);
  const curtainHeight = React.useRef<number>(0);
  const isFading = React.useRef<boolean>(false);
  const headerBoxRef = React.useRef<HTMLDivElement | null>(null);
  const curtainBoxRef = React.useRef<HTMLDivElement | null>(null);
  const bgRef = React.useRef<HTMLDivElement | null>(null);
  const [isRealVisible, setRealVisible] = React.useState(props.isVisible);

  const handleTouchBarStart = React.useCallback((e: React.TouchEvent) => {
    e.stopPropagation();
    const { changedTouches } = e;
    const { length } = changedTouches;
    if (length > 1) {
      return;
    }
    startTouchY.current = changedTouches[0].clientY;
    startTouch.current = changedTouches[0].identifier;
    translateY.current = 0;
  }, []);

  const formatTransformY = React.useCallback((transformY: number) => {
    const maxTransformY = curtainHeight.current || curtainBoxRef.current?.clientHeight || 0;
    return Math.min(maxTransformY, Math.max(0, transformY));
  }, []);

  const touchEnd = React.useCallback((curTranslateY?: number) => {
    const realTranslateY = curTranslateY || translateY.current;
    const height = curtainHeight.current || curtainBoxRef.current?.clientHeight || 0;
    
    if (realTranslateY > height * 0.25) {
      isFading.current = true;
      curtainBoxRef.current!.style.transform = `translateY(${height}px)`;
      curtainBoxRef.current!.style.transition = 'transform 0.2s';
      bgRef.current!.style.opacity = '0';
      bgRef.current!.style.transition = 'opacity 0.6s';
      translateY.current = height;
    } else {
      curtainBoxRef.current!.style.transform = 'translateY(0px)';
      curtainBoxRef.current!.style.transition = 'transform 0.2s';
      bgRef.current!.style.opacity = '1';
      bgRef.current!.style.transition = 'none';
      translateY.current = 0;
    }
    startTouch.current = undefined;
    startTouchY.current = 0;
  }, []);

  const handleTouchBarMove = React.useCallback((e: React.TouchEvent) => {
    const { changedTouches } = e;
    const touch = getStartTouch(changedTouches, startTouch.current || 0);
    if (!touch) {
      touchEnd();
    } else {
      const { clientY } = touch;
      translateY.current = formatTransformY(clientY - startTouchY.current);
      if (curtainBoxRef.current) {
        curtainBoxRef.current.style.transform = `translateY(${translateY.current}px)`;
        curtainBoxRef.current.style.transition = 'none';
      }
    }
  }, []);

  const handleTouchBarEnd = React.useCallback((e: React.TouchEvent) => {
    const { changedTouches } = e;
    const touch = getStartTouch(changedTouches, startTouch.current!);
    if (!touch) {
      touchEnd();
    } else {
      translateY.current = formatTransformY(touch.clientY - startTouchY.current);
      touchEnd(translateY.current);
    }
  }, []);

  const handleBgClick = React.useCallback((e: React.MouseEvent) => {
    e.stopPropagation();
    if (
      e.currentTarget === e.target &&
      typeof onClose === 'function' &&
      bgRef.current!.style.opacity === '1' &&
      !isFading.current &&
      isRealVisible &&
      isVisible
    ) {
      const newTranslateY = curtainHeight.current || curtainBoxRef.current?.clientHeight || 0;
      curtainBoxRef.current!.style.transform = `translateY(${newTranslateY}px)`;
      curtainBoxRef.current!.style.transition = 'transform 0.4s';
      bgRef.current!.style.opacity = '0';
      bgRef.current!.style.transition = 'opacity 0.4s';
      isFading.current = true;
    }
  }, [isRealVisible, isVisible]);

  const handleBgTransitionEnd = React.useCallback(
    () => {
      if (bgRef.current!.style.opacity === '0') {
        onClose && onClose();
        setRealVisible(false);
      }
      isFading.current = false;
    },
    [onClose, isRealVisible],
  );

  const handleBgTransitionCancel = React.useCallback(
    () => {
      isFading.current = false;
    },
    [],
  );

  const onBgRef = React.useCallback((bg: HTMLDivElement) => {
    bgRef.current = bg;
    if (bg) {
      bg.addEventListener('transitionend', handleBgTransitionEnd);
      bg.addEventListener('transitioncancel', handleBgTransitionCancel);
    }
  }, [handleBgTransitionEnd, handleBgTransitionCancel]);

  React.useEffect(() => {
    return () => {
      bgRef.current?.removeEventListener('transitionend', handleBgTransitionEnd);
      bgRef.current?.removeEventListener('transtioncancel', handleBgTransitionCancel);
    };
  }, [handleTouchBarStart, handleBgTransitionEnd, handleTouchBarMove, handleBgTransitionEnd, handleBgTransitionCancel]);

  React.useEffect(() => {
    if (isVisible && !isRealVisible) {
      setRealVisible(true);
    }
  }, [isVisible, isRealVisible]);

  React.useEffect(() => {
    if (isRealVisible) {
      curtainHeight.current = curtainBoxRef.current?.clientHeight || 0;
      if (bgRef.current) {
        bgRef.current.style.transition = 'opacity 0.2s';
        bgRef.current.style.opacity = '1';
      }
      if (curtainBoxRef.current) {
        curtainBoxRef.current.style.transition = 'transform 0.2s';
        curtainBoxRef.current.style.transform = 'translateY(0)';
      }
      isFading.current = true;
    }
  }, [isRealVisible]);

  return destroyAfterClose && !isRealVisible ? null : (
    <Sticker>
      <Background
        ref={onBgRef}
        className={classnames(isRealVisible && 'visible')}
        onClick={handleBgClick}
        onMouseDown={(e) => e.stopPropagation()}
        onMouseUp={(e) => e.stopPropagation()}
      >
        <CurtainBox
          style={style}
          className={classnames(className, {
            visible: isRealVisible,
          })}
          height={height}
          ref={curtainBoxRef}
        >
          <HeaderBox
            ref={headerBoxRef}
            onTouchStart={handleTouchBarStart}
            onTouchMove={handleTouchBarMove}
            onTouchEnd={handleTouchBarEnd}
          >
            <TouchBarWrapper>
              <TouchBar />
            </TouchBarWrapper>
            {
              title ? (<TitleBox>
                {title}
              </TitleBox>) : null
            }
          </HeaderBox>
          <React.Fragment>{children}</React.Fragment>
          <FooterBox>{footer}</FooterBox>
        </CurtainBox>
      </Background>
    </Sticker>
  );
};

export default Curtain;
