import React, { useCallback, useRef, useEffect } from 'react'
import { DeleteNormal } from '../../components/icons'
import { useTranslate } from '@ali/we-util'
import {
  EditorWrapper,
  EditorContent,
  EditorFooter,
  ItemInput,
  SaveButton,
  RemoveButton,
} from './styled'

export interface ILinkEditorProps {
  /**
   * 非受控模式，默认的初始化URL
   */
  defaultHref?: string
  /**
   * 非受控模式，默认的初始化文本
   */
  defaultText?: string
  /**
   * 受控模式，链接的URL
   */
  href?: string
  /**
   * URL发生变化时的回调
   */
  onHrefChange?: (href: string) => void
  /**
   * 受控模式，链接的文本
   */
  text?: string
  /**
   * 文本发生变化时的回调
   */
  onTextChange?: (text: string) => void
  /**
   * 隐藏的回调
   */
  onHide?: () => void
  /**
   * 保存链接
   */
  onSave: (text: string, href: string) => void
  /**
   * 移除链接
   */
  onRemove: () => void
  /**
   * 本地语言类型，如:zh-CN
   */
  locale?: string
  /**
   * LinkEditor focus后，应用层需要回调的函数
   */
  onFocusedLinkEditor?: () => void
  /**
   * 自定义样式，如用于定位position、z-index、left、top等
   */
  style?: React.CSSProperties
}

// eslint-disable-next-line max-lines-per-function
export const LinkEditor: React.FC<ILinkEditorProps> = React.memo((props: ILinkEditorProps) => {
  const {
    defaultHref,
    defaultText,
    href,
    onHrefChange,
    text,
    onTextChange,
    onHide,
    locale,
    onSave,
    onRemove,
    onFocusedLinkEditor,
    style = {},
  } = props
  const t = useTranslate(locale)

  const [innerHref, setInnerHref] = React.useState(defaultHref)
  const [innerText, setInnerText] = React.useState(defaultText)

  const inputFocused = React.useRef(false)

  const save = React.useCallback(() => {
    const currentText = text || innerText || ''
    const currentHref = href || innerHref || ''
    const trimedText = currentText.trim()
    const trimedHref = currentHref.trim()

    if (trimedText.length || trimedHref.length) {
      onSave(trimedText, trimedHref)
    }
    if (onHide) {
      onHide()
    }
  }, [innerText, innerHref, onSave])

  const handleKeyDown = useCallback(
    (e: React.KeyboardEvent<HTMLInputElement>) => {
      const event = e.nativeEvent
      event.stopPropagation()

      const key = event.key.toLowerCase()
      if (event.isComposing) {
        return
      }
      if (key.toLowerCase() === 'escape' || event.keyCode === 27) {
        event.preventDefault()
        // ESC 退出
        if (onHide) {
          onHide()
        }
      } else if ((key === 'enter' || event.keyCode === 13) && inputFocused.current) {
        // Enter 保存
        event.preventDefault()
        save()
      }
    },
    [save],
  )

  const handleHrefChange = React.useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const textValue = e.target.value
    if (onHrefChange) {
      // 受控模式
      onHrefChange(textValue)
    } else {
      setInnerHref(textValue)
    }
  }, [])

  const handleTextChange = React.useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const hrefValue = e.target.value
    if (onTextChange) {
      onTextChange(hrefValue)
    } else {
      setInnerText(hrefValue)
    }
  }, [])

  const handleSave = React.useCallback(
    (event: React.MouseEvent) => {
      event.preventDefault()
      event.stopPropagation()
      save()
    },
    [save],
  )

  const handleRemove = React.useCallback(
    (event: React.MouseEvent) => {
      event.preventDefault()
      event.stopPropagation()
      setInnerHref('')
      onRemove()
    },
    [onRemove],
  )

  const handleFocus = React.useCallback((event: React.FocusEvent) => {
    event.preventDefault()
    event.stopPropagation()
    inputFocused.current = true
    if (onFocusedLinkEditor) {
      onFocusedLinkEditor()
    }
  }, [])

  const handleBlur = React.useCallback(() => {
    inputFocused.current = false
  }, [])

  const currentText = text || innerText
  const currentHref = href || innerHref
  const hrefRef = useRef<HTMLElement>()

  useEffect(() => {
    if (hrefRef.current) {
      hrefRef.current.focus()
    }
  }, [])

  return (
    <EditorWrapper data-testid="link-editor" contentEditable={false} style={style}>
      <EditorContent>
        <ItemInput>
          <label htmlFor="text">{t('we_ui_link_edit_text')}</label>
          <input
            name="text"
            type="text"
            value={currentText}
            onChange={handleTextChange}
            onFocus={handleFocus}
            onBlur={handleBlur}
            autoComplete="off"
            onKeyDown={handleKeyDown}
          />
        </ItemInput>
        <ItemInput>
          <label htmlFor="href">{t('we_ui_link_edit_link')}</label>
          <input
            name="href"
            type="text"
            value={currentHref}
            onChange={handleHrefChange}
            onFocus={handleFocus}
            onBlur={handleBlur}
            autoComplete="off"
            ref={hrefRef as any}
            onKeyDown={handleKeyDown}
          />
        </ItemInput>
      </EditorContent>

      <EditorFooter>
        <RemoveButton onMouseDown={handleRemove} data-testid="link-editor-remove">
          <DeleteNormal />
          <span className="text">{t('we_ui_link_delete')}</span>
        </RemoveButton>
        <SaveButton onMouseDown={handleSave} data-testid="link-editor-save">
          <span className="text">{t('we_ui_link_save')}</span>
        </SaveButton>
      </EditorFooter>
    </EditorWrapper>
  )
})
