import React, {
  useState,
  useMemo,
  useCallback,
  useRef,
  useEffect,
  PropsWithChildren,
} from 'react';
import { ArrowDownLineNormal } from '../../components/icons';
import { Dropdown } from '../../components/dropdown';
import { classnames } from '../../utils';
import { KEY_CODES } from '../../utils/consts';
import { useTranslate, DropdownAnimation } from '@ali/we-util';
import useKeyboards, { IKeyboardPair } from '../../hooks/useKeyboards';
import {
  IMultiSelectValue,
  IMultiSelectProps,
  IState,
  IClickOptionEvent,
  IRawValue,
  IMultiSelectContextValue,
} from './interface';
import { MultiSelectOption } from './MultiSelectOption';
import { SelectOptionGroup } from './SelectOptionGroup';
import {
  MultiSelectWrap,
  TriggerWrap,
  TriggerContent,
  EmptyWrap,
  OverlayWrap,
  ErrorTip,
  MultiSelectWrapper,
  DisableMask,
} from './styled';
import { MultiSelectButton } from './MultiSelectButton';
import { createMultiSelectContext } from './SelectContext';
import { useFlush, useControlledValue } from '../../hooks';
import { isMultiSelectOption, isSelectOptionGroup } from './utils';
import { IInputHandle } from '../input/Input';
import Overflow from 'rc-overflow';
import { Tooltip } from '../tooltip';

// eslint-disable-next-line max-lines-per-function
const MultiSelect = (
  props: PropsWithChildren<IMultiSelectProps<IMultiSelectValue>>,
  ref: React.RefObject<HTMLDivElement>,
) => {
  const {
    children,
    getContainer,
    style,
    className,
    overlayStyle,
    renderTrigger,
    bordered,
    onChange,
    onSelect,
    disabled = false,
    onVisibleChange,
    optionWrapperStyle,
    selectable,
    locale,
    size,
    zIndex,
    showArrow,
    placement,
    overlayClassName,
    mode,
    rules,
    autoClose,
    testid,
    title,
    errored,
    animationPlacement,
    showSelectOnRight,
    tagRender,
    emptyDisplay,
    searchNoMatchDisplay,
    maxTagCount,
    maxTagPlaceholder = (omittedValues: string[] | number[]) =>
      `+${omittedValues.length}...`,
    maxTagTextLength,
    errorTip,
    defaultValue = [],
    optionFilterProp = 'value',
    onSearch,
    onFocus,
    onBlur,
    restHoverTip,
    overlayAutoAlign = false,
    overlayHeight,
    inputVal,
    onInputChange,
    ...restProps
  } = props;
  // value 受控
  const [value, setValue] = useControlledValue(props.value, defaultValue);
  // visible 受控
  const [visible, setVisible] = useControlledValue(
    props.visible,
    props.defaultVisible,
  );
  const [searching, setSearching] = useState(false);
  const [searchVal, setSearchVal] = useControlledValue(inputVal, '');

  // 初始化的时候收集一次 SelectOption 节点，选中的节点
  const OptionChildrenRecord = useMemo(() => {
    const result = new Map<IRawValue, React.ReactNode>();
    function iterator(nodes: React.ReactElement[]) {
      React.Children.forEach(nodes, (node) => {
        if (isSelectOptionGroup(node)) {
          iterator(node.props.children);
        } else if (isMultiSelectOption(node)) {
          result.set(node.props.value, node.props.children);
        }
      });
    }

    iterator(children);
    return result;
  }, [children]);

  // 状态
  const state = useRef<IState<IRawValue>>({
    OptionChildrenRecord,
    OptionDisabledRecord: new Map<IRawValue, boolean>(),
    keyword: '',
    showOptionValues: [],
    isInputFocused: false,
    keyboardAt: null,
  }).current;

  // 是否正在搜索
  const isSearching = searching && mode === 'search';

  // Input 实例
  const inputRef = useRef<IInputHandle>(null);

  // 强制刷新函数
  const flush = useFlush();

  // 国际化
  const t = useTranslate(locale);

  /**
   * 根据keyword找到对应的所有包含此keyword的option的value，模糊搜索
   */
  const obscureSearchByLabel = useCallback(
    (keyword: string) => {
      const result: IMultiSelectValue = [];
      function iterator(nodes: React.ReactElement[]) {
        React.Children.forEach(nodes, (node) => {
          if (isSelectOptionGroup(node)) {
            iterator(node.props.children);
          } else if (isMultiSelectOption(node)) {
            if (node.props.label.includes(keyword)) {
              result.push(node.props.value);
            }
          }
        });
      }
      iterator(children);
      return result;
    },
    [children],
  );

  /**
   * 根据keyword找到的此keyword的option的value
   */
  const exactSearchByLabel = useCallback(
    (keyword: string) => {
      let result;
      function iterator(nodes: React.ReactElement[]) {
        React.Children.forEach(nodes, (node) => {
          if (isSelectOptionGroup(node)) {
            iterator(node.props.children);
          } else if (isMultiSelectOption(node)) {
            if (node.props.label === keyword) {
              result = node.props.value;
            }
          }
        });
      }
      iterator(children);
      return result;
    },
    [children],
  );

  // console.log('children', children);
  // console.log('OptionChildrenRecord', OptionChildrenRecord);
  // console.log('state.showOptionValues', state.showOptionValues);
  // console.log('value', value);

  /**
   * 可见性变化处理函数
   *
   * @param {boolean} e 可见性
   * @return {void}
   */
  const handleVisibleChange = useCallback(
    (e: boolean) => {
      if (e !== visible) {
        if (onVisibleChange) {
          onVisibleChange(e);
        }
        setVisible(e);
      }
      // 下拉选择消失时，手动触发blur事件
      if (!e && inputRef && inputRef.current) {
        inputRef.current.blur();
      }
    },
    [visible, onVisibleChange, setVisible],
  );

  /**
   * 值变化处理函数
   *
   * @param {ValueType} e 变化值
   * @return {void}
   */
  const handleValueChange = useCallback(
    (e: IRawValue): void => {
      if (typeof value !== 'undefined') {
        const newValue = [...value];
        const index = newValue.indexOf(e);
        if (index === -1) {
          newValue.push(e);
        } else {
          newValue.splice(index, 1);
        }
        setValue(newValue);
        flush();
        setSearchVal('');
        onInputChange && onInputChange('');

        // if (onChange && (e !== `${value}` || !selectable)) {
        if (onChange && selectable) {
          let error;
          for (const rule of rules || []) {
            if (typeof rule !== 'object') return;
            const { validator } = rule;
            if (typeof validator === 'function' && !validator(e)) {
              error = { violatedRule: rule, prevVal: newValue };
              break;
            }
          }
          onChange(newValue, error);
        }
      }
    },
    [value, setValue, flush, setSearchVal, onInputChange, onChange, selectable, rules],
  );

  /**
   * 将值添加至value中
   */
  const addValue = useCallback(
    (e: IRawValue): void => {
      if (typeof value !== 'undefined') {
        const newValue = [...value];
        const index = newValue.indexOf(e);
        if (index === -1) {
          newValue.push(e);
        }
        setValue(newValue);
        flush();

        // if (onChange && (e !== `${value}` || !selectable)) {
        if (onChange && selectable) {
          let error;
          for (const rule of rules || []) {
            if (typeof rule !== 'object') return;
            const { validator } = rule;
            if (typeof validator === 'function' && !validator(e)) {
              error = { violatedRule: rule, prevVal: newValue };
              break;
            }
          }
          onChange(newValue, error);
        }
      }
    },
    [value, setValue, flush, onChange, selectable, rules],
  );

  /**
   * 点击 Option 选项
   *
   * @param {IClickOptionEvent<VT>} event { value, disabled }
   * @return {void}
   */
  const handleClickOption = useCallback(
    (event: IClickOptionEvent<IRawValue>): void => {
      if (event.disabled) {
        return;
      }

      if (onSelect) onSelect(event.value);

      // 如果 selectable 为 false，那么一直可以触发 onChange
      if (event.value !== undefined && selectable) {
        handleValueChange(event.value);
      }
      if (autoClose) {
        handleVisibleChange(false);
      }
    },
    [selectable, handleVisibleChange, handleValueChange, autoClose, onSelect],
  );

  /**
   * Option 被移除
   *
   * @param {ValueType} key
   * @return {void}
   */
  const handleRemoveOption = useCallback(
    (key: IRawValue): void => {
      state.OptionChildrenRecord.delete(key);
      flush();
    },
    [state, flush],
  );

  /**
   * Option 被添加或者被更新
   *
   * @param {ValueType} key
   * @return {void}
   */
  const handleUpdateOption = useCallback(
    (key: IRawValue, newChildren: React.ReactNode): void => {
      const oldChildren = state.OptionChildrenRecord.get(key);
      if (oldChildren !== newChildren) {
        state.OptionChildrenRecord.set(key, newChildren);
        flush();
      }
    },
    [state, flush],
  );

  /**
   * Option disabled更新
   *
   * @param {ValueType} key
   * @param {boolean}
   * @return {void}
   */
  const handleCacheOptionDisabled = useCallback(
    (key: IRawValue, newDisabled: boolean): void => {
      state.OptionDisabledRecord.set(key, newDisabled);
    },
    [state],
  );

  /**
   * Option disabled删除
   *
   * @param {ValueType} key
   * @return {void}
   */
  const handleUncacheOptionDisabled = useCallback(
    (key: IRawValue): void => {
      state.OptionDisabledRecord.delete(key);
    },
    [state],
  );

  /**
   * 点开菜单输入框聚焦的时候，点击我们不希望生效，不然会收起弹层
   *
   * @return {void}
   */
  const handleClickTrigger = useCallback(() => {
    if ((visible && state.isInputFocused) || disabled) {
      return false;
    }
  }, [disabled, state.isInputFocused, visible]);

  /**
   * 当展开菜单的时候，点击trigger我们不希望Input失焦
   */
  const handleMouseDownTrigger = useCallback(
    (e: React.MouseEvent<HTMLElement>) => {
      if (visible && state.isInputFocused) {
        e.preventDefault();
        // if (inputRef && inputRef.current) {
        //   inputRef.current.blur();
        // }
      }
    },
    [state.isInputFocused, visible],
  );

  /**
   * 输入框失焦
   *
   * @param {React.FocusEvent<HTMLInputElement>} e
   * @return {void}
   */
  const handleInputBlur = useCallback(
    (e: React.FocusEvent<HTMLInputElement>): void => {
      const inputValue = (searchVal || e.target.value).trim();
      setSearching(false);
      state.showOptionValues = [];
      state.isInputFocused = false;
      const values = Array.from(state.OptionChildrenRecord.keys());
      let inputFinalVal = inputValue;
      if (inputFinalVal !== '') {
        if (mode === 'search') {
          if (optionFilterProp === 'label') {
            const findValByLabel = exactSearchByLabel(inputValue);
            if (!findValByLabel) {
              handleVisibleChange(false);
              setSearchVal('');
              onInputChange && onInputChange('');
              if (onBlur) {
                onBlur();
              }
              return;
            }
            inputFinalVal = findValByLabel;
          }
          const index = values.findIndex((v) => `${v}` === inputFinalVal);
          if (index !== -1) {
            addValue(values[index]);
          }
        } else if (mode === 'input') {
          // input 模式的 ValueType 只支持 string | number
          let processedInputValue: IRawValue;
          if (typeof values[0] === 'number') {
            processedInputValue = Number(inputFinalVal);
          } else {
            processedInputValue = inputFinalVal;
          }
          addValue(processedInputValue);
        }
      }

      handleVisibleChange(false);
      setSearchVal('');
      onInputChange && onInputChange('');

      if (onBlur) {
        onBlur();
      }
    },
    [
      searchVal,
      state,
      optionFilterProp,
      mode,
      handleVisibleChange,
      onBlur,
      exactSearchByLabel,
      addValue,
      setSearchVal,
      onInputChange,
    ],
  );

  /**
   * 输入框聚焦
   *
   * @return {void}
   */
  const handleInputFocus = useCallback((): void => {
    state.isInputFocused = true;
    state.showOptionValues = Array.from(state.OptionChildrenRecord.keys());
    if (onFocus) {
      onFocus();
    }
    flush();
    // if (inputRef.current) {
    //   inputRef.current.select();
    // }
  }, [state, onFocus, flush]);

  /**
   * 输入框回车
   *
   * @return {void}
   */
  const handleInputEnter = useCallback((): void => {
    if (state.keyboardAt === null && inputRef.current) {
      inputRef.current.blur();
    }
  }, [state]);

  useEffect(() => {
    if (mode === 'search' && searching && onSearch) {
      state.OptionChildrenRecord = OptionChildrenRecord;
      state.showOptionValues = Array.from(
        state.OptionChildrenRecord.keys(),
      );
      flush();
    }
  }, [state, OptionChildrenRecord, flush, mode, searching, onSearch]);

  useEffect(() => {
    // 远程搜索
    if (mode === 'search' && searching && onSearch) {
      if (searchVal === '') {
        setSearching(false);
      }
      onSearch(searchVal);
    }
  }, [mode, onSearch, searchVal, searching]);

  /**
   * 输入值变化
   *
   * @param {React.ChangeEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleInputChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>): void => {
      setSearching(true);
      onInputChange && onInputChange(e.target.value);
      setSearchVal(e.target.value);
      state.keyboardAt = null;
      // 远程搜索
      if (onSearch) {
        return;
      }
      // 本地数据搜索
      if (optionFilterProp === 'label') {
        if (e.target.value !== '') {
          state.showOptionValues = obscureSearchByLabel(e.target.value);
        } else {
          setSearching(false);
          state.showOptionValues = Array.from(
            state.OptionChildrenRecord.keys(),
          );
        }
      } else {
        if (e.target.value === '') {
          setSearching(false);
        }
        state.showOptionValues = Array.from(
          state.OptionChildrenRecord.keys(),
        ).filter((v) => {
          return `${v}`.includes(e.target.value);
        });
      }
      flush();
    },
    [onInputChange, setSearchVal, state, onSearch, optionFilterProp, flush, obscureSearchByLabel],
  );

  /**
   * 选项高亮移动
   *
   * @param {1 | -1} step
   * @return {void}
   */
  const handleChangeKeyboardAt = useCallback(
    (step: 1 | -1) => {
      const values = isSearching
        ? state.showOptionValues
        : Array.from(state.OptionChildrenRecord.keys());
      let index = state.keyboardAt ? values.indexOf(state.keyboardAt) : -1;
      // 需要跳过disabled的选项
      do {
        index += step;
      } while (
        index < values.length &&
        !!state.OptionDisabledRecord.get(values[index])
      );
      if (index >= 0 && index <= values.length - 1) {
        if (values[index] !== state.keyboardAt) {
          state.keyboardAt = values[index];
          flush();
        }
      }
    },
    [state, flush, isSearching],
  );

  /**
   * 按下向下键，聚焦时触发选项高亮
   *
   * @return {void}
   */
  const handlePressDown = useCallback((): void => {
    handleChangeKeyboardAt(1);
  }, [handleChangeKeyboardAt]);

  /**
   * 按下向上键，聚焦时触发选项高亮
   *
   * @return {void}
   */
  const handlePressUp = useCallback((): void => {
    handleChangeKeyboardAt(-1);
  }, [handleChangeKeyboardAt]);

  /**
   * 按下Enter键，选择高亮选项
   */
  const handlePressEnter = useCallback((): void => {
    if (state.keyboardAt !== null) {
      handleValueChange(state.keyboardAt);
    }
    handleVisibleChange(false);
  }, [handleValueChange, handleVisibleChange, state.keyboardAt]);

  // 键盘事件定义
  const keyboards: IKeyboardPair[] = useMemo(() => {
    return [
      [[KEY_CODES.DOWN], handlePressDown],
      [[KEY_CODES.UP], handlePressUp],
      [[KEY_CODES.ENTER], handlePressEnter],
    ];
  }, [handlePressDown, handlePressUp, handlePressEnter]);

  // 绑定键盘事件
  const keyboardHandlers = useKeyboards({ keyboards, on: visible });

  // context
  const context = useMemo<IMultiSelectContextValue>(() => {
    return {
      value: value!,
      selectable: !!selectable,
      showOptionValues: state.showOptionValues,
      isSearching,
      keyboardAt: state.keyboardAt,
      optionWrapperStyle,
      showSelectOnRight,
      onClickOption: handleClickOption,
      onUpdateOption: handleUpdateOption,
      onRemoveOption: handleRemoveOption,
      onCacheOptionDisabled: handleCacheOptionDisabled,
      onUncacheOptionDisabled: handleUncacheOptionDisabled,
    };
  }, [
    value,
    selectable,
    isSearching,
    state.keyboardAt,
    state.showOptionValues,
    optionWrapperStyle,
    showSelectOnRight,
    handleClickOption,
    handleUpdateOption,
    handleRemoveOption,
    handleCacheOptionDisabled,
    handleUncacheOptionDisabled,
  ]);

  // 获取选中的 Option 的子节点
  const SelectedOptionChildren = [];
  if (value !== null && value !== undefined) {
    for (const option of value) {
      SelectedOptionChildren.push(
        state.OptionChildrenRecord.get(option) || null,
      );
    }
  }

  const SelectContext = createMultiSelectContext();
  const searchNoMatch = isSearching && state.showOptionValues.length === 0;
  const emptySource = !isSearching && children.length === 0;

  const onClickRemove = useCallback(
    (content: IRawValue) => {
      if (typeof value !== 'undefined') {
        const newValue = [...value];
        const index = newValue.indexOf(content);
        newValue.splice(index, 1);
        setValue(newValue);
        if (onChange) {
          onChange(newValue);
        }
        flush();
      }
    },
    [flush, onChange, setValue, value],
  );

  const renderChosenItems = (content: IRawValue) => {
    const onClose = (e: React.MouseEvent) => {
      e.stopPropagation();
      onClickRemove(content);
    };

    let displayContent = content;

    if (typeof maxTagTextLength === 'number') {
      if (typeof content === 'string' || typeof content === 'number') {
        const strLabel = String(displayContent);

        if (strLabel.length > maxTagTextLength) {
          displayContent = `${strLabel.slice(0, maxTagTextLength)}...`;
        }
      }
    }

    return typeof tagRender === 'function'
      ? tagRender(displayContent, content, !disabled, onClose)
      : defaultRenderChosenItems(displayContent, !disabled, onClose);
  };

  const renderRest = (omittedValues: IRawValue[]) => {
    const content =
      typeof maxTagPlaceholder === 'function'
        ? maxTagPlaceholder(omittedValues)
        : maxTagPlaceholder;
    return (
      <Tooltip title={restHoverTip}>
        {defaultRenderChosenItems(content, false)}
      </Tooltip>
    );
  };

  const defaultRenderChosenItems = (
    content: string | number,
    closable = true,
    onClose?: React.MouseEventHandler,
  ) => {
    return (
      <span
        className="multiple-select-item"
        title={
          typeof content === 'string' || typeof content === 'number'
            ? content.toString()
            : undefined
        }
      >
        <span className="multiple-select-item-content">{content}</span>
        {closable && (
          <span className="multiple-select-item-remove" onClick={onClose}>
            ×
          </span>
        )}
      </span>
    );
  };

  const inputNode = (
    <TriggerContent className="select_left_wrap" style={{ minWidth: 9 }}>
      <MultiSelectButton
        value={searchVal}
        locale={locale}
        renderTrigger={renderTrigger}
        mode={mode}
        visible={visible}
        onBlur={handleInputBlur}
        onFocus={handleInputFocus}
        onEnter={handleInputEnter}
        onChange={handleInputChange}
        inputRef={inputRef}
        testid={testid}
      >
        {(SelectedOptionChildren || <span>{value}</span>) as React.ReactNode}
      </MultiSelectButton>
    </TriggerContent>
  );

  const selectionNode = (
    <>
      <Overflow
        style={{ flexGrow: 1 }}
        prefixCls="multiple-select-overflow"
        data={value}
        renderItem={renderChosenItems}
        renderRest={renderRest}
        suffix={
          !disabled && (mode === 'search' || mode === 'input')
            ? inputNode
            : null
        }
        itemKey="key"
        maxCount={maxTagCount}
      />
      <span className="select_right_wrap" style={{ width: 20 }}>
        <ArrowDownLineNormal
          data-testid={testid && `select-arrow-${testid}`}
          className={classnames('select-arrow', {
            opened: visible,
            hidden: !showArrow,
          })}
        />
      </span>
    </>
  );

  const defaultEmptyDisplay = useMemo(() => {
    return (
      <EmptyWrap>
        <span className="message">
          {t('we_ui_search_no_data')}
        </span>
      </EmptyWrap>);
  }, [t]);

  const optionsNode = useMemo(() => {
    if (emptySource) {
      return (emptyDisplay || defaultEmptyDisplay);
    }
    if (searchNoMatch) {
      return (searchNoMatchDisplay || defaultEmptyDisplay);
    }
    return (
      <div style={{ display: 'block' }}>
        {children}
      </div>);
  }, [
    children,
    defaultEmptyDisplay,
    emptyDisplay,
    emptySource,
    searchNoMatch,
    searchNoMatchDisplay,
  ]);

  const multiSelectStyle = useMemo(() => {
    return { ...style, padding: '0 3px 0 3px' };
  }, [style]);

  return (
    <SelectContext.Provider value={context}>
      <MultiSelectWrapper>
        <Dropdown
          title={title}
          trigger="click"
          visible={visible}
          onVisibleChange={handleVisibleChange}
          getContainer={getContainer}
          overlayStyle={overlayStyle}
          overlayClassName={overlayClassName}
          zIndex={zIndex}
          placement={placement}
          testid={testid}
          overlayAutoAlign={overlayAutoAlign}
          overlay={
            <DropdownAnimation
              visible={!!visible}
              placement={animationPlacement}
            >
              <OverlayWrap
                style={{ height: overlayHeight }}
                data-testid={testid && `overlay-${testid}`}
              >
                {optionsNode}
              </OverlayWrap>
            </DropdownAnimation>
          }
        >
          <MultiSelectWrap
            showArrow={showArrow}
            ref={ref}
            tabIndex={disabled ? undefined : '0'}
            style={multiSelectStyle}
            className={classnames(className, size, {
              bordered,
              focused: visible,
              disabled,
              actived: visible,
              errored: !disabled && errored,
            })}
            {...keyboardHandlers}
            {...restProps}
            onClick={handleClickTrigger}
            onMouseDown={handleMouseDownTrigger}
            data-testid={testid ? `SelectWrap-${testid}` : undefined}
          >
            <TriggerWrap className="select_trigger_wrap">
              {selectionNode}
            </TriggerWrap>
          </MultiSelectWrap>
        </Dropdown>
        {errored && errorTip && <ErrorTip>{errorTip}</ErrorTip>}
        {disabled && <DisableMask />}
      </MultiSelectWrapper>
    </SelectContext.Provider>
  );
};

// 为了 hack VT的传递、Select.Option、Select.OptionGroup的定义
const MultiSelectMemo = (React.memo(
  React.forwardRef(MultiSelect as any),
) as unknown) as typeof MultiSelect & {
  Option: typeof MultiSelectOption;
  OptionGroup: typeof SelectOptionGroup;
};

(MultiSelectMemo as any).defaultProps = {
  defaultVisible: false,
  disabled: false,
  mode: 'default',
  selectable: true,
  size: 'normal',
  showArrow: true,
  autoClose: true,
  errored: false,
  showSelectOnRight: false,
};
(MultiSelectMemo as any).displayName = 'Select';

MultiSelectMemo.Option = MultiSelectOption;
MultiSelectMemo.OptionGroup = SelectOptionGroup;

export { MultiSelectMemo as MultiSelect };
