import * as React from 'react';
import { Portal, ITriggerRect } from '../../utils';
import { LinkEditor } from './LinkEditor';
import { LinkToolbar } from './LinkToolbar';

export interface ILinkPortalProps {
  /**
   * 链接文本
   */
  text: string;
  /**
   * 链接url
   */
  href: string;
  /**
   * 本地语言的类型,如zh_CN
   */
  locale?: string;
  /**
   * 保存链接
   */
  onSave?: (text: string, href: string) => void;
  /**
   * 移除链接
   */
  onRemove?: () => void;
  /**
   * 拷贝成功
   */
  onCopied?: (href: string) => void;
  /**
   * 打开链接
   */
  onOpenLink: (url: string) => void;
  /**
   * 是否为编辑模式的初始值，非受控模式
   */
  defaultEditMode?: boolean;
  /**
   * 默认的初始显示状态,非受控模式
   */
  defaultVisible?: boolean;
  /**
   * 是否显示，受控模式
   */
  visible?: boolean;
  /**
   * visible变化时的回调，受控模式
   */
  onVisibleChange?: (visible: boolean) => void;
  /**
   * 触发点的矩形位置
   */
  triggerRect?: ITriggerRect;
  /**
   * 挂载容器
   */
  getContainer?: () => HTMLElement;
  /**
   * 是否只读
   */
  readOnly?: boolean;
}

interface IState {
  editMode: boolean;
  innerVisible: boolean;
}

export class LinkPortal extends React.PureComponent<ILinkPortalProps, IState> {
  static defaultProps = {
    visible: true,
    readOnly: false,
  };

  portalRef = React.createRef<HTMLDivElement>();

  constructor(props: ILinkPortalProps) {
    super(props);
    const innerVisible = props.visible || props.defaultVisible || false;
    let editMode = props.defaultEditMode || false;
    if (props.defaultEditMode === undefined
      && innerVisible
      && props.href.trim().length === 0) {
      editMode = true;
    }
    this.state = {
      editMode,
      innerVisible,
    };
  }

  handleLinkMouseDown = () => {
    this.onVisibleChange(true);
  };

  handleSave = (text: string, href: string) => {
    const { onSave } = this.props;
    if (onSave) onSave(text, href);
    this.setState({ editMode: false });
    this.handleHide();
  };

  handleRemove = () => {
    const { onRemove } = this.props;
    if (onRemove) onRemove();
    this.setState({ editMode: true });
    this.handleHide();
  };

  onVisibleChange(v: boolean) {
    const { visible, onVisibleChange } = this.props;
    if (onVisibleChange) {
      onVisibleChange(v);
    }
    if (visible === undefined) {
      this.setState({ innerVisible: v });
    }
  }

  handleHide = () => {
    this.onVisibleChange(false);
  };

  getOverlay = () => {
    const { editMode } = this.state;

    // text/href 从 props 读了，已经不算非受控了

    const {
      text,
      href = '',
      locale,
      onCopied,
      onOpenLink,
      readOnly,
    } = this.props;
    const portalStyle: React.CSSProperties = {
      width: 'fit-content',
      position: 'absolute',
    };

    return editMode ? (
      <div ref={this.portalRef} style={portalStyle}>
        <LinkEditor
          defaultText={text}
          defaultHref={href}
          onSave={this.handleSave}
          onRemove={this.handleRemove}
          locale={locale}
          onHide={this.handleHide}
        />
      </div>
    ) : (
      <div ref={this.portalRef} style={portalStyle}>
        <LinkToolbar
          href={href}
          onEdit={() => this.setState({ editMode: true })}
          onRemove={this.handleRemove}
          onHide={this.handleHide}
          onOpenLink={onOpenLink}
          onCopied={onCopied}
          locale={locale}
          readOnly={readOnly}
        />
      </div>
    );
  };

  render() {
    const {
      children,
      triggerRect,
      visible,
      getContainer,
    } = this.props;
    const { innerVisible } = this.state;
    const activeVisible = visible || innerVisible;

    return (
      <Portal
        data-type="LinkPortal"
        className="demo-link-portal"
        visible={activeVisible}
        overlay={this.getOverlay()}
        portalMatchTrigger={false}
        offset={[0, 6]}
        triggerRect={triggerRect}
        getContainer={getContainer}
      >
        {children && React.cloneElement(children as React.ReactElement, {
          onMouseDown: this.handleLinkMouseDown,
        })}
      </Portal>
    );
  }
}
