/* eslint-disable react/static-property-placement */
/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { Component } from 'react';
import { classnames } from '../../utils';
import {
  getTransformByIndex,
  getActiveIndex,
  getTransformPropValue,
} from './utils';
import { ITabsProps } from './Tabs';
import { TabContentWrap } from './styled';

export default class TabContent extends Component<ITabsProps> {
  static defaultProps = {
    tabContentStyle: {},
  };

  getTabPanes() {
    const { props } = this;
    const { activeKey, animated, tabPosition } = props;
    const { children } = props;
    const newChildren: any[] = [];
    const shouldAnimated = animated && tabPosition !== 'left';
    React.Children.forEach(children, (child: any) => {
      if (!child) {
        return;
      }
      const { key } = child;
      const actived = activeKey === key;
      newChildren.push(
        React.cloneElement(child, {
          animated: shouldAnimated,
          actived,
          className: actived ? 'actived-pane' : 'inactived-pane',
        }),
      );
    });

    return newChildren;
  }

  render() {
    const { props } = this;
    const {
      children,
      activeKey,
      animated,
      tabPosition,
    } = props;
    // todo 左侧 切换动画没做，自动关掉，补上再打开
    const shouldAnimated = animated && tabPosition !== 'left';
    let { tabContentStyle: style } = props;

    if (shouldAnimated) {
      const activeIndex = getActiveIndex(children as any[], activeKey as string);
      if (activeIndex !== -1) {
        const animatedStyle = getTransformPropValue(getTransformByIndex(activeIndex));
        style = {
          ...style,
          ...animatedStyle,
        };
      } else {
        style = {
          ...style,
          display: 'none',
        };
      }
    }
    return (
      <TabContentWrap className={classnames(shouldAnimated && 'animated')} style={style}>
        {this.getTabPanes()}
      </TabContentWrap>
    );
  }
}
