export const OptionGroupWrap: any = withTheme(styled.div`
  position: relative;
  &:after {
    display: block;
    content: ' ';
    height: 1px;
    background-color: ${({ theme }) => theme.ui.commonLineLightColor};
    margin: 4px 4px;
  }
  &:before {
    display: block;
    content: ' ';
    height: 1px;
    background-color: ${({ theme }) => theme.ui.commonLineLightColor};
    width: 80%;
    margin: 6px auto;
  }
  &:nth-child(1) {
    &:before {
      display: none;
    }
  }
  &:last-child {
    &:after {
      display: none;
    }
  }
  ~ .select-group-wrapper {
    &:before {
      display: none;
    }
  }
`);

