/* eslint-disable consistent-return */
/* eslint-disable @typescript-eslint/no-non-null-assertion */
/* eslint-disable no-unused-expressions */
/* eslint-disable arrow-body-style */
import React, { useRef, useEffect, useState } from 'react';
import { NumberSliderWrap, NumberSliderNumber } from './styled';
import { classnames } from '../../utils';
import useComponentDidUpdate from '../../hooks/useComponentDidUpdate';
import Animation from '../../utils/Animation';

interface INumberSliderProps {
  number: number;
  numbers: number[];
  onChange: (e: number) => void;
  visible?: boolean;
  /**
   * 测试使用
   */
  testid?: string;
}

export const NumberSlider: React.FC<INumberSliderProps> = React.memo((props) => {
  const {
    numbers,
    number = 0,
    onChange,
    visible,
    testid,
  } = props;

  const containerRef = useRef<HTMLElement>(null);

  // 滚动的时候避免hover样式造成的鼠标闪动
  const [scrolling, setScrolling] = useState(false);

  /**
   * 初始化进来定位不需要动画
   */
  useEffect(() => {
    if (containerRef.current && visible) {
      containerRef.current.scrollTop = 32 * number;
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [visible]);

  /**
   * 滚动处理，一旦number更新了，就开始执行动画
   */
  useComponentDidUpdate(() => {
    if (containerRef.current && visible) {
      const targetValue = 32 * number;
      const currentValue = containerRef.current.scrollTop;
      setScrolling(true);
      const animation = new Animation({
        duration: 240,
        progress(p: number): void {
          if (containerRef.current) {
            containerRef.current.scrollTop = p * (targetValue - currentValue) + currentValue;
          }
        },
        done(): void {
          if (containerRef.current) {
            containerRef.current.scrollTop = targetValue;
          }
          setScrolling(false);
        },
      }).start();
      return (): void => {
        animation.clear();
      };
    }
  }, [number, visible]);

  return (
    <NumberSliderWrap ref={containerRef} data-testid={testid}>
      {
        numbers.map((num) => {
          return (
            <NumberSliderNumber
              key={num}
              className={classnames({
                actived: num === number,
                scrolling,
              })}
              onClick={(): void => { number !== num && onChange(num); }}
            >
              {num}
            </NumberSliderNumber>
          );
        })
      }
    </NumberSliderWrap>
  );
});
