import React, { useCallback, useMemo } from 'react';
import { RadioGroupWrap } from './styled';
import useControlledValue from '../../hooks/useControlledValue';
import { RadioContext, IToggleValueEvent, IRadioContext } from './RadioContext';
import { Radio } from './Radio';

export interface IRadioGroupProps {
  /**
   * 选项
   */
  options?: string[] | IRadioOption[];
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 选中值
   */
  value?: string;
  /**
   * 默认选中值
   */
  defaultValue?: string;
  /**
   * 修改回调
   */
  onChange?: (e: string) => void;
}

export interface IRadioOption {
  label: string;
  value: string;
  disabled?: boolean;
}

const RadioGroup: React.FC<IRadioGroupProps> = React.memo((props) => {
  const { children, disabled, onChange, options } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);

  /**
   * 选中切换
   *
   * @param {string} e
   * @return {void}
   */
  const handleToggleValue = useCallback((e: IToggleValueEvent) => {
    const newValue = e.value;
    if (onChange) {
      onChange(newValue);
    }
    setValue(newValue);
  }, [onChange, setValue]);

  /**
   * 透传给Radio组件的属性
   *
   * @return {IRadioContext} context
   */
  const provider = useMemo<IRadioContext>(() => {
    return {
      disabled: !!disabled,
      onToggleValue: handleToggleValue,
      checkedValue: value!,
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [!!disabled, handleToggleValue, value]);

  return (
    <RadioGroupWrap>
      <RadioContext.Provider value={provider}>
        {
          options ? (options as Array<string | IRadioOption>).map((option: IRadioOption | string) => {
            const item: IRadioOption = typeof option === 'string' ? {
              value: option,
              label: option,
            } : option;
            return (
              <Radio
                key={item.value}
                value={item.value}
                disabled={item.disabled}
              >
                {item.label}
              </Radio>
            );
          }) : children
        }
      </RadioContext.Provider>
    </RadioGroupWrap>
  );
});

RadioGroup.defaultProps = {
  defaultValue: '',
};

RadioGroup.displayName = 'RadioGroup';

export { RadioGroup };
