
export const lineWidthByBuiltIn = [1, 2, 4, 6];
export const lineWidthByCustom = [4, 6];
// 线段属性
export interface ILine {
  content: string;
  // 重复次数
  repeat: number;
  // 默认宽度列表
  sizeList: number[];
  // 线条的宽度(厚度)
  defaultSize: number;
  // 高度
  unitHeight: number;
  // 缩放比
  scale?: number;
}

export interface IBaseLine {
  // 边框最小单元的描述，表示连续不同颜色像素点的排列循环
  units?: number[];
  // 自定义的颜色顺序 length低于units的话会自动重复
  colors?: string[];
  // 从第几项开始重复 默认为0, 会从第n+1项目开始循环
  repeatFrom?: number;
  // 对unit进行缩进,不影响repeatFrom, 可以用来调整初始项的颜色
  indent?: number;
  // 该线段的宽度
  size?: number;
  // 斜角
  angle?: number;
}

export type ICustomLine = IBaseLine[];

