import React from 'react';
import styled from 'styled-components';
import { SelectDownloadNormal } from '@ali/we-icon';

export interface IToolbarProps {
  /**
  * 图片下载
  */
  downloadImage?: (url: string) => void;
  /**
  * 是否可以下载
  */
  enableDownload?: boolean;
  urls: string[];
  currentIndex: number;
}

const ToolbarWrapper = styled.div`
  position: absolute;
  left: 24px;
  right: 24px;
  bottom: 54px;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const ToolbarItemsBox = styled.div`
`;

const ToolbarItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 36px;
  padding: 0 6px;
  background: #303236;
  pointer-events: auto;
  border-radius: 4px;
  color: #fff;
  & + & {
    margin-right: 16px;
  }
`;

const Toolbar: React.FC<IToolbarProps> = (props) => {
  const { downloadImage, enableDownload, currentIndex, urls } = props;
  const showDownload = downloadImage && enableDownload;

  const currentUrl = urls[currentIndex];
  const total = urls.length;

  const handleDownload = React.useCallback((e: React.MouseEvent) => {
    e.stopPropagation();
    e.preventDefault();
    downloadImage && downloadImage(currentUrl);
  }, [currentUrl]);

  return (
    <ToolbarWrapper>
      <ToolbarItemsBox key="leftBox">
        <ToolbarItem>
          {`${currentIndex + 1}/${total}`}
        </ToolbarItem>
      </ToolbarItemsBox>
      <ToolbarItemsBox key="rightBox">
        {
          showDownload && (
            <ToolbarItem onClick={handleDownload}>
              <SelectDownloadNormal style={{ color: '#FFFFFF', fontSize: 24 }} />
            </ToolbarItem>
          )
        }
      </ToolbarItemsBox>
    </ToolbarWrapper>
  )
};

export default Toolbar;
