/* eslint-disable consistent-return */
/* eslint-disable @typescript-eslint/no-explicit-any */
const woff =
  'data:font/woff;charset=utf-8;base64,'

export default class FontDetector {
  public src: string

  public css: string

  private detection: boolean[]

  constructor() {
    this.detection = []
    this.css = `
    @font-face {
      font-family: font-detect-0-woff;
      src: url(${woff}) format('woff');
    }
    #font-detect-test-block {
      display: inline-block;
      position: fixed;
      left: -100%;
      font-family: font-detect-0-woff;
    }`
    this.src = ''
    this.inject()
  }

  inject = (): void => {
    let elem = document.getElementById('font-detect-test-block')
    if (elem == null) {
      elem = document.createElement('div')
      elem.id = 'font-detect-test-block'
      elem.innerHTML = '0'
      document.body.appendChild(elem)
    }
    let sheet = document.getElementById('font-detect-styles')
    if (sheet == null) {
      sheet = document.createElement('style')
      sheet.setAttribute('type', 'text/css')
      sheet.id = 'font-detect-styles'
      sheet.innerHTML = this.css
      const head = document.getElementsByTagName('head')[0]
      head.appendChild(sheet)
    }
  }

  remove = (): any => {
    const elem = document.getElementById('font-detect-test-block')
    if (elem != null) {
      elem.remove()
    }
    const sheet = document.getElementById('font-detect-styles')
    if (sheet != null) {
      return sheet.remove()
    }
  }

  ready = (callback: any): any => {
    setTimeout(() => {
      let elem = document.getElementById('font-detect-test-block')
      if (!elem) {
        this.inject()
        elem = document.getElementById('font-detect-test-block')
      }
      if (elem?.style && elem.clientWidth === 0) {
        return callback()
      }
    }, 0)
  }

  getResult = (fonts: string[], callback: any): any => {
    const elem = document.getElementById(
      'font-detect-test-block'
    ) as HTMLElement
    const results = []
    // eslint-disable-next-line no-restricted-syntax
    for (const font of fonts) {
      elem.style.fontFamily = `'${font}', font-detect-0-woff`
      results.push(elem.clientWidth > 0)
    }
    this.detection = results
    callback(results)
    return this.remove()
  }

  detect = (fonts: string[], callback: any): any =>
    this.detection.length > 0
      ? callback(this.detection)
      : this.ready(() => this.getResult(fonts, callback))
}
