/* eslint-disable max-lines-per-function */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable class-methods-use-this */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable react/static-property-placement */
import React, { Component } from 'react';
import fastdom from 'fastdom';
import ResizeObserver from 'resize-observer-polyfill';
import { CloseBigNormal } from '@ali/we-icon';
import {
  ArrowLeftNormal,
  ArrowRightNormal,
  ArrowDownLineNormal,
  MoreBetaNormal,
} from '../icons';
import { classnames } from '../../utils';
import { getVisibleRangePadding } from './utils';
import debounce from 'lodash/debounce';
import { setTransform, isTransform3dSupported, noop } from './utils';
import {
  CloseAllWrapper,
  CloseAllDivider,
  TabOptionButton,
  NavContainer,
  NavWrap,
  Nav,
  PrevButton,
  NextButton,
  MoreButtonWrapper,
} from './styled';
import { ITabsProps } from './Tabs';
import { ITabPaneProps } from './TabPane';
import { ISaveRef, IGetRef } from './interface';
import { Select, SelectOption } from '../select'
import { MORE_BUTTON_WRAPPER_WIDTH, MORE_BUTTON_WRAPPER_HEIGHT } from './constants';

interface ITabBarNodeProps extends ITabsProps {
  saveRef: ISaveRef;
  getRef: IGetRef;
  panes: React.ReactNode;
}

interface ITabBarNodeState {
  next: boolean;
  prev: boolean;
  needsDropdown: boolean;
  visibleStart: number;
  visibleEnd: number;
  lockAnimation: number;
}

export interface TabSize {
  width: number;
  height: number;
  left: number;
  top: number;
}

type TabSizeMap = Map<React.Key, TabSize>;

const DEFAULT_SIZE = { width: 0, height: 0, left: 0, top: 0 };

const selectStyle = { width: 24, height: 24, borderRadius: 4 };
const selectOptionStyle: React.CSSProperties = { padding: 6 };

export default class TabBarNode extends Component<
  ITabBarNodeProps,
  ITabBarNodeState
> {
  static defaultProps = {
    tabBarPosition: 'left',
    prefixCls: '',
    onPrevClick: noop,
    onNextClick: noop,
  };

  private offset: number;

  private resizeObserver: any;

  private debouncedResize: any;

  private lastNextPrevShown: any;

  private lastWheelDirection: 'x' | 'y' | undefined;

  private tabList: { key: React.Key; tab: React.ReactNode }[];

  private tabSizeMap: TabSizeMap;

  private lockAnimationTimer: number;

  private wrapperOffsetWH = 0;
  private wrapperScrollWH = 0;

  constructor(props: ITabBarNodeProps) {
    super(props);
    this.offset = 0;
    this.tabList = [];
    this.tabSizeMap = new Map();
    this.lockAnimationTimer = 0;
    this.state = {
      next: false,
      prev: false,
      needsDropdown: false,
      visibleStart: 0,
      visibleEnd: 0,
      lockAnimation: 0,
    };
  }

  componentDidMount() {
    const { getRef, excessMode } = this.props;
    this.componentDidUpdate();
    let debounceCallback = noop;

    if (excessMode === 'arrow' || excessMode === 'triangle') {
      debounceCallback = () => {
        this.setNextPrev();
        if (excessMode !== 'triangle') {
          this.scrollToActiveTab();
        }
      };
    } else if (excessMode === 'dropdown') {
      debounceCallback = () => {
        this.handleResize();
        const needsDropdown = this.isNeedsDropdown();
        if (needsDropdown !== this.state.needsDropdown) {
          this.setState({ needsDropdown });
        } else {
          this.scrollToActiveTab();
          this.updateVisibleRange();
        }
      };
    }

    const navWrapper = getRef('navWrap');
    if (navWrapper) {
      navWrapper.addEventListener('wheel', this.handleWheel);
    }

    this.debouncedResize = debounce(debounceCallback, 200);
    this.resizeObserver = new ResizeObserver(this.debouncedResize);
    this.resizeObserver.observe(getRef('container'));
  }

  componentDidUpdate(prevProps?: any, prevState?: any) {
    const { props } = this;
    const { excessMode } = props;
    if (excessMode === 'arrow' || excessMode === 'triangle') {
      this.setNextPrev().then((nextPrev: any) => {
        // wait next, prev show hide
        if (this.isNextPrevShown(this.state) !== this.isNextPrevShown(nextPrev)) {
          // eslint-disable-next-line react/no-did-update-set-state
          this.setState({}, this.scrollToActiveTab);
        } else if (!prevProps || props.activeKey !== prevProps.activeKey) {
          // can not use props.activeKey
          if (excessMode !== 'triangle') {
            this.scrollToActiveTab();
          }
        }
      });
    } 
    if (excessMode === 'dropdown') {
      if (prevState && prevState.needsDropdown !== this.state.needsDropdown) {
        this.setState({}, () => {
          this.handleResize();
          this.updateVisibleRange();
          this.scrollToActiveTab();
        });
      }
      if (!prevProps || props.activeKey !== prevProps.activeKey) {
        this.scrollToActiveTab();
      }
    }
    if (!prevProps || props.panes !== prevProps.panes) {
      const oldTabListKey =
        this.tabList && this.tabList.map((tab) => tab.key).join('_');
      this.tabList = this.getTabList(props.panes);
      const tabListKey = this.tabList.map((tab) => tab.key).join('_');
      if (oldTabListKey !== tabListKey) {
        this.updateTabSizes().then(() => {
          this.updateVisibleRange();
          this.handleResize();
          if (excessMode === 'dropdown') {
            const needsDropdown = this.isNeedsDropdown();
            if (needsDropdown !== this.state.needsDropdown) {
              this.setState({
                needsDropdown,
              });
            } else {
              this.scrollToActiveTab();
            }
          }
        });
      }
    }
    if (this.state.lockAnimation !== prevState?.lockAnimation) {
      this.clearLockAnimationTimer();
      if (this.state.lockAnimation) {
        this.lockAnimationTimer = window.setTimeout(() => {
          this.setState({
           lockAnimation: 0,
          });
         }, 100);
      }
    }
  }

  componentWillUnmount() {
    const { getRef } = this.props;
    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    }
    if (this.debouncedResize && this.debouncedResize.cancel) {
      this.debouncedResize.cancel();
    }
    const navWrapper = getRef('navWrap');
    if (navWrapper) {
      navWrapper.removeEventListener('wheel', this.handleWheel);
    }
  }

  /**
   * 检测左右按钮的激活状态，并调整 offset
   */
  setNextPrev() {
    return new Promise((resolve) => {
      fastdom.measure(() => {
        const { getRef } = this.props;
        const navNode = getRef('nav');
        const navTabsContainer = getRef('navTabsContainer');
        const navNodeWH = this.getScrollWH(navTabsContainer || navNode);
        // Add 1px to fix `offsetWidth` with decimal in Chrome not correct handle
        // https://github.com/ant-design/ant-design/issues/13423
        const containerWH = this.getOffsetWH(getRef('container')) + 1;
        const navWrapNodeWH = this.getOffsetWH(getRef('navWrap'));
        
        fastdom.mutate(() => {
          let { offset } = this;
          const minOffset = containerWH - navNodeWH;
          let { next, prev } = this.state;
  
          if (minOffset >= 0) {
            next = false;
            this.setOffset(0);
            offset = 0;
          } else if (minOffset < offset) {
            next = true;
          } else {
            next = false;
            // Fix https://github.com/ant-design/ant-design/issues/8861
            // Test with container offset which is stable
            // and set the offset of the nav wrap node
            const realOffset = navWrapNodeWH - navNodeWH;
            this.setOffset(realOffset);
            offset = realOffset;
          }
  
          if (offset < 0) {
            prev = true;
          } else {
            prev = false;
          }
  
          this.setNext(next);
          this.setPrev(prev);
          resolve({
            next,
            prev,
          });
        });
      });
    });
  }

  /**
   * 获取节点的宽度
   *
   * @param {HTMLElement} node
   * @return {number} offsetWidth
   */
  getOffsetWH(node: HTMLElement) {
    const { tabPosition = 'top' } = this.props;
    return tabPosition === 'top' ? node.offsetWidth :node.offsetHeight;
  }

  /**
   * 获取节点的滚动宽度
   *
   * @param {HTMLElement} node
   * @return {number} scrollWidth
   */
  getScrollWH(node: HTMLElement) {
    const { tabPosition = 'top' } = this.props;
    return tabPosition === 'top' ? node.scrollWidth : node.scrollHeight;
  }

  /**
   * 获取节点的偏移量
   *
   * @param {HTMLElement} node
   * @return {number} clientRect.left
   */
  getOffsetLT(node: HTMLElement) {
    const { tabPosition = 'top' } = this.props;
    return tabPosition === 'top' ? node.getBoundingClientRect().left : node.getBoundingClientRect().top;
  }

  /**
   * 设置偏移量
   *
   */
  setOffset(offset: number) {
    const { getRef, tabPosition } = this.props;
    const target: number = Math.min(0, offset);
    if (this.offset !== target) {
      this.offset = target;
      let navOffset: any = {};
      const navStyle = getRef('nav').style;
      const transformSupported = isTransform3dSupported(navStyle);
      if (transformSupported) {
        if (tabPosition !== 'left') {
          navOffset = {
            value: `translate3d(${target}px,0,0)`,
          };
        } else {
          navOffset = {
            value: `translate3d(0,${target}px,0)`,
          };
        }
      } else {
        if (tabPosition !== 'left') {
          navOffset = {
            name: 'left',
            value: `${target}px`,
          };
        } else {
          navOffset = {
            name: 'top',
            value: `${target}px`,
          };
        }
      }
      if (transformSupported) {
        setTransform(navStyle, navOffset.value);
      } else {
        navStyle[navOffset.name] = navOffset.value;
      }
      // 滚动后更新下拉菜单中的选项
      if (this.state.needsDropdown) {
        this.updateVisibleRange();
      }
    }
  }

  setPrev(v: boolean) {
    if (this.state.prev !== v) {
      this.setState({
        prev: v,
      });
    }
  }

  setNext(v: boolean) {
    if (this.state.next !== v) {
      this.setState({
        next: v,
      });
    }
  }

  prevTransitionEnd = (e: React.TransitionEvent<HTMLDivElement>) => {
    if (e.propertyName !== 'opacity') {
      return;
    }
    const container = this.props.getRef('container');
    this.scrollToActiveTab({
      target: container,
      currentTarget: container,
    });
  };

  scrollToActiveTab = (e?: any) => {
    const { getRef, excessMode } = this.props;
    const activeTab = getRef('activeTab');
    const navWrap = getRef('navWrap');
    if ((e && e.target !== e.currentTarget) || !activeTab) {
      return;
    }

    // when not scrollable or enter scrollable first time, don't emit scrolling
    if (excessMode === 'arrow' || excessMode === 'triangle') {
      const needToScroll = this.isNextPrevShown() && this.lastNextPrevShown;
      this.lastNextPrevShown = this.isNextPrevShown();
      if (!needToScroll) {
        return;
      }
    }

    const activeTabWH = this.getScrollWH(activeTab);
    const navWrapNodeWH = this.getOffsetWH(navWrap);
    let { offset } = this;
    const wrapOffset = this.getOffsetLT(navWrap);
    const activeTabOffset = this.getOffsetLT(activeTab);
    if (wrapOffset > activeTabOffset) {
      // activeTab 在 wrap 的可见区域左侧
      offset += wrapOffset - activeTabOffset;
      this.setOffset(offset);
      if (excessMode === 'arrow' || excessMode === 'triangle') {
        this.setNextPrev();
      }
    } else if (wrapOffset + navWrapNodeWH < activeTabOffset + activeTabWH) {
      // activeTab 在 wrap 的可见区域右侧
      offset -= activeTabOffset + activeTabWH - (wrapOffset + navWrapNodeWH);
      const minOffset = this.wrapperOffsetWH - this.wrapperScrollWH;
      const maxOffset = 0;
      const newOffset = Math.min(
        Math.max(offset, minOffset),
        maxOffset,
      );
      this.setOffset(newOffset);
      if (excessMode === 'arrow' || excessMode === 'triangle') {
        this.setNextPrev();
      }
    }
  };

  /** 点击左箭头的事件处理函数 */
  prev = (e: React.MouseEvent<HTMLDivElement>) => {
    const { onPrevClick, getRef } = this.props;
    if (onPrevClick) {
      onPrevClick(e);
    }
    const navWrapNode = getRef('navWrap');
    const navWrapNodeWH = this.getOffsetWH(navWrapNode);
    const { offset } = this;
    this.setOffset(offset + navWrapNodeWH);
    this.setNextPrev();
  };

  /** 点击右箭头的事件处理函数 */
  next = (e: React.MouseEvent<HTMLDivElement>) => {
    const { onNextClick, getRef } = this.props;
    if (onNextClick) {
      onNextClick(e);
    }
    const navWrapNode = getRef('navWrap');
    const navWrapNodeWH = this.getOffsetWH(navWrapNode);
    const { offset } = this;
    this.setOffset(offset - navWrapNodeWH);
    this.setNextPrev();
  };

  /**
   * 判断左右的按钮是否显示
   */
  isNextPrevShown(state?: { next: boolean; prev: boolean }) {
    if (state) {
      return state.next || state.prev;
    }
    const { next, prev } = this.state;
    return next || prev;
  }

  /**
   * 检测 dropdown 是否需要显示，并调整 offset
   */
  isNeedsDropdown() {
    const { getRef, tabPosition } = this.props;
    const navNode = getRef('nav');
    const navTabsContainer = getRef('navTabsContainer');
    const navNodeWH = this.getScrollWH(navTabsContainer || navNode);
    const containerWH = this.getOffsetWH(getRef('container'));
    const closeAllWH = getRef('closeAll')? this.getOffsetWH(getRef('closeAll')) : 0;
    // 最小的偏移量（可滚动时为负值）
    const minOffset =
      containerWH - navNodeWH - closeAllWH;
    const needsDropdown = minOffset < 0;
    const moreButtonWH = tabPosition === 'left' ? MORE_BUTTON_WRAPPER_WIDTH : MORE_BUTTON_WRAPPER_HEIGHT;

    if (!needsDropdown) {
      this.setOffset(0);
    } else if (minOffset  - moreButtonWH > this.offset) {
      // 如果此时偏移量比最小偏移量小（可能是窗口 resize 引起），则重置 offset
      this.setOffset(minOffset - moreButtonWH);
    }
    return needsDropdown;
  }

  clearLockAnimationTimer = () => {
    this.lockAnimationTimer && window.clearTimeout(this.lockAnimationTimer);
    this.lockAnimationTimer = 0;
  }

  handleWheel = (e: WheelEvent) => {
    const { wrapperOffsetWH, wrapperScrollWH  } = this;
    if (wrapperOffsetWH >=  wrapperScrollWH) {
      return false;
    }
    const { deltaX, deltaY } = e;
    // Convert both to x & y since wheel only happened on PC
    let mixed: number = 0;
    const absX = Math.abs(deltaX);
    const absY = Math.abs(deltaY);
    if (absX === absY) {
      mixed = this.lastWheelDirection === 'x' ? deltaX : deltaY;
    } else if (absX > absY) {
      mixed = deltaX;
      this.lastWheelDirection = 'x';
    } else {
      mixed = deltaY;
      this.lastWheelDirection = 'y';
    }
    const minOffset = wrapperOffsetWH - wrapperScrollWH;
    const maxOffset = 0;
    const newOffset = Math.min(
      Math.max(-mixed + this.offset, minOffset),
      maxOffset,
    );
    this.setOffset(newOffset);
    this.clearLockAnimationTimer();
    this.doLockAnimation();
    this.updateVisibleRange();
    e.preventDefault();
  };

  doLockAnimation = () => {
    this.setState({
      lockAnimation: Date.now(),
    });
  };

  handleResize = () => {
    fastdom.measure(() => {
      const { getRef, tabPosition = 'top' } = this.props;
      const wrapper = getRef('navWrap');
      const tabs = getRef('navTabsContainer');
      this.wrapperOffsetWH = tabPosition === 'top' ? (wrapper.offsetWidth || 0) : (wrapper.offsetHeight || 0);
      this.wrapperScrollWH = tabPosition === 'top' ? tabs.scrollWidth || 0 : tabs.scrollHeight|| 0;
    });
  };

  getTabList = (panes: React.ReactNode) => {
    const tabList: { key: React.Key; tab: React.ReactNode }[] = [];
    React.Children.forEach(
      panes as React.ReactElement[],
      (child: React.ReactElement<ITabPaneProps>) => {
        if (child && React.isValidElement(child)) {
          tabList.push({
            key: child.key as React.Key,
            tab: child.props.tab,
          });
        }
      },
    );
    return tabList;
  };

  updateTabSizes = () => {
    const { getRef, activeKey } = this.props;
    const tabSizeMap: TabSizeMap = new Map();
    return new Promise<void>((resolve) => {
      fastdom.measure(() => {
        this.tabList.forEach(({ key }) => {
          let btnNode;
          if (activeKey === key) {
            btnNode = getRef('activeTab');
          } else {
            btnNode = getRef(`tab_${key}`);
          }
          if (btnNode) {
            tabSizeMap.set(key, {
              width: btnNode.offsetWidth || 0,
              height: btnNode.offsetHeight || 0,
              left: btnNode.offsetLeft || 0,
              top: btnNode.offsetTop || 0,
            });
          }
        });
        this.tabSizeMap = tabSizeMap;
        resolve();
      });
    });
  };

  updateVisibleRange = () => {
    const { tabPosition = 'top', type = 'line' } = this.props;
    let unit: 'width' | 'height' = 'width';
    let position: 'left' | 'top' = 'left';
    if (tabPosition === 'left') {
      unit = 'height';
      position = 'top';
    }
    const { leftOrTopPadding, rightOrBottomPadding } = getVisibleRangePadding(type, tabPosition)
    const len = this.tabList.length;
    if (!len) {
      this.setState({
        visibleStart: 0,
        visibleEnd: 0,
      });
    }
    const { tabList, tabSizeMap, wrapperOffsetWH } = this;

    let endIndex = len;
    for (let i = 0; i < len; i += 1) {
      const offset = tabSizeMap.get(tabList[i].key) || DEFAULT_SIZE;
      if (offset[position] + offset[unit] + this.offset - rightOrBottomPadding > wrapperOffsetWH) {
        endIndex = i - 1;
        break;
      }
    }

    let startIndex = 0;
    for (let i = len - 1; i >= 0; i -= 1) {
      const offset = tabSizeMap.get(tabList[i].key) || DEFAULT_SIZE;
      if (offset[position] + this.offset + leftOrTopPadding < 0) {
        startIndex = i + 1;
        break;
      }
    }

    return this.setState({
      visibleStart: startIndex,
      visibleEnd: endIndex,
    });
  };

  getDropdown = () => {
    const {
      onTabClick,
      panes,
      activeKey,
      tabPosition,
      type,
    } = this.props;
    const rst: React.ReactElement[] = [];
    const placement = tabPosition === 'left' ? 'rightBottom' : 'bottomRight';
    React.Children.forEach(
      panes as React.ReactElement[],
      (child: any, index: number) => {
        if (
          !child ||
          (index >= this.state.visibleStart && index <= this.state.visibleEnd)
        ) {
          return;
        }
        const { key, props } = child;
        const { disabled, tab } = props;

        const node = (
          <SelectOption
            disabled={disabled}
            value={key}
            style={selectOptionStyle}
            key={key}
          >
            {tab}
          </SelectOption>
        );

        rst.push(node);
      },
    );
    const onChange = (key: string) => {
      onTabClick && onTabClick(key);
    };

    return (
      <MoreButtonWrapper className={classnames(tabPosition || 'top', type)}>
        <Select
          autoClose
          onChange={onChange}
          value={activeKey}
          renderTrigger={() => (<MoreBetaNormal/>)}
          placement={placement}
          showArrow={false}
          className="more-button-inner-icon"
          style={selectStyle}
          selectable={false}
        >
          {rst}
        </Select>
      </MoreButtonWrapper>
    );
  };

  rotateArrow = (angle: number): React.CSSProperties => {
    return {
      flex: 'none',
      transform: `rotate(${angle}deg)`,
      width: 8,
      height: 8,
    };
  };

  getPrevNext = () => {
    const { prev, next, } = this.state;
    const { testid, excessMode } = this.props;
    const showNextPrev = prev || next;
    const { prev: prevCallback, next: nextCallback, prevTransitionEnd } = this;
    const prevButton = (
      <PrevButton
        onClick={prev ? prevCallback : undefined}
        unselectable="on"
        className={classnames('prevbutton', {
          disabled: !prev,
          show: showNextPrev,
        })}
        onTransitionEnd={prevTransitionEnd}
        data-testid={`${testid || ''}_pre`}
      >
        {excessMode === 'arrow' ? (
          <ArrowLeftNormal className="prev-icon" />
        ) : (
          <ArrowDownLineNormal
            className="prev-icon"
            style={this.rotateArrow(90)}
          />
        )}
      </PrevButton>
    );

    const nextButton = (
      <NextButton
        onClick={next ? nextCallback : undefined}
        unselectable="on"
        className={classnames('nextbutton', {
          disabled: !next,
          show: showNextPrev,
        })}
        data-testid={`${testid || ''}_next`}
      >
        {excessMode === 'arrow' ? (
          <ArrowRightNormal className="next-icon" />
        ) : (
          <ArrowDownLineNormal
            className="next-icon"
            style={this.rotateArrow(-90)}
          />
        )}
      </NextButton>
    );

    return (
      <>
        {prevButton}
        {nextButton}
      </>
    );
  };

  render() {
    const { needsDropdown, lockAnimation, prev, next } = this.state;
    const {
      size,
      children,
      saveRef,
      animated,
      type,
      excessMode,
      tabPosition,
      navContainerStyle,
    } = this.props;
    const showNextPrev = prev || next;
    const excessTabsHelper = () => {
      if (excessMode === 'arrow' || excessMode === 'triangle') {
        return showNextPrev ? this.getPrevNext() : undefined;
      } else if (excessMode === 'dropdown') {
        return needsDropdown ? (
          this.getDropdown()
        ) : undefined;
      }
    };

    const renderCloseAll = () => {
      const { onCloseAll, saveRef, tabPosition } = this.props;
      const showDivider = needsDropdown || showNextPrev;
      if (tabPosition !== 'left' && typeof onCloseAll === 'function') {
        return (
          <CloseAllWrapper
            onClick={onCloseAll}
            ref={saveRef('closeAll')}
            data-testid="tabs-close-all"
          >
            <TabOptionButton>
              <CloseBigNormal />
            </TabOptionButton>
            {showDivider && <CloseAllDivider />}
          </CloseAllWrapper>
        );
      }
    };

    return (
      <NavContainer
        className={classnames(size, type, tabPosition && `${tabPosition}`, {
          scrolling:
            excessMode === 'arrow' || excessMode === 'triangle'
              ? showNextPrev
              : needsDropdown,
        })}
        ref={saveRef('container')}
        style={navContainerStyle}
      >
        <NavWrap
          className={classnames(type)}
          ref={saveRef('navWrap')}
        >
          <Nav
            className={classnames(type, animated && 'animated', tabPosition || 'top')}
            ref={saveRef('nav')}
            style={{
              transition: lockAnimation ? 'none' : undefined,
            }}
          >
            {children}
          </Nav>
        </NavWrap>
        {excessTabsHelper()}
        {renderCloseAll()}
      </NavContainer>
    );
  }
}
