/**
 * ··············································································>
 * 🐝 @命名规范：
 * @param {尺度}   ⚖️ mini,tiny,small,medium,large,big,huge,massive(参考sematic-ui)
 * @param {优先级} ⚖ primary,second,third
 * @param {层级}   ⚖ lower,low,normal,high,higher
 * @param {程度}   ⚖ lighter,light, normal, medium,heavy,heavier
 * -------------------------------------------------------------------------------
 *
 * ··············································································>
 * 🐝 @使用规范：
 * @param {颜色值}    🔨 严格使用主题变量，不要使用自定义值
 * @param {zindex}    🔨 严格使用主题变量，不要使用自定义值
 * @param {动画&过渡} 🔨 严格使用主题变量，不要使用自定义值
 * @param {字体相关}  🔨 严格使用主题变量，不要使用自定义值
 * @param {盒子尺寸}  🔨 padding,margin,尽量使用主题变量，border属性严格使用主题变量
 * -------------------------------------------------------------------------------
 */
import colors from './params/colors';
import zindex from './params/zindex';
import size from './params/size';
import font from './params/font';
import { transitions } from './params/animate';
import shadows from './params/shadow';
import switchTheme from './components/switch';
import button from './components/button';
import alert from './components/alert';
import colorPicker from './components/colorpicker';
import datePicker from './components/date-picker';
import toolbar from './components/toolbar';
import dropdown from './components/dropdown';
import tabs from './components/tabs';
import tooltip from './components/tooltip';
import notification from './components/notification';
import modal from './components/modal';
import menu from './components/menu';
import select from './components/select';

export const theme = {
  ...colors,
  ...size,
  ...font,
  ...zindex,
  ...transitions,
  ...shadows,
  ...switchTheme,
  ...button,
  ...colorPicker,
  ...alert,
  ...toolbar,
  ...dropdown,
  ...tabs,
  ...tooltip,
  ...notification,
  ...modal,
  ...datePicker,
  ...menu,
  ...select,
};

export type TTheme = typeof theme;

export const withTheme = (C: React.ComponentType<any>) => {
  C.defaultProps = {
    theme: {
      ui: theme,
    },
  };
  return C;
};
