/* eslint-disable max-lines-per-function */
/* eslint-disable @typescript-eslint/no-shadow */
import React, { useState, useRef, useEffect } from 'react';
import { useTranslate } from '@ali/we-util';

import ColorSelector from './ColorSelector';
import CustomPanel from './CustomPanel';
import ExtraCheckbox from './ExtraCheckbox';
import MoreColorPanel from './MoreColorPanel';
import ColorPreview from './ColorPreview';
import isDark from './utils/isDark';
import { PickerWrapper, ResetButton } from './styled';
import {
  IColorPanel,
  IColorPanelLayout,
  ICheckboxLayout,
  ICurrentColors,
  EColorType,
} from './types';
import {
  DEFAULT_PREVIEW_STATE,
  COLOR_SELECTED_KEY,
  BG_SELECTED_KEY,
  DEFAULT_FONT_COLOR,
  DEFAULT_BG_COLOR,
  COULD_SHOW_TIP_COLORS,
} from './constants';
import EColorPanelRole from './role';

const ColorPanelCore: React.FC<IColorPanel> = (props) => {
  const t = useTranslate('');
  const {
    colorPanelLayout = [],
    checkboxLayout = [],
    currentColors = {},
    enableMoreButton = false,
    renderMorePanel,
    enableResetButton = false,
    onResetButtonClick,
    resetButtonTitle,
    isFirstRowDoubleGap = false,
    role = '',
    shouldShowPreview = false,
    onCustomVisibleChange,
    onMouseEnter,
    onMouseLeave,
  } = props;
  const [previewState, setPreviewState] = useState(DEFAULT_PREVIEW_STATE);

  const [customPanelVisible, setCustomPanelVisible] = useState(false);
  const [customColor, setCustomColor] = useState('');
  const [customClickHandler, setCustomClickHandler] = useState<Function>(
    () => {},
  );
  const [triggerKey, setTriggerKey] = useState('');
  const colorPanelRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    onCustomVisibleChange && onCustomVisibleChange(customPanelVisible);
  }, [customPanelVisible]);

  const renderColorSelector = (
    colorPanelLayout: IColorPanelLayout,
    currentColors: ICurrentColors,
  ) => {
    return (
      <>
        {colorPanelLayout.map((layoutItem) => (
          <ColorSelector
            key={layoutItem.title}
            {...layoutItem}
            currentColors={currentColors}
            isFirstRowDoubleGap={isFirstRowDoubleGap}
            onMouseEnter={handleMouseEnterPreview}
            onMouseLeave={handleMouseLeavePreview}
            customPanelVisible={customPanelVisible}
            setCustomPanelVisible={setCustomPanelVisible}
            setCustomColor={setCustomColor}
            setCustomClickHandler={setCustomClickHandler}
            triggerKey={triggerKey}
            setTriggerKey={setTriggerKey}
            shouldShowPreview={shouldShowPreview}
          />
        ))}
        {customPanelVisible && renderCustomPanel()}
      </>
    );
  };

  const renderCustomPanel = () => {
    const handleCustomColorChange = (color: string) => {
      setCustomColor(color);
      customClickHandler(color);
    };

    const handleCustomVisibleChange = (visible: boolean) => {
      setCustomPanelVisible(visible);
      if (onCustomVisibleChange) {
        onCustomVisibleChange(visible);
      }
    };

    return (
      <CustomPanel
        color={customColor}
        visible={customPanelVisible}
        onColorChange={handleCustomColorChange}
        onVisibleChange={handleCustomVisibleChange}
        containerRef={colorPanelRef}
      />
    );
  };

  const renderExtraCheckboxes = (checkboxLayout: ICheckboxLayout) => {
    return checkboxLayout.map((extraCheckbox) => (
      <ExtraCheckbox key={extraCheckbox.title} {...extraCheckbox} />
    ));
  };

  const renderMoreColorPanel = () => {
    if (typeof renderMorePanel !== 'function') {
      return null;
    }
    const morePanelContent = renderMorePanel();
    return <MoreColorPanel morePanelContent={morePanelContent} />;
  };

  const renderPreviewBlock = () => {
    const { isShow, color, backgroundColor, shouldShowTip } = previewState;

    return (
      isShow && (
        <ColorPreview
          color={color}
          backgroundColor={backgroundColor}
          shouldShowTip={shouldShowTip}
          colorPanelRef={colorPanelRef}
        />
      )
    );
  };

  const handleResetClick = () => {
    if (typeof onResetButtonClick === 'function') {
      const shouldContinue = onResetButtonClick();
      if (!shouldContinue) return;
    }
    // 所有色板恢复默认颜色
    colorPanelLayout.forEach((layoutItem) => {
      const { defaultColor = '', onClick } = layoutItem;
      if (typeof onClick !== 'function') {
        return;
      }
      onClick(defaultColor);
    });

    // 所有 checkbox 恢复默认
    checkboxLayout.forEach((checkboxItem) => {
      const { defaultValue, onValueChange } = checkboxItem;
      if (typeof onValueChange !== 'function') {
        return;
      }
      onValueChange(defaultValue || false);
    });

    // 预览框恢复默认
    shouldShowPreview && setPreviewState(DEFAULT_PREVIEW_STATE);
  };

  const showMoreColorIcon = colorPanelLayout.some(
    (layout) => layout.showMoreIcon,
  );

  // hover 色块，展示预览
  const handleMouseEnterPreview = (
    colorType: EColorType,
    colorOrBgColor: string,
  ) => {
    if (!shouldShowPreview) {
      return;
    }

    switch (colorType) {
      case EColorType.font:
        const backgroundColor =
          currentColors[BG_SELECTED_KEY] || DEFAULT_BG_COLOR;
        return setPreviewState({
          isShow: true,
          shouldShowTip: false,
          color: colorOrBgColor,
          backgroundColor,
        });

      case EColorType.background:
      case EColorType.fontBackground:
        const color = currentColors[COLOR_SELECTED_KEY] || DEFAULT_FONT_COLOR;

        // 当字体颜色为「默认值或第一个色值」且「hover 的背景色为深色时」
        // 推荐用户使用白色字体
        const shouldShowTip =
          COULD_SHOW_TIP_COLORS.includes(color) && isDark(colorOrBgColor);
        return setPreviewState({
          isShow: true,
          shouldShowTip,
          color: shouldShowTip ? '#FFFFFF' : color,
          backgroundColor: colorOrBgColor,
        });

      default:
        return;
    }
  };

  // 离开颜色选择面板时隐藏预览框
  const handleMouseLeavePreview = () => {
    shouldShowPreview && setPreviewState(DEFAULT_PREVIEW_STATE);
  };

  const handleMouseEnterPanel = (e: React.MouseEvent) => {
    onMouseEnter && onMouseEnter(e);
  };

  const handleMouseLeavePanel = (e: React.MouseEvent) => {
    onMouseLeave && onMouseLeave(e);
  };

  return (
    <PickerWrapper
      showMoreColorIcon={showMoreColorIcon}
      data-role={role}
      ref={colorPanelRef}
      onMouseEnter={handleMouseEnterPanel}
      onMouseLeave={handleMouseLeavePanel}
    >
      {renderColorSelector(colorPanelLayout, currentColors)}
      {renderExtraCheckboxes(checkboxLayout)}
      {enableMoreButton && renderMoreColorPanel()}
      {enableResetButton && (
        <ResetButton
          onClick={handleResetClick}
          data-role={EColorPanelRole.resetColor}
        >
          <span>{resetButtonTitle || t('we_ui_clear_color')}</span>
        </ResetButton>
      )}
      {shouldShowPreview && !customPanelVisible && renderPreviewBlock()}
    </PickerWrapper>
  );
};

export default ColorPanelCore;
