/* eslint-disable react/no-find-dom-node */
import React, { useCallback, useMemo, useEffect, useRef } from 'react';
import ResizeObserver from 'resize-observer-polyfill';
import domAlign, { alignPoint } from 'dom-align';
import { PortalComponent } from '../portal-component';
import {
  OverlayWrap,
  OverlayMask,
  PopoverContent,
  PopoverArrow,
} from './styled';
import type { ITriggerProps } from './Trigger';
import { getAlignPointsAndOffsets, debounce, throttle } from '../../utils';
import { useClickOuterRef } from '../../hooks';
import { isElement } from 'lodash-es';

interface IProps
  extends Pick<
    ITriggerProps,
    | 'getContainer'
    | 'overlayStyle'
    | 'placement'
    | 'zIndex'
    | 'overlay'
    | 'overlayClassName'
    | 'pageX'
    | 'pageY'
    | 'testid'
    | 'type'
    | 'maskColor'
    | 'role'
  > {
  visible: boolean;
  mask: boolean;
  offsetY: number;
  offsetX: number;
  backgroundColor?: string;
  /**
   * 延迟销毁
   */
  delayDestroy?: number;
  /**
   * trigger节点手动传入
   */
  triggerNode?: HTMLElement;
  onMouseEnter?: React.MouseEventHandler;
  onMouseLeave?: React.MouseEventHandler;
  onMouseDown?: React.MouseEventHandler;
  onClickMask?: React.MouseEventHandler;
  onClickOuter?: any;
  /**
   * 禁止根据视口自适应展开位置
   */
  disableDomAlign?: boolean;
  /**
   * 监听overlay的宽高变化自适应位置
   */
  overlayAutoAlign?: boolean;
  /**
   * overlayAutoAlign 执行延时
   * 默认 100ms
   */
  overlayAutoAlignDelay?: number;
}

const OFFSET_POPUP = 2;
const OFFSET_POPOVER = 10;
const OFFSET_ARROW = 2;

/**
 * 三角位置的计算
 */
const alginArrow = (offsetX: number, offsetY: number) => {
  return [offsetX, offsetY].map((offset) => {
    let arrowOffset = 0;
    if (offset === 0) {
      return arrowOffset;
    }
    if (offset > 0) {
      arrowOffset = OFFSET_ARROW + offset - OFFSET_POPOVER;
      return arrowOffset < 0 ? 0 : arrowOffset;
    } else {
      arrowOffset = -OFFSET_ARROW + offset + OFFSET_POPOVER;
      return arrowOffset > 0 ? 0 : arrowOffset;
    }
  });
};

// eslint-disable-next-line max-lines-per-function
export const Popup: React.FC<IProps> = (props) => {
  const {
    type,
    visible,
    getContainer,
    overlayStyle,
    overlay,
    triggerNode,
    placement,
    onMouseEnter,
    onMouseLeave,
    zIndex,
    onClickOuter,
    offsetX,
    offsetY,
    mask,
    onClickMask,
    overlayClassName,
    pageX,
    pageY,
    testid,
    backgroundColor,
    maskColor,
    delayDestroy,
    disableDomAlign,
    overlayAutoAlign,
    overlayAutoAlignDelay,
    role = '',
  } = props;

  // 容器
  const oPopupContainer = getContainer ? getContainer() : document.body;

  const overlayRef = useClickOuterRef(onClickOuter, visible);

  const popoverArrowRef = useRef<HTMLElement>(null);

  /**
   * 计算位置
   *
   * @param {HTMLElement} trigger node
   * @return {void}
   */
  const handleAlign = useCallback(
    (oTrigger?: HTMLElement) => {
      if (!overlayRef.current) return;
      const isPopover = type === 'popover';
      const overflow = { adjustX: true, adjustY: true };
      const targetOffset = [0, 0];

      let overlayPoints: string[] = [];
      let overlayOffsets: number[] = [];
      if (isPopover) {
        const overlayPosition = getAlignPointsAndOffsets(
          placement!,
          OFFSET_POPOVER,
        );
        overlayPoints = overlayPosition.points;
        overlayOffsets = overlayPosition.offsets;
      } else {
        const { points, offsets } = getAlignPointsAndOffsets(
          placement!,
          OFFSET_POPUP,
        );
        overlayPoints = points;
        overlayOffsets = offsets;
      }

      const commonPosition = { targetOffset, overflow };

      if (typeof pageX === 'number' && typeof pageY === 'number') {
        alignPoint(
          overlayRef.current,
          { pageX, pageY },
          {
            ...commonPosition,
            offset: [offsetX, offsetY],
            points: overlayPoints,
          },
        );
      } else if (oTrigger) {
        const allowAdjust = disableDomAlign !== true;
        const position = domAlign(overlayRef.current, oTrigger, {
          ...commonPosition,
          points: overlayPoints,
          offset: [overlayOffsets[0] + offsetX, overlayOffsets[1] + offsetY],
          overflow: {
            adjustX: allowAdjust,
            adjustY: allowAdjust,
          },
        });

        overlayRef.current.setAttribute(
          'position',
          (position.points || []).join(),
        );
        if (isPopover) {
          position.offset = alginArrow(
            position.overflow.adjustX
              ? -position.offset[0]
              : position.offset[0],
            position.overflow.adjustY
              ? -position.offset[1]
              : position.offset[1],
          );
          position.overflow = { adjustX: true, adjustY: true };

          domAlign(popoverArrowRef.current, oTrigger, position);
        }
      }
      if (testid) {
        overlayRef.current.setAttribute('data-testid', `Popup-${testid || ''}`);
      }
    },
    [pageX, pageY, offsetY, offsetX, placement, overlayRef, testid],
  );

  useEffect(() => {
    let resizeObserver: ResizeObserver | undefined;

    const handleResize = debounce(() => {
      if (overlayRef.current && triggerNode) {
        handleAlign(triggerNode);
      }
    }, 300);

    if (visible && oPopupContainer && overlayRef.current && triggerNode) {
      resizeObserver = new ResizeObserver(handleResize);
    }

    if (resizeObserver && isElement(oPopupContainer)) {
      resizeObserver.observe(oPopupContainer);
    } else if (!isElement(oPopupContainer)) {
      // throw new Error('Container is not Element')
      console.error('Container is not Element');
    }

    return () => {
      if (resizeObserver) {
        resizeObserver.disconnect();
        if (typeof handleResize.cancel === 'function') {
          handleResize.cancel();
        }
      }
    };
  }, [visible, oPopupContainer, overlayRef, triggerNode, handleAlign]);

  /**
   * 监控overlay的size变化
   */
  useEffect(() => {
    if (!overlayAutoAlign) return;

    if (!overlayRef.current) return;

    let resizeObserver: ResizeObserver | undefined;

    const handleResize = throttle(
      () => {
        if (triggerNode) {
          handleAlign(triggerNode);
        }
      },
      overlayAutoAlignDelay || 100,
      true,
    );

    if (visible && triggerNode) {
      resizeObserver = new ResizeObserver(handleResize);
    }

    if (resizeObserver && isElement(overlayRef.current)) {
      resizeObserver.observe(overlayRef.current);
    }

    return () => {
      if (resizeObserver) {
        resizeObserver.disconnect();
      }
    };
  }, [visible, triggerNode, handleAlign, placement, disableDomAlign]);

  /**
   * 阻止blur
   */
  const handleClick = useCallback((e: React.MouseEvent<HTMLElement>) => {
    const whiteTags = ['input', 'textarea'];
    const tagName = ((e.target as HTMLElement).tagName || '').toLowerCase();
    if (!whiteTags.includes(tagName)) {
      e.preventDefault();
    }
  }, []);

  /**
   * 做位置计算的
   */
  useEffect(() => {
    let t = 0;
    if (visible) {
      if (triggerNode) {
        handleAlign(triggerNode);
      } else {
        handleAlign();
      }
    } else if (overlayRef.current) {
      const el = overlayRef.current;
      t = window.setTimeout(() => {
        el.style.top = '-9999px';
        el.style.left = '-9999px';
      }, delayDestroy || 0);
    }
    return () => clearTimeout(t);
  }, [visible, handleAlign, triggerNode, overlayRef, delayDestroy]);

  /**
   * 合并样式
   */
  const styleCombined = useMemo(() => {
    const styles = { ...(overlayStyle || {}) };
    if (typeof zIndex === 'number') {
      styles.zIndex = zIndex;
    }
    if (backgroundColor) {
      styles.backgroundColor = backgroundColor;
    }
    return styles;
  }, [overlayStyle, zIndex]);

  const popoverBoxStyle = useMemo(() => {
    const styles: React.CSSProperties = {};
    if (typeof zIndex === 'number') {
      styles.zIndex = zIndex + 2;
    }
    return styles;
  }, []);

  return (
    <PortalComponent visible={!!visible} getContainer={getContainer}>
      {mask && (
        <OverlayMask
          data-testid={testid && `Popup_mask_${testid}`}
          className={visible ? undefined : 'hidden'}
          style={
            maskColor ? { zIndex, backgroundColor: maskColor } : { zIndex }
          }
          onClick={onClickMask}
          onContextMenu={(e: React.MouseEvent) => e.preventDefault()}
        />
      )}
      {type === 'popup' ? (
        <OverlayWrap
          ref={overlayRef}
          style={styleCombined}
          className={`${overlayClassName} ${
            visible ? '' : 'hidden'
          } popup-overlay-wrap`}
          onMouseDown={handleClick}
          onMouseEnter={onMouseEnter}
          onMouseLeave={onMouseLeave}
          data-role={role}
        >
          {overlay}
        </OverlayWrap>
      ) : (
        <>
          <PopoverContent
            className={visible ? '' : 'hidden'}
            style={styleCombined}
            ref={overlayRef}
            onMouseDown={handleClick}
            data-role={role}
          >
            <div
              className="popover_content_box"
              style={popoverBoxStyle}
              onMouseEnter={onMouseEnter}
              onMouseLeave={onMouseLeave}
            >
              {overlay}
            </div>
            <PopoverArrow
              style={styleCombined}
              className={visible ? '' : 'hidden'}
              ref={popoverArrowRef}
              onMouseDown={handleClick}
            />
          </PopoverContent>
        </>
      )}
    </PortalComponent>
  );
};
