import React from 'react';
import {
  SuperArrowLeftBetaNormal,
  SuperArrowRightBetaNormal,
  ArrowLeftThinBetaNormal,
  ArrowRightThinBetaNormal,
} from '../../components/icons';
import {
  ControlRoot,
  ControlPrevWrap,
  ControlNextWrap,
  ControlMain,
  ArrowWrap,
} from './styled';

export interface IControlProps {
  hasSuperNext?: boolean;
  hasSuperPrev?: boolean;
  hasNext?: boolean;
  hasPrev?: boolean;
  onClickSuperPrev?: () => void;
  onClickSuperNext?: () => void;
  onClickPrev?: () => void;
  onClickNext?: () => void;
  /**
   * 测试使用
   */
  testid?: string;
}

export const Control: React.FC<IControlProps> = (props) => {
  const {
    children,
    hasPrev,
    hasNext,
    hasSuperPrev,
    hasSuperNext,
    onClickSuperNext,
    onClickNext,
    onClickSuperPrev,
    onClickPrev,
    testid,
  } = props;

  return (
    <ControlRoot>
      <ControlPrevWrap>
        {
          hasSuperPrev && (
            <ArrowWrap onClick={onClickSuperPrev}>
              <SuperArrowLeftBetaNormal className="arrow-icon" testid={testid} />
            </ArrowWrap>
          )
        }
        {
          hasPrev && (
            <ArrowWrap onClick={onClickPrev}>
              <ArrowLeftThinBetaNormal className="arrow-icon" testid={testid} />
            </ArrowWrap>
          )
        }
      </ControlPrevWrap>
      <ControlMain data-testid={testid}>{children}</ControlMain>
      <ControlNextWrap>
        {
          hasNext && (
            <ArrowWrap onClick={onClickNext}>
              <ArrowRightThinBetaNormal className="arrow-icon" testid={testid} />
            </ArrowWrap>
          )
        }
        {
          hasSuperNext && (
            <ArrowWrap onClick={onClickSuperNext}>
              <SuperArrowRightBetaNormal className="arrow-icon" testid={testid} />
            </ArrowWrap>
          )
        }
      </ControlNextWrap>
    </ControlRoot>
  );
};

Control.defaultProps = {
  hasSuperNext: true,
  hasSuperPrev: true,
  hasNext: true,
  hasPrev: true,
  onClickSuperPrev: (): void => {},
  onClickSuperNext: (): void => {},
  onClickPrev: (): void => {},
  onClickNext: (): void => {},
};
