import React, { useMemo, useCallback } from 'react';
import dayjs, { Dayjs } from 'dayjs';
import useControlledValue from '../../hooks/useControlledValue';
import { classnames } from '../../utils';
import { Control } from './Control';
import {
  YearPanelWrap,
  YearPanelBody,
  YearPanelItem,
  ControlWrap,
  PanelControlWrap,
} from './styled';
import { DatePickerPanelType } from './DatePicker';

export interface IYearPanelProps {
  /**
   * 日期值
   */
  value?: Dayjs;
  /**
   * 日期默认值
   */
  defaultValue?: Dayjs;
  /**
   * 是否带左右的控制条
   */
  control?: boolean;
  /**
   * 预览的年份
   */
  startYear?: number;
  /**
   * 修改dayjs值
   */
  onChange?: (e: Dayjs) => void;
  /**
   * 修改显示年份
   */
  onChangeStartYear?: (e: number) => void;
  /**
   * 点击控制条
   */
  onClickControl?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 不可选择的日期
   */
  disabledDate?: (currentDate: Dayjs, view: DatePickerPanelType) => boolean;
  /**
   * 测试使用
   */
  testid?: string;
}

// eslint-disable-next-line max-lines-per-function
const YearPanel: React.FC<IYearPanelProps> = React.memo((props) => {
  const {
    onChange,
    control,
    onChangeStartYear,
    onClickControl,
    style,
    disabledDate,
    testid,
  } = props;

  // 可传dayjs值进来，受控
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);

  // 临时值
  const [startYear, setStartYear] = useControlledValue(props.startYear, value!.year() - 4);

  // 拿到语言包短月份数组
  const years = useMemo(() => {
    return Object.keys(Array.from({ length: 12 })).map((_, i) => i + startYear!);
  }, [startYear]);

  const yearDates = useMemo(() => {
    return years.map((y) => value!.year(y));
  }, [years, value]);

  /**
   * 修改dayjs值
   *
   * @param {number} e
   * @return {void}
   */
  const handleChange = (e: number): void => {
    const newValue = value!.year(e);
    if (onChange) {
      onChange(newValue);
    }
    setValue(newValue);
  };

  /**
   * 按下右按钮修改年份
   *
   * @param {number} e year
   * @return {void}
   */
  const handleAddYear = useCallback(() => {
    const newStartYear = startYear! + 12;
    if (onChangeStartYear) {
      onChangeStartYear(newStartYear);
    }
    setStartYear(newStartYear);
  }, [startYear, onChangeStartYear, setStartYear]);

  /**
   * 按下左按钮修改年份
   *
   * @param {number} e year
   * @return {void}
   */
  const hanldeSubtract = useCallback(() => {
    const newStartYear = startYear! - 12;
    if (onChangeStartYear) {
      onChangeStartYear(newStartYear);
    }
    setStartYear(newStartYear);
  }, [startYear, setStartYear, onChangeStartYear]);

  return (
    <YearPanelWrap style={style}>
      {
        control !== false && (
          <ControlWrap>
            <Control
              hasNext={false}
              hasPrev={false}
              onClickSuperNext={handleAddYear}
              onClickSuperPrev={hanldeSubtract}
              testid={testid ? `YearPanel-${testid}` : ''}
            >
              <PanelControlWrap
                className={classnames({ clickable: !!onClickControl })}
                onClick={onClickControl}
              >
                {startYear} - {startYear! + 11}
              </PanelControlWrap>
            </Control>
          </ControlWrap>
        )
      }
      <YearPanelBody>
        {
          years.map((item, index) => {
            const isDisabled = typeof disabledDate === 'function'
              ? disabledDate(yearDates[index], 'year')
              : false;
            return (
              <YearPanelItem key={item}>
                <span
                  className={classnames({
                    actived: item === value!.year(),
                    isDisabled,
                  })}
                  onClick={(): void => { !isDisabled && handleChange(item); }}
                >
                  {item}
                </span>
              </YearPanelItem>
            );
          })
        }
      </YearPanelBody>
    </YearPanelWrap>
  );
});

YearPanel.defaultProps = {
  control: true,
  defaultValue: dayjs(),
};

export { YearPanel };
