import React, { useMemo } from 'react';

import SketchPicker from '../color-picker/sketch';
import colorObj2String from './utils/colorObj2String';
import { IColorObj, ICustomPanelProps } from './types';
import { useClickOuterRef } from '../../hooks';
import { CustomColorPicker } from './styled';
import EColorPanelRole from './role';

const CUSTOM_PANEL_TRIGGER_SELECTOR = '[data-testid="custom-color-trigger"]';
const CUSTOM_PANEL_WIDTH = 225;

const CustomPanel = (props: ICustomPanelProps) => {
  const { color, onColorChange, onVisibleChange, containerRef } = props;

  const handleClickOuter = (event: React.MouseEvent) => {
    // 点击 trigger 时不关闭
    const ele = event.target as HTMLElement;
    if (ele.closest(CUSTOM_PANEL_TRIGGER_SELECTOR)) {
      return;
    }
    onVisibleChange(false);
  };
  const customPanelRef = useClickOuterRef<HTMLDivElement>(handleClickOuter);

  const blockEvent = (e: React.MouseEvent) => {
    e.stopPropagation();
    e.preventDefault();
  };

  const handleChange = (colorObj: IColorObj) => {
    const color = colorObj2String(colorObj);
    if (onColorChange) {
      onColorChange(color);
    }
  };

  const offsetStyles = useMemo(() => {
    const colorPanel = containerRef.current;
    if (colorPanel === null) {
      return {};
    }
    const { left, width } = colorPanel.getBoundingClientRect();
    const rightSpace = window.innerWidth - left - width;
    if (rightSpace > CUSTOM_PANEL_WIDTH) {
      return {
        left: '100%',
        // 使用透明边框模拟 margin
        // 解决某些情况下 hover 到 margin 导致弹窗关闭的问题
        borderLeft: '3px solid transparent',
      };
    } else {
      return {
        // 当左侧也不够展开时紧邻左侧边缘展开
        left: `-${Math.min(left, CUSTOM_PANEL_WIDTH)}px`,
        borderRight: '3px solid transparent',
      };
    }
  }, [containerRef]);

  return (
    <CustomColorPicker
      onClick={blockEvent}
      style={offsetStyles}
      ref={customPanelRef}
      data-role={EColorPanelRole.customColor}
    >
      <SketchPicker color={color} onChange={handleChange} />
    </CustomColorPicker>
  );
};

export default CustomPanel;
