import React from 'react';
import { isMobile } from '../../../../utils/dom';
import EColorPanelRole from '../../role';
import isGradientColor from '../../utils/isGradientColor';

const SVG_SIZE = isMobile ? 16 : 14;

const FontColorIcon: React.FC<{ color: string }> = ({ color }) => {
  return isGradientColor(color) ? (
    <GradientColorFontIcon color={color} />
  ) : (
    <SimpleColorFontIcon color={color} />
  );
};

const SimpleColorFontIcon: React.FC<{ color: string }> = ({ color }) => {
  return color !== '#FFFFFF' ? (
    <svg
      width={SVG_SIZE}
      height={SVG_SIZE}
      viewBox="0 0 16 17"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      data-role={EColorPanelRole.fontColor}
    >
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M8.77021 0.0934877H6.39978L0.161163 15.69H3.46301L5.05626 11.525H10.1231L11.707 15.69H15.0088L8.77021 0.0934877ZM8.98419 8.55003H6.19513L7.58501 4.98003L8.98419 8.55003Z"
        fill={color}
      />
    </svg>
  ) : (
    <svg
      width={SVG_SIZE}
      height={SVG_SIZE}
      viewBox="0 0 16 17"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M9.77021 0.603436H7.39978L1.16116 16.2H4.46301L6.05626 12.035H11.1231L12.707 16.2H16.0088L9.77021 0.603436ZM9.98419 9.05998H7.19513L8.58501 5.48998L9.98419 9.05998Z"
        fill="white"
        stroke="#C7C8C9"
        strokeWidth="1.19"
      />
    </svg>
  );
};

const GradientColorFontIcon: React.FC<{ color: string }> = ({ color }) => {
  const [startColor, endColor] = color.split('__');
  return (
    <svg
      width={SVG_SIZE}
      height={SVG_SIZE}
      viewBox="0 0 16 17"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      data-role={EColorPanelRole.gradientColor}
    >
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M8.8802 0.603436H6.50977L0.271149 16.2H3.573L5.16624 12.035H10.233L11.817 16.2H15.1188L8.8802 0.603436ZM9.09417 9.05998H6.30511L7.69499 5.48998L9.09417 9.05998Z"
        fill={`url(#paint0_linear_${startColor}:${endColor}`}
      />
      <defs>
        <linearGradient
          id={`paint0_linear_${startColor}:${endColor}`}
          x1="-0.0400061"
          y1="7.86998"
          x2="15.43"
          y2="7.86998"
          gradientUnits="userSpaceOnUse"
        >
          <stop stopColor={startColor} />
          <stop offset="1" stopColor={endColor} />
        </linearGradient>
      </defs>
    </svg>
  );
};

export default FontColorIcon;
