import React from 'react';
import ReactDOM from 'react-dom';
import {
  PreviewImage,
  IPreviewImageProps,
  IPreviewImageStatus,
} from './PreviewImage';
import '../../../locale/zh-CN';
import '../../../locale/en-US';

export interface IPreviewImageConfig extends IPreviewImageProps {
  /**
   * 预览组件 Key
   */
  previewImageKey?: string;
}

export const previewImageMobile = (() => {
  let handleUpdate: Function | null = null;
  let handleSetStatus: Function | null = null;
  let divElement: HTMLElement | null = null;

  const handleClose = (key?: string, callback?: (v: string) => void) => {
    if (key) {
      const previewImageElement = document.getElementById(key);

      if (previewImageElement) {
        ReactDOM.unmountComponentAtNode(previewImageElement);
        document.body.removeChild(previewImageElement);
        divElement = null;
        handleUpdate = null;
        handleSetStatus = null;
        callback && callback('success');
      } else {
        callback && callback('error');
      }
      return;
    }
    if (divElement && handleUpdate) {
      ReactDOM.unmountComponentAtNode(divElement);
      document.body.removeChild(divElement);
      handleUpdate = null;
      handleSetStatus = null;
    }
  };

  function init(config: IPreviewImageConfig) {
    if (handleUpdate && divElement) {
      return;
    }
    divElement = document.createElement('div');
    document.body.appendChild(divElement);
    handleUpdate = (
      config: IPreviewImageConfig,
      status: IPreviewImageStatus,
    ) => {
      const {
        urls,
        current,
        previewImageKey,
        imgStyle,
        enableDownload,
        downloadImage,
        onClose,
        onChange,
        zIndex,
        locale,
      } = config;

      if (previewImageKey && divElement) {
        divElement.setAttribute('id', previewImageKey);
      }
      ReactDOM.render(
        <PreviewImage
          urls={urls}
          current={current}
          onClose={() => {
            handleClose();
            if (onClose) {
              onClose();
            }
          }}
          onChange={onChange}
          downloadImage={downloadImage}
          imgStyle={imgStyle}
          enableDownload={enableDownload}
          status={status}
          zIndex={zIndex}
          locale={locale}
        />,
        divElement
      );
    };
    // TODO
    handleSetStatus = (status: IPreviewImageStatus) => {
      handleUpdate && handleUpdate(config, status);
    };
  }

  return {
    open(config: IPreviewImageConfig) {
      init(config);
      if (handleUpdate) {
        handleUpdate(config);
      }
    },

    show(config: IPreviewImageConfig) {
      this.open(config);
    },

    close(previewImageKey?: string, callback?: any) {
      handleClose(previewImageKey, callback);
    },
    // TODO
    setStatus(status: IPreviewImageStatus) {
      handleSetStatus && handleSetStatus(status);
    },
  };
})();
