import React from 'react';
import { useTranslate } from '@ali/we-util';

import { EColorType, IColorPanelLayoutItem, IUnifiedColorPanel } from './types';
import ColorPanelCore from './ColorPanelCore';
import {
  BACKGROUND_COLOR,
  BACKGROUND_COLOR_ALL,
  DEFAULT_PANEL_LAYOUT,
} from './constants';

const [
  fontColorLayout,
  borderColorLayout,
  backgroundColorLayout,
] = DEFAULT_PANEL_LAYOUT;

const ColorPanel: React.FC<IUnifiedColorPanel> = (props) => {
  // 国际化文案
  const t = useTranslate('');
  fontColorLayout.title = t('we_ui_font_color');
  borderColorLayout.title = t('we_ui_border_color');
  backgroundColorLayout.title = t('we_ui_background_color');

  const {
    // 字体颜色配置项
    color,
    colorTitle = fontColorLayout.title,
    colors = fontColorLayout.colors,
    defaultColor = fontColorLayout.defaultColor,
    onColorClick,
    enableMoreFontColor = fontColorLayout.showMoreIcon,
    enableCustomFontColor = fontColorLayout.enableCustomColor,

    // 边框颜色配置项
    borderColor,
    borderColorTitle = borderColorLayout.title,
    borderColors = borderColorLayout.colors,
    defaultBorderColor = borderColorLayout.defaultColor,
    onBorderColorClick,
    enableMoreBorderColor = borderColorLayout.showMoreIcon,
    enableCustomBorderColor = borderColorLayout.enableCustomColor,

    // 背景颜色配置项
    backgroundColor,
    backgroundColorTitle = backgroundColorLayout.title,
    backgroundColors,
    defaultBackgroundColor = backgroundColorLayout.defaultColor,
    onBackgroundColorClick,
    enableMoreBackgroundColor = backgroundColorLayout.showMoreIcon,
    enableCustomBgColor = backgroundColorLayout.enableCustomColor,
    isFontBgColor = false,
    isFullBackground = false,

    currentColors = {},
    ...rest
  } = props;

  const customColorPanelLayout = [] as IColorPanelLayoutItem[];

  if (typeof onColorClick === 'function') {
    customColorPanelLayout.push({
      ...fontColorLayout,
      title: colorTitle,
      colors,
      defaultColor,
      onClick: onColorClick,
      showMoreIcon: enableMoreFontColor,
      enableCustomColor: enableCustomFontColor,
    });
  }
  if (typeof onBorderColorClick === 'function') {
    customColorPanelLayout.push({
      ...borderColorLayout,
      title: borderColorTitle,
      colors: borderColors,
      defaultColor: defaultBorderColor,
      onClick: onBorderColorClick,
      showMoreIcon: enableMoreBorderColor,
      enableCustomColor: enableCustomBorderColor,
    });
  }
  if (typeof onBackgroundColorClick === 'function') {
    const defaultBgColors = isFullBackground
      ? BACKGROUND_COLOR_ALL
      : BACKGROUND_COLOR;
    customColorPanelLayout.push({
      ...backgroundColorLayout,
      title: backgroundColorTitle,
      colors: backgroundColors || defaultBgColors,
      colorType: isFontBgColor
        ? EColorType.fontBackground
        : EColorType.background,
      defaultColor: defaultBackgroundColor,
      onClick: onBackgroundColorClick,
      showMoreIcon: enableMoreBackgroundColor,
      enableCustomColor: enableCustomBgColor,
    });
  }

  const customCurrentColors = {
    [fontColorLayout.selectedKey]:
      color || currentColors[fontColorLayout.selectedKey],
    [borderColorLayout.selectedKey]:
      borderColor || currentColors[borderColorLayout.selectedKey],
    [backgroundColorLayout.selectedKey]:
      backgroundColor || currentColors[backgroundColorLayout.selectedKey],
  };

  return (
    <ColorPanelCore
      colorPanelLayout={customColorPanelLayout}
      currentColors={customCurrentColors}
      isFirstRowDoubleGap={isFullBackground}
      {...rest}
    />
  );
};

export default ColorPanel;
