import React, { PropsWithChildren } from 'react';
import { MultiSelectInputStyled } from './styled';
import { IMultiSelectButtonProps, IMultiSelectValue } from './interface';

const stopPropagation = (e: React.MouseEvent<HTMLElement>) => {
  e.stopPropagation();
};

export function MultiSelectButton<VT extends IMultiSelectValue = IMultiSelectValue>(
  props: PropsWithChildren<IMultiSelectButtonProps<VT>>,
) {
  const {
    locale,
    renderTrigger,
    value,
    children,
    mode,
    visible,
    onFocus,
    onBlur,
    onEnter,
    onChange,
    inputRef,
    testid,
  } = props;

  // 自定义显示区函数优先级最高
  if (typeof renderTrigger === 'function') {
    return renderTrigger(value);
  }

  // 支持输入的模式
  if (mode === 'search' || mode === 'input') {
    return (
      <MultiSelectInputStyled
        ref={inputRef}
        className="fix-box-shadow"
        placeholder={value}
        locale={locale}
        value={value}
        autoComplete="off"
        readOnly={!visible}
        onFocus={onFocus}
        onChange={onChange}
        onBlur={onBlur}
        onEnter={onEnter}
        onMouseDown={visible ? stopPropagation : undefined}
        onClick={visible ? stopPropagation : undefined}
        data-testid={testid && `input-${testid}`}
      />
    );
  }

  return children as React.ReactElement;
}
