import React, { useMemo } from 'react';
import { Trigger } from '../trigger';
import { ITriggerAction, IPlacement } from '../trigger/Trigger';

export interface IDropdownProps {
  /**
   * 触发机制
   */
  trigger?: 'hover' | 'click' | 'contextMenu';
  /**
   * 弹层节点
   */
  overlay: React.ReactElement | null;
  /**
   * 挂在容器
   */
  getContainer?: () => HTMLElement;
  /**
   * 弹层可见性变化处理函数
   */
  onVisibleChange?: (e: boolean) => void;
  /**
   * 弹层位置
   */
  placement?: IPlacement;
  /**
   * 弹层可见性
   */
  visible?: boolean;
  /**
   * 弹层样式
   */
  overlayStyle?: React.CSSProperties;
  /**
   * 弹层类名
   */
  overlayClassName?: string;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 偏移x
   */
  offsetX?: number;
  /**
   * 偏移Y
   */
  offsetY?: number;
  /**
   * 根据坐标来定位
   */
  pageX?: number;
  /**
   * 根据坐标来定位
   */
  pageY?: number;
  /**
   * trigger节点
   */
  children?: React.ReactElement;
  /**
   * trigger节点手动传入
   */
  triggerNode?: HTMLElement;
  /**
   * Tooltip消息
   */
  title?: string | React.ReactNode;
  /**
   * 延时出现，默认值 = 0
   */
   delayShow?: number
   /**
    * 延时隐藏，默认值 = 200
    */
   delayHide?: number;
  /**
   * 延迟销毁
   */
  delayDestroy?: number;
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string;
  /**
   * 禁止根据视口自适应展开位置
   */
  disableDomAlign?: boolean;
  /**
   * 监听overlay的宽高变化自适应位置
   */
  overlayAutoAlign?: boolean;
  /**
   * overlayAutoAlign 执行延时
   * 默认 100ms
   */
  overlayAutoAlignDelay?: number;
  /**
   * 定义角色
   */
  role?: string;
}

export const Dropdown: React.FC<IDropdownProps> = (props) => {
  const {
    children,
    trigger,
    overlay,
    getContainer,
    onVisibleChange,
    placement,
    overlayStyle,
    visible,
    zIndex,
    overlayClassName,
    offsetX,
    offsetY,
    pageX,
    pageY,
    triggerNode,
    title,
    testid,
    delayDestroy,
    delayShow,
    delayHide,
    disableDomAlign,
    overlayAutoAlign,
    overlayAutoAlignDelay,
    role = '',
  } = props;

  // trigger's action
  const action: ITriggerAction[] = useMemo(
    () => (trigger === 'contextMenu' ? [trigger] : [trigger!]),
    [trigger],
  );

  return (
    <Trigger
      action={action}
      overlay={overlay}
      getContainer={getContainer}
      onVisibleChange={onVisibleChange}
      placement={placement}
      visible={visible}
      overlayStyle={overlayStyle}
      zIndex={zIndex}
      overlayClassName={overlayClassName}
      offsetX={offsetX}
      offsetY={offsetY}
      pageX={pageX}
      pageY={pageY}
      triggerNode={triggerNode}
      title={title}
      delayDestroy={delayDestroy}
      delayShow={delayShow}
      delayHide={delayHide}
      testid={testid}
      disableDomAlign={disableDomAlign}
      overlayAutoAlign={overlayAutoAlign}
      overlayAutoAlignDelay={overlayAutoAlignDelay}
      role={role}
    >
      {children}
    </Trigger>
  );
};

Dropdown.defaultProps = {
  trigger: 'click',
};
