/* eslint-disable react/static-property-placement */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable @typescript-eslint/no-explicit-any */
import { ReactElement, Component } from 'react';
import { IGetRef, ISaveRef } from './interface';

interface ISaveRefProps {
  children: (saveRef: ISaveRef, getRef: IGetRef) => ReactElement | null;
}

export default class SaveRef extends Component<ISaveRefProps> {
  static defaultProps = {
    children: () => null,
  };

  getRef = (name: string): any => this[name];

  saveRef = (name: string) => (node: any) => {
    if (node) {
      this[name] = node;
    }
  };

  [x: string]: any;

  render() {
    const { children } = this.props;
    return children(this.saveRef, this.getRef) as ReactElement;
  }
}
