import React, { useCallback, useContext, useEffect, useMemo } from 'react';
import { classnames } from '../../utils';
import { IMenuItemProps } from './interface';
import {
  MenuItemWrap,
  MenuItemInner,
  MenuItemIconWrap,
  MenuItemContentWrap,
  MenuItemExtraWrap,
} from './styled';
import {
  useController,
  useMenuKeyContext,
  useHotkeyController,
  useHotKeyScrollRef,
  useMousePosition,
} from './hooks';
import {
  TextIndentContext,
  MenuKeyContext,
  PathContext,
  SubMenuContext,
} from './contexts';
import { MENU_VISIBLE_TOGGLE_DELAY } from './consts';
import { Tooltip } from '../tooltip';
import { SelectedNormalNormal } from '@ali/we-icon';

// eslint-disable-next-line max-lines-per-function
export const MenuItem: React.FC<IMenuItemProps> = React.memo((props) => {
  const {
    children,
    disabled,
    disabledTip,
    tooltipZIndex,
    style,
    selected,
    onClick,
    hoverable,
    height,
    testid,
    icon,
    extra,
    description,
    showSelect,
    role='',
  } = props;
  // 接口
  const controller = useController('MenuItem');

  // 快捷键触达的位置key
  const hotkeyController = useHotkeyController('MenuItem');

  const subMenuCtr = useContext(SubMenuContext);

  const {
    hotkeyPosition,
    isHotkeyType,
    registerValidNode,
    unRegisterValidNode,
  } = hotkeyController;

  const { setMousePosition, mousePosition } = useMousePosition();

  // 被 Group 包裹需要缩进
  const textIndent = useContext(TextIndentContext);
  // menuKey处理
  const [menuKeyCtxValue, menuKey] = useMenuKeyContext(props.menuKey);
  // 父组件的key路径
  const parentPath = useContext(PathContext);
  // 支持快捷键时 菜单过长元素自动定位
  const elRef = useHotKeyScrollRef(menuKey, isHotkeyType, hotkeyPosition);
  // 是否选中，props.selected优先级最高
  const isSelected =
    typeof selected === 'boolean'
      ? selected
      : controller.isMenuItemSelected(menuKey);

  // 自动关闭，当前组件的props优先级更高
  const autoClose =
    typeof props.autoClose === 'boolean'
      ? props.autoClose
      : controller.autoClose;

  /**
   * 点击 Item
   *
   * @param {React.MouseEvent<HTMLElement>} event
   * @return {void}
   */
  const handleClick = useCallback(
    (event: React.MouseEvent<HTMLElement>) => {
      // 自动关闭处理
      if (autoClose) {
        controller.closeAllMenu();
      }
      if (disabled) {
        return;
      }
      // 点击处理
      if (onClick) {
        onClick(menuKey, event);
      }
      // 选中处理
      controller.selectMenuItem(menuKey);
      if (controller.clickMenuItem) {
        controller.clickMenuItem(menuKey, event);
      }
    },
    [disabled, autoClose, controller, onClick, menuKey],
  );

  /**
   * 如果有快捷键时新增的鼠标进入事件
   */
  const hotkeyMouseEnterHander = useCallback(
    (event) => {
      if (!disabled && mousePosition !== event.clientY) {
        hotkeyController.setHotkeyType(false);
        setMousePosition(event.clientY);
        hotkeyController.setHotkeyPosition(
          hotkeyController.getActionNode(menuKey),
        );
      }
    },
    [disabled, menuKey, mousePosition],
  );

  /**
   * 鼠标移入 Item
   * 应该收起其他菜单
   *
   * @return {void}
   */
  const handleMouseEnter = useCallback(
    (event) => {
      if (controller.shortcutKey) {
        hotkeyMouseEnterHander(event);
      }
      if (controller.showMenuTimer) {
        window.clearTimeout(controller.showMenuTimer);
      }
      controller.showMenuTimer = window.setTimeout(() => {
        controller.showSubMenu(parentPath);
      }, MENU_VISIBLE_TOGGLE_DELAY);
    },
    [parentPath, controller, hotkeyMouseEnterHander],
  );

  // 整合height样式
  const styleCombined = useMemo(() => {
    const combined: React.CSSProperties = { ...(style || {}) };
    if (typeof height === 'number' || height === 'auto') {
      combined.height = height;
    }
    if (controller.minWidth) {
      combined.minWidth = controller.minWidth;
    }
    return combined;
  }, [height, style, controller.minWidth]);

  /**
   * 快捷键：注册有效节点
   */
  useEffect(() => {
    if (!disabled && controller.shortcutKey && elRef.current) {
      registerValidNode({
        menuKey,
        path: `${parentPath ? `${parentPath}::` : ''}${menuKey}`,
        onClick: handleClick,
        parentPath,
      });
      return () => {
        unRegisterValidNode({
          menuKey,
          path: `${parentPath ? `${parentPath}::` : ''}${menuKey}`,
        });
      };
    }
  }, [disabled, controller.shortcutKey]);

  /**
   * showSelect 为 false 或者为 true 都是指定并且有意义的，需要用 undefined 来做判断是否有效
   */
  const showSelectFinal =
    showSelect !== undefined
      ? showSelect
      : subMenuCtr.showSelect !== undefined
      ? subMenuCtr.showSelect
      : controller.showSelect;

  return (
    <Tooltip
      disabled={!(disabled && disabledTip)}
      title={disabledTip}
      placement={'top'}
      zIndex={tooltipZIndex}
    >
      <MenuItemWrap
        path={`${parentPath ? `${parentPath}::` : ''}${menuKey}`}
        menuId={controller.menuId}
        className={classnames('vertical', {
          selected: isSelected,
          disabled,
          doublueLine: !!description,
          hotkeyHover:
            hotkeyPosition &&
            menuKey === hotkeyPosition.menuKey &&
            !disabled &&
            hoverable &&
            controller.shortcutKey,
        })}
        ref={elRef}
        hoverable={hoverable}
        isHotkeyType={isHotkeyType}
        textIndent={textIndent}
        onClick={handleClick}
        style={styleCombined}
        onMouseEnter={handleMouseEnter}
        data-testid={testid ? `menu-item-${testid}` : undefined}
        data-role={role}
        showSelect={showSelectFinal}
        showSelectOnRight={controller.showSelectOnRight}
      >
        {showSelectFinal && !controller.showSelectOnRight && (
          <SelectedNormalNormal className="icon selected-icon" />
        )}
        <MenuKeyContext.Provider value={menuKeyCtxValue}>
          <MenuItemInner className={classnames({ selected: isSelected })}>
            <div
              style={{
                display: 'flex',
                flex: 1,
                width: '100%',
                alignItems: 'center',
              }}
            >
              {icon ? (
                <MenuItemIconWrap
                  disabled={disabled}
                  doublueLine={!!description}
                >
                  {icon}
                </MenuItemIconWrap>
              ) : null}
              <MenuItemContentWrap>
                <div>{children}</div>
                {!!description && (
                  <div className="description">{description}</div>
                )}
              </MenuItemContentWrap>
            </div>

            <MenuItemExtraWrap isShow={!!extra}>
              {extra || ''}
            </MenuItemExtraWrap>
            {!extra && controller.showSelectOnRight && (
              <SelectedNormalNormal className="icon selected-icon" />
            )}
          </MenuItemInner>
        </MenuKeyContext.Provider>
      </MenuItemWrap>
    </Tooltip>
  );
});

MenuItem.defaultProps = {
  disabled: false,
  hoverable: true,
};
