import React, { useCallback, useContext } from 'react';
import { RadioWrap, RadioContent, RadioInner, RadioText } from './styled';
import useControlledValue from '../../hooks/useControlledValue';
import { classnames } from '../../utils';
import { RadioContext } from './RadioContext';

export interface IRadioProps {
  /**
   * 是否选中
   */
  checked?: boolean;
  /**
   * 默认是否选中
   */
  defaultChecked?: boolean;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 使用Group时每个Radio必须指定value
   */
  value?: string;
  /**
   * 改变处理函数，Group包住时失效
   */
  onChange?: React.ChangeEventHandler<HTMLInputElement>;
  /**
   * role 用于埋点
   */
  role?: string;
}

const Radio: React.FC<IRadioProps> = React.memo(
  React.forwardRef<HTMLInputElement, IRadioProps>((props, ref) => {
    const { children, onChange, value, role } = props;

    // eslint-disable-next-line prefer-const
    let [checked, setChecked] = useControlledValue(props.checked, false);

    const provider = useContext(RadioContext);
    // 如果有Group，直接根据 Group的选中项判断
    if (provider && provider.checkedValue) {
      checked = provider.checkedValue === value;
    }
    // disabled状态，如果有Group包住，且Group的disabled为true，则disabled为true
    const disabled = provider && provider.disabled ? true : props.disabled;

    // Group 修改参数函数
    const onToggleValue = provider && provider.onToggleValue;

    /**
     * 修改选中状态
     *
     * @param {React.ChangeEvent<HTMLInputElement>} e
     * @return {void}
     */
    const handleChange = useCallback(
      (e: React.ChangeEvent<HTMLInputElement>) => {
        if (onToggleValue && value) {
          onToggleValue({
            checked: e.target.checked,
            value,
          });
        } else {
          if (onChange) {
            onChange(e);
          }
          setChecked(e.target.checked);
        }
      },
      [onChange, onToggleValue, value, setChecked],
    );

    return (
      <RadioWrap checked={checked} disabled={disabled} data-role={!disabled ? role : undefined}>
        <RadioContent disabled={disabled} checked={checked}>
          <input
            ref={ref}
            type="radio"
            onChange={handleChange}
            disabled={disabled}
            checked={checked}
          />
          <RadioInner className={classnames({ disabled })} />
        </RadioContent>
        {children && <RadioText>{children}</RadioText>}
      </RadioWrap>
    );
  }),
);

Radio.displayName = 'Radio';

export { Radio };
