/* eslint-disable @typescript-eslint/explicit-function-return-type */
import React from 'react';
import { WatermarkWrapper } from './styled';
import { generateWatermark, IMarkProp } from './generateWatermark';

export interface IWatermarkProp extends IMarkProp {
  /**
   * 是否绘制水印
   */
  watermarkEnable?: boolean;
  /**
   * 组件根节点样式
   */
  style?: React.CSSProperties;
}

export class Watermark extends React.Component<IWatermarkProp> {
  waterRef = React.createRef<HTMLDivElement>();

  componentDidMount() {
    this.updateBackgroundImage();
  }

  componentDidUpdate(prevProps: IWatermarkProp) {
    const { nickName, orgName, watermarkEnable } = this.props;

    if (watermarkEnable !== prevProps.watermarkEnable
      || nickName !== prevProps.nickName
      || orgName !== prevProps.orgName) {
      // 仅在文本内容变化时重绘
      this.updateBackgroundImage();
    }
  }

  updateBackgroundImage = () => {
    const { watermarkEnable, nickName, orgName, markStyle, maxTextLength } = this.props;
    if (!watermarkEnable) {
      if (this.waterRef && this.waterRef.current) {
        this.waterRef.current.style.backgroundImage = 'none';
      }
      return;
    }

    if (this.waterRef && this.waterRef.current) {
      this.waterRef.current.style.backgroundImage = `url(${generateWatermark({
        nickName, orgName, markStyle, maxTextLength,
      })})`;
    }
  };

  render() {
    const { style, ...markProps } = this.props;

    const { watermarkEnable, markStyle = {} } = markProps;
    const { width = 256, height = 300 } = markStyle;
    const bgStyle: React.CSSProperties = {
      backgroundSize: `${width}px ${height}px`,
      display: watermarkEnable ? 'block' : 'none',
    };

    return (
      <WatermarkWrapper ref={this.waterRef} style={{ ...bgStyle, ...style }} />
    );
  }
}
