import React, { PropsWithChildren } from 'react';
import { InputStyled } from './styled';
import { ISelectButtonProps, ISelectValue } from './interface';

const stopPropagation = (e: React.MouseEvent<HTMLElement>) => {
  e.stopPropagation();
};

const getValueString = (
  value: string | number | undefined,
  inputTriggerText?: (v: string) => string,
) => {
  const v = typeof value === 'undefined' ? '' : `${value}`;
  return inputTriggerText ? inputTriggerText(v) : v;
};

export function SelectButton<VT extends ISelectValue = ISelectValue>(
  props: PropsWithChildren<ISelectButtonProps<VT>>,
) {
  const {
    locale,
    renderTrigger,
    value,
    children,
    mode,
    visible,
    keyword,
    isInputFocused,
    onFocus,
    onBlur,
    onEnter,
    onChange,
    inputRef,
    testid,
    inputTriggerText,
  } = props;

  // 自定义显示区函数优先级最高
  if (typeof renderTrigger === 'function') {
    return renderTrigger(value);
  }

  // 支持输入的模式
  if (mode === 'search' || mode === 'input') {
    return (
      <InputStyled
        ref={inputRef}
        className="fix-box-shadow"
        placeholder={getValueString(value, inputTriggerText)}
        locale={locale}
        value={`${!isInputFocused || !visible ? getValueString(value, inputTriggerText) : keyword}`}
        autoComplete="off"
        readOnly={!visible}
        onFocus={onFocus}
        onChange={onChange}
        onBlur={onBlur}
        onEnter={onEnter}
        onMouseDown={visible ? stopPropagation : undefined}
        onClick={visible ? stopPropagation : undefined}
        data-testid={testid && `input-${testid}`}
      />
    );
  }

  return children as React.ReactElement;
}
