import React from 'react';
import { AvatarGroup, IAvatarGroupProps } from './AvatarGroup';
import AvatarPopover from './AvatarPopover';
import AvatarContent, { IAvatarContent } from './AvatarContent';

export interface IAvatarProps extends IAvatarContent {
  /**
   * popoverOverlay 头像hover菜单
   * true 默认的 hover 菜单
   * false 无 hover 菜单
   * React.ReactElement 在 默认的 hover 菜单的基础上增加新的内容
   */
  popoverOverlay?: React.ReactElement | boolean;
}

export const Avatar: React.FC<IAvatarProps> & {
  Group: React.FC<IAvatarGroupProps>;
} = Object.assign(React.memo((props: IAvatarProps) => {
  const {
    status,
    name,
    color,
    popoverOverlay,
    ...rest
  } = props as IAvatarProps;
  
  const avatarContent = (
    <AvatarContent
      {...rest}
      color={color}
      name={name}
    />
  );
  return !popoverOverlay ? avatarContent : (
    <AvatarPopover
      name={name}
      status={status}
      color={color}
      overlay={React.isValidElement(popoverOverlay) ? popoverOverlay : null}
    >
      {avatarContent}
    </AvatarPopover>
  );
}), {
  Group: AvatarGroup,
});

Avatar.defaultProps = {
  size: 'normal',
};

Avatar.displayName = 'Avatar';
