/* eslint-disable no-param-reassign */
export function supportRef(nodeOrComponent: any): boolean {
  // any component node
  if (
    nodeOrComponent.type &&
    nodeOrComponent.type.prototype &&
    !nodeOrComponent.type.prototype.render
  ) {
    return false;
  }

  // Class component，from rc-utils ?? 比较旧的react版本？
  if (
    typeof nodeOrComponent === 'function' &&
    nodeOrComponent.prototype &&
    !nodeOrComponent.prototype.render
  ) {
    return false;
  }

  return true;
}

export function composeRef<T>(...refs: Array<React.Ref<T>>): React.Ref<T> {
  return (node: T) => {
    refs.forEach(ref => {
      fillRef(ref, node);
    });
  };
}

export function fillRef<T>(ref: React.Ref<T>, node: T) {
  if (typeof ref === 'function') {
    ref(node);
  } else if (typeof ref === 'object' && ref && 'current' in ref) {
    (ref as any).current = node;
  }
}
