import React from 'react';

export const getStartTouch = (touchList: React.TouchList, startTouch: number) => {
  if (startTouch === undefined) {
    return null;
  }

  const { length } = touchList;
  for (let i = 0; i < length; i++) {
    if (touchList[i].identifier === startTouch) {
      return touchList[i];
    }
  }
  return null;
}