import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Dropdown } from '../dropdown';
import {
  IAbsolutePopupProps,
  IAbsolutePopupInstance,
} from './interface';

class AbsolutePopup<
P extends IAbsolutePopupProps = IAbsolutePopupProps
> extends Component<P, P> {
  static defaultProps = {
    visible: false,
    offsetX: 8,
    offsetY: 8,
    overlayStyle: {},
  };

  state = { ...this.props };

  /**
   * 创建实例
   */
  static create = <
    P extends IAbsolutePopupProps = IAbsolutePopupProps
  >(props: P): IAbsolutePopupInstance<P> => {
    const popupRef = React.createRef<AbsolutePopup>();
    // 容器节点初始化
    const container = document.createElement('div');
    container.setAttribute('style', 'position: absolute; left: 0; top: 0; width: 100%');
    let appended = false;

    /**
     * 插入容器节点
     *
     * @return {void}
     */
    function handleAppendContainer(visible: boolean) {
      if (visible && !appended) {
        appended = true;
        document.body.appendChild(container);
      }
    }

    handleAppendContainer(!!props.visible);

    ReactDOM.render(
      <AbsolutePopup
        {...props}
        ref={popupRef}
      />,
      container,
    );

    return {
      /**
       * 更新
       *
       * @param {Pick<P, keyof P>}
       * @return {void}
       */
      update(newState: Pick<P, keyof P>) {
        handleAppendContainer(!!newState.visible);
        if (popupRef.current) {
          popupRef.current.handleChangeState(newState);
        }
      },


      /**
       * 隐藏
       *
       * @return {void}
       */
      hide() {
        this.update({ visible: false } as Pick<P, keyof P>);
      },

      /**
       * 销毁
       *
       * @return {void}
       */
      destory() {
        ReactDOM.unmountComponentAtNode(container);
        document.body.removeChild(container);
        appended = false;
      },
    };
  };

  /**
   * 暴露给外面的 ref 进行状态修改
   *
   * @param {Partial<IAbsolutePopupProps>} state
   */
  public handleChangeState(state: Pick<P, keyof P>) {
    const onVisibleChange = 'onVisibleChange' in state ? state.onVisibleChange : this.state.onVisibleChange;
    if ('visible' in state && state.visible !== this.state.visible && onVisibleChange) {
      onVisibleChange(!!state.visible);
    }
    this.setState(state as Pick<P, keyof P>);
  }

  /**
   * Dropdown 显示/隐藏处理
   *
   * @param {boolean} e
   * @return {void}
   */
  handleVisibleChange = (e: boolean): void => {
    this.handleChangeState({ visible: e } as Pick<P, keyof P>);
  };

  render() {
    const {
      overlay,
      left,
      top,
      visible,
      zIndex,
      offsetY,
      offsetX,
      overlayStyle,
      ...restProps
    } = this.state;

    return (
      <Dropdown
        key={`${left}${top}`}
        visible={visible}
        overlay={React.cloneElement(overlay, restProps)}
        zIndex={zIndex}
        placement="bottomLeft"
        offsetX={offsetX!}
        offsetY={offsetY!}
        overlayStyle={overlayStyle}
        onVisibleChange={this.handleVisibleChange}
        pageX={left}
        pageY={top}
      />
    );
  }
}

export { AbsolutePopup };
