import React, { useMemo, useState, useCallback } from 'react';
import { SplitButton, ButtonSize } from '../button';
import { BulletedlistNormal, NumberedlistNormal } from '../icons';
import styled from 'styled-components';
import { withTheme } from '../theme';
import { classnames } from '../../utils';
import { useControlledValue } from '../../hooks';

export type ListMode = 'ul' | 'ol';

export interface IListSetProps {
  /**
   * 列表模式
   */
  mode?: ListMode;
  /**
   * 默认选择的列表样式 (非受控模式)，如不传入则默认为 ul/ol 的第一种样式
   */
  defaultListStyle?: string;
  /**
   * 列表样式（受控模式）
   */
  listStyle?: string;
  /**
   * 类名
   */
  className?: string;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 是否高亮
   */
  active?: boolean;
  /**
   * 选择列表样式变化的回调函数
   */
  onChange?: (listStyle: string) => void;
  /**
   *  点击左侧按钮的回调函数
   */
  onClick?: (listStyle: string) => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * button 的大小
   */
  size?: ButtonSize;
}

