const OverlayContent = styled.div`
  width: 272px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 4px;
`;

const ListStyleItemWrap = styled.div`
  user-select: none;
`;

const ListStyleItem = withTheme(styled.img`
  width: 80px;
  height: 80px;
  margin: 4px;
  border: 1px solid rgba(17, 31, 44, 0.12);
  border-radius: 2px;
  cursor: pointer;

  &:hover {
    border: 1px solid ${({ theme }) => theme.ui.primaryColor};
  }

  &.actived {
    border: 2px solid ${({ theme }) => theme.ui.primaryColor};
  }
`);

export const ListSet: React.FC<IListSetProps> = (props) => {
  const {
    mode = 'ul',
    defaultListStyle,
    listStyle,
    onChange,
    onClick,
    className,
    style,
    disabled,
    title,
    active,
    size,
  } = props;
  const listStyleArray = useMemo(
    () =>
      mode === 'ul' ? DEFAULT_BULLET_LIST_ARRAY : DEFAULT_NUMBER_LIST_ARRAY,
    [mode],
  );
  const [value, setValue] = useControlledValue(
    listStyle,
    defaultListStyle || listStyleArray[0],
  );
  const [overlayVisible, setOverlayVisible] = useState(false);

  const handleChange = useCallback(
    (e: string) => {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
      setOverlayVisible(!overlayVisible);
    },
    [onChange, setValue, overlayVisible],
  );

  const handleVisibleChange = useCallback((visible: boolean) => {
    setOverlayVisible(visible);
  }, []);

  const handleClickLeft = useCallback(() => {
    if (onClick) {
      onClick(value!);
    }
  }, [onClick, value]);

  const getOverlay = () => {
    return (
      <OverlayContent>
        {listStyleArray.map((item) => {
          return (
            <ListStyleItemWrap
              key={item}
              onClick={() => handleChange(item)}
              role='button'
            >
              <ListStyleItem
                src={LIST_STYLE_MAP[item]}
                alt='list-style-item'
                className={classnames({ actived: item === value })}
                data-testid={`option-${item}`}
              />
            </ListStyleItemWrap>
          );
        })}
      </OverlayContent>
    );
  };

  const renderTriggerIcon = useMemo(
    () => (mode === 'ul' ? <BulletedlistNormal /> : <NumberedlistNormal />),
    [mode],
  );

  return (
    <SplitButton
      overlay={getOverlay()}
      onClickLeft={handleClickLeft}
      className={className}
      disabled={disabled}
      style={style}
      active={overlayVisible}
      onActiveChange={handleVisibleChange}
      testid='ListSet'
      title={title}
      highlight={active}
      size={size}
      role='list_set'
    >
      {renderTriggerIcon}
    </SplitButton>
  );
};
