import React from 'react';
import { classnames } from '../../utils';
import {
  ButtonWrap,
  ButtonChildrenWrap,
  ButtonInner,
  LoadingWrapper,
} from './styled';
import { Tooltip } from '../tooltip';
import { IWave } from './IWave';
import { CircleLoadingBetaNormal } from '../icons';
import { hooks } from '@ali/we-util';

export type ButtonType = 'primary' | 'danger' | 'ghost' | 'text' | 'normal';

export type ButtonShape = 'round' | 'circle' | 'rect';

export type ButtonSize = 'small' | 'large' | 'normal';

export interface IButtonProps {
  /**
   * 设置按钮大小
   */
  size?: ButtonSize;
  /**
   * 设置按钮类型
   */
  type?: ButtonType;
  /**
   * 设置按钮形状
   */
  shape?: ButtonShape;
  /**
   * 设置原生button的type值
   */
  htmlType?: string;
  /**
   * 设置按钮的图标组件
   */
  icon?: React.ReactNode;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 是否有边框
   */
  bordered?: boolean;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 类名
   */
  className?: string;
  /**
   * 点击
   */
  onClick?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 双击
   */
  onDoubleClick?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * [mouse down] or [touch start]
   */
  onMouseDown?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 是否为激活态
   */
  active?: boolean;
  /**
   * 聚焦index
   */
  tabIndex?: string;
  /**
   * tooltip
   */
  title?: string | React.ReactNode;
  /**
   * 是否有loading状态
   */
  hasLoading?: boolean;
  /**
   * 是否处于loading状态
   */
  loading?: boolean;
  /**
   * 是否显示波纹效果
   */
  hasWave?: boolean;
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string;
  /**
   * 行为角色
   */
  role?: string;
}

const Button: React.FC<IButtonProps> = React.memo(
  React.forwardRef((props, ref) => {
    const {
      style,
      children,
      className,
      onClick: handleOnClick,
      shape,
      size,
      type,
      onDoubleClick: handleOnDoubleClick,
      onMouseDown,
      bordered,
      icon,
      active,
      disabled,
      tabIndex,
      title,
      hasLoading = false,
      loading = false,
      hasWave = false,
      testid,
      role = '',
      ...restProps
    } = props;

    /**
     * 对传入的 handleOnClick 函数进行处理，当 Button 处于 loading 时不触发
     *
     * @param e
     */
    let onClick = (e: React.MouseEvent<HTMLElement>) => {
      if (!loading) {
        handleOnClick && handleOnClick(e);
      }
    };

    /**
     * 对传入的 handleOnDoubleClick 函数进行处理，当 Button 处于 loading 时不触发
     *
     * @param e
     */
    let onDoubleClick = (e: React.MouseEvent<HTMLElement>) => {
      if (!loading) {
        handleOnDoubleClick && handleOnDoubleClick(e);
      }
    };

    /**
     * 当 handleOnClick 和 handleOnDoubleClick 同时存在时，对两个函数进行处理，
     * 不然会被 click 截获，无法触发到 doubleClick，
     */
    handleOnClick &&
      handleOnDoubleClick &&
      ([onClick, onDoubleClick] = hooks.useClickPreventionOnDoubleClick(
        onClick,
        onDoubleClick,
      ));

    const onlyIcon = !!icon && !children;

    const buttonInner = hasLoading ? (
      <>
        {!onlyIcon && (
          <LoadingWrapper
            data-testid={testid ? `${testid}_loading_wrapper` : undefined}
            visible={loading}
          >
            <CircleLoadingBetaNormal spin={loading} />
          </LoadingWrapper>
        )}
        <ButtonInner
          data-testid={testid ? `${testid}_inner` : undefined}
          visible={!loading || onlyIcon}
        >
          {icon}
          <ButtonChildrenWrap>{children}</ButtonChildrenWrap>
        </ButtonInner>
      </>
    ) : (
      <ButtonInner visible={true}>
        {icon}
        <ButtonChildrenWrap>{children}</ButtonChildrenWrap>
      </ButtonInner>
    );
    const buttonNode = (
      <ButtonWrap
        {...restProps}
        className={classnames(className, { active })}
        shape={shape}
        style={style}
        size={size}
        type={type}
        ref={ref}
        onClick={disabled ? undefined : onClick}
        onDoubleClick={disabled ? undefined : onDoubleClick}
        onMouseDown={disabled ? undefined : onMouseDown}
        bordered={bordered}
        disabled={disabled}
        onlyIcon={onlyIcon}
        tabIndex={tabIndex === '-1' ? undefined : tabIndex || '0'}
        data-testid={testid ? `${testid}` : undefined}
        data-role={role}
      >
        {buttonInner}
      </ButtonWrap>
    );

    const IWaveButton = onlyIcon ? (
      buttonNode
    ) : hasWave ? (
      <IWave testid={testid} type={type} size={size}>
        {buttonNode}
      </IWave>
    ) : (
      buttonNode
    );

    return title ? <Tooltip title={title}>{IWaveButton}</Tooltip> : IWaveButton;
  }),
);

Button.defaultProps = {
  shape: 'rect',
  size: 'normal',
  type: 'normal',
  bordered: true,
  icon: null,
  active: false,
  disabled: false,
};

export { Button };
