import React, { useMemo, useRef, useLayoutEffect, useState } from 'react';
import { useTranslate } from '@ali/we-util';

import {
  ColorPreviewWrapper,
  BackgroundLayout,
  ColorLayout,
  ColorPreviewContent,
  Text,
  Tip,
} from './styled';

import { IColorPreviewProps } from '../types';

// 共有五组文案可选,从中随机选一组
const RANDOM_TOTAL = 5;

const ColorPreview: React.FC<IColorPreviewProps> = (props) => {
  const { color, backgroundColor, shouldShowTip, colorPanelRef } = props;
  const previewBoxRef = useRef<HTMLDivElement>(null);
  const [offsetStyles, setOffsetStyles] = useState({});
  const [isNoSpace, setIsNoSpace] = useState(false);

  const t = useTranslate('');

  useLayoutEffect(() => {
    const colorPanel = colorPanelRef.current;
    const previewBox = previewBoxRef.current;
    if (colorPanel === null || previewBox === null) {
      return;
    }

    const { left, width } = colorPanel.getBoundingClientRect();
    const { width: previewBoxWidth } = previewBox.getBoundingClientRect();
    const rightSpace = window.innerWidth - left - width;

    if (rightSpace > previewBoxWidth) {
      const showAtRightStyles = {
        left: '100%',
        marginLeft: '3px',
      };
      setOffsetStyles(showAtRightStyles);
    } else {
      if (left < previewBoxWidth) {
        // 当左右都没有足够空间时, 不显示预览
        setIsNoSpace(true);
      }
      const showAtLeftStyles = {
        left: `-${previewBoxWidth + 3}px`,
      };
      setOffsetStyles(showAtLeftStyles);
    }
  }, []);

  const paragraphs = useMemo(() => {
    const randomIndex = Math.round(Math.random() * (RANDOM_TOTAL - 1)) + 1;
    const text = [
      t(`we_ui_paragraph_${randomIndex}_1`),
      t(`we_ui_paragraph_${randomIndex}_2`),
      t(`we_ui_paragraph_${randomIndex}_3`),
    ];
    return text;
  }, []);

  const [paragraph1, paragraph2, paragraph3] = paragraphs;

  if (isNoSpace) {
    return null;
  }

  return (
    <ColorPreviewWrapper ref={previewBoxRef} style={offsetStyles}>
      <ColorPreviewContent>
        <Text>{paragraph1}</Text>
        <br />
        <BackgroundLayout backgroundColor={backgroundColor}>
          <ColorLayout color={color}>{paragraph2}</ColorLayout>
        </BackgroundLayout>
        <br />
        <Text>{paragraph3}</Text>
        <br />
      </ColorPreviewContent>
      {shouldShowTip && <Tip>{t('we_ui_recommend_white_color')}</Tip>}
    </ColorPreviewWrapper>
  );
};

export default ColorPreview;
