/* eslint-disable max-lines-per-function */
import React from 'react';
import { useTranslate } from '@ali/we-util';
import classnames from 'classnames';
import { Dropdown } from '../../components/dropdown';
import warning from 'warning';
import {
  AvatarGroupWrap,
  GroupAvatarItem,
  GroupAvatarList,
  GroupMoreItem,
  GroupMoreItemSpan,
  OverlayWrap,
  CountWrap,
  OverlayAvatarItem,
  OverlayAvatarWrap,
  OverlayAvatarName,
  ListWrap,
  Dot,
} from './styled';
import { IAvatarSize } from './types';

export interface IAvatarGroupProps {
  /**
   * 显示区最多显示多少个
   */
  max?: number;
  /**
   * 排列方向，依次为：左到右、右到左
   */
  direction: 'ltr' | 'rtl';
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 类名
   */
  className?: string;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   *  点击头像组中头像（或 dropdown 列表中选项）的回调函数
   *  idx 为头像组中点击的头像的序号 (0-based)
   */
  onClick?: (idx: number) => void;
  /**
   * overlay的样式
   */
  overlayStyle?: React.CSSProperties;
  /**
   * 弹层最大高度
   */
  overlayMaxHeight?: number;
  /**
   * 头像的尺寸 默认 normal 尺寸 28 * 28
   */
  size?: IAvatarSize;
  /**
   * 头像的形状
   */
  shape?: 'circle' | 'rect';
  /**
   * 是否自定义子节点
   */
  isCustomChildren?: boolean;
  /**
   * 自定义超过最大限制的数字时点击的下拉节点样式
   */
  maxDropdownNode?: React.ReactElement;
  /**
   * 更多人面板中的标题，默认 共xx人正在协作
   */
  overlayTitle?: React.ReactElement | string;
}

// 固化头像组中颜色角标的选用和顺序
const COLOR_SCHEME = [
  '#0984FF',
  '#12C764',
  '#BF5AF2',
  '#FF9F0C',
  '#3BBEFA',
  '#FFCC01',
  '#99CAFF',
  '#FFD399',
];
const COLOR_SCHEME_LEN = COLOR_SCHEME.length;

const AvatarGroup: React.FC<IAvatarGroupProps> = React.memo((props) => {
  const {
    children,
    max,
    className,
    style,
    direction,
    zIndex,
    onClick,
    size,
    overlayStyle,
    overlayMaxHeight,
    isCustomChildren = false,
    maxDropdownNode,
    overlayTitle,
  } = props;

  const [isMoreBoxVisible, setMoreBoxVisible] = React.useState(false);
  // 国际化文案
  const t = useTranslate('');

  // 数量
  const avatarCount = React.Children.count(children);
  const isOverflowing = avatarCount > max!;

  if (!isCustomChildren) {
    React.Children.forEach(children, (child) => {
      warning(
        React.isValidElement(child) &&
          (child.type as any).displayName === 'Avatar',
        'AvatarGroup 的子组件应该为 Avatar 组件.',
      );
    });
  }

  const avatarWidth = isOverflowing
    ? `${100 / (max! + 1)}%`
    : `${100 / avatarCount}%`;
  const avatarStyle = React.useMemo(
    () => ({ width: avatarWidth, maxWidth: 28 }),
    [avatarWidth],
  );

  const title = overlayTitle || t('we_ui_total_people', {
    value: avatarCount,
  });

  return (
    <AvatarGroupWrap
      style={{ ...style }}
      className={className}
      direction={direction}
    >
      <GroupAvatarList direction={direction}>
        {React.Children.toArray(children)
          .slice(0, max!)
          .map((child: any, idx: number) => {
            return (
              <GroupAvatarItem
                className="avatar-item"
                key={`${child.props.name}${idx}`}
                style={avatarStyle}
              >
                {React.cloneElement(child, {
                  color:
                    child.props.color || COLOR_SCHEME[idx % COLOR_SCHEME_LEN],
                  onClick: child.props.onClick || (() => onClick?.(idx)),
                  size,
                })}
              </GroupAvatarItem>
            );
          })}
        {isOverflowing && (
          <GroupAvatarItem
            className="more"
            style={{ width: avatarWidth, maxWidth: 28 }}
          >
            <Dropdown
              title={title}
              overlayStyle={overlayStyle}
              onVisibleChange={setMoreBoxVisible}
              overlay={maxDropdownNode ||
                <OverlayWrap>
                  <CountWrap>
                    {
                      title
                    }
                  </CountWrap>
                  <ListWrap style={{ maxHeight: overlayMaxHeight }}>
                    {React.Children.toArray(children).map(
                      (child: any, idx: number) => {
                        const { name, color, className } = child.props;
                        const avatarColor =
                          color || COLOR_SCHEME[idx % COLOR_SCHEME_LEN];

                        return (
                          <OverlayAvatarItem
                            key={`${name}${idx}`}
                            onClick={() => onClick?.(idx)}
                          >
                            <OverlayAvatarWrap>
                              {React.cloneElement(child, {
                                title: '',
                                color: avatarColor,
                                shape: 'rect',
                                size: 'normal',
                                active: false,
                                className: `${className ? className : '' } avatar-in-list`
                              })}
                            </OverlayAvatarWrap>
                            <OverlayAvatarName>{name || ''}</OverlayAvatarName>
                            <Dot color={avatarColor}/>
                          </OverlayAvatarItem>
                        );
                      },
                    )}
                  </ListWrap>
                </OverlayWrap>
              }
              zIndex={zIndex}
            >
              <GroupMoreItem className={classnames('more-item-span', { 'more-box-active': isMoreBoxVisible })} size={size}>
                {Number(avatarCount) < 999 ? (
                  <GroupMoreItemSpan>{`${avatarCount}`}</GroupMoreItemSpan>
                ) : (
                  <>
                    <i /> <i /> <i />
                  </>
                )}
              </GroupMoreItem>
            </Dropdown>
          </GroupAvatarItem>
        )}
      </GroupAvatarList>
    </AvatarGroupWrap>
  );
});

AvatarGroup.defaultProps = {
  max: 8,
  direction: 'ltr',
};

export { AvatarGroup };
