import React from 'react';
import {
  ISpinGridCardProps,
  ISpinGridBaseLineProps,
  ISpinGridInlineProps,
  ISpinGridPreviewCardProps,
  ISpinGridSimpleCardProps,
} from './interface';
import {
  SpinGridCommonLine,
  SpinGridProgressLine,
  SpinGridWrapper,
  SpinGridCardWrapper,
  SpinGridLinesWrapper,
  SpinGridSimpleWrapper,
  SpinErrorIcon,
} from './styled';

export const SpinGridBaseLine = (props: ISpinGridBaseLineProps) => {
  const showProgress = typeof props.percent === 'number';
  return (
    <SpinGridCommonLine
      style={props.style}
      blink={props.blink && !showProgress}
    >
      {showProgress && (
        <SpinGridProgressLine percent={props.percent!} blink={props.blink} />
      )}
    </SpinGridCommonLine>
  );
};

SpinGridBaseLine.defaultProps = {
  blink: true,
};

export const SpinGridInline = (props: ISpinGridInlineProps) => {
  const { style, testid } = props;
  return (
    <SpinGridWrapper style={style} data-testid={testid}>
      <SpinGridCommonLine
        blink={false}
        style={{
          width: 12,
          marginRight: 4,
        }}
      />
      <SpinGridCommonLine
        style={{
          width: '60%',
        }}
      />
    </SpinGridWrapper>
  );
};

export const SpinGridCard = (props: ISpinGridCardProps) => {
  const { style, icon, blink, testid } = props;
  return (
    <SpinGridCardWrapper style={style} data-testid={testid}>
      {!icon ? (
        <SpinGridCommonLine
          blink={false}
          style={{
            width: 40,
            height: 40,
            marginRight: 12,
          }}
        />
      ) : (
        <SpinErrorIcon className="highlight-icon" style={{ marginRight: 12 }}>
          {icon}
        </SpinErrorIcon>
      )}
      <SpinGridLinesWrapper>
        <SpinGridCommonLine
          blink={blink}
          style={{
            width: '30%',
            marginBottom: 12,
          }}
        />
        <SpinGridCommonLine
          blink={blink}
          style={{
            width: '60%',
          }}
        />
      </SpinGridLinesWrapper>
    </SpinGridCardWrapper>
  );
};

export const SpinGridPreviewCard = (props: ISpinGridPreviewCardProps) => {
  const { style, testid } = props;
  return (
    <SpinGridCardWrapper style={style} data-testid={testid}>
      <SpinGridLinesWrapper>
        <SpinGridCommonLine
          blink={props.blink}
          style={{
            width: '30%',
            marginBottom: 28,
          }}
        />
        <SpinGridCommonLine
          blink={props.blink}
          style={{
            width: '80%',
            marginBottom: 12,
          }}
        />
        <SpinGridCommonLine
          blink={props.blink}
          style={{
            width: '40%',
          }}
        />
      </SpinGridLinesWrapper>
    </SpinGridCardWrapper>
  );
};

export const SpinGridSimpleCard = (props: ISpinGridSimpleCardProps) => {
  const { style, testid } = props;
  return (
    <SpinGridSimpleWrapper style={style} data-testid={testid}>
      <SpinGridBaseLine
        blink={props.blink}
        style={{
          width: '30%',
          height: 8,
          minWidth: 8,
        }}
        percent={props.percent}
      />
    </SpinGridSimpleWrapper>
  );
};
