import React, {
  useMemo,
  useRef,
  useImperativeHandle,
  useEffect,
  useCallback,
  useState,
} from 'react';
import ReactDOM from 'react-dom';
import { uuid, debounce } from '../../utils';
import { Dropdown } from '../dropdown';
import {
  ITooltipSingletonHandle,
  ITooltipHandlerEvent,
  IState,
} from './interface';
import { TooltipContent } from './styled';

const DELAY_TO_SHOW = 200;

const tooltipOverlayStyle: React.CSSProperties = {
  background: 'transparent',
  border: 0,
  boxShadow: 'none',
};

// 单例 Ref
const tooltipRef = React.createRef<ITooltipSingletonHandle>();

let inited = false;

// 组件
const TooltipSingleton = React.memo(
  React.forwardRef<ITooltipSingletonHandle>((_, ref) => {
    const currentId = useRef('');
    const [state, setState] = useState<IState>({
      visible: false,
      title: '',
      zIndex: undefined,
      triggerNode: undefined,
    });

    const showTooltip = useCallback(
      debounce((id: string, props: ITooltipHandlerEvent) => {
        if (currentId.current !== id) return;
        setState((prevState) => ({ ...prevState, ...props, visible: true }));
      }, DELAY_TO_SHOW),
      [],
    );

    useImperativeHandle(ref, () => ({
      handleEnter(id, props) {
        currentId.current = id;
        showTooltip.cancel();
        showTooltip(id, props);
      },
      handleLeave(id: string) {
        if (currentId.current !== id) return;
        showTooltip.cancel();
        setState((prevState) => ({ ...prevState, visible: false }));
      },
      handleUpdate(id: string, props) {
        if (currentId.current !== id) return;
        setState((prevState) => ({ ...prevState, ...props }));
      },
    }));

    return (
      <Dropdown
        visible={state.visible && !state.disabled}
        getContainer={state.getContainer && state.getContainer}
        overlay={
          <TooltipContent
            className={state.overlayClassName}
            style={state.overlayStyle}
          >
            {state.title}
          </TooltipContent>
        }
        zIndex={state.zIndex}
        placement={state.placement}
        overlayStyle={tooltipOverlayStyle}
        triggerNode={state.triggerNode}
        trigger="hover"
      />
    );
  }),
);

function init() {
  if (inited) {
    return;
  }
  inited = true;
  // 单例初始化
  const container = document.createElement('div');
  container.setAttribute(
    'style',
    'position: absolute; left: 0; top: 0; width: 100%; line-height: 0;',
  );
  document.body.appendChild(container);
  ReactDOM.render(<TooltipSingleton ref={tooltipRef} />, container);
}

/**
 * useSingleton
 */
export default () => {
  const id = useMemo(() => uuid(), []);

  // 节点被清除的时候应该也把tooltip清除
  useEffect(() => {
    return () => {
      if (tooltipRef.current) {
        tooltipRef.current.handleLeave(id);
      }
    };
  }, [id]);

  return useRef({
    enter: (e: ITooltipHandlerEvent) => {
      init();
      if (tooltipRef.current && !e.disabled) {
        tooltipRef.current.handleEnter(id, e);
      }
    },
    leave: () => {
      if (tooltipRef.current) {
        tooltipRef.current.handleLeave(id);
      }
    },
    update: (e: ITooltipHandlerEvent) => {
      init();
      if (tooltipRef.current) {
        tooltipRef.current.handleUpdate(id, e);
      }
    },
  }).current;
};
