import React, { useRef, useMemo, useEffect } from 'react';
import ReactDOM from 'react-dom';

export interface IPortalComponentProps {
  visible: boolean;
  getContainer?: () => HTMLElement;
}

export const PortalComponent: React.FC<IPortalComponentProps> = (props) => {
  const { visible, getContainer, children } = props;
  const isAppended = useRef(false);
  const oContainer = getContainer ? (getContainer() || document.body) : document.body;

  const oComponentContainer = useMemo(() => {
    const node = document.createElement('div');
    node.style.position = 'absolute';
    node.style.left = '0px';
    node.style.top = '0px';
    node.style.width = '100%';
    isAppended.current = false;
    return node;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [oContainer]);

  // 首次visible append 到body中
  if (!isAppended.current) {
    if (visible) {
      isAppended.current = true;
      oContainer.appendChild(oComponentContainer);
    }
  }

  useEffect(() => {
    const didAppended = isAppended.current;

    return () => {
      if (didAppended && oComponentContainer.parentElement === oContainer) {
        oContainer.removeChild(oComponentContainer);
        isAppended.current = false;
      }
    };
  }, [oComponentContainer, oContainer]);

  return ReactDOM.createPortal(children, oComponentContainer);
};
