import React from 'react';

import { EColorType, IColorBox } from '../types';
import {
  FontColorBox,
  BorderColorBox,
  BackgroundColorBox,
  FontBackgroundColorBox,
} from './styled';
import EColorPanelRole from '../role';

const ColorBox = (props: IColorBox) => {
  const { colorType, ...rest } = props;

  switch (colorType) {
    case EColorType.font:
      return <FontColorBox {...rest} />;
    case EColorType.border:
      return (
        <BorderColorBox {...rest} data-role={EColorPanelRole.borderColor} />
      );
    case EColorType.background:
      return (
        <BackgroundColorBox {...rest} data-role={EColorPanelRole.bgColor} />
      );
    case EColorType.fontBackground:
      return (
        <FontBackgroundColorBox {...rest} data-role={EColorPanelRole.bgColor} />
      );
    default:
      return null;
  }
};

export default ColorBox;
