/* eslint-disable arrow-body-style */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
import styled from 'styled-components';
import { resetComponent } from '../../utils/styled.mixin';
import { withTheme } from '../theme';

export const SwitchButton = withTheme(styled.button<any>`
  ${resetComponent}
  border: 0;
  display: inline-block;
  position: relative;
  outline: none;
  transition: all .36s cubic-bezier(.78,.14,.15,.86);
  ${({ size, theme }) => {
    if (size === 'small') {
      return `
        width: ${theme.ui.switchWidthSmall};
        height: ${theme.ui.switchHeightSmall};
      `;
    }
    return `
      width: ${theme.ui.switchWidth};
      height: ${theme.ui.switchHeight};
    `;
  }}
  border-radius: ${({ theme }) => theme.ui.switchHeight};
  background-color: ${({ theme }) => theme.ui.colorText1};
  overflow: hidden;
  cursor: pointer;
  ${({ checked, theme }) => {
    return checked ? `
      background-color: ${theme.ui.primaryColor};
    ` : '';
  }}

  ${({ disabled, checked, theme }) => {
    return disabled ? `
      cursor: not-allowed;
      background-color: ${checked ? theme.ui.primaryDisabledColor : theme.ui.disabledBackgroundColor};
    ` : '';
  }}
`);

export const SwitchInner = withTheme(styled.span<any>`
  ${resetComponent}
  display: block;
  left: 2px;
  top: 2px;
  transition: all .36s cubic-bezier(.78,.14,.15,.86);
  ${({ checked, size, theme }) => {
    const height = size === 'small' ? parseInt(theme.ui.switchHeightSmall, 10) : parseInt(theme.ui.switchHeight, 10);
    return checked ? `
      left: calc(100% - ${height - 2}px);
    ` : '';
  }}

  border-radius: 50%;
  position: absolute;
  background-color: ${({ theme }) => theme.ui.colorGray1};
  ${({ size, theme }) => {
    if (size === 'small') {
      return `
        width: ${parseInt(theme.ui.switchHeightSmall, 10) - 4}px;
        height: ${parseInt(theme.ui.switchHeightSmall, 10) - 4}px;
      `;
    }
    return `
      width: ${parseInt(theme.ui.switchHeight, 10) - 4}px;
      height: ${parseInt(theme.ui.switchHeight, 10) - 4}px;
    `;
  }}
`);
