import React from 'react';
// react-window 包体积远小于 react-virtualized
import { FixedSizeList } from 'react-window';
import memoize from 'memoize-one';
import { CheckboxGroup } from '../checkbox';
import { ListSelectWrap } from './styled';
import { ListSelectItem, IListItem } from './ListSelectItem';

export type IListSelectProps = {
  /**
   * 数据列表
   */
  dataList: IListItem[];
  /**
   * 滚动高度
   */
  height: number;
  /**
   * 滚动宽度
   */
  width: number;
  /**
   * 外层样式
   */
  style?: React.CSSProperties;
  /**
   * 外层样式
   */
  itemStyle?: React.CSSProperties;
  /**
   * 选中的checkbox列表
   */
  value: string[];
  /**
   * 勾选改变处理函数
   */
  onChange: (value: string[]) => void;
  /**
   * 每一个滚动项的高度
   */
  itemHeight?: number;
  /**
   * 测试使用
   */
  testid?: string;
};

const createItemData = memoize((dataList, itemStyle) => ({
  dataList,
  itemStyle,
}));
export const ListSelect: React.FC<IListSelectProps> = (props) => {
  const {
    dataList,
    onChange,
    width,
    height,
    value = [],
    style = {},
    itemHeight,
    testid,
    itemStyle,
  } = props;
  const itemData = createItemData(dataList, itemStyle);
  return (
    <ListSelectWrap style={style}>
      <CheckboxGroup onChange={onChange} value={value} testid={testid}>
        {/* @ts-ignore */}
        <FixedSizeList
          height={height}
          itemCount={dataList.length}
          itemData={itemData}
          itemSize={itemHeight as number}
          width={width}
          itemKey={(index, data: IListItem[]) => {
            const item = data[index];
            return `FixedSizeList_${item?.value || index}`;
          }}
        >
          {ListSelectItem}
        </FixedSizeList>
      </CheckboxGroup>
    </ListSelectWrap>
  );
};

ListSelect.defaultProps = {
  itemHeight: 20,
  value: [],
};
