import styled, { css } from 'styled-components';

const lightLevel2BaseColor = 'rgba(199, 200, 201, 1)';
const lightLevel3BaseColor = 'rgba(162, 163, 165, 1)';
const lightLevel4BaseColor = 'rgba(116, 118, 119, 1)';

export const scroll = css`
  overflow-y: overlay;

  &::-webkit-scrollbar {
    width: 0px;
  }

  &:hover::-webkit-scrollbar {
    width: 6px;
  }
  
  &::-webkit-scrollbar-track {} /* 视觉暂未给出track的设计 */
  
  &::-webkit-scrollbar-thumb {
    background-color: ${(p) => p.theme?.color?.scrollNormal || lightLevel2BaseColor};
    border-radius: 6px;

    &:hover {
      background-color: ${(p) => p.theme?.color?.scrollHover || lightLevel3BaseColor};
    }

    &:active {
      background-color: ${(p) => p.theme?.color?.scrollClick || lightLevel4BaseColor};
    }
  }
`;

export const Container = styled.div`
  position: relative;
  width: 212px;
  border: 1px solid rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  overflow: hidden;

  &:hover {
    border-color: #0089ff;
  }

  &.focused {
    border-color: #0089ff;
    box-shadow: 0px 0px 0px 3px rgba(0, 106, 254, 0.12);
  }

  &.readOnlyFocused {
    background-color: #f6f6f6;
    border-color: #0089ff;
  }

  &.disabled {
    background-color: #f6f6f6;
    border-color: rgba(0, 0, 0, 0.16);
    cursor: not-allowed;
  }

  &.readOnly {
    background-color: #f6f6f6;
    border-color: rgba(0, 0, 0, 0.16);

    &:hover {
      border-color: #0089ff;
    }
  }

  &.error {
    border-color: #ff5219;
    box-shadow: unset;
  }

  textarea {
    /* opacity: 0.5; */
    background-color: transparent;
    border: 0;
    padding: 4px 8px;
    box-sizing: border-box;

    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    resize: inherit;

    ${scroll}

    &:focus {
      outline: none;
    }

    &:disabled {
      cursor: not-allowed;
      color: rgba(23, 26, 29, 0.24);
    }

    ::placeholder {
      color: rgba(23, 26, 29, 0.24);
    }
  }
`;

export const Pre = styled.div`
  visibility: hidden;
  display: inline-block;
  /* opacity: 0.5; */
  width: 100%;
  margin: 0;
`;

export const FakeLine = styled.div`
  padding: 4px 8px;
  word-break: break-all;
  word-wrap: break-word;
`;

export const ErrorTip = styled.div`
  font-size: 12px;
  line-height: 18px;
  margin-top: 4px;
  color: #FF5219;
`;
