import React from 'react';
import { SpinWrapper } from './styled';
import { ISpinProps } from './interface';

export const Spin = (props: ISpinProps) => {
  const { size = 'small', theme = 'gray' } = props;
  return <SpinWrapper size={size} theme={theme} />;
};

Spin.defaultProps = {
  size: 'small',
  theme: 'gray',
};
