/* eslint-disable max-lines-per-function */
import React, {
  TextareaHTMLAttributes,
  useState,
  useRef,
  useCallback,
  useEffect,
  useMemo,
  useImperativeHandle,
} from 'react';
import { Container, Pre, FakeLine, ErrorTip } from './styled';
import { classnames } from '../../utils';

export type ITextAreaProps = TextareaHTMLAttributes<any> & {
  /**
   * 文本编辑框的样式
   */
  style?: React.CSSProperties;
  /**
   * 最外层容器的样式
   */
  className?: string;
  /**
   * rows表示初始时显示的行数。（和原生textarea组件一致）
   */
  /**
   * maxRows表示最多显示多少行，超过这个行数出滚动条
   */
  maxRows?: number;
  /**
   * 显示多少行时，可设置的偏移量，例如显示2行再多一些。单位是px
   */
  offsetRowHeight?: number;
  /**
   * 是否显示error样式
   */
  errored?: boolean;
  /**
   * 错误提示的文字，显示在组件下方。errored为true时才生效
   */
  errorTip?: string;
  /**
   * 只读状态
   */
  readOnly?: boolean;
};

/**
 * 外部通过 ref 能获取的属性和方法
 */
export interface ITextareaHandle {
  focus: () => void;
  select: () => void;
  blur: () => void;
}

const WRAP_TEXT = '\n';

const TextArea = React.memo(React.forwardRef<ITextareaHandle, ITextAreaProps>((props, ref) => {
  const {
    style,
    className,
    value,
    rows = 2,
    maxRows = rows,
    offsetRowHeight = 10,
    disabled,
    readOnly,
    errored,
    errorTip,
    ...restProps
  } = props;

  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const [isFocused, setIsFocused] = useState<boolean>(false);
  const [isSelected, setIsSelected] = useState<boolean>(false);
  const [maxHeight, setMaxHeight] = useState<string>('unset');

  useEffect(() => {
    if (maxRows < 1 || !textareaRef.current) {
      return;
    }
    const { lineHeight } = window.getComputedStyle(
      textareaRef.current,
      null,
    );
    const tmpMaxHeight = String(maxRows * parseInt(lineHeight, 10) + offsetRowHeight);
    setMaxHeight(tmpMaxHeight);
  }, [maxRows, offsetRowHeight]);

  useImperativeHandle(ref, () => ({
    focus: () => {
      textareaRef.current!.focus();
    },
    select: () => {
      textareaRef.current!.select();
    },
    blur: () => {
      textareaRef.current!.blur();
    },
  }));

  const handleFocus = useCallback(() => {
    setIsFocused(true);
  }, []);

  const handleBlur = useCallback(() => {
    setIsFocused(false);
    setIsSelected(false);
  }, []);

  const handleSelect = useCallback(() => {
    setIsSelected(true);
  }, []);

  const initPlaceholder = useMemo(() => {
    const placeholders = [];
    for (let i = 0; i < rows; i++) {
      placeholders.push(<FakeLine key={i}>{i}</FakeLine>);
    }
    return placeholders;
  }, [rows]);

  const currentPlaceholder = useMemo(() => {
    return (value?.toString() || '').split(WRAP_TEXT).map((v, vIndex) => {
      return <FakeLine key={vIndex}>{v || vIndex}</FakeLine>;
    });
  }, [value]);

  const containerStyle = useMemo(() => {
    return { ...style, maxHeight: `${maxHeight}px` };
  }, [maxHeight, style]);

  return (
    <div>
      <Container
        style={containerStyle}
        className={classnames(className, {
          focused: !readOnly && isFocused,
          readOnlyFocused: readOnly && isSelected,
          disabled,
          readOnly: readOnly && !isSelected,
          error: errored,
        })}
      >
        <div>
          <div>
            <Pre>{currentPlaceholder}</Pre>
            <Pre style={{ width: 0 }}>{initPlaceholder}</Pre>
          </div>
          <textarea
            ref={textareaRef}
            value={value}
            rows={rows}
            disabled={disabled}
            readOnly={readOnly}
            onSelect={handleSelect}
            onFocus={handleFocus}
            onBlur={handleBlur}
            {...restProps}
          />
        </div>
      </Container>
      {errored && errorTip && <ErrorTip>{errorTip}</ErrorTip>}
    </div>
  );
}));

TextArea.displayName = 'TextArea';
export { TextArea };
