import React from 'react';
import { Trigger, ITriggerProps } from '../trigger';

export interface IPopoverProps
  extends Omit<
    ITriggerProps,
    'pageX' | 'pageY' | 'type' | 'placement' | 'action'
  > {
  placement?: 'bottom' | 'right' | 'top' | 'left';
  action?: ITriggerProps['action'];
}

export const Popover = (props: IPopoverProps) => {
  const { children, action = ['click'], ...otherProps } = props;

  return (
    <Trigger {...otherProps} type="popover" action={action}>
      {props.children}
    </Trigger>
  );
};

Popover.defaultProps = {
  placement: 'bottom',
  action: ['click'],
};
