import React, { useMemo } from 'react'
import dayjs, { Dayjs } from 'dayjs'
import { useTranslate } from '@ali/we-util'
import useControlledValue from '../../hooks/useControlledValue'
import { NumberSlider } from './NumberSlider'
import { PanelWrap, PanelColumnWrap, PanelColumnTitle, PanelColumnBody } from './styled'

type TimeUnit = 'hour' | 'minute' | 'second';
export interface ITimePanelProps {
  /**
   * 设置国际化语言
   */
  locale?: string
  /**
   * 时间值
   */
  value?: Dayjs
  /**
   * 默认时间值
   */
  defaultValue?: Dayjs
  /**
   * 修改回调
   */
  onChange?: (time: Dayjs, timeString: string) => void
  /**
   * 样式
   */
  style?: React.CSSProperties

  visible?: boolean
  /**
   * format格式
   */
  format?: string
  /**
   * 测试使用
   */
  testid?: string
}

const matchTimeTag = (format: string, type: TimeUnit) => {
  const TIME_TAGS = {
    hour: ['h', 'H'],
    minute: ['m'],
    second: ['s']
  }
  const tags = TIME_TAGS[type];

  return tags.some((tag) => format.indexOf(tag) > -1);
}

const TimePanel: React.FC<ITimePanelProps> = (props) => {
  const { locale, onChange, style, visible, testid, format = 'HH:mm:ss' } = props

  // 国际化
  const t = useTranslate(locale)

  // 受控
  const [value, setValue] = useControlledValue(props.value, props.defaultValue)

  // 时 0 - 23
  const hourNumbers = useMemo(() => Object.keys(Array.from({ length: 24 })).map((_, i) => i), [])
  // 分 0 - 59
  const minuteNumbers = useMemo(() => Object.keys(Array.from({ length: 60 })).map((_, i) => i), [])
  // 秒 0 - 59
  const secondNumbers = useMemo(() => Object.keys(Array.from({ length: 60 })).map((_, i) => i), [])

  /**
   * 修改回调函数
   */
  const handleChanges = useMemo(() => {
    const handleChange = (type: TimeUnit) => (e: number): void => {
      const newValue: Dayjs = (value || dayjs().hour(0).minute(0).second(0))[type](e) as Dayjs
      if (onChange) {
        onChange(newValue, newValue.format(format))
      }
      setValue(newValue)
    }

    return {
      hour: handleChange('hour'),
      minute: handleChange('minute'),
      second: handleChange('second'),
    }
  }, [value, onChange, setValue])

  return (
    <PanelWrap
      style={{
        ...(style || {}),
        display: visible ? 'flex' : 'none',
      }}
    >
      {
        matchTimeTag(format, 'hour') ? (
          <PanelColumnWrap>
            <PanelColumnTitle>{t('we_ui_time_hour')}</PanelColumnTitle>
            <PanelColumnBody>
              <NumberSlider
                visible={visible}
                number={value ? value.hour() : 0}
                numbers={hourNumbers}
                onChange={handleChanges.hour}
                testid={`${testid}_hour`}
              />
            </PanelColumnBody>
          </PanelColumnWrap>
        ): null
      }
      {
        matchTimeTag(format, 'minute') ? (
          <PanelColumnWrap>
            <PanelColumnTitle>{t('we_ui_time_minute')}</PanelColumnTitle>
            <PanelColumnBody>
              <NumberSlider
                visible={visible}
                number={value ? value.minute() : 0}
                numbers={minuteNumbers}
                onChange={handleChanges.minute}
                testid={`${testid}_minute`}
              />
            </PanelColumnBody>
          </PanelColumnWrap>
        ): null
      }
      {
        matchTimeTag(format, 'second') ? (
          <PanelColumnWrap>
            <PanelColumnTitle>{t('we_ui_time_second')}</PanelColumnTitle>
            <PanelColumnBody>
              <NumberSlider
                visible={visible}
                number={value ? value.second() : 0}
                numbers={secondNumbers}
                onChange={handleChanges.second}
                testid={`${testid}_second`}
              />
            </PanelColumnBody>
          </PanelColumnWrap>
        ) : null
      }
    </PanelWrap>
  )
}

TimePanel.defaultProps = {
  visible: true,
}

const TimePanelMemo = React.memo(TimePanel)

export { TimePanelMemo as TimePanel }
