import React, { useState, useCallback, useEffect } from 'react'
import { Container, Box } from './styled'
import { useTranslate } from '@ali/we-util'
import { InputComponent } from '../input'
import { classnames } from '../../../utils'

export interface ReplaceProps {
  /**
   * 国际化
   */
  local?: string

  /**
   * 是否可见
   */
  visible: boolean

  /**
   * 类名
   */
  className?: string

  /**
   * 是否禁止点击
   */
  disabled?: boolean

  /**
   * 按钮模式
   */
  btnMode?: boolean

  /**
   * replace mod change
   */
  onExpand?: () => void

  /**
   * 替换当前
   */
  replace: (value: string) => void

  /**
   * 替换全部
   */
  replaceAll: (value: string) => void
  /**
   * 禁用替换功能
   */
  disableReplace?: boolean
}

export const Replace: React.FC<ReplaceProps> = (props) => {
  const {
    visible,
    disabled,
    className,
    onExpand,
    replace,
    replaceAll,
    btnMode,
    disableReplace = false,
  } = props

  const [text, setText] = useState<string>('')
  const [isMiniMode, setMode] = useState<boolean>(true)
  // 国际化
  const t = useTranslate(props.local)
  // 是否显示
  const style = { display: visible ? 'flex' : 'none' }

  useEffect(() => {
    btnMode !== undefined && setMode(!!btnMode)
  }, [btnMode])

  /**
   * text change处理
   */
  const hansleChange = useCallback((value: string) => {
    setText(value)
  }, [])

  /**
   * mode切换
   */
  const handleMode = useCallback(
    (e: React.MouseEvent) => {
      if (!disabled) {
        setMode(false)
        onExpand && onExpand()
      }
      e.preventDefault()
    },
    [disabled, onExpand],
  )

  /**
   * 替换
   */
  const handleReplace = useCallback(
    (e: React.MouseEvent) => {
      if (disableReplace) {
        return
      }
      replace(text)
      e.preventDefault()
    },
    [replace, text, disableReplace],
  )

  /**
   * 替换全部
   */
  const handleReplaceAll = useCallback(() => {
    setMode(true)
    replaceAll(text)
  }, [replaceAll, text])

  return (
    <div style={style} className={className}>
      {isMiniMode ? (
        <Box
          className={`round ${disabled ? 'disable' : ''}`}
          onMouseDown={handleMode}
        >
          {t('we_ui_find_and_replace_replace')}
        </Box>
      ) : (
        <Container>
          <InputComponent
            prefix={false}
            text={text}
            onChange={hansleChange}
            placeholder={t('we_ui_find_and_replace_replace_description')}
          />
          <Box
            onMouseDown={handleReplace}
            className={classnames({ disable: disableReplace })}
          >
            {t('we_ui_find_and_replace_replace')}
          </Box>
          <Box onClick={handleReplaceAll}>
            {t('we_ui_find_and_replace_replace_all')}
          </Box>
        </Container>
      )}
    </div>
  )
}
