import { IColorObj } from '../types';

const decimalToHex = (alpha: number): string =>
  alpha === 0
    ? '00'
    : Math.round(255 * alpha)
        .toString(16)
        .padStart(2, '0');

const colorObj2String = (colorObj: IColorObj): string => {
  let result = colorObj.hex;

  // 这里可能是 "transparent"
  if (result.startsWith('#')) {
    if (colorObj.rgb && colorObj.rgb.a !== 1) {
      result = `${colorObj.hex}${decimalToHex(
        colorObj.rgb.a || 0,
      )}`.toLowerCase();
    }
  }

  return result;
};

export default colorObj2String;
