 export const preventDefault = (e: React.MouseEvent | React.TouchEvent) => {
  e.preventDefault();
};

export class CustomEventEmitter {
  private readonly eventHandlersMap = new Map<string, Array<Function>>();
  public on (event: string, handler: Function) {
    if (this.eventHandlersMap.has(event)) {
      const handlerArray = this.eventHandlersMap.get(event) as Array<Function>;
      handlerArray.push(handler);
    } else {
      this.eventHandlersMap.set(event, [handler]);
    }
  }
  public off (event: string, handler: Function) {
    if (this.eventHandlersMap.has(event)) {
      const handlerArray = this.eventHandlersMap.get(event) as Array<Function>;
      const handlerIndex = handlerArray.indexOf(handler);
      if (handlerIndex > -1) {
        handlerArray.splice(handlerIndex, 1);
      }
    }
  }
  public emit(event: string, ...params: any) {
    const handlerArray = this.eventHandlersMap.get(event);
    handlerArray?.forEach((handler) => {
      handler(...params);
    });
  }
}