/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable @typescript-eslint/no-explicit-any */
import styled from 'styled-components';
import { SelectIcon } from './SelectIcon';
import { resetComponent } from '../../utils/styled.mixin';
import { withTheme } from '../theme';

const grayColor = 'rgba(126, 134, 142, 0.24)';
const hoverColor = '#39A3FF';
const activeColor = '#0065D5';

export const CheckboxSelect = styled(SelectIcon)`
  display: none;
  &.checked {
    display: inline-block;
    position: absolute;
    top: -1px;
    left: -1px;
  }
`;

export const CheckboxInner: any = withTheme(styled.span<any>`
  ${resetComponent}
  display: inline-block;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  border: 1px solid ${grayColor};
  background-color: ${({ theme }) => theme.ui.colorGray1};
  transition: all 0.24s ease-in;
  border-radius: 2px;
  position: relative;
  &.disabled {
    background-color: ${grayColor};
  }
`);

export const CheckboxWrap = withTheme(styled.label<any>`
  ${resetComponent}
  cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'pointer')};
  display: inline-block;
  ${({ disabled, theme }) => {
    return disabled
      ? ''
      : `
      ${CheckboxInner} {
        &:hover { 
          border-color: rgba(0, 137, 255, 0.48);
        }
        &:active {
          border-color: ${theme.ui.primaryColor};
        }
      }
    `;
  }};

  ${({ checked, theme: { ui }, disabled, indeterminate }) => {
    if (indeterminate) {
      return `
      ${CheckboxInner} {
        border-color: ${ui.primaryColor};
        background-color: ${ui.primaryColor};
        &:after {
          content: "";
          position: absolute;
          display: block;
          background-color: #fff;
          height: 2px;
          transform: scale(.5);
          left: 0;
          right: 0;
          top: 6px;
        }
        &:hover { 
          border-color: ${hoverColor};
          background-color: ${hoverColor};
        }
        &:active { 
          border-color: ${activeColor};
          background-color: ${activeColor};
        }
      }
      `;
    }
    return checked
      ? `
      ${CheckboxInner} {
        border-color: ${disabled ? grayColor : ui.primaryColor};
        background-color: ${disabled ? grayColor : ui.primaryColor};
      }
      ${
        disabled
          ? ''
          : `
        ${CheckboxInner} {
          &:hover { 
            border-color: ${hoverColor};
            background-color: ${hoverColor};
          }
          &:active { 
            border-color: ${activeColor};
            background-color: ${activeColor};
          }
        }`
      }
    `
      : '';
  }}
`);

export const CheckboxText = withTheme(styled.span<any>`
  ${resetComponent}
  display: inline-block;
  vertical-align: middle;
  padding: 0 ${({ theme }) => theme.ui.paddingMedium};
  color: ${({ theme }) => theme.ui.commonActionIconColor};
  font-size: ${({ theme }) => theme.ui.fontSizeMenu};
`);

export const CheckboxContent = withTheme(styled.span<any>`
  ${resetComponent}
  position: relative;
  input {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 0;
    height: 0;
    cursor: pointer;
    opacity: 0;
  }
`);

export const CheckboxGroupWrap = withTheme(styled.div<any>`
  ${resetComponent}
`);
