import { FindAndReplace, IFindAndReplaceBaseProps } from './FindAndReplace';
import { dragAndDropHOC } from '../../utils/dragAndDropHOC';

export const FindAndReplacePortal = dragAndDropHOC<IFindAndReplaceBaseProps>(
  FindAndReplace,
  {
    shouldDragElement: (event: any) => {
      const tagName = event.target.tagName.toLowerCase();
      // 鼠标点击在输入框上时，不可拖拽
      return tagName !== 'input';
    },
  },
);
