import React from 'react';
import { debounce } from 'lodash';
import ActionableBox from './ActionableBox';
import ActionableImage from './ActionableImage';
import Toolbar from './Toolbar';
import {
  PreviewImageWrap,
} from './styled';
import { CustomEventEmitter } from './utils';

export interface IPreviewImageProps {
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * 图片地址数组
   */
  urls: string[];
  /**
   * 当前图片地址
   */
  current: string;
  /**
   * 当前图片索引，优先级大于 current
   */
  currentIndex?: number
  /**
   * 图层 z-index 值
   */
  zIndex?: number;
  /**
   * 关闭事件处理函数
   */
  onClose?: () => void;
  /**
   * 图片展示标签img的样式
   */
  imgStyle?: React.CSSProperties;
  /**
   * 图片组件状态
   */
  status?: IPreviewImageStatus;
  /**
   * 图片组件状态改变回调，如：切换图片、缩放图片
   */
  onChange?: (status: IPreviewImageStatus) => void;
  /**
  * 图片下载
  */
  downloadImage?: (url: string) => void;
   /**
   * 是否可以下载
   */
  enableDownload?: boolean;
}

interface IOffset {
  x: number;
  y: number;
}

export interface IPreviewImageStatus {
  index?: number;
  scale?: number;
  rotation?: number;
  offset?: IOffset;
}

export const PreviewImage = React.memo<IPreviewImageProps>((props) => {
  const {
    urls,
    current,
    currentIndex: initialIndex,
    zIndex,
    enableDownload,
    downloadImage,
    onClose,
    imgStyle,
  } = props;

  const [wrapWidth, setWrapWidth] = React.useState(window.innerWidth);
  const [wrapHeight, setWrapHeight] = React.useState(window.innerHeight);
  const [visible, setVisible] = React.useState(true);
  const wrapRef = React.useRef<HTMLDivElement | null>(null);
  const eventEmitterRef = React.useRef<CustomEventEmitter>(new CustomEventEmitter());
  const length = urls.length;

  const [currentIndex, setCurrentIndex] = React.useState(() => {
    if (typeof initialIndex === 'number' && Number.isInteger(initialIndex) && initialIndex < length) {
      return initialIndex;
    }
    const maybeIndex = urls.indexOf(current)
    return  maybeIndex > -1 ? maybeIndex : 0;
  });

  const debouncedHandleResize = React.useMemo(() => {
    return debounce(() => {
      if (wrapRef.current) {
        setWrapWidth(wrapRef.current.offsetWidth);
        setWrapHeight(wrapRef.current.offsetHeight);
      }
    }, 200);
  }, []);

  React.useEffect(() => {
    window.addEventListener('resize', debouncedHandleResize);
    return () => {
      window.removeEventListener('resize', debouncedHandleResize);
    }
  }, [debouncedHandleResize]);

  React.useEffect(() => {
    const handleClose = () => {
      setVisible(false);
      onClose && onClose();
    }
    eventEmitterRef.current.on('click', handleClose);
    return () => {
      eventEmitterRef.current.off('click', handleClose);
    }
  }, [onClose]);

  return visible ? (
    <PreviewImageWrap
      style={{ zIndex: zIndex || 1000 }}
      ref={wrapRef}
    >
      {
        urls.map((url, index) => {
          return (
            Math.abs(currentIndex - index) <= 1 ? (
              <ActionableImage  
                src={url}
                index={index}
                currentIndex={currentIndex}
                outerHeight={wrapHeight}
                outerWidth={wrapWidth}
                total={urls.length}
                onCurrentIndexChange={setCurrentIndex}
                key={index}
                eventEmitter={eventEmitterRef.current}
                imgStyle={imgStyle}
              />
            ) : null
          );
        })
      }
      <ActionableBox eventEmitter={eventEmitterRef.current}/>
      <Toolbar enableDownload={enableDownload} downloadImage={downloadImage} currentIndex={currentIndex} urls={urls}/>
    </PreviewImageWrap>
  ) : null;
});
