import React, { useCallback, useMemo } from 'react';
import { CheckboxGroupWrap } from './styled';
import useControlledValue from '../../hooks/useControlledValue';
import { CheckboxContext, IToggleValueEvent, ICheckboxContext } from './CheckboxContext';
import { Checkbox } from './Checkbox';

export interface ICheckboxGroupProps {
  /**
   * 选项
   */
  options?: string[] | ICheckboxOption[];
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 选中值
   */
  value?: string[];
  /**
   * 默认选中值
   */
  defaultValue?: string[];
  /**
   * 修改回调
   */
  onChange?: (e: string[]) => void;
  /**
   * 测试使用
   */
  testid?: string;
}

export interface ICheckboxOption {
  label: string | React.ReactElement;
  value: string;
  disabled?: boolean;
}

const CheckboxGroup: React.FC<ICheckboxGroupProps> = React.memo((props) => {
  const { children, disabled, onChange, options, testid } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);

  /**
   * 选中切换
   *
   * @param {string} e
   * @return {void}
   */
  const handleToggleValue = useCallback((e: IToggleValueEvent) => {
    const newValue = [...value!];
    let changed = false;
    const index = newValue.indexOf(e.value);
    if (e.checked && index === -1) {
      changed = true;
      newValue.push(e.value);
    } else if (!e.checked && index !== -1) {
      changed = true;
      newValue.splice(index, 1);
    }
    if (changed) {
      if (onChange) {
        onChange(newValue);
      }
      setValue(newValue);
    }
  }, [onChange, value, setValue]);

  /**
   * 透传给Checkbox组件的属性
   *
   * @return {ICheckboxContext} context
   */
  const provider = useMemo<ICheckboxContext>(() => {
    return {
      disabled: !!disabled,
      onToggleValue: handleToggleValue,
      checkedValues: value!,
      testid,
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [disabled, handleToggleValue, value, testid]);

  const optionsNode = useMemo(() => ((options || []) as Array<string | ICheckboxOption>)
    .map((option: ICheckboxOption | string) => {
      const item: ICheckboxOption = typeof option === 'string' ? {
        value: option,
        label: option,
      } : option;
      return (
        <Checkbox
          key={item.value}
          value={item.value}
          disabled={item.disabled}
        >
          {item.label}
        </Checkbox>
      );
    }), [options]);

  return (
    <CheckboxGroupWrap data-testid={testid ? `CheckboxGroup-${testid}` : undefined}>
      <CheckboxContext.Provider value={provider}>
        {
          options ? optionsNode : children
        }
      </CheckboxContext.Provider>
    </CheckboxGroupWrap>
  );
});

CheckboxGroup.defaultProps = {
  defaultValue: [],
};

CheckboxGroup.displayName = 'CheckboxGroup';

export { CheckboxGroup };
