import React, { useEffect } from 'react';
import raf from './raf';
import { ButtonSize, ButtonType } from './Button';
import { IWaveWrap } from './styled';
import { composeRef, supportRef } from '../../utils/ref';

const isHidden = (element: HTMLElement) => {
  if (process.env.NODE_ENV === 'test') {
    return false;
  }
  return !element || element.offsetParent === null || element.hidden;
};

const supportedType = ['normal', 'primary'];

interface IWaveProps {
  children: React.ReactElement;
  type?: ButtonType;
  size?: ButtonSize;
  testid?: string;
}

export const IWave = (props: IWaveProps) => {
  const containerRef = React.createRef<HTMLDivElement>();
  let destroyed: boolean = false;
  let animationStartId: number;
  let animationStart: boolean = false;
  let clickWaveTimeoutId: number;
  let instance:
    | {
        cancel: () => void;
      }
    | undefined;
  const { size, testid } = props;

  useEffect(() => {
    const node = containerRef.current as HTMLDivElement;
    const { type } = props;
    // 如果 type 不为 undefined 且为不支持波纹效果的 type，直接 return
    if (
      (type && !supportedType.includes(type)) ||
      !node ||
      node.nodeType !== 1
    ) {
      return;
    }
    instance = bindAnimationEvent(node);
    return () => {
      if (instance) {
        instance.cancel();
      }
      if (clickWaveTimeoutId) {
        clearTimeout(clickWaveTimeoutId);
      }
      destroyed = true;
    };
  }, []);

  const handleOnClick = (node: HTMLElement) => {
    if (!node || isHidden(node) || node.className.indexOf('-leave') >= 0) {
      return;
    }
    const waveClassName = getClassName();
    node.className = node.className + ' ' + waveClassName;
    ['transition', 'animation'].forEach((name) => {
      node.addEventListener(`${name}start`, onTransitionStart as any);
      node.addEventListener(`${name}end`, onTransitionEnd as any);
    });
  };

  const getClassName = () => {
    const { type } = props;
    if (type === undefined) {
      return 'we-click-wave-animating-normal';
    }
    return `we-click-wave-animating-${type}`;
  };

  const onTransitionStart = (e: AnimationEvent) => {
    if (destroyed) {
      return;
    }

    const node = containerRef.current as HTMLDivElement;
    if (!e || e.target !== node || animationStart) {
      return;
    }
    resetEffect(node);
  };

  const onTransitionEnd = (e: AnimationEvent) => {
    if (!e || e.animationName !== 'fadeEffect') {
      return;
    }
    resetEffect(e.target as HTMLElement);
  };

  const bindAnimationEvent = (node: HTMLElement) => {
    if (
      !node ||
      !node.getAttribute ||
      node.getAttribute('disabled') !== null ||
      node.className.indexOf('disabled') >= 0
    ) {
      return;
    }
    const handleNodeOnClick = (e: MouseEvent) => {
      // Fix radio button click twice
      if (
        (e.target as HTMLElement).tagName === 'INPUT' ||
        isHidden(e.target as HTMLElement)
      ) {
        return;
      }
      resetEffect(node);
      clickWaveTimeoutId = window.setTimeout(() => {
        handleOnClick(node);
      }, 0);

      // handleOnClick(node);

      raf.cancel(animationStartId);
      animationStart = true;

      // Render to trigger transition event cost 3 frames. Let's delay 10 frames to reset this.
      animationStartId = raf(() => {
        animationStart = false;
      }, 10);
    };
    node.addEventListener('click', handleNodeOnClick, true);
    return {
      cancel: () => {
        node.removeEventListener('click', handleNodeOnClick, true);
      },
    };
  };

  const resetEffect = (node: HTMLElement) => {
    if (!node || !(node instanceof Element)) {
      return;
    }
    const waveClassName = getClassName();
    node.className = node.className.replace(` ${waveClassName}`, '');

    ['transition', 'animation'].forEach((name) => {
      node.removeEventListener(`${name}start`, onTransitionStart as any);
      node.removeEventListener(`${name}end`, onTransitionEnd as any);
    });
  };

  const renderWave = () => {
    const { children } = props;
    if (!React.isValidElement(children)) return children;
    let ref: React.Ref<any> = containerRef;
    if (supportRef(children)) {
      ref = composeRef((children as any).ref, containerRef as any);
    }
    return (
      <IWaveWrap
        data-testid={testid ? `${testid}_wave` : undefined}
        size={size}
      >
        {React.cloneElement(children, { ref } as any)}
      </IWaveWrap>
    );
  };
  return renderWave();
};
