import * as React from 'react';
import {
  Wrapper,
  Line,
  Single,
} from './styled';
import { CustomLine } from './customLine';
import { commonLineMap } from './common';
import type { ICustomLine } from './common' 
import type { ILine } from './common';

const defaultLineColor = '#000';

export type TLineStyle =
  'custom'
  | 'single'
  | 'dotted'
  | 'dashed'
  | 'dotDash'
  | 'dotDotDash'
  | 'double'
  | 'thickThinSmallGap'
  | 'thinThickThinMediumGap'
  | 'dashStroked'
  | 'wave'
  | 'doubleWave'
  | 'dashDotStroked'
  | 'widthDoubleWave'
  | 'widthWave'
  | 'roundDot';


export type TLineWidth = number;

function repeatContent(content?: string, times?: number): string {
  if (!content || !times) return '';
  const res = content.repeat(times);
  return res;
}

export interface ICommonLineProps {
  color?: string; // 线条颜色
  lineType: TLineStyle; // 线条样式 TODO
  length?: number; // 线条总长, 单位：px
  size?: TLineWidth; // 线条粗度, 单位：px
  value?: ICustomLine; // 自定义线段的value, 仅在lineType === 'custom'时有效
}

const ICONFONT_CSS = `
@font-face {
  font-family: 'commonLineIconfont';  /* project id 1679878 */
  src: url('//at.alicdn.com/t/font_1679878_3m8w8kt4k7i.eot');
  src: url('//at.alicdn.com/t/font_1679878_3m8w8kt4k7i.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_1679878_3m8w8kt4k7i.woff2') format('woff2'),
  url('//at.alicdn.com/t/font_1679878_3m8w8kt4k7i.woff') format('woff'),
  url('//at.alicdn.com/t/font_1679878_3m8w8kt4k7i.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_1679878_3m8w8kt4k7i.svg#iconfont') format('svg');
}
`;

const ICONFONT_ID = 'common-line-font-styles';

const inject = (): void => {
  let styleSheet = document.getElementById(ICONFONT_ID);
  if (!styleSheet) {
    styleSheet = document.createElement('style');
    styleSheet.setAttribute('type', 'text/css');
    styleSheet.id = ICONFONT_ID;
    styleSheet.innerHTML = ICONFONT_CSS;
    const head = document.getElementsByTagName('head')[0];
    if (head) {
      head.appendChild(styleSheet);
    }
  }
};

export const CommonLine: React.FC<ICommonLineProps> = (props) => {
  const { size, length, color, lineType: lineStyle, value } = props;

  if (lineStyle === 'custom') {
    return <>{value && <CustomLine value={value} width={length} />}</>;
  }

  // 注入全局 iconfont 样式
  inject();

  let finalColor = color;
  // 解决css color 无auto的问题
  if (color === undefined || color === '' || color === 'auto') {
    finalColor = defaultLineColor;
  }
  let finalLineStyle = lineStyle;
  // 加入最小单位高度, 用于做transform的scale比例计算
  let unitHeight = 8;
  let scale = 1;
  const selectedLine: ILine = commonLineMap[lineStyle];
  let content = commonLineMap.single && commonLineMap.single.content;
  if (selectedLine) {
    content = repeatContent(selectedLine.content, selectedLine.repeat);
    unitHeight = selectedLine.unitHeight;
    scale = selectedLine.scale || 1;
  } else {
    // 如果未找到, 则single 代替
    finalLineStyle = 'single';
  }
  /* eslint-disable */
  return (
    <Wrapper>
      {finalLineStyle === 'single' ? (
        <Single
          length={length}
          bgColor={finalColor}
          size={size}
          color={finalColor}
        />
      ) : (
        <Line
          length={length}
          lineContent={content}
          size={size}
          color={color}
          unitHeight={unitHeight}
          scale={scale}
        />
        )}
    </Wrapper>
  );
};

CommonLine.defaultProps = {
  lineType: 'single',
  size: 1, // 默认值 兼容之前hr颜色和厚度方案
  color: defaultLineColor,
};


