import React, { useCallback, useEffect, useState } from 'react';
import { Button } from '../../../components/button';
import {
  ArrowLeftNormal,
  ArrowRightNormal,
} from '../../../components/icons';
import { Container, ModalText, SplitLine } from './styled';

export interface SwitchProps {
  /**
   * 当前索引
   */
  index: number;

  /**
   * 总数
   */
  count: number;

  /**
   * 类名
   */
  className?: string;

  /**
   * 切换至前一个
   */
  pre?: (index: number) => void;

  /**
   * 切换至后一个
   */
  next?: (index: number) => void;
}

export const SearchSwitch: React.FC<SwitchProps> = (props) => {
  const {
    index = 0,
    count, pre,
    next = 0,
    className } = props;

  const [focusIndex, setIndex] = useState<number>(index);

  useEffect(() => { setIndex(Number(index)); }, [index]);

  /**
   * 切换至前一个
   */
  const handlePre = useCallback((e: React.MouseEvent) => {
    // eslint-disable-next-line no-nested-ternary
    const preIndex = focusIndex - 1 <= 0
      ? count
      : focusIndex - 1;

    setIndex(preIndex);
    pre && pre(preIndex);
    e.preventDefault();
  }, [count, focusIndex, pre]);

  /**
   * 切换至后一个
   */
  const handleNext = useCallback((e: React.MouseEvent) => {
    // eslint-disable-next-line no-nested-ternary
    const nextIndex = focusIndex + 1 > count
      ? (count ? 1 : 0)
      : focusIndex + 1;

    setIndex(nextIndex);
    next && next(nextIndex);
    e.preventDefault();
  }, [count, focusIndex, next]);

  const gray = { color: 'rgba(17,31,44,0.56)' };

  return (
    <Container className={className}>
      <Button
        type="text"
        size="small"
        icon={<ArrowLeftNormal style={!focusIndex ? gray : {}} />}
        tabIndex="-1"
        onMouseDown={handlePre}
      />
      <SplitLine />
      <ModalText style={{ wordBreak: 'keep-all', whiteSpace: 'nowrap' }}>
        <span>{`${focusIndex}/${count}`}</span>
      </ModalText>
      <SplitLine />
      <Button
        type="text"
        size="small"
        icon={<ArrowRightNormal style={focusIndex === count ? gray : {}} />}
        tabIndex="-1"
        onMouseDown={handleNext}
      />
    </Container>
  );
};
