import React, { useCallback } from 'react'
import { classnames } from '../../utils'
import {
  RoundcloseThickNormal,
  ReportFillNormal,
  CloseBetaNormal,
} from '../../components/icons'
import useControlledValue from '../../hooks/useControlledValue'
import {
  AlertWrap,
  MessageWrap,
  DescriptionContent,
  DescriptionText,
  MessageContent,
  ActionWrap,
  AlertRoot,
  MessageText,
  CloseButton,
  IconWrap,
} from './styled'

export type AlertType = 'info' | 'warning' | 'error'

export interface IAlertProps {
  /**
   * 可见性
   */
  visible?: boolean
  /**
   * 默认的可见性
   */
  defaultVisible?: boolean
  /**
   * 消息
   */
  message: string
  /**
   * 详细描述
   */
  description?: string
  /**
   * 类型
   */
  type?: AlertType
  /**
   * 类名
   */
  className?: string
  /**
   * 样式
   */
  style?: React.CSSProperties
  /**
   * 查看详情的文字
   */
  actionText?: string
  /**
   * 点击“查看详情”文字处理函数
   */
  onAction?: () => void | false
  /**
   * 点击关闭按钮处理函数
   */
  onClose?: () => void | false
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string
}

// [向左]TODO：info 类型图标需要等视觉给过来后替换
export const Alert: React.FC<IAlertProps> = React.memo((props) => {
  const {
    message,
    description,
    className,
    type,
    style,
    actionText,
    onAction,
    onClose,
    testid,
  } = props

  // visible 是受控的
  const [visible, setVisible] = useControlledValue(
    props.visible,
    props.defaultVisible
  )

  // 图标
  let TypeIconNode = ReportFillNormal
  if (type === 'warning') {
    TypeIconNode = RoundcloseThickNormal
  } else if (type === 'error') {
    TypeIconNode = RoundcloseThickNormal
  }

  /**
   * 点击查看详情
   */
  const handleAction = useCallback((): void => {
    let autoClose = true
    if (onAction) {
      const returnedValue = onAction()
      autoClose = returnedValue !== false
    }
    if (autoClose) {
      setVisible(false)
    }
  }, [onAction, setVisible])

  /**
   * 点击关闭按钮
   */
  const handleClose = useCallback((): void => {
    let autoClose = true
    if (onClose) {
      const returnedValue = onClose()
      autoClose = returnedValue !== false
    }
    if (autoClose) {
      setVisible(false)
    }
  }, [setVisible, onClose])

  return (
    <AlertRoot
      style={style}
      className={classnames(className, type, { hidden: !visible })}
    >
      <AlertWrap>
        <IconWrap>
          <TypeIconNode className="type-icon" />
        </IconWrap>
        <MessageWrap>
          <MessageContent size={description ? 'large' : 'default'}>
            <MessageText>{message}</MessageText>
          </MessageContent>
          {!!description && (
            <DescriptionContent>
              <DescriptionText>{description}</DescriptionText>
            </DescriptionContent>
          )}
        </MessageWrap>
        {!!actionText && (
          <ActionWrap bordered={false} shape="rect" onClick={handleAction}>
            {actionText}
          </ActionWrap>
        )}
        {
          <CloseButton
            bordered={false}
            shape="rect"
            icon={
              <CloseBetaNormal style={{ color: 'rgba(17, 31, 44, 0.4)' }} />
            }
            onClick={handleClose}
            testid={testid ? `Alert-${testid}` : undefined}
          />
        }
      </AlertWrap>
    </AlertRoot>
  )
})

Alert.defaultProps = {
  type: 'info',
  defaultVisible: true,
}

