// 图片基本组件，图片加载状态以及
import React from 'react';
import styled from 'styled-components';
import { LoadingNormalNormal, CloseBigNormal } from '@ali/we-icon';
export interface IBaseImageProps {
  src: string;
  style?: React.CSSProperties;
  onError?: (src: string, e: React.SyntheticEvent<HTMLImageElement, Event>) => void;
  onLoad?: React.ReactEventHandler<HTMLImageElement>;
}

const Img = styled.img`
  width: 100%;
  opacity: 1;
  background: #fff;
  transition: opacity .3s ease;
`;

const ImgWrapper = styled.div`
  position: relative;
  &.is-unloaded {
    height: 100%;
    ${Img} {
      opacity: 0;
    }
  }
`;

const AlignCenterWrapper = styled.div`
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 0;
`;

const iconStyle: React.CSSProperties = {
  fontSize: 48,
  color: '#fff',
};

const pathColors = ['#fff'];

const BaseImage: React.FC<IBaseImageProps> = (props) => {
  const { src, onError, onLoad, style } = props;
  const [isLoaded, setLoaded] = React.useState(false);
  const [isError, setError] = React.useState(false);

  const handleError = React.useCallback<React.ReactEventHandler<HTMLImageElement>>((e) => {
    setError(true);
    onError && onError(src, e);
  }, []);

  const handleLoad = React.useCallback((e: Parameters<React.ReactEventHandler<HTMLImageElement>>[0]) => {
    setLoaded(true);
    onLoad && onLoad(e);
  }, [onLoad]);

  const content = (isError || !src)
    ? (<AlignCenterWrapper><CloseBigNormal style={iconStyle}/></AlignCenterWrapper>)
    : (
    <React.Fragment>
      <Img src={src} onError={handleError} onLoad={handleLoad} style={style}/>
      {
        !isLoaded && <AlignCenterWrapper><LoadingNormalNormal style={iconStyle} pathColors={pathColors} /></AlignCenterWrapper>
      }
    </React.Fragment>
    );
  return (
    <ImgWrapper className={!isLoaded ? 'is-unloaded' : ''}>
      { content }
    </ImgWrapper>
  );
}

export default BaseImage;
