import React, { memo } from 'react';
import { areEqual } from 'react-window';
import { Checkbox, ICheckboxOption } from '../checkbox';
import {
  CheckboxWrapper,
  CheckboxContent,
  ItemCount,
  ListItemWrapper,
} from './styled';

export interface IListItem extends ICheckboxOption {
  count?: number;
}

export type IListSelectItemProps = {
  /**
   * 传入的数据列表
   */
  data: {
    dataList: IListItem[];
    itemStyle?: React.CSSProperties;
  };
  /**
   * 数组索引
   */
  index: number;
  /**
   * 传入的样式
   */
  style: React.CSSProperties;
  /**
   * 是否正在滚动
   */
  isScrolling?: boolean;
};
export const ListSelectItem: React.FC<IListSelectItemProps> = memo((props) => {
  const { data, index, style } = props;
  const { dataList, itemStyle = {} } = data;
  const item = dataList[index];
  if (!item) {
    return null;
  }
  return (
    <ListItemWrapper style={style}>
      <CheckboxWrapper>
        <Checkbox value={item.value} disabled={item.disabled}>
          <CheckboxContent style={itemStyle}>{item.label}</CheckboxContent>
        </Checkbox>
      </CheckboxWrapper>
      {item.count ? <ItemCount>{item.count}</ItemCount> : null}
    </ListItemWrapper>
  );
}, areEqual);
