import styled from 'styled-components';
import { resetComponent } from '../../utils/styled.mixin';
import { withTheme } from '../theme';

const grayColor = 'rgba(126, 134, 142, 0.24)';
const hoverColor = '#39A3FF';
const activeColor = '#0065D5';

export const RadioInner: any = withTheme(styled.span<any>`
  ${resetComponent}
  display: inline-block;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  border: 2px solid ${grayColor};
  background-color: ${({ theme }) => theme.ui.colorGray1};
  transition: all 0.24s ease-in;
  border-radius: 50%;
  position: relative;
  &.disabled {
    background-color: ${grayColor};
  }
`);

export const RadioWrap = withTheme(styled.label<any>`
  ${resetComponent}
  cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'pointer')};
  display: inline-block;
  ${({ disabled, theme }) => {
    return disabled
      ? ''
      : `
      ${RadioInner} {
        &:hover {
          border-color: rgba(0, 137, 255, 0.48);
        }
        &:active { 
          border-color: ${theme.ui.primaryColor};
        }
      }`;
  }};

  ${({ checked, theme: { ui }, disabled }) => {
    return checked
      ? `
      ${RadioInner} {
        border-color: ${disabled ? grayColor : ui.primaryColor};
        border-width: 4px;
        background-color: ${ui.colorGray1};
      }
      ${
        disabled
          ? ''
          : `
      ${RadioInner} {
        &:hover {
          border-color: ${hoverColor};
        }
        &:active { 
          border-color: ${activeColor};
        }
      }`
      }
    `
      : '';
  }}
`);

export const RadioText = withTheme(styled.span<any>`
  ${resetComponent}
  display: inline-block;
  vertical-align: middle;
  padding: 0 ${({ theme }) => theme.ui.paddingMedium};
  color: ${({ theme }) => theme.ui.commonActionIconColor};
  font-size: ${({ theme }) => theme.ui.fontSizeMenu};
`);

export const RadioContent = withTheme(styled.span<any>`
  ${resetComponent}
  position: relative;
  input {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 0;
    height: 0;
    cursor: pointer;
    opacity: 0;

    &:focus ~ ${RadioInner} {
      border-color: ${({ theme }: any) => theme.ui.primaryColor};
    }
  }
`);

export const RadioGroupWrap = withTheme(styled.div<any>`
  ${resetComponent}
`);
