/* eslint-disable max-lines-per-function */
import React from 'react';
import ReactDOM from 'react-dom';
import { PortalWrapper } from './styled';
import { debounce } from '..';

export * from './hooks/useDragStyle';

export interface IDragAndDropHOChandlers {
  shouldDragElement?: (event: any) => boolean;
}
export interface IDraggableArea {
  top: number;
  right: number;
  bottom: number;
  left: number;
}

export interface IDragAndDropPosition {
  marginTop: number;
  marginRight: number;
}

export interface IDragAndDropHOCProps {
  /**
   * 对话框可拖拽的区域
   */
  draggableArea: IDraggableArea;
  /**
   * 初始坐标位置
   */
  initialPosition: IDragAndDropPosition;
  /**
   * 是否显示查找框
   */
  visible: boolean;
  /**
   * 是否显示发生变化的回调
   */
  onVisibleChange: (visible: boolean) => void;
  /**
   * 拖拽元素的z-index, 默认为auto
   */
  zIndex?: number;
  /**
   * HOC 内部组件的引用
   */
  wrappedRef?: React.RefObject<any>;
  /**
   * 用于测试
   */
  testid?: string;
}

export interface IDragAndDropHOCState {
  dragging: boolean;
}

export const dragAndDropHOC = function <P>(
  WrappedComponent: React.FC<P> | (new (props: P) => React.Component<P, any>),
  handlers: IDragAndDropHOChandlers,
) {
  return class DragAndDrop extends React.PureComponent<
    P & IDragAndDropHOCProps,
    IDragAndDropHOCState
  > {
    startPageX = 0;

    startPageY = 0;

    startMarginTop = 0;

    startMarginRight = 0;

    maxMarginTop = 0;

    maxMarginRight = 0;

    modalRef = React.createRef<HTMLDivElement>();

    constructor(props: P & IDragAndDropHOCProps) {
      super(props);
      this.state = {
        dragging: false,
      };
    }

    componentDidMount() {
      window.addEventListener('resize', this.handleResize);
    }

    componentWillUnmount() {
      document.removeEventListener('mousemove', this.handleDragging);
      document.removeEventListener('mouseup', this.handleDragEnd);
      window.removeEventListener('resize', this.handleResize);
    }

    handleMouseDown = (event: any) => {
      if (!event.target || !event.target.tagName) {
        return;
      }
      if (!handlers.shouldDragElement || handlers.shouldDragElement(event)) {
        this.handleDragStart(event);
      }
    };

    setMax = () => {
      const { draggableArea } = this.props;
      const dom = this.modalRef.current as HTMLElement;
      if (dom) {
        this.startMarginTop = parseInt(dom.style.marginTop, 10) || 0;
        this.startMarginRight = parseInt(dom.style.marginRight, 10) || 0;

        this.maxMarginTop =
          window.innerHeight -
          dom.offsetHeight -
          draggableArea.top -
          draggableArea.bottom;
        this.maxMarginRight =
          window.innerWidth -
          dom.offsetWidth -
          draggableArea.left -
          draggableArea.right;
      }
    };

    // eslint-disable-next-line @typescript-eslint/member-ordering
    handleResize = debounce(() => {
      this.setMax();
      const dom = this.modalRef.current as HTMLElement;
      if (dom && this.props.visible) {
        dom.style.marginTop =
          parseInt(dom.style.marginTop, 10) >= this.maxMarginTop
            ? `${Math.max(this.maxMarginTop, 0)}px`
            : dom.style.marginTop;
        dom.style.marginRight =
          parseInt(dom.style.marginRight, 10) >= this.maxMarginRight
            ? `${Math.max(this.maxMarginRight, 0)}px`
            : dom.style.marginRight;
      }
    }, 300);

    handleDragStart = (event: any) => {
      const { pageX, pageY } = event;
      this.startPageX = pageX;
      this.startPageY = pageY;

      this.setMax();

      document.addEventListener('mousemove', this.handleDragging);
      document.addEventListener('mouseup', this.handleDragEnd);

      this.setState({ dragging: true });
    };

    handleDragging = (event: MouseEvent) => {
      const { dragging } = this.state;
      if (!dragging) {
        return;
      }

      const { pageX, pageY } = event;
      const offsetX = pageX - this.startPageX;
      const offsetY = pageY - this.startPageY;

      const dom = this.modalRef.current as HTMLElement;
      if (dom) {
        const modalMarginTop = this.startMarginTop + offsetY;
        if (modalMarginTop >= 0 && modalMarginTop <= this.maxMarginTop) {
          dom.style.marginTop = `${modalMarginTop}px`;
        }
        const modalMarginRight = this.startMarginRight - offsetX;
        if (modalMarginRight >= 0 && modalMarginRight <= this.maxMarginRight) {
          dom.style.marginRight = `${modalMarginRight}px`;
        }
      }
    };

    handleDragEnd = () => {
      this.startPageX = 0;
      this.startPageY = 0;
      document.removeEventListener('mousemove', this.handleDragging);
      document.removeEventListener('mouseup', this.handleDragEnd);

      this.setState({ dragging: false });
    };

    renderFindAndReplace = () => {
      const {
        draggableArea,
        initialPosition,
        visible,
        zIndex,
        wrappedRef,
        onVisibleChange,
        testid,
      } = this.props;
      const { dragging } = this.state;
      // 设置Modal对话框起始位置(marginTop, marginRight)
      const {
        marginTop: initMarginTop = 0,
        marginRight: initMarginRight = 0,
      } = initialPosition;
      const styleModal = {
        zIndex: (zIndex === undefined ? 'auto' : zIndex) as number | 'auto',
        marginTop: initMarginTop,
        marginRight: initMarginRight,
      };

      return (
        <PortalWrapper
          top={draggableArea.top}
          right={draggableArea.right}
          dragging={dragging}
          onMouseDown={this.handleMouseDown}
          ref={this.modalRef}
          visible={visible}
          style={styleModal}
          data-testid={testid ? `Portal_wrapper_${testid}` : undefined}
        >
          {visible && (
            <WrappedComponent
              {...this.props}
              ref={wrappedRef}
              onClose={() => onVisibleChange(false)}
            />
          )}
        </PortalWrapper>
      );
    };

    render() {
      return ReactDOM.createPortal(this.renderFindAndReplace(), document.body);
    }
  };
};
