import React, { useCallback, useState } from 'react';
import dayjs, { Dayjs } from 'dayjs';
import { Dropdown } from '../../components/dropdown';
import { Input } from '../../components/input';
import { ClockNormal } from '../../components/icons';
import { classnames } from '../../utils';
import { useTranslate } from '@ali/we-util';
import useControlledValue from '../../hooks/useControlledValue';
import {
  Trigger,
  TriggerWrap,
  OverlayWrap,
  OverlayInputs,
  TimePanelWrap,
} from './styled';
import { TimePanel } from './TimePanel';
import useComponentDidUpdate from '../../hooks/useComponentDidUpdate';

export interface ITimePickerProps {
  /**
   * 国际化语言
   */
  locale?: string;
  /**
   * 选择面板是否可见
   */
  visible?: boolean;
  /**
   * 默认是否可见
   */
  defaultVisible?: boolean;
  /**
   * 时间值，Dayjs对象
   */
  value?: Dayjs;
  /**
   * 默认时间值，Dayjs对象
   */
  defaultValue?: Dayjs;
  /**
   * 修改回调
   */
  onChange?: (e: Dayjs) => void;
  /**
   * 时间格式
   */
  format?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 自定义显示区
   */
  renderTrigger?: (e: Dayjs | undefined) => React.ReactElement;
  /**
   * 提示消息
   */
  placeholder?: string;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 可见性变化
   */
  onVisibleChange?: (e: boolean) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 测试使用
   */
  testid?: string;
}

// eslint-disable-next-line max-lines-per-function
const TimePicker: React.FC<ITimePickerProps> = React.memo((props) => {
  const {
    locale,
    renderTrigger,
    disabled,
    format,
    style,
    onVisibleChange,
    onChange,
    zIndex,
    testid,
  } = props;

  const t = useTranslate(locale);

  const [value, setValue] = useControlledValue(props.value, props.defaultValue);

  const [visible, setVisible] = useControlledValue(
    props.visible,
    props.defaultVisible,
  );

  const [inputValue, setInputValue] = useState(
    value ? value.format(format as string) : '',
  );

  const placeholder = props.placeholder || t('we_ui_timepicker_placeholder');

  /**
   * 修改函数
   */
  const handleChange = useCallback(
    (e: Dayjs) => {
      if (value !== e) {
        if (onChange) {
          onChange(e);
        }
        setValue(e);
      }
    },
    [value, onChange, setValue],
  );

  /**
   * 阻止冒泡，Dropdown自动关闭阻止
   *
   * @param {React.MouseEvent<HTMLElement>}
   * @return {void}
   */
  const handleClickOverlay = useCallback((e) => {
    e.stopPropagation();
  }, []);

  /**
   * 输入框输入
   *
   * @param {React.ChangeEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleChangeInput = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setInputValue(e.target.value);
    },
    [],
  );

  /**
   * 输入框失焦
   *
   * @param {React.FocusEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleInputBlur = useCallback(
    (e) => {
      const v = e.target.value.trim();
      // 时间format需要加个虚拟的日期进去
      let timeDate = dayjs(`1992-10-22 ${v}`, format);
      const isValid = timeDate.isValid();
      if (!isValid) {
        setInputValue(value ? value.format(format as string) : '');
      } else {
        if (value) {
          timeDate = timeDate
            .year(value.year())
            .month(value.month())
            .date(value.date());
        }
        setInputValue(timeDate.format(format));
        handleChange(timeDate);
      }
    },
    [format, value, handleChange],
  );

  // 时间输入框的值受控于value和format
  useComponentDidUpdate(() => {
    setInputValue(value ? value.format(format) : '');
  }, [value, format]);

  const handleVisibleChange = useCallback(
    (e: boolean) => {
      if (onVisibleChange) {
        onVisibleChange(e);
      }
      setVisible(e);
    },
    [onVisibleChange, setVisible],
  );

  return (
    <Dropdown
      trigger="click"
      visible={visible}
      zIndex={zIndex}
      onVisibleChange={handleVisibleChange}
      overlay={
        <OverlayWrap onMouseDown={handleClickOverlay}>
          <OverlayInputs>
            <Input
              value={inputValue}
              placeholder={format}
              className="input"
              onChange={handleChangeInput}
              onBlur={handleInputBlur}
              testid={testid}
            />
          </OverlayInputs>
          <TimePanelWrap>
            <TimePanel
              value={value}
              onChange={handleChange}
              format={format}
              testid={testid} 
            />
          </TimePanelWrap>
        </OverlayWrap>
      }
    >
      {typeof renderTrigger === 'function' ? (
        renderTrigger(value)
      ) : (
        <Trigger
          className={classnames({
            placeholder: !value,
            disabled,
          })}
          style={style}
        >
          <TriggerWrap>
            <span className="text">
              {value ? value.format(format) : placeholder}
            </span>
            <ClockNormal className="calendar-icon" testid={testid} />
          </TriggerWrap>
        </Trigger>
      )}
    </Dropdown>
  );
});

TimePicker.defaultProps = {
  format: 'HH:mm:ss',
  defaultVisible: false,
};

export { TimePicker };
