type TParam = string | any;

export default (...args: TParam[]) => {
  const names: string[] = [];
  args.forEach((item) => {
    if (!item) return;
    if ((typeof item === 'string')) {
      names.push(item);
      return;
    }
    Object.keys(item).forEach((k) => {
      if (item[k]) names.push(k);
    });
  });
  return names.join(' ');
};
