import * as React from 'react';
import {
  ArrowLeftNormal,
  ArrowRightNormal,
  ClosedNormal,
} from '../../components/icons';

import {
  isEscHotKey,
  isEnterHotKey,
  isShiftEnterHotKey,
  isComposing,
} from '../../utils';

import { Tabs, TabPane } from '../../components/tabs';

import { TranslateComp } from '@ali/we-util';

import {
  ModalWrapper,
  ModalText,
  ModalCloseButton,
  ModalInput,
  PanelWrapper,
  OperationPanel,
  FocusPanel,
  OperationButtonWrapper,
  Description,
  RowWrapper,
  FlexRowWrapper,
  // FlexItemOperationPanel,
} from './styled';
import { Button } from '../../components/button';
import { TranslateProvider } from '@ali/we-util';

export interface IFindAndReplaceBaseProps {
  /**
   * 默认打开的Tab面板，find or replace
   */
  defaultTabKey: 'find' | 'replace';
  /**
   * 是否只有查找功能(应用于只读模式下)
   */
  isFindOnly: boolean;
  /**
   * 查找的字符串
   */
  findText: string;
  /**
   * 查找输入框 内容变化的回调
   */
  onFindInputChange: (text: string) => void;
  /**
   * 查找到的匹配结果的数量
   */
  matchedCount: number;
  /**
   * 当前高亮的索引, 索引从1开始计数
   */
  focusIndex: number;
  /**
   * 当前高亮的索引发生变化的回调
   */
  onFocusIndexChange: (index: number) => void;
  /**
   * 默认的要替换的字符串
   */
  defaultReplaceText?: string;
  /**
   * 替换当前查找项
   */
  onReplaceCurrent: (text: string) => void;
  /**
   * 替换所有查找项的方法
   */
  onReplaceAll: (text: string) => void;
  /**
   * 激活的 tabKey
   */
  activeTabKey?: 'find' | 'replace';
  /**
   * 光标是否focus到查找对话框
   */
  focused?: boolean;
  /**
   * 本地语言类型，如zh-CN
   */
  locale?: string;
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string;
  /**
   * 面板的 DOM 节点引用
   */
  domRef?: React.RefObject<Element>;
  /**
   * 禁用替换按钮
   */
  disableReplace?: boolean;
  /**
   * 禁用替换全部按钮
   */
  disableReplaceAll?: boolean;
  /**
   * 点击 tab 后的回调
   */
  onTabClick?: (nextKey: 'find' | 'replace') => void;
}

interface IFindAndReplaceProps extends IFindAndReplaceBaseProps {
  /**
   * 关闭时的回调函数
   */
  onClose?: () => void;
}

interface IState {
  replaceText: string;
}

export class FindAndReplace extends React.Component<
  IFindAndReplaceProps,
  IState
> {
  inputRef = React.createRef<HTMLInputElement>();

  constructor(props: IFindAndReplaceProps) {
    super(props);
    this.state = {
      replaceText: props.defaultReplaceText || '',
    };
  }

  handleReplaceCurrent = () => {
    const { onReplaceCurrent } = this.props;
    const { replaceText = '' } = this.state;
    if (onReplaceCurrent) {
      onReplaceCurrent(replaceText);
    }
  };

  handleReplaceAll = () => {
    const { onReplaceAll } = this.props;
    const { replaceText = '' } = this.state;
    if (onReplaceAll) {
      onReplaceAll(replaceText);
    }
  };

  handleFindInputChange = (event: any) => {
    const { value: text = '' } = event.target;
    const { onFindInputChange } = this.props;
    if (onFindInputChange) {
      onFindInputChange(text);
    }
  };

  handleClose = () => {
    const { onClose, onFindInputChange } = this.props;
    onClose && onClose();
    onFindInputChange('');
    this.setState({ replaceText: '' });
  };

  handleFindInputKeyDown = (event: any) => {
    const { onFocusIndexChange, focusIndex } = this.props;
    if (isShiftEnterHotKey(event)) {
      onFocusIndexChange(focusIndex - 1);
    } else if (
      !isComposing(event) &&
      isEnterHotKey(event) &&
      onFocusIndexChange
    ) {
      onFocusIndexChange(focusIndex + 1);
    } else if (isEscHotKey(event)) {
      this.handleClose();
    }
  };

  handleFocusPrevMatchedItem = () => {
    const { onFocusIndexChange, focusIndex } = this.props;
    if (onFocusIndexChange) {
      onFocusIndexChange(focusIndex - 1);
    }
  };

  handleFocusNextMatchedItem = () => {
    const { onFocusIndexChange, focusIndex } = this.props;
    if (onFocusIndexChange) {
      onFocusIndexChange(focusIndex + 1);
    }
  };

  handleReplaceInputChange = (event: any) => {
    const { value: text = '' } = event.target;
    this.setState({ replaceText: text });
  };

  handleReplaceInputKeyDown = (event: any) => {
    const { onReplaceCurrent, disableReplace } = this.props;
    const { replaceText } = this.state;
    if (
      !isComposing(event) &&
      isEnterHotKey(event) &&
      !disableReplace &&
      onReplaceCurrent
    ) {
      onReplaceCurrent(replaceText);
    } else if (isEscHotKey(event)) {
      this.handleClose();
    }
  };

  focusInput = () => {
    if (this.inputRef.current) {
      this.inputRef.current.focus();
    }
  };

  selectInput = () => {
    if (this.inputRef.current) {
      this.inputRef.current.select();
    }
  };

  renderFocusIndex = () => {
    const {
      matchedCount,
      focusIndex,
      testid,
      // locale,
    } = this.props;
    return (
      <FocusPanel>
        <Button
          shape="circle"
          disabled={!matchedCount}
          onClick={this.handleFocusPrevMatchedItem}
          icon={<ArrowLeftNormal />}
          tabIndex="-1"
          testid={testid ? `ArrowLeftNormal-${testid}` : undefined}
        />
        <ModalText style={{ wordBreak: 'keep-all', whiteSpace: 'nowrap' }}>
          {matchedCount > 0 ? (
            <span>{`${focusIndex}/${matchedCount}`}</span>
          ) : (
            // : <TranslateComp locale={locale} textKey={'we_ui_find_and_replace_result'} />
            <span>0/0</span>
          )}
        </ModalText>
        <Button
          shape="circle"
          disabled={!matchedCount}
          onClick={this.handleFocusNextMatchedItem}
          icon={<ArrowRightNormal />}
          tabIndex="-1"
          testid={testid ? `ArrowRightNormal-${testid}` : undefined}
        />
      </FocusPanel>
    );
  };

  renderFindPanel = () => {
    const { findText } = this.props;

    return (
      <PanelWrapper>
        <FlexRowWrapper>
          <TranslateProvider>
            {(t) => (
              <ModalInput
                autoFocus
                data-testid="J_findValueInput"
                ref={this.inputRef}
                value={findText}
                onChange={this.handleFindInputChange}
                onKeyDown={this.handleFindInputKeyDown}
                placeholder={t('we_ui_find_and_replace_find_placeholder')}
              />
            )}
          </TranslateProvider>
        </FlexRowWrapper>
        <OperationPanel>{this.renderFocusIndex()}</OperationPanel>
      </PanelWrapper>
    );
  };

  renderReplacePanel = () => {
    const {
      findText,
      locale,
      matchedCount,
      disableReplace = false,
      disableReplaceAll = false,
    } = this.props;
    const { replaceText } = this.state;

    return (
      <PanelWrapper>
        <Description>
          <TranslateComp
            locale={locale}
            textKey={'we_ui_find_and_replace_find_description'}
          />
        </Description>
        <TranslateProvider>
          {(t) => (
            <ModalInput
              autoFocus
              data-testid="J_findValueInput"
              ref={this.inputRef}
              value={findText}
              onChange={this.handleFindInputChange}
              onKeyDown={this.handleFindInputKeyDown}
              placeholder={t('we_ui_find_and_replace_find_placeholder')}
            />
          )}
        </TranslateProvider>
        <RowWrapper>
          <Description>
            <TranslateComp
              locale={locale}
              textKey={'we_ui_find_and_replace_replace_description'}
            />
          </Description>
          <TranslateProvider>
            {(t) => (
              <ModalInput
                data-testid="J_replaceValueInput"
                value={replaceText}
                onChange={this.handleReplaceInputChange}
                onKeyDown={this.handleReplaceInputKeyDown}
                placeholder={t('we_ui_find_and_replace_replace_placeholder')}
              />
            )}
          </TranslateProvider>
        </RowWrapper>
        <OperationPanel>
          <OperationButtonWrapper>
            <Button
              shape="round"
              disabled={matchedCount === 0 || disableReplace}
              testid="J_replaceOne"
              onClick={this.handleReplaceCurrent}
            >
              <TranslateComp
                locale={locale}
                textKey={'we_ui_find_and_replace_replace'}
              />
            </Button>
            <Button
              shape="round"
              disabled={matchedCount === 0 || disableReplaceAll}
              style={{ marginLeft: 10 }}
              testid="J_replaceAll"
              onClick={this.handleReplaceAll}
            >
              <TranslateComp
                locale={locale}
                textKey={'we_ui_find_and_replace_replace_all'}
              />
            </Button>
          </OperationButtonWrapper>
          {this.renderFocusIndex()}
        </OperationPanel>
      </PanelWrapper>
    );
  };

  render() {
    const {
      activeTabKey,
      focused = false,
      isFindOnly,
      locale,
      defaultTabKey,
      testid,
      domRef,
      onTabClick,
    } = this.props;

    return (
      <ModalWrapper focused={focused} ref={domRef}>
        <Tabs
          defaultActiveKey={defaultTabKey}
          activeKey={activeTabKey}
          onTabClick={onTabClick as (nextKey: string) => void}
        >
          <TabPane
            tab={
              <TranslateComp
                locale={locale}
                textKey={'we_ui_find_and_replace_find'}
              />
            }
            key="find"
          >
            {this.renderFindPanel()}
          </TabPane>
          {isFindOnly ? null : (
            <TabPane
              tab={
                <TranslateComp
                  locale={locale}
                  textKey={'we_ui_find_and_replace_replace'}
                />
              }
              key="replace"
            >
              {this.renderReplacePanel()}
            </TabPane>
          )}
        </Tabs>
        <ModalCloseButton onMouseDown={this.handleClose}>
          <ClosedNormal testid={testid} />
        </ModalCloseButton>
      </ModalWrapper>
    );
  }
}
