import React from 'react';
import { NewsWrapper, DOT_CLASS_NAME, TEXT_CLASS_NAME } from './styled';

interface INewBadge {
  type?: 'dot' | 'text';
  style?: React.CSSProperties;
  // size: 'small';
}

export const NewBadge = React.forwardRef<HTMLDivElement,INewBadge>((props, ref) => {
  const {
    type,
    style,
    // size = 'small',
  } = props;
  return type === 'dot'
    ? <NewsWrapper style={style} className={DOT_CLASS_NAME} ref={ref}/>
    : <NewsWrapper style={style} className={TEXT_CLASS_NAME} ref={ref}><span>NEW</span></ NewsWrapper>
});

NewBadge.defaultProps = {
  type: 'dot'
}
