import React from 'react';
import { Switch } from '../switch';

import { CheckboxWrapper, CheckboxTitle } from './styled';
import type { ICheckboxLayoutItem } from './types';

const ExtraCheckbox: React.FC<ICheckboxLayoutItem> = (props) => {
  const { title, defaultValue = false, onValueChange, value } = props;
  return (
    <CheckboxWrapper>
      <CheckboxTitle>{title}</CheckboxTitle>
      <Switch
        defaultChecked={defaultValue}
        onChange={onValueChange}
        checked={value}
      />
    </CheckboxWrapper>
  );
};

export default ExtraCheckbox;
