import React from 'react';
import classnames from 'classnames';
import { useTranslate } from '@ali/we-util';
import { SpinNetworkErrorIcon } from './icons';
import {
  SpinErrorWrapper,
  SpinErrorIcon,
  SpinErrorTitle,
  SpinErrorTip,
  SpinErrorRefresh,
  SpinErrorInlineWrapper,
  SpinErrorInlineRightWrapper,
  SpinErrorText,
} from './styled';
import { ISpinErrorCardProps, ISpinErrorInlineCardProps } from './interface';

export const SpinErrorCard = (props: ISpinErrorCardProps) => {
  const {
    style,
    icon,
    highlightIcon,
    title,
    tip,
    onRefresh,
    transparent,
    refreshPlacement = 'right',
    onClick,
    testid,
    role,
  } = props;
  const t = useTranslate('');
  const refreshText = t('we_ui_refresh');
  const showRefreshAtRight = onRefresh && refreshPlacement === 'right';
  return (
    <SpinErrorWrapper
      style={style}
      transparent={transparent}
      onClick={onClick}
      data-testid={testid}
      data-role={role}
    >
      <SpinErrorIcon
        className={classnames({
          'highlight-icon': highlightIcon,
        })}
      >
        {icon || <SpinNetworkErrorIcon />}
      </SpinErrorIcon>
      {title && <SpinErrorTitle>{title}</SpinErrorTitle>}
      {(tip || showRefreshAtRight) && (
        <SpinErrorTip>
          {tip && (
            <SpinErrorText
              className={classnames({
                'with-r-gap': showRefreshAtRight,
              })}
            >
              {tip}
            </SpinErrorText>
          )}
          {showRefreshAtRight && (
            <SpinErrorRefresh onClick={onRefresh}>
              {refreshText}
            </SpinErrorRefresh>
          )}
        </SpinErrorTip>
      )}
      {onRefresh && refreshPlacement === 'bottom' ? (
        <SpinErrorRefresh onClick={onRefresh}>{refreshText}</SpinErrorRefresh>
      ) : null}
    </SpinErrorWrapper>
  );
};

export const SpinErrorInlineCard = (props: ISpinErrorInlineCardProps) => {
  const {
    style,
    icon,
    title,
    tip,
    onRefresh,
    transparent,
    highlightIcon,
    onClick,
    testid,
    role,
  } = props;
  const t = useTranslate('');
  const refreshText = t('we_ui_refresh');
  return (
    <SpinErrorInlineWrapper
      style={style}
      transparent={transparent}
      onClick={onClick}
      data-testid={testid}
      data-role={role}
    >
      {icon && (
        <SpinErrorIcon
          className={classnames({
            'highlight-icon': highlightIcon,
          })}
          style={{ marginRight: 8 }}
        >
          {icon}
        </SpinErrorIcon>
      )}
      <SpinErrorInlineRightWrapper>
        {title && (
          <SpinErrorTitle style={{ marginTop: 0 }}>{title}</SpinErrorTitle>
        )}
        {(tip || onRefresh) && (
          <SpinErrorTip style={{ margin: !title ? 0 : '4px 0 0 0' }}>
            {tip && (
              <SpinErrorText
                className={classnames({
                  'with-r-gap': onRefresh,
                })}
              >
                {tip}
              </SpinErrorText>
            )}
            {onRefresh && (
              <SpinErrorRefresh onClick={onRefresh}>
                {refreshText}
              </SpinErrorRefresh>
            )}
          </SpinErrorTip>
        )}
      </SpinErrorInlineRightWrapper>
    </SpinErrorInlineWrapper>
  );
};

SpinErrorCard.defaultProps = {
  refreshPlacement: 'right',
};
