import React, { PureComponent, ReactElement } from 'react';
import warning from 'warning';
import { isMobile } from '../../utils/dom';
import { Table, Title, TableWrapper } from './styled';

export interface ITablePanelProps {
  /**
   * 选择器的总行数
   */
  row?: number;
  /**
   * 选择器的总列数
   */
  col?: number;
  /**
   * 选中行数
   */
  selectedRow?: number;
  /**
   * 默认选中列数
   */
  selectedCol?: number;
  /**
   * 变更行列触发
   */
  onChange?: (row: number, col: number) => void;
  /**
   * 选择 row行 col列 后的回调函数
   */
  onPick?: (row: number, col: number) => void;
}

export class TablePanel extends PureComponent<ITablePanelProps> {
  // eslint-disable-next-line react/static-property-placement
  static defaultProps = {
    row: 10,
    col: 10,
    selectedRow: 0,
    selectedCol: 0,
  };

  // eslint-disable-next-line react/state-in-constructor
  state = { currentRow: 0, currentCol: 0 };

  constructor(props: ITablePanelProps) {
    super(props);
    this.state = {
      currentRow: props.selectedRow || 0,
      currentCol: props.selectedCol || 0,
    };
  }

  onMouseLeave = () => {
    this.setState({ currentRow: 0, currentCol: 0 });
  };

  pick = () => {
    const { currentRow, currentCol } = this.state;
    // eslint-disable-next-line react/destructuring-assignment
    this.props.onPick?.(currentRow, currentCol);
  };

  render() {
    const { row, col, selectedCol, selectedRow, onChange } = this.props;
    const currentRow = selectedRow || this.state.currentRow;
    const currentCol = selectedCol || this.state.currentCol;

    if (!row || !col) {
      warning('Please define the row and col properties.');
      return null;
    }

    const vRows: Array<ReactElement<any>> = [];
    for (let r = 0; r < row; r += 1) {
      const vCols: Array<ReactElement<any>> = [];
      for (let c = 0; c < col; c += 1) {
        const props = {
          key: `row-${r}::col-${c}`,
          className: r < currentRow && c < currentCol ? 'active' : '',
          onMouseEnter: () => {
            this.setState({ currentRow: r + 1, currentCol: c + 1 });
            onChange?.(r + 1, c + 1);
          },
        };
        vCols.push(<td {...props} />);
      }
      vRows.push(<tr key={`row-${r}`}>{vCols}</tr>);
    }

    return (
      <TableWrapper>
        {!isMobile && <Title>{`${currentRow} × ${currentCol}`}</Title>}
        <Table
          onMouseLeave={this.onMouseLeave}
          onClick={this.pick}
          data-testid="table-TableSet"
        >
          <tbody>{vRows}</tbody>
        </Table>
      </TableWrapper>
    );
  }
}
