import React, { Context } from 'react';
import { ISelectContextValue, ISelectValue, IMultiSelectContextValue } from './interface';

let executed = false;
let context: any;

// 为了传人VT，需要写成函数的形式，但是应该是单例
export const createSelectContext = <VT extends ISelectValue = ISelectValue>() => {
  if (!executed) {
    // eslint-disable-next-line @typescript-eslint/consistent-type-assertions
    context = React.createContext<ISelectContextValue<VT>>({} as ISelectContextValue<VT>);
    executed = true;
  }
  return context as Context<ISelectContextValue<VT>>;
};

export const createMultiSelectContext = () => {
  if (!executed) {
    // eslint-disable-next-line @typescript-eslint/consistent-type-assertions
    context = React.createContext<IMultiSelectContextValue>({} as IMultiSelectContextValue);
    executed = true;
  }
  return context as Context<IMultiSelectContextValue>;
};
