import { useCallback, useState, useRef, CSSProperties, useMemo, useEffect } from 'react';
import { debounce } from 'lodash-es';

interface IUseDragParams {
  /**
   * 获取拖拽时候要移动的dom
   */
  getDragDom?: () => HTMLElement | undefined | null;
  /**
   * 指定拖拽的范围
   */
  dragArea?: { top?: number, right?: number, bottom?: number, left?: number }
  /**
   * 是否作为noop进行（比如暂时禁用拖拽， 节省计算资源）
   */
  noopOnly?: boolean;
  /**
   * 自动修改dom的style
   */
  autoChangeDom?: boolean;
}

const noop = () => undefined;
const getDefaultDragRef = () => ({
  startTop: 0, startRight: 0,
  domStartTop: 0, domStartRight: 0,
  maxTop: 0, maxRight: 0,
  endTop: 0, endRight: 0,
  dragging: false,
});

export const useDragStyle = (params: IUseDragParams) => {

  const {
    getDragDom = noop, dragArea, noopOnly = false, autoChangeDom = false,
  } = params;

  const dragRef = useRef(getDefaultDragRef());

  const [endPoint, setEndPoint] = useState([undefined, undefined] as [number | undefined, number | undefined]);

  const setDragArea = useCallback(() => {
    if (noopOnly) { return; }
    const dragDom  = getDragDom();
    if (!dragDom) { return; }
    const { top = 0, right = 0, bottom = 0, left = 0 } = dragArea || {};
    dragRef.current.maxTop = window.innerHeight - dragDom.offsetHeight - top - bottom;
    dragRef.current.maxRight = window.innerWidth - dragDom.offsetWidth - left - right;
  }, [dragArea, getDragDom, noopOnly]);

  const handleDragging = useCallback((event: MouseEvent) => {
    if (!dragRef.current.dragging || noopOnly) {
      return;
    }

    let { endTop, endRight } = dragRef.current;

    const { pageX, pageY } = event
    const offsetX = pageX - dragRef.current.startRight;
    const offsetY = pageY - dragRef.current.startTop;

    const modalMarginTop =  dragRef.current.domStartTop + offsetY;
    if (modalMarginTop >= 0 && modalMarginTop <= dragRef.current.maxTop) {
      endTop = modalMarginTop;
    }
    const modalMarginRight = dragRef.current.domStartRight - offsetX;
    if (modalMarginRight >= 0 && modalMarginRight <= dragRef.current.maxRight) {
      endRight = modalMarginRight;
    }
    setEndPoint([endTop, endRight]);
    dragRef.current.endTop = endTop;
    dragRef.current.endRight = endRight;
  }, [noopOnly]);

  const handleDragEnd = useCallback(() => {
    if (noopOnly) { return; }
    dragRef.current.endTop = 0;
    dragRef.current.endRight = 0;
    document.removeEventListener('mousemove', handleDragging);
    document.removeEventListener('mouseup', handleDragEnd);

    dragRef.current.dragging = false;
  }, [noopOnly]);


  const handleDragStart = useCallback((e: React.MouseEvent) => {
    const dragDom = getDragDom();
    if (!dragDom || noopOnly) {
      return;
    }

    const { pageX, pageY } = e;
    
    dragRef.current.startTop = pageY;
    dragRef.current.startRight = pageX;

    const domStartTop = dragDom.offsetTop;
    const domStartEnd = window.innerWidth - dragDom.offsetLeft - dragDom.offsetWidth;

    dragRef.current.domStartTop = domStartTop;
    dragRef.current.domStartRight = domStartEnd;
    setEndPoint([domStartTop, domStartEnd]);

    setDragArea();
    dragRef.current.dragging = true;

    document.addEventListener('mousemove', handleDragging);
    document.addEventListener('mouseup', handleDragEnd);
    
  }, [setDragArea, getDragDom]);

  const handleResize = useCallback(debounce(() => {
    if (noopOnly) { return; }
    setDragArea();
    const { maxRight, maxTop, endRight, endTop } = dragRef.current;
    if (endRight >= maxRight) { dragRef.current.endRight = Math.max(maxRight, 0) }
    if (endTop >= maxTop) { dragRef.current.endTop = Math.max(maxTop, 0) }
    dragRef.current.endTop = endTop;
    dragRef.current.endRight = endRight;
    setEndPoint([endTop, endRight]);
  }, 300), [setDragArea, noopOnly]);

  const reset = useCallback(() => {
    setEndPoint([undefined, undefined]);
    dragRef.current = getDefaultDragRef();
  }, []);

  useEffect(() => {
    if (noopOnly) { return; }
    window.addEventListener('resize', handleResize);
    return () => {
      document.removeEventListener('mousemove', handleDragging);
      document.removeEventListener('mouseup', handleDragEnd);
      window.removeEventListener('resize', handleResize);
    }
  }, [noopOnly]);

  const wrapperStyle: CSSProperties = useMemo(() => {
    if (noopOnly) { return {}; }
    const dragDom = getDragDom();
    if (dragDom && typeof endPoint[0] === 'number' && typeof endPoint[1] === 'number') {
      const style = {
        position: 'fixed',
        top: endPoint[0],
        right: endPoint[1],
      };
      if (autoChangeDom) {
        dragDom.style.position = style.position;
        dragDom.style.top = `${style.top}px`;
        dragDom.style.right = `${style.right}px`;
      }
      return style;
    }
    return {};

  }, [endPoint, getDragDom, noopOnly, autoChangeDom]);

  return {
    reset,
    handleDragStart,
    wrapperStyle,
  };
};