/* eslint-disable react/no-find-dom-node */
import React, { useCallback, useRef, useState } from 'react';
import useControlledValue from '../../hooks/useControlledValue';
import { KEY_CODES } from '../../utils/consts';
import { supportRef, composeRef } from '../../utils/ref';
import { findDOMNode, elementContain } from '../../utils/dom';
import { Popup } from './Popup';
import { Tooltip } from '../tooltip';

const DELAY_TO_HIDE = 200;

export type ITriggerAction = 'hover' | 'click' | 'focus' | 'contextMenu';

export type IPlacement =
  | 'top'
  | 'bottom'
  | 'left'
  | 'right'
  | 'topLeft'
  | 'bottomLeft'
  | 'topRight'
  | 'bottomRight'
  | 'leftTop'
  | 'leftBottom'
  | 'rightTop'
  | 'rightBottom';

export interface ITriggerProps {
  /**
   * 触发动作
   */
  action: ITriggerAction[];
  overlay: React.ReactElement | null;
  type?: 'popup' | 'popover';
  children?: React.ReactElement;
  getContainer?: () => HTMLElement;
  onVisibleChange?: (e: boolean) => void;
  placement?: IPlacement;
  visible?: boolean;
  defaultVisible?: boolean;
  overlayStyle?: React.CSSProperties;
  zIndex?: number;
  offsetX?: number;
  offsetY?: number;
  pageX?: number;
  pageY?: number;
  maskColor?: string;
  overlayClassName?: string;
  backgroundColor?: string;
  mask?: boolean;
  /**
   * trigger节点手动传入
   */
  triggerNode?: HTMLElement;
  /**
   * tooltip消息
   */
  title?: string | React.ReactNode;
  /**
   * 延时出现，默认值 = 0
   */
  delayShow?: number
  /**
   * 延时隐藏，默认值 = 200
   */
  delayHide?: number;
  /**
   * 延迟销毁
   */
  delayDestroy?: number;
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string;
  /**
   * 禁止根据视口自适应展开位置
   */
  disableDomAlign?: boolean;
  /**
   * 监听overlay的宽高变化自适应位置
   */
  overlayAutoAlign?: boolean;
  /**
   * overlayAutoAlign 执行延时
   * 默认 100ms
   */
  overlayAutoAlignDelay?: number;
  /**
   * 定义角色
   */
  role?: string;
}

// eslint-disable-next-line max-lines-per-function
export const Trigger: React.FC<ITriggerProps> = React.memo((props) => {
  const {
    type,
    children,
    overlay,
    action,
    onVisibleChange,
    getContainer,
    placement,
    overlayStyle,
    zIndex,
    offsetX,
    offsetY,
    pageX,
    pageY,
    overlayClassName,
    mask,
    triggerNode,
    title,
    testid,
    delayHide = DELAY_TO_HIDE,
    delayShow,
    delayDestroy,
    backgroundColor,
    maskColor,
    disableDomAlign,
    overlayAutoAlign,
    overlayAutoAlignDelay,
    role = '',
  } = props;

  const triggerRef = useRef<React.ReactInstance | HTMLElement | null>(null);
  const [visible, setVisible] = useControlledValue(
    props.visible,
    props.defaultVisible,
  );

  const coord = useRef({ x: 0, y: 0 }).current;
  const [isTriggerFocused, setTriggerFocuesed] = useState(false);

  const hasFocusAction = action.includes('focus');
  const hasHoverAction = action.includes('hover');
  const hasClickAction = action.includes('click');
  const hasContextMenuAction = action.includes('contextMenu');

  const child = children
    ? (React.Children.only(children) as React.ReactElement)
    : null;

  const cloneProps: any = {};

  const {
    onFocus,
    onBlur,
    onMouseEnter,
    onMouseLeave,
    onClick,
    onContextMenu,
    onKeyDown,
  } = (child && child.props) || {};

  const timerEnter = useRef(0);
  const timerLeave = useRef(0);
  const isEntered = useRef({ trigger: false, overlay: false }).current;

  const getTriggerNode = useCallback((): HTMLElement | undefined => {
    if (triggerRef.current) {
      return findDOMNode(triggerRef.current);
    }
    return triggerNode;
  }, [triggerNode]);

  /**
   * visible变化
   *
   * @param {boolean}
   * @return {void}
   */
  const handleVisibleChange = useCallback(
    (e: boolean) => {
      if (visible !== e) {
        if (onVisibleChange) {
          onVisibleChange(e);
        }
        setVisible(e);
      }
    },
    [onVisibleChange, setVisible, visible],
  );

  /**
   * 清除进入的定时器
   *
   * @param {void}
   */
  const handleClearTimerEnter = useCallback(() => {
    if (timerEnter.current) {
      window.clearTimeout(timerEnter.current);
      timerEnter.current = 0;
    }
  }, []);

  /**
   * 清除离开的定时器
   *
   * @param {void}
   */
  const handleClearTimerLeave = useCallback(() => {
    if (timerLeave.current) {
      window.clearTimeout(timerLeave.current);
      timerLeave.current = 0;
    }
  }, []);

  /**
   * 进入trigger/overlay
   *
   * @return void
   */
  const handleEnter = useCallback(() => {
    handleClearTimerEnter();
    handleClearTimerLeave();
    const newVisible = isEntered.trigger || isEntered.overlay;
    if (newVisible && !visible) {
      if (delayShow) {
        timerEnter.current = window.setTimeout(() => {
          handleVisibleChange(newVisible);
        }, delayShow);
      } else {
        handleVisibleChange(newVisible);
      }
    }
  }, [
    isEntered,
    handleClearTimerEnter,
    handleClearTimerLeave,
    handleVisibleChange,
    visible,
    delayShow,
  ]);

  /**
   * 离开trigger/overlay
   *
   * @return void
   */
  const handleLeave = useCallback(() => {
    handleClearTimerEnter();
    handleClearTimerLeave();

    const newVisible = isEntered.trigger || isEntered.overlay;
    if (!newVisible && visible) {
      if (delayHide) {
        timerLeave.current = window.setTimeout(() => {
          handleVisibleChange(false);
        }, DELAY_TO_HIDE);
      } else {
        handleVisibleChange(false);
      }
    }
  }, [
    handleClearTimerEnter,
    handleClearTimerLeave,
    isEntered,
    visible,
    handleVisibleChange,
    delayHide,
  ]);

  /**
   * 进入trigger
   *
   * @param {React.MouseEvent<HTMLElement>} e
   * @return {void}
   */
  const handleEnterTrigger = useCallback(
    (e: React.MouseEvent<HTMLElement>): void => {
      const tNode = getTriggerNode();
      if (tNode) {
        // 等于或者包含
        if (elementContain(tNode, e.target as HTMLElement)) {
          onMouseEnter && onMouseEnter(e);
          isEntered.trigger = true;
          handleEnter();
        }
      }
    },
    [onMouseEnter, isEntered, handleEnter, getTriggerNode],
  );

  /**
   * 聚焦
   *
   * @param {React.FocusEvent<HTMLElement>} e
   * @return {void}
   */
  const handleTriggerFocus = useCallback(
    (e: React.FocusEvent<HTMLElement>) => {
      const tNode = getTriggerNode();
      if (tNode) {
        // 等于或者包含
        if (elementContain(tNode, e.target as HTMLElement)) {
          onFocus && onFocus(e);
          setTriggerFocuesed(true);
        }
      }
    },
    [onFocus, getTriggerNode],
  );

  /**
   * 失焦
   *
   * @param {React.FocusEvent<HTMLElement>} e
   * @return {void}
   */
  const handleTriggerBlur = useCallback(
    (e: React.FocusEvent<HTMLElement>) => {
      const tNode = getTriggerNode();
      if (tNode) {
        // 等于或者包含
        if (elementContain(tNode, e.target as HTMLElement)) {
          onBlur && onBlur(e);
          setTriggerFocuesed(false);
          if (visible) {
            handleVisibleChange(false);
          }
        }
      }
    },
    [onBlur, handleVisibleChange, visible, getTriggerNode],
  );

  /**
   * 离开trigger
   *
   * @param {React.MouseEvent<HTMLElement>} e
   * @return {void}
   */
  const handleLeaveTrigger = useCallback(
    (e: React.MouseEvent<HTMLElement>) => {
      const tNode = getTriggerNode();
      if (tNode) {
        // 等于或者包含
        if (elementContain(tNode, e.target as HTMLElement)) {
          onMouseLeave && onMouseLeave(e);
          isEntered.trigger = false;
          handleLeave();
        }
      }
    },
    [onMouseLeave, isEntered, handleLeave, getTriggerNode],
  );

  /**
   * 进入overlay
   *
   * @return {void}
   */
  const handleEnterOverlay = useCallback(() => {
    isEntered.overlay = true;
    handleEnter();
  }, [handleEnter, isEntered]);

  /**
   * 离开overlay
   *
   * @return {void}
   */
  const handleLeaveOverlay = useCallback(() => {
    isEntered.overlay = false;
    handleLeave();
  }, [handleLeave, isEntered]);

  /**
   * 点击overlay外需要收起
   */
  const handleClickOverlayOuter = useCallback(
    (e: MouseEvent) => {
      const tNode = getTriggerNode();
      if (tNode) {
        // 等于或者包含
        if (!elementContain(tNode, e.target as HTMLElement)) {
          handleVisibleChange(false);
        }
      } else {
        handleVisibleChange(false);
      }
    },
    [handleVisibleChange, getTriggerNode],
  );

  /**
   * 点击trigger
   *
   * @param {React.MouseEvent<HTMLElement>}
   * @return {void}
   */
  const handleClickTrigger = useCallback(
    (e: React.MouseEvent<HTMLElement>) => {
      const tNode = getTriggerNode();
      if (tNode) {
        // 等于或者包含
        if (elementContain(tNode, e.target as HTMLElement)) {
          if (onClick) {
            const rst = onClick(e);
            if (rst === false) {
              return false;
            }
          }
          handleVisibleChange(!visible);
        }
      }
    },
    [onClick, visible, handleVisibleChange, getTriggerNode],
  );

  /**
   * 右键惨淡展开
   *
   * @param {React.MouseEvent<HTMLElement>}
   * @return {void}
   */
  const handleContextMenuTrigger = useCallback(
    (e: React.MouseEvent<HTMLElement>) => {
      const tNode = getTriggerNode();
      if (tNode) {
        // 等于或者包含
        if (elementContain(tNode, e.target as HTMLElement)) {
          e.preventDefault();
          if (onContextMenu) {
            onContextMenu(e);
          }
          if (tNode.getBoundingClientRect) {
            const clientRect = tNode.getBoundingClientRect();
            coord.x = e.clientX - clientRect.left;
            coord.y = e.clientY - clientRect.top - clientRect.height;
          } else {
            coord.x = e.clientX;
            coord.y = e.clientY;
          }

          handleVisibleChange(!visible);
        }
      }
    },
    [onContextMenu, handleVisibleChange, getTriggerNode, visible, coord],
  );

  /**
   * 遮罩
   */
  const handleClickMask = useCallback(() => {
    handleVisibleChange(false);
  }, [handleVisibleChange]);

  const handleKeyDown = useCallback(
    (e: React.KeyboardEvent) => {
      if (onKeyDown) {
        onKeyDown(e);
      }
      if (isTriggerFocused) {
        // 方向键展开
        if (
          [
            KEY_CODES.UP,
            KEY_CODES.DOWN,
            KEY_CODES.LEFT,
            KEY_CODES.RIGHT,
          ].includes(e.which)
        ) {
          e.preventDefault();
          if (!visible) {
            handleVisibleChange(true);
          }
        }
        // ESC 关闭
        if (e.which === KEY_CODES.ESC) {
          e.preventDefault();
          if (visible) {
            handleVisibleChange(false);
          }
        }
      }
    },
    [isTriggerFocused, handleVisibleChange, visible, onKeyDown],
  );

  if (child && supportRef(child)) {
    cloneProps.ref = composeRef(triggerRef, (child as any).ref);
  }

  if (hasHoverAction) {
    cloneProps.onMouseEnter = handleEnterTrigger;
    cloneProps.onMouseLeave = handleLeaveTrigger;
  } else {
    cloneProps.onMouseEnter = onMouseEnter;
    cloneProps.onMouseLeave = onMouseLeave;
  }

  if (hasFocusAction) {
    cloneProps.onFocus = handleTriggerFocus;
    cloneProps.onBlur = handleTriggerBlur;
    cloneProps.onKeyDown = handleKeyDown;
  }

  if (hasClickAction) {
    cloneProps.onClick = handleClickTrigger;
  }

  if (hasContextMenuAction) {
    cloneProps.onContextMenu = handleContextMenuTrigger;
  }

  return (
    <>
      {!!child && (
        <Tooltip title={title} disabled={visible}>
          {React.cloneElement(child, cloneProps)}
        </Tooltip>
      )}
      <Popup
        backgroundColor={backgroundColor}
        maskColor={maskColor}
        zIndex={zIndex}
        type={type}
        overlay={overlay}
        visible={!!visible}
        placement={hasContextMenuAction ? 'bottomLeft' : placement}
        triggerNode={getTriggerNode()}
        overlayStyle={overlayStyle}
        overlayClassName={overlayClassName}
        mask={hasContextMenuAction || !!mask}
        getContainer={getContainer}
        offsetX={hasContextMenuAction ? coord.x + offsetX! : offsetX!}
        offsetY={hasContextMenuAction ? coord.y + offsetY! : offsetY!}
        pageX={pageX}
        pageY={pageY}
        onMouseEnter={hasHoverAction ? handleEnterOverlay : undefined}
        onMouseLeave={hasHoverAction ? handleLeaveOverlay : undefined}
        onClickOuter={hasClickAction ? handleClickOverlayOuter : undefined}
        onClickMask={hasContextMenuAction ? handleClickMask : undefined}
        testid={testid}
        delayDestroy={delayDestroy}
        disableDomAlign={disableDomAlign}
        overlayAutoAlign={overlayAutoAlign}
        overlayAutoAlignDelay={overlayAutoAlignDelay}
        role={role}
      />
    </>
  );
});

Trigger.defaultProps = {
  action: [],
  type: 'popup',
  placement: 'bottomLeft',
  overlayStyle: {},
  defaultVisible: false,
  offsetX: 0,
  offsetY: 0,
};
