import {
  useContext,
  useMemo,
  useRef,
  useLayoutEffect,
  useState,
  useCallback,
  useEffect,
} from 'react';
import { uuid, debounce } from '../../utils';
import {
  ControllerContext,
  MenuKeyContext,
  PathContext,
  HotkeyPositionContext,
} from './contexts';
import { IUseMenuKeyContext, ISimpleTreeNode } from './interface';

/**
 * 获取 Controller
 *
 * @param {String} name Component's name
 * @return {Controller | never}
 */
export const useController = (name: string) => {
  const ctrl = useContext(ControllerContext);
  if (!ctrl) {
    throw new Error(`${name} 组件必须为 Menu 组件的子组件`);
  }
  return ctrl;
};

export const useHotkeyController = (name: string) => {
  const ctrl = useContext(HotkeyPositionContext);
  if (!ctrl) {
    throw new Error(`${name} 组件必须为 Menu 组件的子组件`);
  }
  return ctrl;
};

/**
 * 获取当前 key 路径
 * @param {string} key
 * @return {string}
 */
export const usePath = (key: string): string => {
  const parentPath = useContext(PathContext);
  return [...parentPath.split('::'), key].filter((k) => k).join('::');
};

/**
 * 处理menukey
 *
 * @param {string} key
 * @return {IUseMenuKeyContext}
 */
export const useMenuKeyContext = (key?: string): IUseMenuKeyContext => {
  const menuKeys = useRef<Record<string, any>>({});
  const ctx = useContext(MenuKeyContext);

  // 获取 key，如果没传，自动生成一个uuid
  const id = useMemo(() => `menu-key-${uuid()}`, []);
  const menuKey = key || id;

  // key的添加和删除方法
  const menuKeyCtxValue = useMemo(() => {
    return {
      addKey: (k: string, keys: Record<string, any>) => {
        menuKeys.current[k] = keys || {};
      },
      delKey: (k: string) => {
        delete menuKeys.current[k];
      },
    };
  }, []);

  if (ctx) {
    ctx.addKey(menuKey, menuKeys.current);
  }

  // 添加和删除
  useLayoutEffect(() => {
    return () => {
      if (ctx) {
        ctx.delKey(menuKey);
      }
    };
  }, [menuKey, ctx]);

  return [menuKeyCtxValue, menuKey, menuKeys.current];
};

// 获取鼠标最后停留位置
let mousePosition = 0;
export const useMousePosition = () => {
  return {
    getMousePosition: () => mousePosition,
    mousePosition,
    setMousePosition: (v: number) => (mousePosition = v),
  };
};

/**
 * 菜单快捷键 有滚动条是 滚动到合适位置
 */

export const useHotKeyScrollRef = (
  menuKey: string,
  isHotkeyType?: boolean,
  hotkeyPosition?: ISimpleTreeNode | null,
) => {
  const ref = useRef<HTMLElement>();
  const el = ref.current;
  useEffect(() => {
    if (
      hotkeyPosition &&
      menuKey === hotkeyPosition.menuKey &&
      el &&
      isHotkeyType
    ) {
      if (el.scrollIntoView && typeof el.scrollIntoView === 'function') {
        const { top, position } = el.style;
        el.style.position = 'relative';
        el.style.top = hotkeyPosition.eventKey === 'arrowdown' ? '4px' : '-4px';
        el.scrollIntoView({ block: 'nearest', inline: 'nearest' });
        el.style.top = top;
        el.style.position = position;
      }
    }
  }, [hotkeyPosition, menuKey]);

  return ref;
};

export const useRegisterValidNode = (
  menuId: string,
  shouldSelectFirstItem: boolean,
  openedMenuKeys: string[],
) => {
  const eleMap = useRef<{ [id: string]: ISimpleTreeNode }>({});
  const hotKeyPath = useRef<ISimpleTreeNode | null>();
  const validNodes = useRef<ISimpleTreeNode[]>([]);
  const [hotkeyPosition, setHotkeyPosition] = useState<ISimpleTreeNode | null>(
    null,
  );

  // 获取当前节点挂载的父级节点
  const getParentEle = useCallback(
    (
      nodes: ISimpleTreeNode[],
      parentPathSteps: string[],
    ): ISimpleTreeNode | undefined => {
      const el = nodes.find((o) => o.menuKey === parentPathSteps[0]);
      if (parentPathSteps.length === 1) {
        return el;
      } else if (el) {
        return getParentEle(el.children || [], parentPathSteps.slice(1));
      }
      return undefined;
    },
    [],
  );

  const selectFirstItem = useCallback(() => {
    if (!hotkeyPosition && validNodes.current.length > 0) {
      setHotkeyPosition(validNodes.current[0]);
      hotKeyPath.current = validNodes.current[0];
    }
  }, [hotkeyPosition, setHotkeyPosition]);

  // 根据dom 实际渲染情况，重绘 menuTree 方便快捷键调用
  const getHotkeyPosition = useCallback(
    debounce((openedKeys: string[]) => {
      const nodes: Array<{
        path: string | null;
        index: number;
        offset: number;
      }> = [];
      let prevTop: number | null;
      let offset = 0;
      let nodeIndex = 0;
      document
        .querySelectorAll(`*[data-menuid=${menuId}]`)
        .forEach((o: Element) => {
          const { top } = o.getBoundingClientRect();
          if (prevTop === top) {
            offset += 1;
          } else {
            prevTop = top;
            offset = 0;
            nodeIndex += 1;
          }
          nodes.push({
            path: o.getAttribute('data-path'),
            index: nodeIndex,
            offset,
          });
        });
      let nodeTree: ISimpleTreeNode[] = [];
      try {
        nodes
          .sort((a, b): number => {
            if (a.path && b.path) {
              return (
                a.path.split('::').length - b.path.split('::').length ||
                a.index - b.index ||
                a.offset - b.offset
              );
            }
            return -1;
          })
          .forEach((o) => {
            const pathAttr = o.path;
            if (pathAttr) {
              const path = pathAttr.split('::');
              const menuKey = path[path.length - 1];
              if (!eleMap.current[menuKey]) return;
              const el = eleMap.current[menuKey];
              if (path.length === 1) {
                nodeTree = nodeTree.concat({
                  ...el,
                  index: o.index,
                  offset: o.offset,
                });
              } else {
                const parentPathSteps = path.slice(0, -1);
                const parentEle = getParentEle(nodeTree, parentPathSteps);
                if (parentEle) {
                  const children = parentEle.children || [];
                  const elIndex = { ...el, index: children.length };
                  parentEle.children = children.concat(elIndex);
                  // 判断节点是否是在开一个新的子层级
                  const isOpenSubMenu =
                    hotKeyPath.current &&
                    hotKeyPath.current.path.split('::').length !==
                      elIndex.path.split('::').length;
                  // 高亮subMenu 打开时第一个节点
                  if (
                    isOpenSubMenu &&
                    parentEle.children.length === 1 &&
                    parentPathSteps.join('::') === openedKeys.join('::')
                  ) {
                    setHotkeyPosition(elIndex);
                    hotKeyPath.current = elIndex;
                  }
                } else {
                  throw new Error(
                    `菜单实例 ${menuId} 子节点 ${path} 找不到父节点 ${parentPathSteps.join(
                      '::',
                    )}`,
                  );
                }
              }
            } else {
              throw new Error(
                `菜单实例 ${menuId} 存在节点缺少【data-path】属性`,
              );
            }
          });
      } catch (error) {
        throw new Error(`菜单实例注册失败，${error}`);
      }
      validNodes.current = nodeTree;
      // 默认选中第一个节点
      if (shouldSelectFirstItem) {
        selectFirstItem();
      }
    }, 200),
    [shouldSelectFirstItem, hotkeyPosition, setHotkeyPosition, selectFirstItem],
  );

  const registerValidNode = useCallback(
    (node: ISimpleTreeNode) => {
      if (eleMap.current[node.menuKey]) return;
      // 如果是新增节点触发重绘
      eleMap.current[node.menuKey] = node;
      getHotkeyPosition(openedMenuKeys);
    },
    [openedMenuKeys],
  );

  const unRegisterValidNode = useCallback(
    (node: ISimpleTreeNode) => {
      delete eleMap.current[node.menuKey];
      getHotkeyPosition(openedMenuKeys);
    },
    [openedMenuKeys],
  );

  return {
    registerValidNode,
    unRegisterValidNode,
    hotkeyPosition,
    setHotkeyPosition,
    selectFirstItem,
    validNodes,
    hotKeyPath,
  };
};
