import React, { Component, PureComponent } from 'react';
import reactCSS from 'reactcss';

const DEFAULT_ARROW_OFFSET = 1;

const UP_KEY_CODE = 38;
const DOWN_KEY_CODE = 40;
const ENTER_KEY_CODE = 13;
const VALID_KEY_CODES = [UP_KEY_CODE, DOWN_KEY_CODE];
const isValidKeyCode = (keyCode) => VALID_KEY_CODES.indexOf(keyCode) > -1;
const getNumberValue = (value) => Number(String(value).replace(/%/g, ''));

export class EditableInput extends (PureComponent || Component) {
  constructor(props) {
    super();

    this.state = {
      value: String(props.value).toUpperCase(),
      blurValue: String(props.value).toUpperCase(),
    };
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.value !== this.state.value &&
      (prevProps.value !== this.props.value ||
        prevState.value !== this.state.value)
    ) {
      if (this.input === document.activeElement) {
        this.setState({ blurValue: String(this.props.value).toUpperCase() });
      } else {
        this.setState({
          value: String(this.props.value).toUpperCase(),
          blurValue:
            !this.state.blurValue && String(this.props.value).toUpperCase(),
        });
      }
    }
  }

  componentWillUnmount() {
    this.unbindEventListeners();
  }

  getValueObjectWithLabel(value) {
    return {
      [this.props.label]: value,
    };
  }

  handleBlur = (e) => {
    // 给 e 加一个 _blur 属性
    // 用于 onChangeComplete
    e._blur = true;

    this.setUpdatedValue(this.state.value, e);

    if (this.state.blurValue) {
      this.setState({ value: this.state.blurValue, blurValue: null });
    }
  };

  handleChange = (e) => {
    this.setUpdatedValue(e.target.value, e);
  };

  getArrowOffset() {
    return this.props.arrowOffset || DEFAULT_ARROW_OFFSET;
  }

  handleKeyDown = (e) => {
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();

    // In case `e.target.value` is a percentage remove the `%` character
    // and update accordingly with a percentage
    // https://github.com/casesandberg/react-color/issues/383
    const value = getNumberValue(e.target.value);

    if (!isNaN(value) && isValidKeyCode(e.keyCode)) {
      const offset = this.getArrowOffset();
      const updatedValue =
        e.keyCode === UP_KEY_CODE ? value + offset : value - offset;
      this.setUpdatedValue(updatedValue, e);
    }

    // Enter to blur
    if (e.keyCode === ENTER_KEY_CODE) {
      e.target.blur();
    }
  };

  setUpdatedValue(value, e) {
    const onChangeValue = this.props.label
      ? this.getValueObjectWithLabel(value)
      : value;

    // 给 e 加一个 _input 属性
    // 表明这次的 onChange 由输入框触发
    e._input = true;
    this.props.onChange && this.props.onChange(onChangeValue, e);

    this.setState({ value });
  }

  handleDrag = (e) => {
    if (this.props.dragLabel) {
      const newValue = Math.round(this.props.value + e.movementX);
      if (newValue >= 0 && newValue <= this.props.dragMax) {
        this.props.onChange &&
          this.props.onChange(this.getValueObjectWithLabel(newValue), e);
      }
    }
  };

  handleMouseDown = (e) => {
    if (this.props.dragLabel) {
      e.preventDefault();
      this.handleDrag(e);
      window.addEventListener('mousemove', this.handleDrag);
      window.addEventListener('mouseup', this.handleMouseUp, { capture: true });
    }
  };

  handleMouseUp = () => {
    this.unbindEventListeners();
  };

  unbindEventListeners = () => {
    window.removeEventListener('mousemove', this.handleDrag);
    window.removeEventListener('mouseup', this.handleMouseUp, { capture: true });
  };

  blockEvent = (e) => {
    e.stopPropagation();
  };

  render() {
    const styles = reactCSS(
      {
        default: {
          wrap: {
            position: 'relative',
          },
        },
        'user-override': {
          wrap:
            this.props.style && this.props.style.wrap
              ? this.props.style.wrap
              : {},
          input:
            this.props.style && this.props.style.input
              ? this.props.style.input
              : {},
          label:
            this.props.style && this.props.style.label
              ? this.props.style.label
              : {},
        },
        'dragLabel-true': {
          label: {
            cursor: 'ew-resize',
          },
        },
      },
      {
        'user-override': true,
      },
      this.props,
    );

    return (
      <div style={styles.wrap}>
        <input
          style={styles.input}
          ref={(input) => {
            this.input = input;
          }}
          value={this.state.value}
          onKeyDown={this.handleKeyDown}
          onChange={this.handleChange}
          onBlur={this.handleBlur}
          onMouseDown={this.blockEvent}
          placeholder={this.props.placeholder}
          spellCheck="false"
          data-testid="custom-color-input"
        />
        {this.props.label && !this.props.hideLabel ? (
          <span style={styles.label} onMouseDown={this.handleMouseDown}>
            {this.props.label}
          </span>
        ) : null}
      </div>
    );
  }
}

export default EditableInput;
