import React, { CSSProperties, useCallback } from 'react';
import styled, { keyframes } from 'styled-components';
import { SmallScreenNormal } from '@ali/we-icon';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { PortalComponent } from '../portal-component';

interface FullScreenProps {
  /**
   * z-index
   */
  zIndex?: number;

  style?: CSSStyleSheet;
  /**
   * logo 图标
   */
  logo?: React.ReactElement;
  /**
   * 标题
   */
  title: React.ReactElement | string;
  /**
   * 标题边上描述文案
   */
  headerContent?: React.ReactElement | string;
  /**
   * 右边操作区域
   */
  extra?: React.ReactElement | string;
  /**
   * 是否显示
   */
  visible: boolean;
  /**
   * visible 变更回调
   */
  onVisibleChange?: (visible: boolean) => void;
  /**
   * testid
   */
  testid?: string;

  /**
   * 挂载容器
   */
  getContainer?: () => HTMLElement;
  /**
   * 头部样式复写
   */
  headerStyle?: CSSProperties;
  /**
   * 内容区样式复写
   */
  bodyStyle?: CSSProperties;
}

const enterTime = 250;
const leaveTime = 250;

export const FullScreen: React.FC<FullScreenProps> = React.memo((props) => {
  const {
    zIndex,
    style,
    logo,
    title,
    headerContent,
    extra,
    children,
    visible,
    onVisibleChange,
    testid,
    getContainer,
    headerStyle,
    bodyStyle,
  } = props;

  const stopEvent = useCallback((e: React.MouseEvent) => {
    e.stopPropagation();
    // e.preventDefault();  如果有这个 会影响 编辑区域的 input 选中等行为
  }, []);

  return (
    <PortalComponent visible={!!visible} getContainer={getContainer}>
      <FullScreenWrap data-testid={testid}>
        <CSSTransitionGroup
          transitionName="full-screen-transition"
          transitionEnter={true}
          transitionEnterTimeout={enterTime}
          transitionLeave={true}
          transitionLeaveTimeout={leaveTime}
        >
          {visible && (
            <div
              className="full-screen-box"
              style={{
                zIndex,
                ...style,
              }}
              onClick={stopEvent}
              onMouseDown={stopEvent}
            >
              <div className="full-screen-box-header" style={headerStyle}>
                <div className="full-screen-box-header-wrap">
                  <div className="full-screen-box-header-left">
                    {logo && (
                      <div className="full-screen-box-header-logo">{logo}</div>
                    )}
                    <div className="full-screen-box-header-title">{title}</div>
                    {headerContent}
                  </div>
                  <div className="full-screen-box-header-right">{extra}</div>
                </div>
                <div
                  className="full-screen-box-header-close"
                  data-testid={`${testid || ''}_close`}
                  onClick={() => onVisibleChange && onVisibleChange(false)}
                >
                  <div className="full-screen-box-header-close-icon-button">
                    <SmallScreenNormal />
                  </div>
                </div>
              </div>
              <div className="full-screen-box-body" style={bodyStyle}>
                {children}
              </div>
            </div>
          )}
        </CSSTransitionGroup>
      </FullScreenWrap>
    </PortalComponent>
  );
});

FullScreen.defaultProps = {
  zIndex: 999,
};

const scaleUpCenter = keyframes`
 0% {

    transform: translateZ(-80px);
    opacity: 0;
  }
  100% {

    transform: translateZ(0);
    opacity: 1;
  }
`;

const scaleDownCenter = keyframes`
    0% {
    transform: translateZ(0);
    opacity: 1;
  }
  100% {
    transform: translateZ(-80px);
    opacity: 0;
  }
`;

const FullScreenWrap = styled.div`
  .full-screen-box {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f5f6f8;
    display: flex;
    flex-direction: column;
    .full-screen-box-header {
      height: 48px;
      border-bottom: 1px solid rgba(126, 134, 142, 0.16);
      display: flex;
      padding: 8px;
      background-color: #fff;
      box-sizing: border-box;
      .full-screen-box-header-close {
        font-size: 20px;
        padding: 0 20px 0 12px;
        border-left: 1px solid rgba(126, 134, 142, 0.16);
        display: flex;
        align-items: center;
        .full-screen-box-header-close-icon-button {
          width: 28px;
          height: 28px;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 4px;
          &:hover {
            background-color: rgba(25, 31, 37, 0.08);
          }
          &:active {
            background-color: rgba(25, 31, 37, 0.16);
          }
          .weicon {
            font-size: 20px;
          }
        }
      }
      .full-screen-box-header-wrap {
        display: flex;
        flex: 1;
        padding-left: 6px;
        .full-screen-box-header-left {
          flex: 1;
          display: flex;
          align-items: center;
          .full-screen-box-header-logo {
            margin-right: 8px;
          }
          .full-screen-box-header-title {
            font-size: 14px;
            color: #171a1d;
            margin-right: 9px;
            font-weight: 600;
          }
        }
        .full-screen-box-header-right {
          display: flex;
          align-items: center;
          font-size: 12px;
        }
      }
    }
    .full-screen-box-body {
      flex: 1;
    }
    &.full-screen-transition-enter {
      animation: ${scaleUpCenter} ${enterTime}ms
        cubic-bezier(0.39, 0.575, 0.565, 1);
    }
    &.full-screen-transition-leave {
      animation: ${scaleDownCenter} ${leaveTime}ms
        cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    }
  }
`;
