import React from 'react';
import Form, { useForm } from 'rc-field-form';
import type { FormInstance } from 'rc-field-form';

export interface FormProps {
  /**
   * 控制表单的整体布局方式
   */
  layout?: 'horizontal' | 'vertical';

  /**
   * label 占据的 span 数，总 span 为 24
   * Form.Item 可以覆盖这个设置
   */
  labelSpan?: number;

  /**
   * form 实例
   */
  form?: FormInstance;

  /**
   * 初始值
   */
  initialValues?: object;

  /**
   * 提交表单并且数据验证成功以后的回调
   */
  onFinish?: (values: object) => void;

  /**
   * 提交表单，数据验证失败以后的回调
   */
  onFinishFailed?: (errorInfo: any) => void;

  /**
   * 字段值更新时的回调
   */
  onValuesChange?: (values: object) => void;
}

export type FormContext = Pick<FormProps, 'layout' | 'labelSpan'>;

const defaultValueForContext: FormContext = {
  layout: 'horizontal',
};

export const FormContext = React.createContext<FormContext>(
  defaultValueForContext
);

const Main: React.ForwardRefRenderFunction<FormInstance, FormProps> = (
  {
    form,
    initialValues,
    onFinish,
    onFinishFailed,
    onValuesChange,
    children,
    labelSpan,
    ...rest
  },
  ref
) => {
  const { layout } = { ...defaultValueForContext, ...rest };

  return (
    <FormContext.Provider value={{ layout, labelSpan }}>
      <Form
        onValuesChange={onValuesChange}
        onFinishFailed={onFinishFailed}
        onFinish={onFinish}
        initialValues={initialValues}
        form={form}
        ref={ref}
      >
        {children}
      </Form>
    </FormContext.Provider>
  );
};

export default Main;

export { useForm };

export type { FormInstance };
