import React from 'react';
import { IThemeProps } from './interface';
import { ClearItem, ThemeItem } from './styled';

export const Theme: React.FC<IThemeProps> = React.memo((props) => {
  const { theme } = props;
  const isEmpty = theme.iconColors.length === 0;

  return isEmpty ? (
    <ClearItem />
  ) : (
    <ThemeItem>
      {
        theme.iconColors.map((iconColor) => {
          return <i key={iconColor} style={{ backgroundColor: iconColor }} />;
        })
      }
    </ThemeItem>
  );
});
