import React, { useState, useRef, useEffect, useMemo } from 'react';
import ReactDOM from 'react-dom';
import { ContextMenu } from './ContextMenu';

export const useContextMenuRef = <
HE extends HTMLElement = HTMLElement
>(menuNode: React.ReactElement): React.RefObject<HE> => {
  const ctxMenuRef = useRef<HE>(null);
  const [visible, setVisible] = useState(false);
  const [position, setPosition] = useState({ x: 0, y: 0 });

  const oRoot = useMemo(() => document.createElement('div'), []);

  useEffect(() => {
    /**
     * 右键菜单操作
     */
    const handleContextMenu = (e: MouseEvent): void => {
      e.preventDefault();
      setPosition({ x: e.clientX, y: e.clientY });
      setVisible(true);
    };

    const oElem = ctxMenuRef.current;

    if (oElem) {
      oElem.addEventListener('contextmenu', handleContextMenu);
    }

    return (): void => {
      if (oElem) {
        oElem.removeEventListener('contextmenu', handleContextMenu);
      }
    };
  }, []);

  useEffect(() => {
    ReactDOM.render(
      <ContextMenu
        position={position}
        visible={visible}
        onVisibleChange={setVisible}
      >
        {menuNode}
      </ContextMenu>,
      oRoot,
    );
  }, [visible, position, setVisible, menuNode, oRoot]);

  return ctxMenuRef;
};
