/* eslint-disable max-lines-per-function */
import React, { CSSProperties, useRef, useCallback } from 'react'

import {
  ArrowUpTriangleNormal,
  ArrowDownTriangleNormal,
} from '../../components/icons'

import {
  formatNumber,
  ENumberStrategy,
  addFloat,
  needsRounding,
  classnames,
} from '../../utils'

import {
  InputNumberInner,
  InputStyled,
  Adjuster,
  IconWrapper,
  UnitWrapper,
  NumberInputWrap,
} from './styled'
import { useControlledValue } from '../../hooks'
import { IInputHandle } from '../input/Input'

export interface INumberInputProps {
  /**
   * 增加或减少的步距
   */
  step: number
  /**
   * 允许输入的最小值
   */
  min: number
  /**
   * 允许输入的最大值
   */
  max: number
  /**
   * 单位,如cm
   */
  unit?: string
  /**
   * 数值的精度位数，默认0位小数
   */
  accuracy?: number
  /**
   * 受控模式下数值
   */
  value?: number | string
  /**
   * 默认值
   */
  defaultValue?: number
  /**
   * 内容变化的回调
   */
  onChange?: (value: number | string) => void
  /**
   * 是否禁用
   */
  disabled?: boolean
  /**
   * 类名
   */
  className?: string
  /**
   * 边框外层DIV的样式
   */
  style?: React.CSSProperties
  /**
   * 提示
   */
  placeholder?: string
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string
}

const iconStyle: CSSProperties = { width: 8, height: 8, lineHeight: 12 }

export const NumberInput: React.FC<INumberInputProps> = (props) => {
  const {
    step,
    min,
    max,
    accuracy = 0,
    defaultValue,
    unit,
    onChange,
    disabled,
    className,
    style,
    placeholder = '',
    testid,
  } = props
  const [value, setValue] = useControlledValue<number | string | undefined>(
    props.value,
    defaultValue
  )
  const inputRef = useRef<IInputHandle>()
  // 上一个合法值，用于解析得到非法值的恢复
  const lastVal = useRef<number>(min)

  const handleChange = useCallback(
    (e: string | number) => {
      if (onChange) {
        onChange(e)
      }
      setValue(e)
    },
    [onChange, setValue]
  )

  const handleIncrease = useCallback(
    (e: React.MouseEvent<HTMLSpanElement>) => {
      e.preventDefault()

      const addResult = addFloat(Number(value), step)
      const newValue = formatNumber(addResult, accuracy, ENumberStrategy.FLOOR)
      if (newValue <= max) {
        lastVal.current = newValue
        handleChange(newValue)
      }
    },
    [accuracy, handleChange, max, step, value]
  )

  const handleDecrease = useCallback(
    (e: React.MouseEvent<HTMLSpanElement>) => {
      e.preventDefault()

      const addResult = addFloat(Number(value), -step)
      const newValue = formatNumber(addResult, accuracy, ENumberStrategy.FLOOR)
      if (newValue >= min) {
        lastVal.current = newValue
        handleChange(newValue)
      }
    },
    [accuracy, handleChange, min, step, value]
  )

  const handleInputBlur = useCallback(() => {
    // parse value
    let parsedValue = Number(value)

    if (isNaN(parsedValue)) {
      parsedValue = lastVal.current
    } else if (parsedValue > max) {
      parsedValue = max
    } else if (parsedValue < min) {
      parsedValue = min
    } else if (needsRounding(value!, accuracy)) {
      parsedValue = formatNumber(parsedValue, accuracy, ENumberStrategy.FLOOR)
    }

    lastVal.current = parsedValue
    handleChange(parsedValue)
  }, [value, accuracy, lastVal, max, min, handleChange])

  const handleInputEnter = useCallback(() => {
    if (inputRef.current) {
      inputRef.current.blur()
    }
  }, [inputRef])

  const handleInputChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      handleChange(e.target.value)
    },
    [handleChange]
  )

  const val = Number(value)

  return (
    <NumberInputWrap>
      <InputNumberInner>
        <InputStyled
          value={`${value === undefined ? '' : value}`}
          style={style}
          disabled={disabled}
          ref={inputRef}
          // className 应放在哪里？
          className={className}
          onChange={handleInputChange}
          onBlur={handleInputBlur}
          onEnter={handleInputEnter}
          placeholder={placeholder}
          testid={testid ? `NumberInput-${testid}` : 'input-NumberInput'}
        />
        <Adjuster>
          <IconWrapper
            isUpper
            onClick={handleIncrease}
            data-testid="arrow-increase"
          >
            <ArrowUpTriangleNormal
              testid={testid}
              style={iconStyle}
              className={classnames({ disabled: val + step > max })}
            />
          </IconWrapper>
          <IconWrapper
            isUpper={false}
            onClick={handleDecrease}
            data-testid="arrow-decrease"
          >
            <ArrowDownTriangleNormal
              testid={testid}
              style={iconStyle}
              className={classnames({ disabled: val - step < min })}
            />
          </IconWrapper>
        </Adjuster>
      </InputNumberInner>
      {unit && <UnitWrapper>{unit}</UnitWrapper>}
    </NumberInputWrap>
  )
}
