/* eslint-disable @typescript-eslint/explicit-function-return-type */
import styled from 'styled-components';
import { withTheme } from '../theme';
import { Ellipsis } from '../../utils/styled.mixin';

export const ContainerRoot = withTheme(styled.div`
  position: fixed;
  left: 0px;
  top: calc(16px + env(safe-area-inset-top));
  width: 100%;
  box-sizing: border-box;
  z-index: ${({ theme }) => theme.ui.zIndexMessage};
  font-size: ${({ theme }) => theme.ui.fontSize};
  color: ${({ theme }) => theme.ui.colorText4};
  * {
    box-sizing: border-box;
  }
`);

export const Container = withTheme(styled.div`
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  text-align: center;
`);


export const MessageBox = withTheme(styled.div`
  margin-bottom: ${({ theme }) => theme.ui.margin};
  .animated {
    transition: all .24s cubic-bezier(.645,.045,.355,1);
  }
  &.message-enter {
    .animated {
      opacity: 0;
      transform: translate(0, -100%);
    }
  }

  &.message-enter-active {
    .animated {
      opacity: 1;
      transform: translate(0, 0);
    }
  }

  &.message-leave {
    .animated {
      opacity: 1;
      transform: translate(0, 0);
    }
  }

  &.message-leave-active {
    .animated {
      opacity: 0;
      transform: translate(0, -100%);
    }
  }
`);


export const MessageWrap = withTheme(styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 36px;
  padding: 8px 10px 8px 8px;
  background-color: rgba(69, 72, 74, 0.9);
  box-shadow: ${({ theme }) => theme.ui.boxShadow};
  border: 1px solid ${({ theme }) => theme.ui.borderColor};
  border-radius: 4px;
  font-size: 13px;
  width: fit-content;
  max-width: 445px;

  .icon {
    margin-right: ${({ theme }) => theme.ui.marginSmall};
    font-size: 20px;
    &.success-icon {
      color: ${({ theme }) => theme.ui.successColor};
    }
    &.info-icon, &.loading-icon {
      color: ${({ theme }) => theme.ui.infoColor};
    }
    &.error-icon {
      color: ${({ theme }) => theme.ui.errorColor};
    }
    &.warning-icon {
      color: ${({ theme }) => theme.ui.warningColor};
    }
  }
`);

export const MessageContent = withTheme(styled.span`
  line-height: 1.5;
  color: rgba(238, 242, 248, 1);
  ${Ellipsis}
`);

export const MessageFlex = withTheme(styled.span`
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
`);
