export const isEscHotKey = (event: KeyboardEvent) => {
  const { key, keyCode } = event
  if (!key || !keyCode) {
    return false
  }

  if (key.toLowerCase() === 'escape' || keyCode === 27) {
    return true
  }

  return false
}

export const isEnterHotKey = (event: KeyboardEvent) => {
  const { key, keyCode } = event
  if (!key || !keyCode) {
    return false
  }

  if (key.toLowerCase() === 'enter' || keyCode === 13) {
    return true
  }

  return false
}

export const isShiftEnterHotKey = (event: KeyboardEvent) => {
  const { key, keyCode } = event
  if (!key || !keyCode) {
    return false
  }

  if (event.shiftKey && (key.toLowerCase() === 'enter' || keyCode === 13)) {
    return true
  }

  return false
}

export const isUpHotKey = (event: KeyboardEvent) => {
  const { key, keyCode } = event
  if (!key || !keyCode) {
    return false
  }
  if (key.toLowerCase() === 'up arrow' || keyCode === 38) {
    return true
  }
  return false
}
export const isDownHotKey = (event: KeyboardEvent) => {
  const { key, keyCode } = event
  if (!key || !keyCode) {
    return false
  }
  if (key.toLowerCase() === 'down arrow' || keyCode === 40) {
    return true
  }
  return false
}


export const isComposing = (event: React.KeyboardEvent) => {
  return event.nativeEvent?.isComposing;
};
